const initState = {
  accounts: [],
  transaction_journal_reports: [],
  account_types: [],
  locations: [],
  projects: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function transactionJournalReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_TRANSACTION_JOURNAL_REPORTS_REQUEST": {
      return {
        ...state,
        //transaction_journal_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_TRANSACTION_JOURNAL_REPORTS_SUCCESS": {
      return {
        ...state,
        transaction_journal_reports: action.transaction_journal_reports,
        // current_page: action.current_page,
        // page_size: action.page_size,
        // total: action.total,
        filter: action.filter,
        search: action.search,
        loading: false
      };
    }
    case "GET_TRANSACTION_JOURNAL_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_TRANSACTION_JOURNAL_REPORT_REQUEST": {
      return {
        ...state,
        errorField: null
      };
    }

    case "GET_TRANSACTION_JOURNAL_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        accounts: action.accounts,
        account_types: action.account_types,
        locations: action.locations,
        projects: action.projects
      };
    }
    case "GET_TRANSACTION_JOURNAL_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
