
const initState = {
  receivable_payment_reports: [],
  locations: [],
  customers: [],
  cash_banks: [],
  sort_lists: [],
  messages: [],
  search: null,
  filter: null,
  filter_select: [],
  loading: false,
  success: null,
  error: null,
  errorField: null,
};

export default function receivablePaymentReportsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_RECEIVABLE_PAYMENT_REPORTS_REQUEST': {
      return {
        ...state,
        //receivable_payment_reports: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null,
      };
    }
    case 'GET_RECEIVABLE_PAYMENT_REPORTS_SUCCESS': {
      return {
        ...state,
        receivable_payment_reports: action.receivable_payment_reports,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_RECEIVABLE_PAYMENT_REPORTS_ERROR': {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      }
    }
    case 'CREATE_RECEIVABLE_PAYMENT_REPORT_REQUEST': {
      return {
        ...state,
        receivable_payment_report: null,
        errorField: null,
      };
    }

    case 'GET_RECEIVABLE_PAYMENT_REPORT_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        customers: action.customers,
        cash_banks: action.cash_banks,
        sort_lists: action.sort_lists,
        messages: action.messages,
      };
    }
    case 'GET_RECEIVABLE_PAYMENT_REPORT_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
