const initState = {
  first_profit_loss_reports: [],
  second_profit_loss_reports: [],
  locations: [],
  departments: [],
  projects: [],
  company: null,
  filter: null,
  filter_select: [],
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function profitLossComparativeReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_PROFIT_LOSS_COMPARATIVE_REPORTS_REQUEST": {
      return {
        ...state,
        //profit_loss_comparative_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_PROFIT_LOSS_COMPARATIVE_REPORTS_SUCCESS": {
      return {
        ...state,
        first_profit_loss_reports: action.first_profit_loss_reports,
        second_profit_loss_reports: action.second_profit_loss_reports,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PROFIT_LOSS_COMPARATIVE_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    // case "CREATE_PROFIT_LOSS_COMPARATIVE_REPORT_REQUEST": {
    //   return {
    //     ...state,
    //     profit_loss_comparative_report: null,
    //     errorField: null
    //   };
    // }

    case "GET_PROFIT_LOSS_COMPARATIVE_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        departments: action.departments,
        projects: action.projects,
        company: action.company
      };
    }
    case "GET_PROFIT_LOSS_COMPARATIVE_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
