import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPurchaseInvoices({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [purchaseInvoices, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "purchases/purchase-invoices?search=" + search + "&page=" + page
            ),
            call(get, "purchases/purchase-invoices/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "purchases/purchase-invoices?" + filter + "&page=" + page),
            call(get, "purchases/purchase-invoices/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "purchases/purchase-invoices?search=" + search),
          call(get, "purchases/purchase-invoices/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "purchases/purchase-invoices?" + filter),
          call(get, "purchases/purchase-invoices/foreign-keys")
        ]);

    // console.log(purchaseInvoices);

    // Jika sukses get data purchaseInvoices
    if (purchaseInvoices.status === "success") {
      // Jika di API menggunakan pagination
      if (purchaseInvoices.result.current_page && search) {
        yield put({
          type: "GET_PURCHASE_INVOICES_SUCCESS",
          purchaseInvoices: purchaseInvoices.result.data,
          current_page: purchaseInvoices.result.current_page,
          page_size: purchaseInvoices.result.per_page,
          total: purchaseInvoices.result.total,
          search: search
        });
      } else if (purchaseInvoices.result.current_page && filter) {
        yield put({
          type: "GET_PURCHASE_INVOICES_SUCCESS",
          purchaseInvoices: purchaseInvoices.result.data,
          current_page: purchaseInvoices.result.current_page,
          page_size: purchaseInvoices.result.per_page,
          total: purchaseInvoices.result.total,
          filter: filter
        });
      } else if (purchaseInvoices.result.current_page) {
        yield put({
          type: "GET_PURCHASE_INVOICES_SUCCESS",
          purchaseInvoices: purchaseInvoices.result.data,
          current_page: purchaseInvoices.result.current_page,
          page_size: purchaseInvoices.result.per_page,
          total: purchaseInvoices.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PURCHASE_INVOICES_SUCCESS",
          purchaseInvoices: purchaseInvoices.result,
          page_size: null,
          total: null
        });
      }
    } else if (purchaseInvoices.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_PURCHASE_INVOICES_ERROR",
        error: { message: purchaseInvoices.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_PURCHASE_INVOICES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_PURCHASE_INVOICE_FOREIGN_KEYS_SUCCESS",
        baseCurrency: foreign.result.base_currency,
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        cashBanks: foreign.result.cash_banks,
        accounts: foreign.result.accounts,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
        prevTrans: foreign.result.purchase_receipts,
        prevTransConfirm: foreign.result.purchase_receipt_info,
        purchaseOrders: foreign.result.purchase_orders
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PURCHASE_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PURCHASE_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Get Items data
export function* getItemsBySupplier({ payload }) {
  const { id_supplier, id_location, date, search } = payload;
  let filter = "items?is_active=1&limit=1";
  filter = search
    ? `${filter}&search=${search}&id_supplier=${id_supplier}&id_location=${id_location}&transaction_date=${date}&item_type=1,2,4`
    : `${filter}&id_supplier=${id_supplier}&id_location=${id_location}&transaction_date=${date}&item_type=1,2,4`;

  try {
    const items = yield call(get, filter);

    if (items.status === "success") {
      yield put({
        type: "GET_FILTER_ITEMS_BY_SUPPLIER_PI_SUCCESS",
        items: items.result
      });
    } else if (items.status === "fail") {
      yield put({
        type: "GET_FILTER_ITEMS_BY_SUPPLIER_PI_ERROR",
        error: { message: items.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_ITEMS_BY_SUPPLIER_PI_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Get Items with discount
export function* getItemDiscount({ payload }) {
  const { body } = payload;

  try {
    const itemDiscount = yield call(
      post,
      "purchases/purchase-invoices/discount-price",
      body
    );
  
    if (itemDiscount.status === "success") {
      yield put({
        type: "GET_DISCOUNT_PI_SUCCESS",
        itemDiscount: itemDiscount.result
      });
    } else if (itemDiscount.status === "fail") {
      yield put({
        type: "GET_DISCOUNT_PI_ERROR",
        itemDiscount: body.purchase_invoice_details,
        error: {
          message: itemDiscount.message
        }
      });
    } else {
      yield put({
        type: "GET_DISCOUNT_PI_ERROR",
        itemDiscount: body.purchase_invoice_details,
        error: {
          message: itemDiscount.message
            ? itemDiscount.message
            : "Check your internet connection"
        }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_DISCOUNT_PI_ERROR",
      itemDiscount: body.purchase_invoice_details,
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Create data
export function* createPurchaseInvoice() {
  try {
    const [transactions, purchaseReceipts, foreign] = yield all([
      call(get, "purchases/purchase-invoices?get_old_trans=1"),
      call(get, "purchases/purchase-receipts?get_old_trans=1&status=2"),
      call(get, "purchases/purchase-invoices/foreign-keys")
    ]);
  
    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PURCHASE_INVOICES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (purchaseReceipts.status === "success") {
      yield put({
        type: "GET_FILTER_PURCHASE_RECEIPTS_SUCCESS",
        purchaseReceipts: purchaseReceipts.result.data,
        current_page: purchaseReceipts.result.current_page,
        page_size: purchaseReceipts.result.per_page,
        total: purchaseReceipts.result.total
      });
    } else if (purchaseReceipts.status === "fail") {
      yield put({
        type: "GET_FILTER_PURCHASE_RECEIPTS_ERROR",
        error: { message: purchaseReceipts.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PURCHASE_RECEIPTS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_PURCHASE_INVOICE_FOREIGN_KEYS_SUCCESS",
        baseCurrency: foreign.result.base_currency,
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        cashBanks: foreign.result.cash_banks,
        accounts: foreign.result.accounts,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
        prevTrans: foreign.result.purchase_receipts,
        prevTransConfirm: foreign.result.purchase_receipt_info,
        purchaseOrders: foreign.result.purchase_orders
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PURCHASE_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PURCHASE_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_PURCHASE_RECEIPTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

