// Store data
export function* storePurchaseInvoice({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "purchases/purchase-invoices", body);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_PURCHASE_INVOICE_SUCCESS",
        success: { message: resultStore.message },
        id_purchase_invoice: resultStore.result.id_purchase_invoice
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_PURCHASE_INVOICE_ERROR",
        error: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_PURCHASE_INVOICE_ERROR",
        error: { message: resultStore.message }
      });
    } else {
      yield put({
        type: "STORE_PURCHASE_INVOICE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_PURCHASE_INVOICE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

