// Update data
export function* updatePurchaseInvoice({ payload }) {
  const { id_purchase_invoice, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "purchases/purchase-invoices/" + id_purchase_invoice,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        put({
          type: "UPDATE_PURCHASE_INVOICE_SUCCESS",
          success: { message: resultUpdate.message },
          id_purchase_invoice: id_purchase_invoice
        }),
        put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: { id_purchase_invoice: id_purchase_invoice }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_PURCHASE_INVOICE_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: {
            id_purchase_invoice: id_purchase_invoice
          }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_PURCHASE_INVOICE_ERROR",
        error: { message: resultUpdate.message }
      });
    } else {
      yield put({
        type: "UPDATE_PURCHASE_INVOICE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_PURCHASE_INVOICE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

