export function* confirmPurchaseInvoice({ payload }) {
  const { id_purchase_invoice } = payload;

  try {
    // POST data ke API
    const confirmResult = yield call(
      putApi,
      "purchases/purchase-invoices/process-transaction/" + id_purchase_invoice
    );

    // Jika sukses
    if (confirmResult.status === "success") {
      // Set session message
      yield all([
        put({
          type: "CONFIRM_PURCHASE_INVOICE_SUCCESS",
          success: { message: confirmResult.message }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: {
            id_purchase_invoice: id_purchase_invoice
          }
        })
      ]);
    } else if (confirmResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_PURCHASE_INVOICE_ERROR",
          error: { message: confirmResult.message }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: { id_purchase_invoice: id_purchase_invoice }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_PURCHASE_INVOICE_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: { id_purchase_invoice: id_purchase_invoice }
        })
      ]);
    }
  } catch (error) {
    yield all([
      put({
        type: "CONFIRM_PURCHASE_INVOICE_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      put({
        type: "EDIT_PURCHASE_INVOICE_REQUEST",
        payload: { id_purchase_invoice: id_purchase_invoice }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

