// Upload Document
export function* uploadPurchaseInvoiceDocument({ payload }) {
  const { id_purchase_invoice, body } = payload;
  let data = {
    ...body,
    id_purchase_invoice
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "purchases/purchase-invoices/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_PURCHASE_INVOICE_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: { id_purchase_invoice }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_PURCHASE_INVOICE_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        }),
        yield put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: { id_purchase_invoice }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_PURCHASE_INVOICE_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        }),
        yield put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: { id_purchase_invoice }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_PURCHASE_INVOICE_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: { id_purchase_invoice }
        })
      ]);
    }

    // }
  } catch (error) {
    yield all([
      yield put({
        type: "UPLOAD_PURCHASE_INVOICE_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_PURCHASE_INVOICE_REQUEST",
        payload: { id_purchase_invoice }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadPurchaseInvoiceDocument({ payload }) {
  const { id_purchase_invoice_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "purchases/purchase-invoices/documents/download/" +
        id_purchase_invoice_document
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// delete document
export function* deletePurchaseInvoiceDocument({ payload }) {
  const { id_purchase_invoice_document, id_purchase_invoice } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "purchases/purchase-invoices/documents/" + id_purchase_invoice_document
      //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_PURCHASE_INVOICE_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: { id_purchase_invoice: id_purchase_invoice }
        })
      ]);
    } else if (deleteResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_PURCHASE_INVOICE_DOCUMENT_ERROR",
          error: { message: deleteResult.message }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: { id_purchase_invoice: id_purchase_invoice }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_PURCHASE_INVOICE_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_PURCHASE_INVOICE_REQUEST",
          payload: { id_purchase_invoice: id_purchase_invoice }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DELETE_PURCHASE_INVOICE_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "EDIT_PURCHASE_INVOICE_REQUEST",
        payload: { id_purchase_invoice: id_purchase_invoice }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_PURCHASE_INVOICES_REQUEST", getPurchaseInvoices),
    yield takeEvery("CREATE_PURCHASE_INVOICE_REQUEST", createPurchaseInvoice),
    yield takeEvery("GET_FILTER_PURCHASE_INVOICES_REQUEST", getTransactions),
    yield takeEvery("GET_DISCOUNT_PI_REQUEST", getItemDiscount),
    yield takeEvery(
      "GET_FILTER_PURCHASE_RECEIPTS_PI_REQUEST",
      getPurchaseReceipts
    ),
    yield takeEvery(
      "GET_FILTER_ITEMS_BY_SUPPLIER_PI_REQUEST",
      getItemsBySupplier
    ),
    yield takeEvery("STORE_PURCHASE_INVOICE_REQUEST", storePurchaseInvoice),
    yield takeEvery("EDIT_PURCHASE_INVOICE_REQUEST", editPurchaseInvoice),
    yield takeEvery("UPDATE_PURCHASE_INVOICE_REQUEST", updatePurchaseInvoice),
    yield takeEvery("SHOW_PURCHASE_INVOICE_REQUEST", showPurchaseInvoice),
    yield takeEvery("CONFIRM_PURCHASE_INVOICE_REQUEST", confirmPurchaseInvoice),
    yield takeEvery("CANCEL_PURCHASE_INVOICE_REQUEST", cancelPurchaseInvoice),
    yield takeEvery(
      "UPLOAD_PURCHASE_INVOICE_DOCUMENT_REQUEST",
      uploadPurchaseInvoiceDocument
    ),
    yield takeEvery(
      "DOWNLOAD_PURCHASE_INVOICE_DOCUMENT_REQUEST",
      downloadPurchaseInvoiceDocument
    ),
    yield takeEvery(
      "DELETE_PURCHASE_INVOICE_DOCUMENT_REQUEST",
      deletePurchaseInvoiceDocument
    )
  ]);
}
