import antdID from 'antd/lib/locale-provider/id_ID';
import appLocaleData from 'react-intl/locale-data/id';
import idMessages from '../locales/id_ID.json';

const idLang = {
  messages: {
    ...idMessages
  },
  antd: antdID,
  locale: 'id-ID',
  data: appLocaleData
};
export default idLang;