const initState = {
  couriers: [],
  courier: null,
  employees: [],
  search: null,
  filter: null,
  loading: false,
  loadingEdit: false,
  loadingSubmit: false,
  loadingFilterEmployees: true,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_courier: null,
};

export default function couriersReducer(state = initState, action) {
  switch (action.type) {
    case "GET_COURIERS_REQUEST": {
      return {
        ...state,
        //couriers: [],
        employees: [],
        loading: true,
        loadingFilterEmployees: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_COURIERS_SUCCESS": {
      return {
        ...state,
        couriers: action.couriers,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_COURIERS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_FILTER_EMPLOYEES_SUCCESS": {
      return {
        ...state,
        employees: action.employees,
        loadingFilterEmployees: false,
      }
    }
    
    case "CREATE_COURIER_REQUEST": {
      return {
        ...state,
        employees : [],
        loading:true,
        loadingFilterEmployees: true,
      };
    }
    case "CREATE_COURIER_SUCCESS": {
      return {
        ...state,
        loading: false,
      };
    }

    case "STORE_COURIER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null,
        id_courier: null,
      };
    }
    case "STORE_COURIER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_courier: action.id_courier
      };
    }
    case "STORE_COURIER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_COURIER_REQUEST": {
      return {
        ...state,
        courier: null,
        employees: [],
        loading: true,
        loadingEdit: true,
        loadingFilterEmployees: true,
        errorField: null
      };
    }
    case "EDIT_COURIER_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        loadingEdit: false,
        courier: action.courier
      };
    }
    case "EDIT_COURIER_ERROR": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_COURIER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_COURIER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_COURIER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_COURIER_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_COURIER_SUCCESS": {
      return {
        ...state,
        loading: false,
        couriers: action.couriers
      };
    }
    case "DEACTIVATE_COURIER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_COURIER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_COURIER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_COURIER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_COURIER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_COURIER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
