const initState = {
  sales_return_detail_reports: [],
  total_amount: null,
  total_discount: null,
  total_qty: null,
  total_tax: null,
  total_item: null,
  locations: [],
  customers: [],
  currencies: [],
  taxes: [],
  cash_banks: [],
  accounts: [],
  cash_flow_types: [],
  payment_categories: [],
  payment_terms: [],
  departments: [],
  projects: [],
  base_currency: null,
  messages: [],
  return_reasons: [],
  search: null,
  filter: null,
  filter_select: [],
  loading: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function salesReturnDetailReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_SALES_RETURN_DETAIL_REPORTS_REQUEST": {
      return {
        ...state,
        //sales_return_detail_reports: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_SALES_RETURN_DETAIL_REPORTS_SUCCESS": {
      return {
        ...state,
        sales_return_detail_reports: action.sales_return_detail_reports,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_SALES_RETURN_DETAIL_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_SALES_RETURN_DETAIL_REPORT_REQUEST": {
      return {
        ...state,
        sales_return_report: null,
        errorField: null
      };
    }

    case "GET_SALES_RETURN_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        customers: action.customers,
        currencies: action.currencies,
        taxes: action.taxes,
        cash_banks: action.cash_banks,
        accounts: action.accounts,
        cash_flow_types: action.cash_flow_types,
        payment_categories: action.payment_categories,
        payment_terms: action.payment_terms,
        departments: action.departments,
        projects: action.projects,
        base_currency: action.base_currency,
        messages: action.messages,
        return_reasons: action.return_reasons
      };
    }
    case "GET_SALES_RETURN_DETAIL_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
