import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getSizes({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [sizes, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "items/sizes?search=" + search + "&page=" + page), 
            call(get, "items/sizes/foreign-keys"), 
          ])
        : // Jika filter
          yield all([
            call(get, "items/sizes?" + filter + "&page=" + page), 
            call(get, "items/sizes/foreign-keys"), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "items/sizes?search=" + search),  
          call(get, "items/sizes/foreign-keys"), 
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "items/sizes?" + filter),
          call(get, "items/sizes/foreign-keys"), 
        ]);

    // console.log(sizes);
    // console.log(accounts);

    // Jika sukses get data sizes
    if (sizes.status === "success") {
      // Jika di API menggunakan pagination
      if (sizes.result.current_page && search) {
        yield put({
          type: 'GET_SIZES_SUCCESS',
          sizes: sizes.result.data,
          current_page: sizes.result.current_page,
          page_size: sizes.result.per_page,
          total: sizes.result.total,
          search: search
        });
      }
      // else if(sizes.result.current_page && filter)
      // {
      //   yield put({
      //     type: 'GET_SIZES_SUCCESS',
      //     sizes: sizes.result.data,
      //     current_page: sizes.result.current_page,
      //     page_size: sizes.result.per_page,
      //     total: sizes.result.total,
      //     filter: filter
      //   });
      // }
      else if (sizes.result.current_page) {
        yield put({
          type: 'GET_SIZES_SUCCESS',
          sizes: sizes.result.data,
          current_page: sizes.result.current_page,
          page_size: sizes.result.per_page,
          total: sizes.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_SIZES_SUCCESS',
          sizes: sizes.result,
          page_size: null,
          total: null
        });
      }
    }
    else if(sizes.status === "fail"){
      yield put({
        type: 'GET_SIZES_ERROR',
        error: sizes
      })
    }
    else {
      yield put({
        type: 'GET_SIZES_ERROR',
        error: {message: "Check your internet connection"}
      })
    }

    // sukses get data foreign key
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_SIZE_FOREIGN_KEYS_SUCCESS',
        units: foreign_keys.result.units
      });
    }
    else{
      yield put({
        type: 'GET_SIZE_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_SIZES_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createSize() {
  try {
    const [foreign_keys] = 
      yield all([
        call(get, "items/sizes/foreign-keys"), 
      ])

    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_SIZE_FOREIGN_KEYS_SUCCESS',
        units: foreign_keys.result.units
      });
    }
    else{
      yield put({
        type: 'GET_SIZE_FOREIGN_KEYS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_SIZE_FOREIGN_KEYS_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeSize({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "items/sizes", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_SIZE_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_SIZE_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_SIZE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_SIZE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editSize({ payload }) {
  //console.log("masuk")
  const { id_size } = payload;

  try {
    const [size, foreign_keys] = yield all([
      call(get, "items/sizes/" + id_size),
      call(get, "items/sizes/foreign-keys"),
    ]);
  
    // Jika sukses
    if (size.status === "success") {
      yield put({
        type: 'EDIT_SIZE_SUCCESS',
        size: size.result
      });
    }
    else if(size.status === "fail"){
      yield put({
        type: 'EDIT_SIZE_ERROR',
        error: size
      })
    }
    else{
      yield put({
        type: 'EDIT_SIZE_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  
  
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_SIZE_FOREIGN_KEYS_SUCCESS',
        units: foreign_keys.result.units
      });
    }
    else{
      yield put({
        type: 'GET_SIZE_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'EDIT_SIZE_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateSize({ payload }) {
  const { id_size, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "items/sizes/" + id_size,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_SIZE_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_SIZE_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_SIZE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_SIZE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showSize({ payload }) {
  const { id_size } = payload;

  try {
    const resultShow = yield call(get, "items/sizes/" + id_size);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_SIZE_SUCCESS',
        size: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateSize({ payload }) {
  const { id_size } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "items/sizes/" + id_size,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_SIZE_SUCCESS',
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_SIZES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_SIZE_ERROR',
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_SIZES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_SIZE_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_SIZES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_SIZE_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_SIZES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateSize({ payload }) {
  const { id_size } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "items/sizes/" + id_size,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_SIZE_SUCCESS',
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_SIZES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: 'ACTIVATE_SIZE_ERROR',
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_SIZES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_SIZE_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_SIZES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_SIZE_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_SIZES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_SIZES_REQUEST', getSizes),
    yield takeEvery('CREATE_SIZE_REQUEST', createSize),
    yield takeEvery('STORE_SIZE_REQUEST', storeSize),
    yield takeEvery('EDIT_SIZE_REQUEST', editSize),
    yield takeEvery('UPDATE_SIZE_REQUEST', updateSize),
    yield takeEvery('SHOW_SIZE_REQUEST', showSize),
    yield takeEvery('DEACTIVATE_SIZE_REQUEST', deactivateSize),
    yield takeEvery('ACTIVATE_SIZE_REQUEST', activateSize)
  ]);
}
