const initState = {
  financial_ratio_reports: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function financialRatioReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_FINANCIAL_RATIO_REPORTS_REQUEST": {
      return {
        ...state,
        //financial_ratio_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_FINANCIAL_RATIO_REPORTS_SUCCESS": {
      return {
        ...state,
        financial_ratio_reports: action.financial_ratio_reports,
        // filter: action.filter,
        // search: action.search,
        loading: false
      };
    }
    case "GET_FINANCIAL_RATIO_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
