import { all, takeEvery, takeLatest, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

export function* getAssetCategories() {
  try {
    // Cek apakah ada pagination di request atau tidak
    const [asset_categories] = yield all([
      call(get, "assets/categories?is_active=treelist"),
    ]);

    // Jika sukses get data account
    if (asset_categories.status === "success") {
      yield put({
        type: "GET_ASSET_CATEGORIES_SUCCESS",
        asset_categories: asset_categories.result.tree,
        asset_categories_list: asset_categories.result.list
      })
    }
    else if (asset_categories.status === "fail") {
      yield put({ 
        type: "GET_ASSET_CATEGORIES_ERROR",
        error: asset_categories
      });
    }
    else {
      yield put({ 
        type: "GET_ASSET_CATEGORIES_ERROR",
        error: {message: "Check your internet connection"} 
      });
    }
  } catch (error) {
    yield put({ 
      type: "GET_ASSET_CATEGORIES_ERROR",
      error: {message: "Check your internet connection"} 
    });
    bugsnagClient.notify(error);
  }
}

export function* storeAssetCategory({ payload }) {
  const { body } = payload;

  try {
    const asset_category = yield call(post, "assets/categories", body);

    // Jika sukses get data asset category
    if (asset_category.status === "success") {
      yield all([
        put({
          type: "STORE_ASSET_CATEGORY_SUCCESS",
          asset_category: asset_category.result,
          success: asset_category
        }),
        put({
          type: "GET_ASSET_CATEGORIES_REQUEST",
        }),
        put({
          type: "SHOW_ASSET_CATEGORY_REQUEST",
          payload: { id_asset_category: asset_category.result.id_asset_category }
        })
      ]);
    }
    else if (asset_category.status === "fail" || asset_category.errors) {
      yield put({
        type: "STORE_ASSET_CATEGORY_ERROR",
        error: asset_category
      });
    }
    else {
      // Set message ke session storage
      yield put({
        type: "STORE_ASSET_CATEGORY_ERROR",
        error: {message: "Check your internet connection"} 
      });
    } 
  } catch (error) {
    yield put({
      type: "STORE_ASSET_CATEGORY_ERROR",
      error: {message: "Check your internet connection"} 
    });

    bugsnagClient.notify(error)
  }
}

export function* showAssetCategory({ payload }) {
  const { id_asset_category } = payload;

  try {
    const asset_category = yield call(get, "assets/categories/" + id_asset_category);

    // Jika sukses get data account
    if (asset_category.status === "success") {
      yield put({
        type: "SHOW_ASSET_CATEGORY_SUCCESS",
        asset_category: asset_category.result.asset_category,
        dropdown_update: asset_category.result.tree
      });
    } else {
      // Set message ke session storage
      yield put({ 
        type: "SHOW_ASSET_CATEGORY_ERROR",
        error: {message: "Check your internet connection"} 
      });
    }
  } catch (error) {
    yield put({ 
      type: "SHOW_ASSET_CATEGORY_ERROR",
      error: {message: "Check your internet connection"} 
    });
    bugsnagClient.notify(error);
  }
  
}

