export const isElectron = () => {
  // Renderer process
  if (
    typeof window !== "undefined" &&
    typeof window.process === "object" &&
    window.process.type === "renderer"
  ) {
    return true;
  }

  // Main process
  if (
    typeof process !== "undefined" &&
    typeof process.versions === "object" &&
    !!process.versions.electron
  ) {
    return true;
  }

  // Detect the user agent when the `nodeIntegration` option is set to true
  if (
    typeof navigator === "object" &&
    typeof navigator.userAgent === "string" &&
    navigator.userAgent.indexOf("Electron") >= 0
  ) {
    return true;
  }

  return false;
};

/**
 * Generate name for pdf file name in print mode
 *
 * @param {String} module_name
 * @param {String} transaction_no - Optional
 * @param {Bool} has_transaction_no - Optional
 */
export const getFileName = (
  module_name,
  transaction_no = null,
  has_transaction_no = true
) => {
  let filename;

  if (transaction_no) {
    transaction_no
      .split("/")
      .map(() => (transaction_no = transaction_no.replace("/", "-")));
  }
  let pre_format =
    transaction_no && has_transaction_no
      ? `${module_name}_${transaction_no}`
      : !has_transaction_no
      ? module_name
      : `new_${module_name}`;

  // Menghilangkan karakter kurung buka, kurung tutup, slash dan mengganti spasi dengan underscore
  pre_format = pre_format
    .replace(/[&#,+()$~%.'":*?<>{}]/g, "")
    .replace(/[/ ]/g, "_")
    .toLowerCase();

  filename = `eq_${pre_format}_${moment().format("YYYY_MM_DD_HH_mm_ss")}`;

  return filename;
};

