/**
 * @param {array} currency_rate array currency rate
 * @param {moment} data tanggal transaksi
 * */
export const getCurrencyRate = (currency_rates, date) => {
  let currency_rate = 1;
  if (Array.isArray(currency_rates)) {
    let rate = currency_rates.find(data =>
      moment(data.rate_date, "YYYY-MM-DD HH:mm:ss").isSameOrBefore(date)
    );

    if (!rate) {
      rate = currency_rates[currency_rates.length - 1];
    }
    currency_rate = rate ? parseFloat(rate.rate) : 1;
  }

  return currency_rate;
};

/**
 * @param {array} currency_rate array currency rate
 * @param {moment} data tanggal transaksi
 * */
export const getRate = (currency_rates, date) => {
  let currency_rate = 1;
  let fiscal_rate = 1;
  if (Array.isArray(currency_rates)) {
    let rate = currency_rates.find(data =>
      moment(data.rate_date, "YYYY-MM-DD HH:mm:ss").isSameOrBefore(date)
    );

    if (!rate) {
      rate = currency_rates[currency_rates.length - 1];
    }

    currency_rate = rate ? parseFloat(rate.rate) : 1;
    fiscal_rate = rate ? parseFloat(rate.fiscal_rate) : 1;
  }

  return { currency_rate: currency_rate, fiscal_rate: fiscal_rate };
};

/**
 * @param {string} discount diskon dalam % atau langsung ammount
 * */
export const parseDiscount = discount => {
  if (discount) {
    discount = discount.toString();
    discount = discount.replace(",", ".");
    discount = parseFloat(discount).toString();

    return discount;
  } else {
    return "0";
  }
};

/**
 * @param {string} discount diskon dalam % atau langsung ammount
 * */
export const convertDiscountMask = discount => {
  if (discount) {
    if (discount.includes("+")) {
      return discount;
    } else if (discount.slice(-1) === "%") {
      discount = `${parseFloat(discount)}%`;
    } else {
      return parseFloat(discount)
        .toFixed(2)
        .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
  } else {
    discount = "0";
  }

  return discount;
};

/**
 * @param {string} discount diskon dalam % atau langsung ammount
 * */
export const convertDiscount = discount => {
  if (discount) {
    discount =
      discount.slice(-1) === "%"
        ? parseFloat(discount) / 100
        : parseFloat(discount);
  } else {
    discount = "0.00";
  }

  return parseFloat(discount);
};

/**
 * @param {string} discount diskon dalam % atau langsung ammount
 * @param {string} price harga
 * */
export const getDiscount = (discount, price) => {
  discount = discount + "";
  if (discount) {
    discount =
      discount.slice(-1) === "%"
        ? (parseFloat(discount) / 100) * parseFloat(price)
        : parseFloat(discount);
  } else {
    discount = "0.00";
  }

  return parseFloat(discount);
};

/**
 * @param {object} item item yang akan dihitung discount bill nya
 * @param {array} items list item
 * @param {string} suppDiscount discount supplier
 * */
export const getDiscountBillPerItem = (item, items, suppDiscount) => {
  // const tax_pct = parseFloat(item.tax_pct);
  let discountBillPerItem = 0;
  // if (tax_pct === 0) {
  //   return parseFloat("0.00");
  // } else {
  const sumSubtotal = getSumSubTotal(items);
  const discountBill = getDiscount(suppDiscount, sumSubtotal);
  if (sumSubtotal === 0) {
    discountBillPerItem = 0;
  } else {
    discountBillPerItem =
      (getSubTotalPerItem(item.item_price, item.qty, item.discount) /
        sumSubtotal) *
      discountBill;
  }
  // }
  return parseFloat(discountBillPerItem);
};

/**
 * @param {object} item item yang akan dihitung ppn discount nya
 * */
export const getPpnDiscountPerItem = item => {
  const tax_pct = parseFloat(item.tax_pct);
  let ppnDiscountPerItem = 0;
  if (tax_pct === 0) {
    /* Beneran kah subtota price ? */
    ppnDiscountPerItem = getSubTotalPrice(item.item_price, item.qty) * tax_pct;
  } else {
    const price = parseFloat(item.item_price);
    const qty = parseFloat(item.qty);
    const subTot = price * qty;

    ppnDiscountPerItem = getDiscount(item.discount, subTot) * tax_pct;
  }
  return parseFloat(ppnDiscountPerItem);
};

/**
 * @param {object} item item yang akan dihitung ppn discount bill nya
 * @param {array} items list items
 * @param {string} suppDiscount discount supplier
 * */
export const getPpnDiscountBillPerItem = (item, items, suppDiscount) => {
  const tax_pct = parseFloat(item.tax_pct);
  const ppnDiscountBillPerItem =
    getDiscountBillPerItem(item, items, suppDiscount) * tax_pct;

  return parseFloat(ppnDiscountBillPerItem);
};

/**
 * @param {array} items list item
 * */
export const getSumSubTotal = items => {
  let sumSubtotal = 0;
  items.map(
    data =>
      (sumSubtotal += getSubTotalPerItem(
        data.item_price,
        data.qty,
        data.discount
      ))
  );
  return parseFloat(sumSubtotal);
};

/**
 * @param {string} price harga
 * @param {string} qty jumlah barang
 * */
export const getSubTotalPrice = (price, qty) => {
  price = parseFloat(price);
  qty = parseFloat(qty);
  const sub_total = price * qty;

  return parseFloat(sub_total);
};

/**
 * @param {string} price harga
 * @param {string} qty jumlah barang
 * @param {string} discount diskon dalam % atau langsung ammount
 * */
export const getSubTotalPerItem = (price, qty, discount) => {
  price = parseFloat(price);
  qty = parseFloat(qty);
  const sub_total =
    getSubTotalPrice(price, qty) - getDiscount(discount, price * qty);

  return parseFloat(sub_total);
};

/**
 * @param {object} item item yang akan dihitung subtotal ppn & diskonnya
 * @param {array} items list items
 * @param {string} suppDiscount discount supplier
 * */
export const getSubTotalPriceNppnDiscountPerItem = (
  item,
  items,
  suppDiscount
) => {
  const sub_total =
    getSubTotalPrice(item.item_price, item.qty) +
    getPpnDiscountPerItem(item) +
    getDiscountBillPerItem(item, items, suppDiscount);
  return parseFloat(sub_total);
};

/**
 * @param {object} item item yang akan dihitung subtotal ppn & diskonnya
 * @param {array} items list items
 * @param {string} suppDiscount discount supplier
 * @param {boolean} inclusiveTax inclusive tax atau tidak
 * */
export const getSubTotalTaxPerItem = (
  item,
  items,
  suppDiscount,
  inclusiveTax
) => {
  const tax_pct = parseFloat(item.tax_pct);
  let subTotalTaxPerItem = "0.00";
  let subtotalPerItem = 0;
  let discountBillPerItem = 0;
  if (tax_pct === 0) {
    return parseFloat(subTotalTaxPerItem);
  } else if (tax_pct > 0) {
    subtotalPerItem = getSubTotalPerItem(
      item.item_price,
      item.qty,
      item.discount
    );
    discountBillPerItem = getDiscountBillPerItem(item, items, suppDiscount);

    subTotalTaxPerItem = (subtotalPerItem - discountBillPerItem) * tax_pct;
  } else {
    subtotalPerItem = getSubTotalPerItem(
      item.item_price,
      item.qty,
      item.discount
    );
    discountBillPerItem = getDiscountBillPerItem(item, items, suppDiscount);
    subTotalTaxPerItem = (subtotalPerItem - discountBillPerItem) * tax_pct;
  }

  return parseFloat(subTotalTaxPerItem);
};

/**
 * @param {object} item item yang akan dihitung subtotal ppn & diskonnya
 * @param {array} items list items
 * @param {string} suppDiscount discount supplier
 * @param {boolean} inclusiveTax inclusive tax atau tidak
 * */
export const getSubTotalTaxPerItemInclusive = (
  item,
  items,
  suppDiscount,
  inclusiveTax
) => {
  const tax_pct = parseFloat(item.tax_pct);
  let subTotalTaxPerItem = "0.00";
  let subtotalPerItem = 0;
  let discountBillPerItem = 0;
  if (tax_pct === 0) {
    return parseFloat(subTotalTaxPerItem);
  } else if (tax_pct > 0) {
    subtotalPerItem = getSubTotalPerItem(
      item.item_price,
      item.qty,
      item.discount
    );
    discountBillPerItem = getDiscountBillPerItem(item, items, suppDiscount);

    subTotalTaxPerItem =
      ((subtotalPerItem - discountBillPerItem) * tax_pct * 100) /
      (100 + 100 * tax_pct);
  }
  return parseFloat(subTotalTaxPerItem);
};

/**
 * @param {object} item item yang akan dihitung subtotal ppn & diskonnya
 * @param {array} items list items
 * @param {boolean} inclusiveTax inclusive tax atau tidak
 * @param {string} suppDiscount discount supplier
 * @param {array} freightCosts data freight & cost
 * */
export const getNettPurchPrice = (
  item,
  items,
  inclusiveTax,
  suppDiscount,
  freightCosts
) => {
  let npp = 0;
  const tax_pct = parseFloat(item.tax_pct);
  const qty = parseFloat(item.qty);
  const subtotal = getSubTotalPerItem(item.item_price, item.qty, item.discount);
  const sumSubtotal = getSumSubTotal(items);
  const discountBill = getDiscount(suppDiscount, sumSubtotal);
  let costTax = 0;
  let costTaxPerQty = 0;

  // cari nilai freight per item dari account
  if (Array.isArray(freightCosts)) {
    // console.log(freightCosts);
    const filteredFreightCost = freightCosts.filter(freight => {
      if (Array.isArray(freight.freight_cost_items)) {
        const freight_cost_items = freight.freight_cost_items.map(
          data => data["index_no"]
        );
        return freight_cost_items.includes(item.index_no);
      }
    });
    // console.log(item.index_no, filteredFreightCost);
    filteredFreightCost.map(filtered => {
      const sumSubtotalPerItemPerAccount = getSumSubtotalPerItemPerAccount(
        filtered
      );
      if (item && filtered) {
        costTax += getFreightPerItemPerAccount(
          item.sub_total,
          sumSubtotalPerItemPerAccount,
          filtered.currency_amount
        );
      }
    });
  }

  if (costTax > 0) {
    costTaxPerQty = costTax / item.qty;
  }

  if (sumSubtotal === 0) {
    return 0;
  }
  if (inclusiveTax) {
    if (tax_pct > 0) {
      npp =
        (subtotal / qty - ((subtotal / sumSubtotal) * discountBill) / qty) *
        (100 / (100 + tax_pct * 100));
    } else {
      npp = subtotal / qty - ((subtotal / sumSubtotal) * discountBill) / qty;
    }
  } else {
    npp = subtotal / qty - ((subtotal / sumSubtotal) * discountBill) / qty;
  }

  npp = parseFloat(npp) + costTaxPerQty;
  return parseFloat(npp);
};

/**
 * @param row data item yang baru diubah
 */
export const getBaseQty = row => {
  /* Konvesi ke unit yang dituju */
  const unit = row.unit_conversion.find(
    currData => currData.id_unit === row.id_unit
  );
  const baseQty = parseFloat(row.qty) * parseFloat(unit.unit_conversion);
  return parseFloat(baseQty);
};

/**
 * @param {string} qty data qty yang akan dikonversi
 * @param {string} id_unit data id_unit yang akan dikonversi
 * @param {array} unit_conversion data unit conversion
 */
export const convertQty = (qty, id_unit, unit_conversion) => {
  /* Konvesi ke unit yang dituju */
  const unit = unit_conversion.find(currData => currData.id_unit === id_unit);
  const baseQty = parseFloat(qty) * parseFloat(unit.unit_conversion);
  return parseFloat(baseQty);
};

/**
 * @param {string} price data price yang akan dikonversi
 * @param {string} unit_conv data unit_conversion terakhir
 * @param {string} id_unit data id_unit yang akan dikonversi
 * @param {array} unit_conversion data unit conversion
 */
export const convertPrice = (price, unit_conv, id_unit, unit_conversion) => {
  /* Konvesi ke unit yang dituju */
  const unit = unit_conversion.find(currData => currData.id_unit === id_unit);
  const newPrice =
    (parseFloat(price) / parseFloat(unit_conv)) *
    parseFloat(unit.unit_conversion);
  return parseFloat(newPrice);
};

/**
 * @param row data item yang baru diubah
 */
export const getItemPrice = row => {
  /* Konvesi harga ke unit yang dituju */
  const unit = row.unit_conversion.find(
    currData => currData.id_unit === row.id_unit
  );
  const itemPrice =
    (parseFloat(row.item_price) / row.unit_conv) *
    parseFloat(unit.unit_conversion);
  return parseFloat(itemPrice);
};

/**
 * @param {string} id_unit id_unit yang dicari
 * @param {array} unit_conversion data unit conversion
 */
export const getUnitConversion = (id_unit, unit_conversion) => {
  const unit = unit_conversion.find(currData => currData.id_unit === id_unit);
  if (unit) {
    return parseFloat(unit.unit_conversion);
  } else {
    return 1;
  }
};

/**
 * @param {array} items list item yang ada
 * @param {string} suppDiscount diskon dari supplier
 */
export const getSupplierDiscount = (items, suppDiscount) => {
  let sub_total = 0;
  items.map(data => {
    sub_total += getSubTotalPerItem(data.item_price, data.qty, data.discount);
  });
  const totDisc = getDiscount(suppDiscount, sub_total);
  return parseFloat(totDisc);
};

/**
 * @param {Object} freightCost list freight cost yang ada
 */
export const getSumSubtotalPerItemPerAccount = freightCost => {
  let sumSubtotalPerItemPerAccount = 0;

  if (freightCost && Array.isArray(freightCost.selected_items)) {
    freightCost.selected_items.map(itemCost => {
      sumSubtotalPerItemPerAccount += parseFloat(itemCost.sub_total);
    });
  }

  sumSubtotalPerItemPerAccount = sumSubtotalPerItemPerAccount;
  return parseFloat(sumSubtotalPerItemPerAccount);
};

/**
 * @param {string} subtotalPerItem subtotal dari item
 * @param {string} sumSubtotalPerItemPerAccount jumlah subtotal item yang ada di account tersebut
 * @param {string} costAmount nilai cost dari account tersebut
 */
export const getFreightPerItemPerAccount = (
  subtotalPerItem,
  sumSubtotalPerItemPerAccount,
  costAmount
) => {
  subtotalPerItem = parseFloat(subtotalPerItem);
  sumSubtotalPerItemPerAccount = parseFloat(sumSubtotalPerItemPerAccount);
  costAmount = parseFloat(costAmount);

  const freightCostPerItemPerAccount =
    (subtotalPerItem / sumSubtotalPerItemPerAccount) * costAmount;
  return parseFloat(freightCostPerItemPerAccount);
};

/**
 * @param {array} freightCosts list freight cost yang ada
 */
export const getTotalCostTax = freightCosts => {
  let totalCostTax = 0;
  if (Array.isArray(freightCosts)) {
    freightCosts.map(data => {
      let sumSubtotalPerItemPerAccount = 0;
      data.selected_items.map(itemCost => {
        sumSubtotalPerItemPerAccount += parseFloat(itemCost.sub_total);
      });

      data.selected_items.map(itemCost => {
        const tax_pct = itemCost.tax_pct ? parseFloat(itemCost.tax_pct) : 0;
        if (tax_pct > 0) {
          const costTax =
            getFreightPerItemPerAccount(
              itemCost.sub_total,
              sumSubtotalPerItemPerAccount,
              data.currency_amount
            ) * tax_pct;
          totalCostTax += costTax;
        }
      });
    });
  }

  totalCostTax = totalCostTax;
  return parseFloat(totalCostTax);
};

/**
 * @param {array} items list item yang ada
 * @param {string} suppDiscount diskon dari supplier
 * @param {boolean} inclusiveTax inclusive tax atau tidak
 * @param {string} totalCostTax -OPTIONAL- jika ingin menambahkan total Tax dari Freight & Cost
 */
export const getTotalTax = (
  items,
  suppDiscount,
  inclusiveTax,
  totalCostTax
) => {
  let totalTax = 0;
  totalCostTax = totalCostTax ? parseFloat(totalCostTax) : 0;
  if (inclusiveTax) {
    items.map(data => {
      totalTax += getSubTotalTaxPerItemInclusive(
        data,
        items,
        suppDiscount,
        inclusiveTax
      );
    });
  } else {
    items.map(data => {
      totalTax += getSubTotalTaxPerItem(
        data,
        items,
        suppDiscount,
        inclusiveTax
      );
    });
  }
  // totalTax = (totalTax + totalCostTax);
  totalTax = totalTax;
  return parseFloat(totalTax);
};

/**
 * @param {array} items list item yang ada
 * @param {boolean} inclusiveTax inclusive tax atau tidak
 * @param {string} suppDiscount diskon dari supplier
 * @param {string} expense -OPTIONAL- total expense
 */
export const getExlDiscTax = (items, inclusiveTax, suppDiscount, expense) => {
  let exlDiscTax = 0;
  expense = expense ? parseFloat(expense) : 0;
  if (inclusiveTax) {
    let sumSubtotalPrice = 0;
    let sumSubtotalTaxPerItemInclusive = 0;
    items.map(currData => {
      sumSubtotalPrice += getSubTotalPrice(currData.item_price, currData.qty);
      sumSubtotalTaxPerItemInclusive += getSubTotalTaxPerItemInclusive(
        currData,
        items,
        suppDiscount,
        inclusiveTax
      );
    });

    exlDiscTax = sumSubtotalPrice - sumSubtotalTaxPerItemInclusive + expense;
    return parseFloat(exlDiscTax);
  } else {
    items.map(
      data => (exlDiscTax += getSubTotalPrice(data.item_price, data.qty))
    );
    exlDiscTax = exlDiscTax + expense;
    return parseFloat(exlDiscTax);
  }
};

/**
 * @param {Array} items array of items dengan standar key purchase dan sales
 */
export const sumSubtotalInlcudeDiscountItem = items => {
  let sum_subtotal_include_discount_item = 0;

  items.map(data => {
    sum_subtotal_include_discount_item += getDiscount(
      data.discount,
      parseFloat(data.item_price) * parseFloat(data.qty)
    );
  });

  return parseFloat(sum_subtotal_include_discount_item);
};

/**
 * @param {array} items list item yang ada
 * @param {string} suppDiscount diskon dari supplier
 * @param {boolean} inclusiveTax inclusive tax atau tidak
 */
export const getTotalDiscount = (items, suppDiscount, inclusiveTax) => {
  let totDisc = 0;
  let sumDiscountItem = 0;
  const sumSubtotal = getSumSubTotal(items);
  const discountBill = getDiscount(suppDiscount, sumSubtotal);
  if (inclusiveTax) {
    items.map(data => {
      // const disc =
      //   (getDiscount(
      //     data.discount,
      //     parseFloat(data.item_price) * parseFloat(data.qty)
      //   ) *
      //     100) /
      //   (100 + parseFloat(data.tax_pct) * 100);
      sumDiscountItem += getDiscount(
        data.discount,
        parseFloat(data.item_price) * parseFloat(data.qty)
      );
    });
  } else {
    items.map(data => {
      sumDiscountItem += getDiscount(
        data.discount,
        parseFloat(data.item_price) * parseFloat(data.qty)
      );
    });
  }

  totDisc = sumDiscountItem + discountBill;
  return parseFloat(totDisc);
};

/**
 * @param {array} items list item yang ada
 * @param {string} suppDiscount diskon dari supplier
 * @param {boolean} inclusiveTax inclusive tax atau tidak
 * @param {string} expense -OPTIONAL- total expense
 * @param {string} freightCost freight cost
 * @param {string} totalCostTax -OPTIONAL- jika ingin menambahkan total Tax dari Freight & Cost
 */
export const getTotalAmount = (
  items,
  suppDiscount,
  inclusiveTax,
  expense,
  freightCost,
  totalCostTax
) => {
  freightCost = freightCost ? parseFloat(freightCost) : 0;
  totalCostTax = totalCostTax ? parseFloat(totalCostTax) : 0;

  const exlDiscTax = getExlDiscTax(items, inclusiveTax, suppDiscount, expense);
  const totDisc = getTotalDiscount(items, suppDiscount, inclusiveTax);
  const totalTax = getTotalTax(items, suppDiscount, inclusiveTax, totalCostTax);
  const totalAmount = exlDiscTax + freightCost - totDisc + totalTax;

  return parseFloat(totalAmount);
};

/**
 *
 * @param {array} items array of object item
 * @param {boolean} inclusiveTax inclusive tax atau tidak
 */
export const getTaxBase = (items, inclusiveTax) => {
  let tax_bases = [];
  let item_groups = groupBy(items, "tax_name");

  if (item_groups && inclusiveTax) {
    Object.keys(item_groups)
      .sort()
      .reverse()
      .map(tax_name => {
        let tax_base = 0;

        item_groups[tax_name].map(item => {
          const tax = parseFloat(item.tax_pct) * 100;
          const tax_base_item =
            (100 / (100 + tax)) *
            (parseFloat(item.sub_total) - parseFloat(item.bill_discount));

          tax_base += tax_base_item;
        });

        tax_bases.push({
          tax_name: tax_name,
          tax_base: tax_base
        });
      });
  } else if (item_groups && !inclusiveTax) {
    Object.keys(item_groups)
      .sort()
      .reverse()
      .map(tax_name => {
        let tax_base = 0;

        item_groups[tax_name].map(item => {
          const tax_base_item =
            parseFloat(item.sub_total) - parseFloat(item.bill_discount);

          tax_base += tax_base_item;
        });

        tax_bases.push({
          tax_name: tax_name,
          tax_base: tax_base
        });
      });
  }

  return tax_bases;
};

/**
 * @description return default item location price dan discount 0
 * @param {Object} item item yang akan dicari price nya
 * @param {Number} rate currency rate
 */
export const getPurchasePrice = (item, rate = 1) => {
  rate = parseFloat(rate);
  let price = item.purhcase_order_no
    ? parseFloat(item.location_price) * item.unit_conv
    : (parseFloat(item.location_price) / rate) * item.unit_conv;
  let discount = "0.00";

  // sort supplier pricelist by price list type
  // keperluan : optimasi searching
  const supplier_pricelists = item.supplier_pricelists.sort(
    (a, b) =>
      a.supplier_pricelist.pricelist_type - b.supplier_pricelist.pricelist_type
  );

  // search pricelist
  // diutamakan type = 1
  for (let i = 0; i < supplier_pricelists.length; i++) {
    const price_pricelist = parseFloat(supplier_pricelists[i].item_price);
    if (
      supplier_pricelists[i].id_unit === item.id_unit &&
      supplier_pricelists[i].supplier_pricelist.pricelist_type === 1
    ) {
      price = price_pricelist ? price_pricelist : price;
      discount = supplier_pricelists[i].discount;
      break;
    } else if (
      supplier_pricelists[i].id_unit === item.id_unit &&
      supplier_pricelists[i].supplier_pricelist.pricelist_type === 2
    ) {
      price = price_pricelist ? price_pricelist : price;
      discount = supplier_pricelists[i].discount;
      break;
    }
  }

  return { price, discount };
};

/**
 * @description return sales price from location_price
 * @param {Object} item item yang akan dicari price nya
 * @param {Number} rate currency rate
 */
export const getSalesPriceFromUnitConversion = (item, rate = 1) => {
  rate = parseFloat(rate);
  let price = parseFloat(item.location_price) / rate;

  const findUnitConversion = item.unit_conversion.find(
    data => data.id_unit === item.id_unit
  );
  // cek apakah harga merupakan is_fixed_price
  if (findUnitConversion.is_fixed_price) {
    price = findUnitConversion.fixed_price_sales;
  } else {
    const price_unit = price * parseFloat(findUnitConversion.unit_conversion);
    // cek bentuk dari margin
    if (findUnitConversion.margin.slice(-1) === "%") {
      price =
        price_unit + price_unit * (parseFloat(findUnitConversion.margin) / 100);
    } else {
      price = price_unit + parseFloat(findUnitConversion.margin);
    }
  }
  return price;
};

/**
 * @description return default item location price dan discount of non discount management pricing
 * @param {Object} item item yang akan dicari price nya
 * @param {Number} rate currency rate
 */
export const getSalesPriceAndDiscountNonDM = (item, rate = 1) => {
  rate = parseFloat(rate);
  let price = getSalesPriceFromUnitConversion(item);
  let discount = "0.00";

  // sort customer pricelist by price list type
  // keperluan : optimasi searching
  if (item.customer_pricelists && item.customer_pricelists.length > 0) {
    const customer_pricelists = item.customer_pricelists.sort(
      (a, b) =>
        a.customer_pricelist.pricelist_type -
        b.customer_pricelist.pricelist_type
    );

    // search pricelist
    // diutamakan type = 1
    for (let i = 0; i < customer_pricelists.length; i++) {
      const price_pricelist = parseFloat(customer_pricelists[i].item_price);
      if (
        customer_pricelists[i].id_unit === item.id_unit &&
        customer_pricelists[i].customer_pricelist.pricelist_type === 1
      ) {
        price = price_pricelist ? price_pricelist : price;
        discount = customer_pricelists[i].discount;
        break;
      } else if (
        customer_pricelists[i].id_unit === item.id_unit &&
        customer_pricelists[i].customer_pricelist.pricelist_type === 2
      ) {
        price = price_pricelist ? price_pricelist : price;
        discount = customer_pricelists[i].discount;
        break;
      }
    }
  }

  return { price, discount };
};

/**
 *
 * @param {Object} item item yang akan dicari diskonnya
 * @param {*} discountItems list discount item yang valid berdasarkan customer location dan payment
 * category
 */
export const getItemDiscountDM = (item, discountItems, from) => {
  let discountItem = {};
  let itemValidDisc = {};
  let discount = "0%";
  if (Array.isArray(discountItems) && discountItems.length > 0) {
    // ambil diskon yang terakhir dibuat
    discountItem = { ...discountItems[0] };
  }

  if (discountItem) {
    // untuk item_plu
    if (item.item_code === discountItem.item_plu) {
      itemValidDisc = { ...discountItem };
    }
    // untuk item_sku
    else if (item.sku === discountItem.item_sku) {
      itemValidDisc = { ...discountItem };
    } else if (item.item && item.item.sku === discountItem.item_sku) {
      itemValidDisc = { ...discountItem };
    }
    // untuk item_category
    else if (item.id_item_category === discountItem.id_item_category) {
      itemValidDisc = { ...discountItem };
    }
    // untuk plu, sku, category kosong semua
    else if (
      !discountItem.item_plu &&
      !discountItem.item_sku &&
      !discountItem.id_item_category
    ) {
      // console.log("into not categorized discount");
      itemValidDisc = { ...discountItem };
    }

    if (itemValidDisc) {
      discount = validateDiscountDetail(item, itemValidDisc);
      if (parseFloat(discount)) {
        item.id_discount = itemValidDisc.id_discount_management;
        item.discount_code = itemValidDisc.discount_code;
        item.discount = discount;
        item.discount_code_active = itemValidDisc.discount_code;
      } else {
        item.id_discount = null;
        item.discount = item.item_discount ? item.item_discount : "0%";
        item.discount_code = "-";
        item.discount_code_active = item.discount_code;
      }
    } else {
      item.id_discount = null;
      item.discount = item.item_discount ? item.item_discount : "0%";
      item.discount_code = "-";
      item.discount_code_active = item.discount_code;
    }
  }

  return item;
};

/**
 *
 * @param {Object} item item yang dicari diskonnya
 * @param {Object} validDiscount diskon yang sesuai dengan item
 */
export const validateDiscountDetail = (item, validDiscount) => {
  let discount = "0%";

  // type 1 - event
  if (validDiscount.discount_type === 1) {
    discount = validDiscount.discount_management_details[0].discount_value;
  }
  // type 2 - quantity
  else if (validDiscount.discount_type === 2) {
    const discManagDetail = validDiscount.discount_management_details;
    // filtering discount with the same id_unit
    const discManagDetailSameUnits = discManagDetail.filter(
      data => data.id_unit === item.id_unit
    );

    // filtering discount with all unit
    const discManagDetailAllUnits = discManagDetail.filter(
      data => !data.id_unit
    );

    // local function to find which discount management detail will be used
    function searchDisc(data, index, arr) {
      if (arr[index + 1]) {
        return (
          parseFloat(item.qty) < parseFloat(arr[index + 1].min_qty_amount) &&
          parseFloat(item.qty) >= parseFloat(data.min_qty_amount)
        );
      } else {
        return parseFloat(item.qty) >= parseFloat(data.min_qty_amount);
      }
    }

    // create temp var for saving index of the found discount
    let indexFindDiscManagDetailSame, indexFindDiscManagDetailAll;

    // finding discount management detail for the same unit
    if (discManagDetailSameUnits.length > 0) {
      indexFindDiscManagDetailSame = discManagDetailSameUnits.findIndex(
        (data, index) => searchDisc(data, index, discManagDetailSameUnits)
      );
    }

    // finding discount management detail for all unit
    if (discManagDetailAllUnits.length > 0) {
      indexFindDiscManagDetailAll = discManagDetailAllUnits.findIndex(
        (data, index) => searchDisc(data, index, discManagDetailAllUnits)
      );
    }

    // deciding which discount management detail to use
    if (indexFindDiscManagDetailSame >= 0) {
      discount =
        discManagDetailSameUnits[indexFindDiscManagDetailSame].discount_value;
    } else if (indexFindDiscManagDetailAll >= 0) {
      discount =
        discManagDetailAllUnits[indexFindDiscManagDetailAll].discount_value;
    }
  }

  return discount;
};

/**
 *
 * @param {String} so_discount diskon dari Sales Order
 * @param {Float32Array} so_qty quantity yang di order
 */
export const getDiscountFromSalesOrder = (
  so_discount = "0%",
  so_qty = 1,
  si_qty = 1
) => {
  const check = (discount, so_qty, si_qty) => {
    if (discount.slice(-1) === "%") {
      return discount;
    } else {
      return (
        (parseFloat(discount) / parseFloat(so_qty)) *
        parseFloat(si_qty)
      ).toString();
    }
  };

  if (typeof so_discount === "string") {
    return check(so_discount, so_qty, si_qty);
  } else {
    so_discount = so_discount.toString();
    return check(so_discount, so_qty, si_qty);
  }
};

/**
 *
 * @param {String} so_discount discount dari Sales Order
 * @param {Number} so_total_amount total amount dari Sales Order
 * @param {Number} si_total_amount total amount Sales Invoice
 */
export const getDiscountBillFromSalesOrder = (
  so_discount = "0%",
  so_total_amount = 1,
  si_total_amount = 1
) => {
  const check = (discount, so_total_amount, si_total_amount) => {
    if (discount.slice(-1) === "%") {
      return discount;
    } else {
      return (
        (parseFloat(si_total_amount) / parseFloat(so_total_amount)) *
        discount
      ).toString();
    }
  };

  if (typeof so_discount === "string") {
    return check(so_discount, so_total_amount, si_total_amount);
  } else {
    so_discount = so_discount.toString();
    return check(so_discount, so_total_amount, si_total_amount);
  }
};

