/**
 *
 * @param {number|string} number angka yang akan diformat
 * @param {string} radix (OPTIONAL) "." / "," untuk pemisah ribuan, default ","
 * @param {number} precision (OPTIONAL) berapa angka dibelakang koma, default null
 * @param {boolean} kurung_minus (OPTIONAL) bentuk minus jika kurang dari 0, default false
 */
export const formatNumber = (
  number,
  radix = ",",
  precision = 2,
  kurung_minus = false
) => {
  number = parseFloat(number);
  const regex = radix === "," ? /\d(?=(\d{3})+\,)/g : /\d(?=(\d{3})+\.)/g;
  const replacer = radix === "," ? "$&." : "$&,";

  // format - menjadi kurung
  if (number < 0 && kurung_minus) {
    number =
      radix === "."
        ? number.toFixed(precision).replace(".", ",")
        : number.toFixed(precision);

    number = number.replace(regex, replacer).replace("-", "(") + ")";
  } else {
    number =
      radix === "."
        ? number.toFixed(precision).replace(".", ",")
        : number.toFixed(precision);

    number = number.replace(regex, replacer);
  }

  return number;
};

/**
 *
 * @param {array} arr array of object
 * @param {string} key key yang digunakan untuk group by
 */
export const groupBy = (arr, key) => {
  return arr.reduce((rv, x) => {
    (rv[x[key]] = rv[x[key]] || []).push(x);
    return rv;
  }, {});
};

/**
 * @description mencari perbedaan dalam object satu dengan
 * object yang lain, -1 berarti tidak ada perubahan
 * @param {Object} data data yang ingin di track perubahannya
 * @param {Object} comparison data orisinil sebagai pembanding
 * @param {Array} except (OPTIONAL) array of string yang tidak ingin di cek
 */
export const findChanges = (data, comparison, except = []) => {
  let count = -1;
  if (data && comparison) {
    Object.keys(data).map(field => {
      const pass = except.includes(field);
      if (!pass && data[field] !== comparison[field]) {
        return count++;
      }
    });
  }

  return count;
};

