import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getFixedAssets({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [fixed_assets, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "fixed-assets?search=" + search + "&page=" + page),
            call(get, "fixed-assets/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "fixed-assets?" + filter + "&page=" + page),
            call(get, "fixed-assets/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "fixed-assets?search=" + search),
          call(get, "fixed-assets/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "fixed-assets?" + filter),
          call(get, "fixed-assets/foreign-keys")
        ]);

    // console.log(fixed_assets);
    // console.log(foreign_keys);

    // Jika sukses get data fixed_assets
    if (fixed_assets.status === "success") {
      // Jika di API menggunakan pagination
      if (fixed_assets.result.current_page && search) {
        yield put({
          type: "GET_FIXED_ASSETS_SUCCESS",
          fixed_assets: fixed_assets.result.data,
          current_page: fixed_assets.result.current_page,
          page_size: fixed_assets.result.per_page,
          total: fixed_assets.result.total,
          search: search
        });
      } else if (fixed_assets.result.current_page && filter) {
        yield put({
          type: "GET_FIXED_ASSETS_SUCCESS",
          fixed_assets: fixed_assets.result.data,
          current_page: fixed_assets.result.current_page,
          page_size: fixed_assets.result.per_page,
          total: fixed_assets.result.total,
          filter: filter
        });
      } else if (fixed_assets.result.current_page) {
        yield put({
          type: "GET_FIXED_ASSETS_SUCCESS",
          fixed_assets: fixed_assets.result.data,
          current_page: fixed_assets.result.current_page,
          page_size: fixed_assets.result.per_page,
          total: fixed_assets.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_FIXED_ASSETS_SUCCESS",
          fixed_assets: fixed_assets.result,
          page_size: null,
          total: null
        });
      }
    } else if (fixed_assets.status === "fail") {
      yield put({
        type: "GET_FIXED_ASSETS_ERROR",
        error: fixed_assets
      });
    } else {
      yield put({
        type: "GET_FIXED_ASSETS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // Jika sukses get data fixed_asset types
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_FIXED_ASSET_FOREIGN_KEYS_SUCCESS",
        locations: foreign_keys.result.locations,
        currencies: foreign_keys.result.currencies,
        departments: foreign_keys.result.departments,
        accounts: foreign_keys.result.accounts,
        sub_ledger_accounts: foreign_keys.result.sub_ledger_accounts,
        asset_fiscals: foreign_keys.result.asset_fiscals,
        asset_types: foreign_keys.result.asset_types,
        asset_categories: foreign_keys.result.asset_categories,
        asset_service_types: foreign_keys.result.asset_service_types,
        base_currency: foreign_keys.result.base_currency,
        messages: foreign_keys.result.messages
      });
    } else {
      yield put({
        type: "GET_FIXED_ASSET_FOREIGN_KEYS_ERROR"
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FIXED_ASSETS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Create data
export function* createFixedAsset() {
  try {
    const [foreign_keys] = yield all([call(get, "fixed-assets/foreign-keys")]);

    // Jika sukses get data account
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_FIXED_ASSET_FOREIGN_KEYS_SUCCESS",
        locations: foreign_keys.result.locations,
        currencies: foreign_keys.result.currencies,
        departments: foreign_keys.result.departments,
        accounts: foreign_keys.result.accounts,
        sub_ledger_accounts: foreign_keys.result.sub_ledger_accounts,
        asset_fiscals: foreign_keys.result.asset_fiscals,
        asset_types: foreign_keys.result.asset_types,
        asset_categories: foreign_keys.result.asset_categories,
        asset_service_types: foreign_keys.result.asset_service_types,
        base_currency: foreign_keys.result.base_currency,
        messages: foreign_keys.result.messages
      });
    } else {
      yield put({
        type: "GET_FIXED_ASSET_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FIXED_ASSET_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Store data
export function* storeFixedAsset({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "fixed-assets", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_FIXED_ASSET_SUCCESS",
        id_fixed_asset: resultStore.result.id_fixed_asset,
        success: resultStore
      });
    } else if (resultStore.errors || resultStore.status === "fail") {
      yield put({
        type: "STORE_FIXED_ASSET_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_FIXED_ASSET_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_FIXED_ASSET_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

