// Edit data
export function* editFixedAsset({ payload }) {
  const { id_fixed_asset } = payload;

  try {
    const [fixed_asset, foreign_keys] = yield all([
      call(get, "fixed-assets/" + id_fixed_asset),
      call(get, "fixed-assets/foreign-keys")
    ]);

    // Jika sukses
    if (fixed_asset.status === "success") {
      yield put({
        type: "EDIT_FIXED_ASSET_SUCCESS",
        fixed_asset: fixed_asset.result
      });
    } else {
      yield put({
        type: "EDIT_FIXED_ASSET_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // Jika sukses get data fixed_asset types
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_FIXED_ASSET_FOREIGN_KEYS_SUCCESS",
        locations: foreign_keys.result.locations,
        currencies: foreign_keys.result.currencies,
        departments: foreign_keys.result.departments,
        accounts: foreign_keys.result.accounts,
        sub_ledger_accounts: foreign_keys.result.sub_ledger_accounts,
        asset_fiscals: foreign_keys.result.asset_fiscals,
        asset_types: foreign_keys.result.asset_types,
        asset_categories: foreign_keys.result.asset_categories,
        asset_service_types: foreign_keys.result.asset_service_types,
        base_currency: foreign_keys.result.base_currency,
        messages: foreign_keys.result.messages
      });
    } else {
      yield put({
        type: "GET_FIXED_ASSET_FOREIGN_KEYS_ERROR"
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_FIXED_ASSET_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Update data
export function* updateFixedAsset({ payload }) {
  const { id_fixed_asset, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "fixed-assets/" + id_fixed_asset,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_FIXED_ASSET_SUCCESS",
          success: resultUpdate
        }),
        yield put({
          type: "EDIT_FIXED_ASSET_REQUEST",
          payload: { id_fixed_asset }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_FIXED_ASSET_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_FIXED_ASSET_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_FIXED_ASSET_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

