const initState = {
  employees: [],
  employee: null,
  employee_contact: null,
  employee_address: null,
  job_titles: [],
  locations: [],
  departments: [],
  countries: [],
  provinces: [],
  cities: [],
  districts: [],
  roles: [],
  users: [],
  default_id_country: null,
  array_id_location: null,
  search: null,
  filter: null,
  loading: false,
  loadingEdit: false,
  loadingSubmit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_employee: null,
};

export default function unitsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_EMPLOYEES_REQUEST": {
      return {
        ...state,
        //employees: [],
        loading: true,
        // loadingFilterJobTitles: true,
        // loadingFilterLocations: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_EMPLOYEES_SUCCESS": {
      return {
        ...state,
        employees: action.employees,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_EMPLOYEES_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_EMPLOYEE_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        roles: action.roles,
        job_titles: action.job_titles,
        locations: action.locations,
        departments: action.departments,
        countries: action.countries,
        provinces: action.provinces,
        cities: action.cities,
        districts: action.districts,
        default_id_country: action.default_id_country,
        array_id_location: action.array_id_location,
        users: action.users,
      }
    }
    case 'GET_EMPLOYEE_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message
      }
    }
    case "CREATE_EMPLOYEE_REQUEST": {
      return {
        ...state,
        job_titles: [],
        locations: [],
        departments: [],
        countries: [],
        provinces: [],
        cities: [],
        districts: [],
        roles: [],
        loading:true,
      };
    }
    case "CREATE_EMPLOYEE_SUCCESS": {
      return {
        ...state,
        loading: false,
      };
    }
    case "STORE_EMPLOYEE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null,
        id_employee: null,
      };
    }
    case "STORE_EMPLOYEE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_employee: action.id_employee
      };
    }
    case "STORE_EMPLOYEE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_EMPLOYEE_REQUEST": {
      return {
        ...state,
        employee: null,
        job_titles: [],
        locations: [],
        departments: [],
        countries: [],

        cities: [],
        loading: true,
        loadingEdit: true,
        // loadingFilterJobTitles: true,
        // loadingFilterLocations: true,
        // loadingFilterDepartments: true,
        // loadingFilterCountries: true,
        // loadingFilterCities: true,
        errorField: null
      };
    }
    case "EDIT_EMPLOYEE_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        loadingEdit: false,
        employee: action.employee
      };
    }
    case "EDIT_EMPLOYEE_ERROR": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_EMPLOYEE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_EMPLOYEE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_EMPLOYEE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_EMPLOYEE_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_EMPLOYEE_SUCCESS": {
      return {
        ...state,
        loading: false,
        employees: action.employees
      };
    }
    case "DEACTIVATE_EMPLOYEE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_EMPLOYEE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_EMPLOYEE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DEACTIVATE_EMPLOYEE_CONTACT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_EMPLOYEE_CONTACT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_EMPLOYEE_CONTACT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_EMPLOYEE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_EMPLOYEE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_EMPLOYEE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
