import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPettyCashes({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [petty_cashes, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "financials/petty-cashes?search=" + search + "&page=" + page
            ),
            call(get, "financials/petty-cashes/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "financials/petty-cashes?" + filter + "&page=" + page),
            call(get, "financials/petty-cashes/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "financials/petty-cashes?search=" + search),
          call(get, "financials/petty-cashes/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "financials/petty-cashes?" + filter),
          call(get, "financials/petty-cashes/foreign-keys")
        ]);

    // console.log(petty_cashes);
    // console.log(foreign_keys);

    // Jika sukses get data petty_cashes
    if (petty_cashes.status === "success") {
      // Jika di API menggunakan pagination
      if (petty_cashes.result.current_page && search) {
        yield put({
          type: "GET_PETTY_CASHES_SUCCESS",
          petty_cashes: petty_cashes.result.data,
          current_page: petty_cashes.result.current_page,
          page_size: petty_cashes.result.per_page,
          total: petty_cashes.result.total,
          search: search
        });
      } else if (petty_cashes.result.current_page && filter) {
        yield put({
          type: "GET_PETTY_CASHES_SUCCESS",
          petty_cashes: petty_cashes.result.data,
          current_page: petty_cashes.result.current_page,
          page_size: petty_cashes.result.per_page,
          total: petty_cashes.result.total,
          filter: filter
        });
      } else if (petty_cashes.result.current_page) {
        yield put({
          type: "GET_PETTY_CASHES_SUCCESS",
          petty_cashes: petty_cashes.result.data,
          current_page: petty_cashes.result.current_page,
          page_size: petty_cashes.result.per_page,
          total: petty_cashes.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PETTY_CASHES_SUCCESS",
          petty_cashes: petty_cashes.result,
          page_size: null,
          total: null
        });
      }
    } else if (petty_cashes.status === "fail") {
      yield put({
        type: "GET_PETTY_CASHES_ERROR",
        error: petty_cashes
      });
    } else {
      yield put({
        type: "GET_PETTY_CASHES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // Jika sukses get data petty_cash types
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_PETTY_CASH_FOREIGN_KEYS_SUCCESS",
        locations: foreign_keys.result.locations,
        petty_cash_types: foreign_keys.result.petty_cash_types,
        employees: foreign_keys.result.employees,
        accounts: foreign_keys.result.accounts,
        base_currency: foreign_keys.result.base_currency
      });
    } else {
      yield put({
        type: "GET_PETTY_CASH_FOREIGN_KEYS_ERROR"
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "GET_PETTY_CASHES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Create data
export function* createPettyCash() {
  try {
    const [foreign_keys] = yield all([
      call(get, "financials/petty-cashes/foreign-keys")
    ]);

    // Jika sukses get data account
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_PETTY_CASH_FOREIGN_KEYS_SUCCESS",
        locations: foreign_keys.result.locations,
        petty_cash_types: foreign_keys.result.petty_cash_types,
        employees: foreign_keys.result.employees,
        accounts: foreign_keys.result.accounts,
        base_currency: foreign_keys.result.base_currency
      });
    } else {
      yield put({
        type: "GET_PETTY_CASH_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_PETTY_CASH_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Store data
export function* storePettyCash({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "financials/petty-cashes", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_PETTY_CASH_SUCCESS",
        id_petty_cash: resultStore.result.id_petty_cash,
        success: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_PETTY_CASH_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_PETTY_CASH_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_PETTY_CASH_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Edit data
export function* editPettyCash({ payload }) {
  const { id_petty_cash } = payload;

  try {
    const [petty_cash, foreign_keys] = yield all([
      call(get, "financials/petty-cashes/" + id_petty_cash),
      call(get, "financials/petty-cashes/foreign-keys")
    ]);

    // Jika sukses
    if (petty_cash.status === "success") {
      yield put({
        type: "EDIT_PETTY_CASH_SUCCESS",
        petty_cash: petty_cash.result
      });
    } else {
      yield put({
        type: "EDIT_PETTY_CASH_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // Jika sukses get data petty_cash types
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_PETTY_CASH_FOREIGN_KEYS_SUCCESS",
        locations: foreign_keys.result.locations,
        petty_cash_types: foreign_keys.result.petty_cash_types,
        employees: foreign_keys.result.employees,
        accounts: foreign_keys.result.accounts,
        base_currency: foreign_keys.result.base_currency
      });
    } else {
      yield put({
        type: "GET_PETTY_CASH_FOREIGN_KEYS_ERROR"
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_PETTY_CASH_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Update data
export function* updatePettyCash({ payload }) {
  const { id_petty_cash, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "financials/petty-cashes/" + id_petty_cash,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_PETTY_CASH_SUCCESS",
          success: resultUpdate
        }),
        yield put({
          type: "EDIT_PETTY_CASH_REQUEST",
          payload: { id_petty_cash }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_PETTY_CASH_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_PETTY_CASH_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_PETTY_CASH_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

