// Upload Document
export function* uploadPettyCashDocument({ payload }) {
  const { id_petty_cash, body } = payload;
  let data = {
    ...body,
    id_petty_cash
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "financials/petty-cashes/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_PETTY_CASH_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_PETTY_CASH_REQUEST",
          payload: { id_petty_cash }
        })
      ]);
    } else if (uploadResult.errors) {
      yield put({
        type: "UPLOAD_PETTY_CASH_DOCUMENT_ERROR",
        error: { message: uploadResult.errors.document[0] }
      });
    } else if (uploadResult.status === "fail") {
      yield put({
        type: "UPLOAD_PETTY_CASH_DOCUMENT_ERROR",
        error: uploadResult
      });
    } else {
      yield put({
        type: "UPLOAD_PETTY_CASH_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // }
  } catch (error) {
    yield put({
      type: "UPLOAD_PETTY_CASH_DOCUMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadPettyCashDocument({ payload }) {
  const { id_petty_cash_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "financials/petty-cashes/documents/download/" + id_petty_cash_document
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deletePettyCashDocument({ payload }) {
  const { id_petty_cash_document } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "financials/petty-cashes/documents/" + id_petty_cash_document
      //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_PETTY_CASH_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_PETTY_CASH_REQUEST",
          payload: { id_petty_cash: deleteResult.result.id_petty_cash }
        })
      ]);
    } else if (deleteResult.status === "fail") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_PETTY_CASH_DOCUMENT_ERROR",
          error: deleteResult
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_PETTY_CASH_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        })
      ]);
    }
  } catch (error) {
    yield put({
      type: "DELETE_PETTY_CASH_DOCUMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_PETTY_CASHES_REQUEST", getPettyCashes),
    yield takeEvery("CREATE_PETTY_CASH_REQUEST", createPettyCash),
    yield takeEvery("STORE_PETTY_CASH_REQUEST", storePettyCash),
    yield takeEvery("EDIT_PETTY_CASH_REQUEST", editPettyCash),
    yield takeEvery("UPDATE_PETTY_CASH_REQUEST", updatePettyCash),
    yield takeEvery("SHOW_PETTY_CASH_REQUEST", showPettyCash),
    yield takeEvery("PROCESS_PETTY_CASH_REQUEST", processPettyCash),
    yield takeEvery("CANCEL_PETTY_CASH_REQUEST", cancelPettyCash),
    yield takeEvery(
      "UPLOAD_PETTY_CASH_DOCUMENT_REQUEST",
      uploadPettyCashDocument
    ),
    yield takeEvery(
      "DOWNLOAD_PETTY_CASH_DOCUMENT_REQUEST",
      downloadPettyCashDocument
    ),
    yield takeEvery(
      "DELETE_PETTY_CASH_DOCUMENT_REQUEST",
      deletePettyCashDocument
    )
  ]);
}
