import React, { Component } from "react";
import { connect } from "react-redux";

import IntlMessages from "../../components/utility/intlMessages";
import notification from "../../components/notification";
// import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Col, Row, Modal, Button } from "antd";
import { baseUrl } from "../../helpers/helper";

import actions from "../../redux/serverSetting/actions";
import authAction from "../../redux/auth/actions";

const { Option } = Select;
const {
  serverSetup,
  serverValidate,
  typingServer,
  serverSuccessValidateRedirect,
  clearSuccess,
  clearError
} = actions;
const { logout } = authAction;

const stripTrailingSlash = str => {
  if (str.slice(-1) === "/") {
    return str;
  }
  return str + "/";
};

class ModalChangeServer extends Component {
  handleSubmit = () => {
    // e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const server = baseUrl();
        const data = `${values.protocol}//${stripTrailingSlash(values.url)}`;
        if (data !== server) {
          this.props.serverValidate(data);
        } else {
          this.props.handleModal();
        }
      }
    });
  };

  componentDidMount = () => {
    this.props.serverSetup()
  }

  componentDidUpdate = prevProps => {
    const { serverAddress, success, error } = this.props.serverSetting;

    if (success) {
      this.props.handleModal();
      notification("success", success);
      this.props.clearSuccess();
      this.props.logout();
      localStorage.setItem("new-server", serverAddress);
      this.props.history.push("/signin");
    } else if (error) {
      notification("error", error);
      this.props.clearError();
    }
  };

  render() {
    const { getFieldDecorator } = this.props.form;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const server = baseUrl().split("/");

    const addon = getFieldDecorator("protocol", {
      initialValue: server[0]
    })(
      <Select>
        <Option value="http:">http://</Option>
        <Option value="https:">https://</Option>
      </Select>
    );

    return (
      <Modal
        title={<IntlMessages id="changeUrl" />}
        visible={this.props.visible}
        centered
        // closable={false}
        // maskClosable={false}
        onCancel={this.props.handleModal}
        width="30%"
        footer={
          <Row justify="end" type="flex">
            <Col>
              <Button
                onClick={() => {
                  this.props.handleModal();
                }}
              >
                <IntlMessages id="button.cancel" />
              </Button>

              {/* <Button
                onClick={() => {
                  resetFields();
                }}
              >
                <IntlMessages id="filter.reset" />
              </Button> */}

              <Button
                type="primary"
                onClick={() => {
                  this.handleSubmit();
                }}
                loading={this.props.serverSetting.loadingModal}
              >
                <IntlMessages id="button.ok" />
              </Button>
            </Col>
          </Row>
        }
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row>
            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="serverAddress" />}
                extra={<IntlMessages id="page.serverSettingDescription" />}
              >
                {getFieldDecorator("url", {
                  initialValue: server.length > 0 ? server[2] : null,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Input
                    addonBefore={addon}
                    placeholder="example.com"
                    onPressEnter={e => {
                      e.preventDefault();
                      this.handleSubmit();
                    }}
                  />
                )}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedChangeServerModal = Form.create({ name: "change_server_modal" })(
  ModalChangeServer
);

const mapStateToProps = state => {
  return { serverSetting: state.serverSetting };
};

export default connect(
  mapStateToProps,
  {
    serverSetup,
    serverValidate,
    typingServer,
    serverSuccessValidateRedirect,
    clearSuccess,
    clearError,
    logout
  }
)(WrappedChangeServerModal);
