import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getJournals({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [journals, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
        yield all([
          call(get, "journals?search=" + search + "&page=" + page),
          call(get, "journals/foreign-keys"),
        ])
        : // Jika filter
        yield all([
          call(get, "journals?" + filter + "&page=" + page),
          call(get, "journals/foreign-keys"),
        ])
      : search
        ? // Jika search
        yield all([
          call(get, "journals?search=" + search),
          call(get, "journals/foreign-keys"),
        ])
        : // Jika tidak ada 
        yield all([
          call(get, "journals?" + filter),
          call(get, "journals/foreign-keys"),
        ]);

    // console.log(journals);
    // console.log(foreign_keys);

    // Jika sukses get data journals
    if (journals.status === "success") {
      // Jika di API menggunakan pagination
      if (journals.result.current_page && search) {
        yield put({
          type: 'GET_JOURNALS_SUCCESS',
          journals: journals.result.data,
          current_page: journals.result.current_page,
          page_size: journals.result.per_page,
          total: journals.result.total,
          search: search
        });
      }
      else if (journals.result.current_page && filter) {
        yield put({
          type: 'GET_JOURNALS_SUCCESS',
          journals: journals.result.data,
          current_page: journals.result.current_page,
          page_size: journals.result.per_page,
          total: journals.result.total,
          filter: filter
        });
      }
      else if (journals.result.current_page) {
        yield put({
          type: 'GET_JOURNALS_SUCCESS',
          journals: journals.result.data,
          current_page: journals.result.current_page,
          page_size: journals.result.per_page,
          total: journals.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_JOURNALS_SUCCESS',
          journals: journals.result,
          page_size: null,
          total: null
        });
      }
    } else if (journals.status === "fail") {
      yield put({
        type: 'GET_JOURNALS_ERROR',
        error: journals
      })
    } else {
      yield put({
        type: 'GET_JOURNALS_ERROR',
        error: { message: "Check your internet connection" }
      })
    }

    // Jika sukses get data journal types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_JOURNAL_FOREIGN_KEYS_SUCCESS',
        accounts: foreign_keys.result.accounts,
        cashbanks: foreign_keys.result.cashbanks,
        currencies: foreign_keys.result.currencies,
        customers: foreign_keys.result.customers,
        departments: foreign_keys.result.departments,
        messages: foreign_keys.result.messages,
        locations: foreign_keys.result.locations,
        projects: foreign_keys.result.projects,
        suppliers: foreign_keys.result.suppliers
      });
    }
    else {
      yield put({
        type: 'GET_JOURNAL_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_JOURNALS_ERROR',
      error: { message: "Check your internet connection" }
    })
    bugsnagClient.notify(error)
  }
}

// Get all data
export function* getImportJournals({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [journals] = page
      ? search
        ? // Jika search dan pagination
        yield all([
          call(get, "journals?get_old_trans=1&search=" + search + "&page=" + page),
        ])
        : // Jika filter
        yield all([
          call(get, "journals?get_old_trans=1&" + filter + "&page=" + page),
        ])
      : search
        ? // Jika search
        yield all([
          call(get, "journals?get_old_trans=1&search=" + search),
        ])
        : // Jika tidak ada 
        yield all([
          call(get, "journals?get_old_trans=1&" + filter),
        ]);

    // console.log(journals);

    // Jika sukses get data journals
    if (journals.status === "success") {
      // Jika di API menggunakan pagination
      if (journals.result.current_page && search) {
        yield put({
          type: 'GET_IMPORT_JOURNALS_SUCCESS',
          journals: journals.result.data,
          current_page: journals.result.current_page,
          page_size: journals.result.per_page,
          total: journals.result.total,
          search: search
        });
      }
      else if (journals.result.current_page && filter) {
        yield put({
          type: 'GET_IMPORT_JOURNALS_SUCCESS',
          journals: journals.result.data,
          current_page: journals.result.current_page,
          page_size: journals.result.per_page,
          total: journals.result.total,
          filter: filter
        });
      }
      else if (journals.result.current_page) {
        yield put({
          type: 'GET_IMPORT_JOURNALS_SUCCESS',
          journals: journals.result.data,
          current_page: journals.result.current_page,
          page_size: journals.result.per_page,
          total: journals.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_JOURNALS_SUCCESS',
          journals: journals.result,
          page_size: null,
          total: null
        });
      }
    } else if (journals.status === "fail") {
      yield put({
        type: 'GET_JOURNALS_ERROR',
        error: journals
      })
    } else {
      yield put({
        type: 'GET_JOURNALS_ERROR',
        error: { message: "Check your internet connection" }
      })
    }
  } catch (error) {
    yield put({
      type: 'GET_JOURNALS_ERROR',
      error: { message: "Check your internet connection" }
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createJournal() {
  try {
    const [foreign_keys] = yield all([
      call(get, "journals/foreign-keys"),
    ]);
  
    // Jika sukses get data account
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_JOURNAL_FOREIGN_KEYS_SUCCESS',
        accounts: foreign_keys.result.accounts,
        cashbanks: foreign_keys.result.cashbanks,
        currencies: foreign_keys.result.currencies,
        customers: foreign_keys.result.customers,
        departments: foreign_keys.result.departments,
        messages: foreign_keys.result.messages,
        locations: foreign_keys.result.locations,
        projects: foreign_keys.result.projects,
        suppliers: foreign_keys.result.suppliers
      });
    }
    else {
      yield put({
        type: 'GET_JOURNAL_FOREIGN_KEYS_ERROR',
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_JOURNAL_FOREIGN_KEYS_ERROR',
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeJournal({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "journals", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_JOURNAL_SUCCESS',
        id_journal: resultStore.result.id_journal,
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_JOURNAL_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_JOURNAL_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_JOURNAL_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editJournal({ payload }) {
  const { id_journal } = payload;

  try {
    const [journal, foreign_keys] = yield all([
      call(get, "journals/" + id_journal),
      call(get, "journals/foreign-keys"),
    ]);
  
    // Jika sukses
    if (journal.status === "success") {
      yield put({
        type: 'EDIT_JOURNAL_SUCCESS',
        journal: journal.result
      });
    }
    else {
      yield put({
        type: "EDIT_JOURNAL_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    // Jika sukses get data journal types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_JOURNAL_FOREIGN_KEYS_SUCCESS',
        accounts: foreign_keys.result.accounts,
        cashbanks: foreign_keys.result.cashbanks,
        currencies: foreign_keys.result.currencies,
        customers: foreign_keys.result.customers,
        departments: foreign_keys.result.departments,
        messages: foreign_keys.result.messages,
        locations: foreign_keys.result.locations,
        projects: foreign_keys.result.projects,
        suppliers: foreign_keys.result.suppliers
      });
    }
    else {
      yield put({
        type: 'GET_JOURNAL_FOREIGN_KEYS_ERROR',
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_JOURNAL_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateJournal({ payload }) {
  const { id_journal, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "journals/" + id_journal,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: 'UPDATE_JOURNAL_SUCCESS',
          success: resultUpdate
        }),
        yield put({
          type: 'EDIT_JOURNAL_REQUEST',
          payload: { id_journal }
        })
      ])
  
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_JOURNAL_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_JOURNAL_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_JOURNAL_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showJournal({ payload }) {
  const { id_journal } = payload;

  try {
    const resultShow = yield call(get, "journals/" + id_journal);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_JOURNAL_SUCCESS',
        journal: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Decancel data (mengubah is_active => 0)
export function* processJournal({ payload }) {
  const { id_journal } = payload;

  try {
    // POST data ke API
    const processResult = yield call(
      putApi,
      "journals/process-transaction/" + id_journal,
      // body
    );

    // Jika sukses
    if (processResult.status === "success") {
      yield all([
        yield put({
          type: 'PROCESS_JOURNAL_SUCCESS',
          success: processResult
        }),
        yield put({
          type: 'EDIT_JOURNAL_REQUEST',
          payload: { id_journal }
        })
      ])
    }
    else if (processResult.status === "fail" || processResult.errors) {
      yield put({
        type: 'PROCESS_JOURNAL_ERROR',
        error: processResult
      })
    }
    else {
      yield put({
        type: 'PROCESS_JOURNAL_ERROR',
        error: { message: "Check your internet connection" }
      })
    }
  } catch (error) {
    yield put({
      type: 'PROCESS_JOURNAL_ERROR',
      error: { message: "Check your internet connection" }
    })
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* cancelJournal({ payload }) {
  const { id_journal, body } = payload;

  try {
    // POST ke API
    const cancelResult = yield call(
      putApi,
      "journals/cancel-transaction/" + id_journal,
      body
    );

    // Jika sukses
    if (cancelResult.status === "success") {
      yield all([
        yield put({
          type: 'CANCEL_JOURNAL_SUCCESS',
          success: cancelResult
        }),
        yield put({
          type: 'EDIT_JOURNAL_REQUEST',
          payload: { id_journal }
        })
      ])
    }
    else if (cancelResult.status === "fail" || cancelResult.errors) {
      yield put({
        type: 'CANCEL_JOURNAL_ERROR',
        error: cancelResult
      })
    }
    else {
      yield put({
        type: 'CANCEL_JOURNAL_ERROR',
        error: { message: "Check your internet connection" }
      })
    }
  } catch (error) {
    yield put({
      type: 'CANCEL_JOURNAL_ERROR',
      error: { message: "Check your internet connection" }
    })
    bugsnagClient.notify(error);
  }
}

// Upload Document
export function* uploadJournalDocument({ payload }) {
  const { id_journal, body } = payload;
  let data = {
    ...body,
    id_journal
  }

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "journals/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: 'UPLOAD_JOURNAL_DOCUMENT_SUCCESS',
          success: uploadResult
        }),
        yield put({
          type: 'EDIT_JOURNAL_REQUEST',
          payload: { id_journal }
        })
      ])
    }
    else if (uploadResult.errors) {
      yield put({
        type: 'UPLOAD_JOURNAL_DOCUMENT_ERROR',
        error: { message: uploadResult.errors.document[0] }
      })
    }
    else if (uploadResult.status === "fail") {
      yield put({
        type: 'UPLOAD_JOURNAL_DOCUMENT_ERROR',
        error: uploadResult
      })
    }
    else {
      yield put({
        type: 'UPLOAD_JOURNAL_DOCUMENT_ERROR',
        error: { message: "Check your internet connection" }
      })
    }

    // }
  } catch (error) {
    yield put({
      type: 'UPLOAD_JOURNAL_DOCUMENT_ERROR',
      error: { message: "Check your internet connection" }
    })
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadJournalDocument({ payload }) {
  const { id_journal_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "journals/documents/download/" + id_journal_document,
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deleteJournalDocument({ payload }) {
  const { id_journal_document } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "journals/documents/" + id_journal_document,
      //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_JOURNAL_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'EDIT_JOURNAL_REQUEST',
          payload: { id_journal: deleteResult.result.id_journal }
        })
      ]);
    }
    else if (deleteResult.status === "fail") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_JOURNAL_DOCUMENT_ERROR",
          error: deleteResult
        }),
      ]);
    }
    else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_JOURNAL_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
      ]);
    }
  } catch (error) {
    yield put({
      type: "DELETE_JOURNAL_DOCUMENT_ERROR",
      error: { message: "Check your internet connection" }
    })
    bugsnagClient.notify(error);
  }
}


export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_JOURNALS_REQUEST', getJournals),
    yield takeEvery('GET_IMPORT_JOURNALS_REQUEST', getImportJournals),
    yield takeEvery('CREATE_JOURNAL_REQUEST', createJournal),
    yield takeEvery('STORE_JOURNAL_REQUEST', storeJournal),
    yield takeEvery('EDIT_JOURNAL_REQUEST', editJournal),
    yield takeEvery('UPDATE_JOURNAL_REQUEST', updateJournal),
    yield takeEvery('SHOW_JOURNAL_REQUEST', showJournal),
    yield takeEvery('PROCESS_JOURNAL_REQUEST', processJournal),
    yield takeEvery('CANCEL_JOURNAL_REQUEST', cancelJournal),
    yield takeEvery('UPLOAD_JOURNAL_DOCUMENT_REQUEST', uploadJournalDocument),
    yield takeEvery('DOWNLOAD_JOURNAL_DOCUMENT_REQUEST', downloadJournalDocument),
    yield takeEvery('DELETE_JOURNAL_DOCUMENT_REQUEST', deleteJournalDocument),

  ]);
}
