const initState = {
  cash_management_reports: [],
  locations: [],
  cash_banks: [],
  cash_flow_groups: [],
  currencies: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function cashManagementReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_CASH_MANAGEMENT_REPORTS_REQUEST": {
      return {
        ...state,
        //cash_management_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_CASH_MANAGEMENT_REPORTS_SUCCESS": {
      return {
        ...state,
        cash_management_reports: action.cash_management_reports,
        filter: action.filter,
        search: action.search,
        loading: false
      };
    }
    case "GET_CASH_MANAGEMENT_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_CASH_MANAGEMENT_REPORT_REQUEST": {
      return {
        ...state,
        errorField: null
      };
    }

    case "GET_CASH_MANAGEMENT_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        cash_banks: action.cash_banks,
        cash_flow_groups: action.cash_flow_groups,
        currencies: action.currencies,
      };
    }
    case "GET_CASH_MANAGEMENT_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
