const initState = {
  purchase_process_history_reports: [],
  locations: [],
  suppliers: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function purchaseProcessHistoryReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_PURCHASE_PROCESS_HISTORY_REPORTS_REQUEST": {
      return {
        ...state,
        //purchase_process_history_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_PURCHASE_PROCESS_HISTORY_REPORTS_SUCCESS": {
      return {
        ...state,
        purchase_process_history_reports:
          action.purchase_process_history_reports,
        filter: action.filter,
        search: action.search,
        loading: false
      };
    }
    case "GET_PURCHASE_PROCESS_HISTORY_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_PURCHASE_PROCESS_HISTORY_REPORT_REQUEST": {
      return {
        ...state,
        purchase_process_history_report: null,
        errorField: null
      };
    }

    case "GET_PURCHASE_PROCESS_HISTORY_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        locations: action.locations,
        suppliers: action.suppliers
      };
    }
    case "GET_PURCHASE_PROCESS_HISTORY_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
