import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// get report
export function* getStockValuationReports({ payload }) {
  const { page, search, filter } = payload;

  try {

    let url = "reports/inventory-transactions?";
    if (search) {
      url += "search=" + search;
    }
    if (filter) {
      search ? (url += "&" + filter) : (url += filter);
    }

    if(filter){
      const [stock_valuation_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/inventory-transactions/foreign-keys")
      ]);
   
  
      // console.log(stock_valuation_reports);
      if (stock_valuation_reports.status === "success") {
        yield put({
          type: "GET_STOCK_VALUATION_REPORTS_SUCCESS",
          reports: stock_valuation_reports.result,
          filter
        });
      } else if (stock_valuation_reports.status === "fail") {
        yield put({
          type: "GET_STOCK_VALUATION_REPORTS_ERROR",
          error: stock_valuation_reports
        });
      } else {
        yield put({
          type: "GET_STOCK_VALUATION_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }

      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_STOCK_VALUATION_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_STOCK_VALUATION_REPORT_FOREIGN_KEYS_ERROR",
          error: foreign_keys
        });
      } else {
        yield put({
          type: "GET_STOCK_VALUATION_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }

    }
    else{
      const foreign_keys = yield call(get,"reports/inventory-transactions/foreign-keys");

      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_STOCK_VALUATION_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_STOCK_VALUATION_REPORT_FOREIGN_KEYS_ERROR",
          error: foreign_keys
        });
      } else {
        yield put({
          type: "GET_STOCK_VALUATION_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } 
  catch (error) {
    yield put({
      type: "GET_STOCK_VALUATION_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}


export function* getItemsForStockValuationReport({ payload }) {
  const { id_location, search } = payload;

  let filter = "items?is_active=1&limit=1&item_type=1";
  filter = search
    ? `${filter}&search=${search}&id_location=${id_location}`
    : `${filter}&id_location=${id_location}`;

  try {
    const items = yield call(get, filter);

  if (items.status === "success") {
    yield put({
      type: "GET_FILTER_ITEMS_FOR_STOCK_VALUATION_REPORT_SUCCESS",
      items: items.result
    });
  }
  else if(items.status === "fail"){
    yield put({
      type: "GET_FILTER_ITEMS_FOR_STOCK_VALUATION_REPORT_ERROR",
      error: items
    });
  }
  else{
    yield put({
      type: "GET_FILTER_ITEMS_FOR_STOCK_VALUATION_REPORT_ERROR",
      error: { message: "Check your internet connection" }
    });
  }
  } catch (error) {
    yield put({
      type: "GET_FILTER_ITEMS_FOR_STOCK_VALUATION_REPORT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_STOCK_VALUATION_REPORTS_REQUEST",
      getStockValuationReports
    ),
    yield takeEvery(
      "GET_ITEMS_FOR_STOCK_VALUATION_REPORT_REQUEST",
      getItemsForStockValuationReport
    )
  ]);
}
