const initState = {
  issue_receipt_reports: [],
  locations: [],
  adjustment_types: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function issueReceiptReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_ISSUE_RECEIPT_REPORTS_REQUEST": {
      return {
        ...state,
        //issue_receipt_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_ISSUE_RECEIPT_REPORTS_SUCCESS": {
      return {
        ...state,
        issue_receipt_reports: action.issue_receipt_reports,
        filter: action.filter,
        search: action.search,
        loading: false
      };
    }
    case "GET_ISSUE_RECEIPT_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_ISSUE_RECEIPT_REPORT_REQUEST": {
      return {
        ...state,
        issue_receipt_report: null,
        errorField: null
      };
    }

    case "GET_ISSUE_RECEIPT_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        adjustment_types: action.adjustment_types
      };
    }
    case "GET_ISSUE_RECEIPT_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
