import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";
// import { create } from "domain";
// import { log } from "util";

// Get all data
export function* getProjects({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [projects, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "projects?search=" + search + "&page=" + page),
            call(get, "projects/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "projects?" + filter + "&page=" + page),
            call(get, "projects/foreign-keys")
          ])
      : search
        ? // Jika search
          yield all([
            call(get, "projects?search=" + search),
            call(get, "projects/foreign-keys")
          ])
        : // Jika tidak ada
          yield all([
            call(get, "projects?" + filter),
            call(get, "projects/foreign-keys")
          ]);

    // console.log(projects);

    // Jika sukses get data projects
    if (projects.status === "success") {
      // Jika di API menggunakan pagination
      if (projects.result.current_page && search) {
        yield put({
          type: "GET_PROJECTS_SUCCESS",
          projects: projects.result.data,
          current_page: projects.result.current_page,
          page_size: projects.result.per_page,
          total: projects.result.total,
          search: search
        });
      }
      else if(projects.result.current_page && filter)
      {
        yield put({
          type: 'GET_PROJECTS_SUCCESS',
          projects: projects.result.data,
          current_page: projects.result.current_page,
          page_size: projects.result.per_page,
          total: projects.result.total,
          filter: filter
        });
      }
      else if (projects.result.current_page) {
        yield put({
          type: "GET_PROJECTS_SUCCESS",
          projects: projects.result.data,
          current_page: projects.result.current_page,
          page_size: projects.result.per_page,
          total: projects.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PROJECTS_SUCCESS",
          projects: projects.result,
          page_size: null,
          total: null
        });
      }
    } else if (projects.status === "fail") {
      yield put({
        type: "GET_PROJECTS_ERROR",
        error: projects
      });
    } else {
      yield put({
        type: "GET_PROJECTS_ERROR",
        error: { message: "Check your internet connection" },
      });
    }

    if (foreign_keys.status === "success") {
      yield put({
        type: "CREATE_PROJECT_SUCCESS",
        customers: foreign_keys.result.customers,
        employees: foreign_keys.result.employees,
        project_parents: foreign_keys.result.project_parents
      });
    }
  } catch (error) {
    yield put({
      type: "GET_PROJECTS_ERROR",
      error: { message: "Check your internet connection" },
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createProject() {
  try {
    const [foreign_keys] = yield all([call(get, "projects/foreign-keys")]);
    //console.log(roles);
    if (foreign_keys.status === "success") {
      yield put({
        type: "CREATE_PROJECT_SUCCESS",
        customers: foreign_keys.result.customers,
        employees: foreign_keys.result.employees,
        project_parents: foreign_keys.result.project_parents
      });
    }
    else{
      yield put({
        type: "CREATE_PROJECT_ERROR",
        error: { message: "Check your internet connection" },
      });
    }
  } catch (error) {
    yield put({
      type: "CREATE_PROJECT_ERROR",
      error: { message: "Check your internet connection" },
    });
    bugsnagClient.notify(error)
  } 
}

// Store data
export function* storeProject({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "projects", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_PROJECT_SUCCESS",
        success: resultStore
      });
    } 
    else if (resultStore.status ==="fail" || resultStore.errors) {
      yield put({
        type: "STORE_PROJECT_ERROR",
        error: resultStore
      });
    } 
    else {
      yield put({
        type: "STORE_PROJECT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_PROJECT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editProject({ payload }) {
  // console.log("masuk");
  const { id_project } = payload;

  try {
    const [project, foreign_keys] = yield all([
      call(get, "projects/" + id_project),
      call(get, "projects/foreign-keys")
    ]);
    //  console.log(project);
    //  console.log(roles);
  
    // Jika sukses
    if (project.status === "success") {
      yield put({
        type: "EDIT_PROJECT_SUCCESS",
        project: project.result
      });
    } else if (project.status === "fail") {
      yield put({
        type: "EDIT_PROJECT_ERROR",
        error: project
      });
    } else {
      yield put({
        type: "EDIT_PROJECT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign_keys.status === "success") {
      yield put({
        type: "CREATE_PROJECT_SUCCESS",
        customers: foreign_keys.result.customers,
        employees: foreign_keys.result.employees,
        project_parents: foreign_keys.result.project_parents
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_PROJECT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateProject({ payload }) {
  const { id_project, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "projects/" + id_project,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_PROJECT_SUCCESS",
          success: resultUpdate
        }),
        yield put({
          type: "EDIT_PROJECT_REQUEST",
          payload: { id_project: resultUpdate.result.id_project }
        })
      ])
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_PROJECT_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_PROJECT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_PROJECT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update History data
export function* updateProjectHistory({ payload }) {
  const { body } = payload;

  try {
    const resultUpdate = yield call(post, "projects/histories", body);
  
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_PROJECT_HISTORY_SUCCESS",
          success: resultUpdate
        }),
        yield put({
          type: "EDIT_PROJECT_REQUEST",
          payload: { id_project: resultUpdate.result.id_project }
        })
      ])
    } else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_PROJECT_HISTORY_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_PROJECT_HISTORY_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_PROJECT_HISTORY_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showProject({ payload }) {
  const { id_project } = payload;
  
  try {
    const resultShow = yield call(get, "projects/" + id_project);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: "SHOW_PROJECT_SUCCESS",
        unit: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateProject({ payload }) {
  const { id_project } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "projects/" + id_project,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DEACTIVATE_PROJECT_SUCCESS",
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PROJECTS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (deactivateResult.status === "fail" || deactivateResult.errors) {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DEACTIVATE_PROJECT_ERROR",
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PROJECTS_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DEACTIVATE_PROJECT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PROJECTS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DEACTIVATE_PROJECT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_PROJECTS_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error)
  }
}

// Activate data (mengubah is_active => 1)
export function* activateProject({ payload }) {
  const { id_project } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "projects/" + id_project,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: "ACTIVATE_PROJECT_SUCCESS",
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PROJECTS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
    else if(activateResult.status === "fail" || activateResult.errors){
      yield all([
        yield put({
          type: "ACTIVATE_PROJECT_ERROR",
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PROJECTS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
    else {
      yield all([
        yield put({
          type: "ACTIVATE_PROJECT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PROJECTS_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "ACTIVATE_PROJECT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_PROJECTS_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_PROJECTS_REQUEST", getProjects),
    yield takeEvery("CREATE_PROJECT_REQUEST", createProject),
    yield takeEvery("STORE_PROJECT_REQUEST", storeProject),
    yield takeEvery("EDIT_PROJECT_REQUEST", editProject),
    yield takeEvery("UPDATE_PROJECT_REQUEST", updateProject),
    yield takeEvery("UPDATE_PROJECT_HISTORY_REQUEST", updateProjectHistory),
    yield takeEvery("SHOW_PROJECT_REQUEST", showProject),
    yield takeEvery("DEACTIVATE_PROJECT_REQUEST", deactivateProject),
    yield takeEvery("ACTIVATE_PROJECT_REQUEST", activateProject)
  ]);
}
