const initState = {
  id_receivable_payment: null,
  id_location: null,
  id_customer: null,
  receivablePayments: [],
  receivablePayment: null,
  printData: {},
  items: [],
  prevTransConfirm: {},
  transactions: [],
  creditMemos: [],
  prevTrans: [],
  locations: [],
  customers: [],
  accounts: [],
  departments: [],
  projects: [],
  currencies: [],
  cashBanks: [],
  cashFlowTypes: [],
  paymentCategories: [],
  messages: [],
  search: null,
  filter: null,
  loading: false,
  loadingForeign: false,
  loadingSubmit: false,
  loadingTransactions: false,
  loadingCreditMemos: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function receivablePaymentsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_RECEIVABLE_PAYMENTS_REQUEST": {
      return {
        ...state,
        //receivablePayments: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_RECEIVABLE_PAYMENTS_SUCCESS": {
      return {
        ...state,
        receivablePayments: action.receivablePayments,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_RECEIVABLE_PAYMENTS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_RECEIVABLE_PAYMENT_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_RECEIVABLE_PAYMENT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        locations: action.locations,
        customers: action.customers,
        currencies: action.currencies,
        accounts: action.accounts,
        departments: action.departments,
        projects: action.projects,
        cashBanks: action.cashBanks,
        cashFlowTypes: action.cashFlowTypes,
        paymentCategories: action.paymentCategories,
        messages: action.messages
      };
    }
    case "GET_RECEIVABLE_PAYMENT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_RECEIVABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        id_receivable_payment: null,
        id_location: null,
        receivablePayment: null,
        itemDiscount: [],
        loadingTransactions: true,
        loadingSaleReceipts: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null
      };
    }
    case "GET_FILTER_SALE_INVOICES_RP_REQUEST": {
      return {
        ...state,
        loadingTransactions: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_FILTER_SALE_INVOICES_RP_SUCCESS": {
      return {
        ...state,
        loadingTransactions: false,
        transactions: action.transactions,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_FILTER_SALE_INVOICES_RP_ERROR": {
      return {
        ...state,
        loadingTransactions: false,
        error: action.error.message
      };
    }
    case "GET_FILTER_CREDIT_MEMOS_RP_REQUEST": {
      return {
        ...state,
        loadingCreditMemos: true
      };
    }
    case "GET_FILTER_CREDIT_MEMOS_RP_SUCCESS": {
      return {
        ...state,
        loadingCreditMemos: false,
        creditMemos: action.creditMemos
      };
    }
    case "GET_FILTER_CREDIT_MEMOS_RP_ERROR": {
      return {
        ...state,
        loadingCreditMemos: false,
        error: action.error.message
      };
    }
    case "STORE_RECEIVABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_RECEIVABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_receivable_payment: action.id_receivable_payment
      };
    }
    case "STORE_RECEIVABLE_PAYMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_RECEIVABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loading: true,
        id_receivable_payment: null,
        id_location: null,
        loadingTransactions: true,
        loadingSaleReceipts: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null
      };
    }
    case "EDIT_RECEIVABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        receivablePayment: action.receivablePayment
      };
    }
    case "EDIT_RECEIVABLE_PAYMENT_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_RECEIVABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_RECEIVABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_receivable_payment: action.id_receivable_payment
      };
    }
    case "UPDATE_RECEIVABLE_PAYMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_RECEIVABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_RECEIVABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        receivablePayments: action.receivablePayments
      };
    }
    case "CONFIRM_RECEIVABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CONFIRM_RECEIVABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CONFIRM_RECEIVABLE_PAYMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CANCEL_RECEIVABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CANCEL_RECEIVABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CANCEL_RECEIVABLE_PAYMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPLOAD_RECEIVABLE_PAYMENT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_RECEIVABLE_PAYMENT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_RECEIVABLE_PAYMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_RECEIVABLE_PAYMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_RECEIVABLE_PAYMENT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_RECEIVABLE_PAYMENT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_RECEIVABLE_PAYMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "SET_PRINT_DATA_RECEIVABLE_PAYMENT": {
      return {
        ...state,
        printData: action.payload.body
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
