import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getItemTransferReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/item-transfers?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if(filter){
      const [item_transfer_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/item-transfers/foreign-keys")
      ]);
    
      // Jika sukses get data item_transfer_reports
      if (item_transfer_reports.status === "success") {
        yield put({
          type: "GET_ITEM_TRANSFER_REPORTS_SUCCESS",
          item_transfer_reports: item_transfer_reports.result,
          filter,
          search
        });
      } else if (item_transfer_reports.status === "fail") {
        yield put({
          type: "GET_ITEM_TRANSFER_REPORTS_ERROR",
          error: { message: item_transfer_reports.message }
        });
      } else {
        yield put({
          type: "GET_ITEM_TRANSFER_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data item_transfer_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ITEM_TRANSFER_REPORT_FOREIGN_KEYS_SUCCESS",
          to_locations: foreign_keys.result.to_locations,
          from_locations: foreign_keys.result.from_locations,
          adjustment_types: foreign_keys.result.adjustment_types,
          couriers: foreign_keys.result.couriers
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ITEM_TRANSFER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ITEM_TRANSFER_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/item-transfers/foreign-keys")
      ]);
  
      yield put({
        type: "GET_ITEM_TRANSFER_REPORTS_SUCCESS",
        item_transfer_reports: []
      });
    
      // Jika sukses get data item_transfer_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ITEM_TRANSFER_REPORT_FOREIGN_KEYS_SUCCESS",
          to_locations: foreign_keys.result.to_locations,
          from_locations: foreign_keys.result.from_locations,
          adjustment_types: foreign_keys.result.adjustment_types,
          couriers: foreign_keys.result.couriers
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ITEM_TRANSFER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ITEM_TRANSFER_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_ITEM_TRANSFER_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_ITEM_TRANSFER_REPORTS_REQUEST", getItemTransferReports)
  ]);
}
