import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getInventoryHistoryReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/inventory-transactions/histories?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (search || filter) {
      const [inventory_history_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/inventory-transactions/foreign-keys")
      ]);
  
      // Jika sukses get data inventory_history_reports
      if (inventory_history_reports.status === "success") {
        
        yield put({
          type: "GET_INVENTORY_HISTORY_REPORTS_SUCCESS",
          inventory_history_reports: inventory_history_reports.result,
          filter: filter,
          search: search
        });
        
      } else if (inventory_history_reports.status === "fail") {
        yield put({
          type: "GET_INVENTORY_HISTORY_REPORTS_ERROR",
          error: { message: inventory_history_reports.message }
        });
      } else {
        yield put({
          type: "GET_INVENTORY_HISTORY_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data inventory_history_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_INVENTORY_HISTORY_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          item_categories: foreign_keys.result.item_categories
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_INVENTORY_HISTORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_INVENTORY_HISTORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/inventory-transactions/foreign-keys")
      ]);
  
      yield put({
        type: "GET_INVENTORY_HISTORY_REPORTS_SUCCESS",
        inventory_history_reports: []
      });
  
      // Jika sukses get data inventory_history_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_INVENTORY_HISTORY_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          item_categories: foreign_keys.result.item_categories
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_INVENTORY_HISTORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_INVENTORY_HISTORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_INVENTORY_HISTORY_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_INVENTORY_HISTORY_REPORTS_REQUEST",
      getInventoryHistoryReports
    )
  ]);
}
