const initState = {
  supplierTypes: [],
  supplierType: null,
  search: null,
  //filter: null,
  loading: false,
  loadingSubmit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function supplierTypesReducer(state = initState, action) {
  switch (action.type) {
    case "GET_SUPPLIER_TYPES_REQUEST": {
      return {
        ...state,
        //supplierTypes: [],
        loading: true,
        search: action.payload.search,
        //filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_SUPPLIER_TYPES_SUCCESS": {
      return {
        ...state,
        supplierTypes: action.supplierTypes,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        //filter: action.filter,
        loading: false
      };
    }
    case "GET_SUPPLIER_TYPES_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "STORE_SUPPLIER_TYPE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_SUPPLIER_TYPE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "STORE_SUPPLIER_TYPE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_SUPPLIER_TYPE_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "EDIT_SUPPLIER_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        supplierType: action.supplierType
      };
    }
    case "EDIT_SUPPLIER_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_SUPPLIER_TYPE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_SUPPLIER_TYPE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_SUPPLIER_TYPE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_SUPPLIER_TYPE_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_SUPPLIER_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        supplierTypes: action.supplierTypes
      };
    }
    case "DEACTIVATE_SUPPLIER_TYPE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_SUPPLIER_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_SUPPLIER_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_SUPPLIER_TYPE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_SUPPLIER_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_SUPPLIER_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
