const initState = {
  taxes: [],
  tax: null,
  accounts: [],
  search: null,
  filter: null,
  loading: false,
  loadingEdit:false,
  loadingFilterAccounts: true,
  current_page: 1,
  page_size: null,
  total: null,
  success:null,
  error: null,
  errorField: null
};

export default function taxesReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_TAXES_REQUEST': {
      return {
        ...state,
        //taxes: [],  
        accounts: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField:null,
      };
    }
    case 'GET_TAXES_SUCCESS': {
      return {
        ...state,
        taxes: action.taxes,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case "GET_TAXES_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case 'CREATE_TAX_REQUEST': {
      return {
        ...state,
        tax: null,
        errorField:null,
        loadingFilterAccounts: true,
      };
    }

    case 'GET_FILTER_ACCOUNTS_SUCCESS': {
      return {
        ...state,
        loadingFilterAccounts: false,
        accounts: action.accounts
      };
    }
    case 'GET_FILTER_ACCOUNTS_ERROR': {
      return {
        ...state,
        error: action.error.message,
      };
    }
    case 'STORE_TAX_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField:null,
      };
    }
    case 'STORE_TAX_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }

    case 'STORE_TAX_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }

    case 'EDIT_TAX_REQUEST': {
      return {
        ...state,
        accounts:[],
        loading: true,
        loadingEdit: true,
        loadingFilterAccounts: true,
        errorField: null
      };
    }
    case 'EDIT_TAX_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        tax: action.tax
      };
    }
    case "EDIT_TAX_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingEdit: false,
        error: action.error.message
      };
    }
    case 'UPDATE_TAX_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField:null,
      };
    }
    case 'UPDATE_TAX_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }

    case 'UPDATE_TAX_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }

    case 'SHOW_TAX_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case 'SHOW_TAX_SUCCESS': {
      return {
        ...state,
        loading: false,
        taxes: action.taxes
      };
    }
    case "DEACTIVATE_TAX_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_TAX_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_TAX_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_TAX_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_TAX_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_TAX_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
