import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getBalanceSheetComparativeReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/balance-sheets/comparative?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [balance_sheet_percentage_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/balance-sheets/foreign-keys")
      ]);
    
      // Jika sukses get data balance_sheet_percentage_reports
      if (balance_sheet_percentage_reports.status === "success") {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_BALANCE_SHEET_COMPARATIVE_REPORTS_SUCCESS",
          first_balance_sheet_reports: balance_sheet_percentage_reports.result.first_balance_sheet,
          second_balance_sheet_reports: balance_sheet_percentage_reports.result.second_balance_sheet,
          filter
        });
      } else if (balance_sheet_percentage_reports.status === "fail") {
        yield put({
          type: "GET_BALANCE_SHEET_COMPARATIVE_REPORTS_ERROR",
          error: { message: balance_sheet_percentage_reports.message }
        });
      } else {
        yield put({
          type: "GET_BALANCE_SHEET_COMPARATIVE_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data balance_sheet_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_BALANCE_SHEET_COMPARATIVE_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_BALANCE_SHEET_COMPARATIVE_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_BALANCE_SHEET_COMPARATIVE_REPORT_FOREIGN_KEYS_ERROR",
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/balance-sheets/foreign-keys")
      ]);
  
      yield put({
        type: "GET_BALANCE_SHEET_COMPARATIVE_REPORTS_SUCCESS",
        first_balance_sheet_reports: [],
        second_balance_sheet_reports: [],
      });
    
      // Jika sukses get data balance_sheet_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_BALANCE_SHEET_COMPARATIVE_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_BALANCE_SHEET_COMPARATIVE_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_BALANCE_SHEET_COMPARATIVE_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_BALANCE_SHEET_COMPARATIVE_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}
  

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_BALANCE_SHEET_COMPARATIVE_REPORTS_REQUEST",
      getBalanceSheetComparativeReports
    )
  ]);
}
