import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPurchaseReceipts({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [purchaseReceipts, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "purchases/purchase-receipts?search=" + search + "&page=" + page
            ),
            call(get, "purchases/purchase-receipts/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "purchases/purchase-receipts?" + filter + "&page=" + page),
            call(get, "purchases/purchase-receipts/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "purchases/purchase-receipts?search=" + search),
          call(get, "purchases/purchase-receipts/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "purchases/purchase-receipts?" + filter),
          call(get, "purchases/purchase-receipts/foreign-keys")
        ]);

    // console.log(purchaseReceipts);

    // Jika sukses get data purchaseReceipts
    if (purchaseReceipts.status === "success") {
      // Jika di API menggunakan pagination
      if (purchaseReceipts.result.current_page && search) {
        yield put({
          type: "GET_PURCHASE_RECEIPTS_SUCCESS",
          purchaseReceipts: purchaseReceipts.result.data,
          current_page: purchaseReceipts.result.current_page,
          page_size: purchaseReceipts.result.per_page,
          total: purchaseReceipts.result.total,
          search: search
        });
      } else if (purchaseReceipts.result.current_page && filter) {
        yield put({
          type: "GET_PURCHASE_RECEIPTS_SUCCESS",
          purchaseReceipts: purchaseReceipts.result.data,
          current_page: purchaseReceipts.result.current_page,
          page_size: purchaseReceipts.result.per_page,
          total: purchaseReceipts.result.total,
          filter: filter
        });
      } else if (purchaseReceipts.result.current_page) {
        yield put({
          type: "GET_PURCHASE_RECEIPTS_SUCCESS",
          purchaseReceipts: purchaseReceipts.result.data,
          current_page: purchaseReceipts.result.current_page,
          page_size: purchaseReceipts.result.per_page,
          total: purchaseReceipts.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PURCHASE_RECEIPTS_SUCCESS",
          purchaseReceipts: purchaseReceipts.result,
          page_size: null,
          total: null
        });
      }
    } else if (purchaseReceipts.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_PURCHASE_RECEIPTS_ERROR",
        error: { message: purchaseReceipts.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_PURCHASE_RECEIPTS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_SUCCESS",
        purchaseOrders: foreign.result.purchase_orders,
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        accounts: foreign.result.accounts,
        cashBanks: foreign.result.cash_banks,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_PURCHASE_RECEIPTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createPurchaseReceipt() {
  try {
    const [transactions, foreign] = yield all([
      call(get, "purchases/purchase-orders?transaction_status=1"),
      call(get, "purchases/purchase-receipts/foreign-keys")
    ]);
  
    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_PO_PURCHASE_RECEIPT_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_PO_PURCHASE_RECEIPT_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PO_PURCHASE_RECEIPT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_SUCCESS",
        purchaseOrders: foreign.result.purchase_orders,
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        accounts: foreign.result.accounts,
        cashBanks: foreign.result.cash_banks,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_PO_PURCHASE_RECEIPT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getTransactions({ payload }) {
  const { page, search, filter } = payload;
  // console.log(filter);

  try {
    const transactions = filter
      ? yield call(
          get,
          "purchases/purchase-orders?transaction_status=1&" +
            filter +
            "&page=" +
            page
        )
      : search
      ? yield call(
          get,
          "purchases/purchase-orders?transaction_status=1&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(
          get,
          "purchases/purchase-orders?transaction_status=1" + "&page=" + page
        );

    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_PO_PURCHASE_RECEIPT_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_PO_PURCHASE_RECEIPT_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PO_PURCHASE_RECEIPT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_PO_PURCHASE_RECEIPT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storePurchaseReceipt({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "purchases/purchase-receipts", body);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_PURCHASE_RECEIPT_SUCCESS",
        success: { message: resultStore.message },
        id_purchase_receipt: resultStore.result.id_purchase_receipt
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_PURCHASE_RECEIPT_ERROR",
        error: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_PURCHASE_RECEIPT_ERROR",
        error: { message: resultStore.message }
      });
    } else {
      yield put({
        type: "STORE_PURCHASE_RECEIPT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_PURCHASE_RECEIPT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editPurchaseReceipt({ payload }) {
  // console.log("masuk");
  const { id_purchase_receipt } = payload;
  let transactions = [];

  try {
    const [purchaseReceipt, foreign] = yield all([
      call(get, "purchases/purchase-receipts/" + id_purchase_receipt),
      call(get, "purchases/purchase-receipts/foreign-keys")
    ]);
  
    // console.log(purchaseReceipt);
  
    // Jika sukses
    if (purchaseReceipt.status === "success") {
      // Get RQ by id_location
      const filter =
        "id_location=" +
        purchaseReceipt.result.id_receipt_location +
        "&id_supplier=" +
        purchaseReceipt.result.id_supplier;
      const success = yield all([
        yield put({
          type: "EDIT_PURCHASE_RECEIPT_SUCCESS",
          purchaseReceipt: purchaseReceipt.result
        }),
        yield call(
          get,
          "purchases/purchase-orders?transaction_status=1&" + filter
        )
      ]);
      transactions = success[1];
    } else if (purchaseReceipt.status === "fail") {
      yield put({
        type: "EDIT_PURCHASE_RECEIPT_ERROR",
        error: { message: purchaseReceipt.message }
      });
    } else {
      yield put({
        type: "EDIT_PURCHASE_RECEIPT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_PO_PURCHASE_RECEIPT_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_PO_PURCHASE_RECEIPT_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_PO_PURCHASE_RECEIPT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_SUCCESS",
        purchaseOrders: foreign.result.purchase_orders,
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        accounts: foreign.result.accounts,
        cashBanks: foreign.result.cash_banks,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_PURCHASE_RECEIPT_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_PURCHASE_RECEIPT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updatePurchaseReceipt({ payload }) {
  const { id_purchase_receipt, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "purchases/purchase-receipts/" + id_purchase_receipt,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_PURCHASE_RECEIPT_SUCCESS",
          success: { message: resultUpdate.message },
          id_purchase_receipt: id_purchase_receipt
        }),
        yield put({
          type: "EDIT_PURCHASE_RECEIPT_REQUEST",
          payload: {
            id_purchase_receipt: id_purchase_receipt
          }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_PURCHASE_RECEIPT_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_PURCHASE_RECEIPT_REQUEST",
          payload: {
            id_purchase_receipt: id_purchase_receipt
          }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_PURCHASE_RECEIPT_ERROR",
        error: { message: resultUpdate.message }
      });
    } else {
      yield put({
        type: "UPDATE_PURCHASE_RECEIPT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_PURCHASE_RECEIPT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

