// delete document
export function* deletePurchaseReceiptDocument({ payload }) {
  const { id_purchase_receipt_document, id_purchase_receipt } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "purchases/purchase-receipts/documents/" + id_purchase_receipt_document
      //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_PURCHASE_RECEIPT_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_PURCHASE_RECEIPT_REQUEST",
          payload: { id_purchase_receipt: id_purchase_receipt }
        })
      ]);
    } else if (deleteResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_PURCHASE_RECEIPT_DOCUMENT_ERROR",
          error: { message: deleteResult.message }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_PURCHASE_RECEIPT_REQUEST",
          payload: { id_purchase_receipt: id_purchase_receipt }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_PURCHASE_RECEIPT_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_PURCHASE_RECEIPT_REQUEST",
          payload: { id_purchase_receipt: id_purchase_receipt }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DELETE_PURCHASE_RECEIPT_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "EDIT_PURCHASE_RECEIPT_REQUEST",
        payload: { id_purchase_receipt: id_purchase_receipt }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_PURCHASE_RECEIPTS_REQUEST", getPurchaseReceipts),
    yield takeEvery("CREATE_PURCHASE_RECEIPT_REQUEST", createPurchaseReceipt),
    yield takeEvery("GET_FILTER_PO_PURCHASE_RECEIPT_REQUEST", getTransactions),
    yield takeEvery("STORE_PURCHASE_RECEIPT_REQUEST", storePurchaseReceipt),
    yield takeEvery("EDIT_PURCHASE_RECEIPT_REQUEST", editPurchaseReceipt),
    yield takeEvery("UPDATE_PURCHASE_RECEIPT_REQUEST", updatePurchaseReceipt),
    yield takeEvery("SHOW_PURCHASE_RECEIPT_REQUEST", showPurchaseReceipt),
    yield takeEvery("CONFIRM_PURCHASE_RECEIPT_REQUEST", confirmPurchaseReceipt),
    yield takeEvery("REJECT_PURCHASE_RECEIPT_REQUEST", rejectPurchaseReceipt),
    yield takeEvery("CANCEL_PURCHASE_RECEIPT_REQUEST", cancelPurchaseReceipt),
    yield takeEvery(
      "UPLOAD_PURCHASE_RECEIPT_DOCUMENT_REQUEST",
      uploadPurchaseReceiptDocument
    ),
    yield takeEvery(
      "DOWNLOAD_PURCHASE_RECEIPT_DOCUMENT_REQUEST",
      downloadPurchaseReceiptDocument
    ),
    yield takeEvery(
      "DELETE_PURCHASE_RECEIPT_DOCUMENT_REQUEST",
      deletePurchaseReceiptDocument
    )
  ]);
}
