const initState = {
  id_purchase_invoice: null,
  id_location: null,
  id_supplier: null,
  baseCurrency: null,
  purchaseInvoices: [],
  purchaseInvoice: null,
  items: [],
  itemDiscount: [],
  purchaseReceipts: [],
  prevTransConfirm: {},
  purchaseOrders: [],
  transactions: [],
  prevTrans: [],
  itemRequests: [],
  suppliers: [],
  taxes: [],
  accounts: [],
  cashBanks: [],
  cashFlowTypes: [],
  currencies: [],
  currencyRates: [],
  paymentCategories: [],
  paymentTerms: [],
  projects: [],
  departments: [],
  couriers: [],
  // selectedTransaction: null,
  locations: [],
  search: null,
  filter: null,
  loading: false,
  loadingForeign: false,
  loadingSubmit: false,
  loadingTable: false,
  loadingItems: false,
  loadingPurchaseReceipts: false,
  current_page: 1,
  page_size: null,
  total: null,
  current_page_pr: 1,
  page_size_pr: null,
  total_pr: null,
  success: null,
  error: null,
  errorField: null,
  tax_bases: []
};

export default function purchaseInvoicesReducer(state = initState, action) {
  switch (action.type) {
    case "GET_PURCHASE_INVOICES_REQUEST": {
      return {
        ...state,
        //purchaseInvoices: [],
        itemDiscount: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_PURCHASE_INVOICES_SUCCESS": {
      return {
        ...state,
        purchaseInvoices: action.purchaseInvoices,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PURCHASE_INVOICES_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_PURCHASE_INVOICE_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_PURCHASE_INVOICE_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        baseCurrency: action.baseCurrency,
        locations: action.locations,
        suppliers: action.suppliers,
        currencies: action.currencies,
        taxes: action.taxes,
        accounts: action.accounts,
        cashBanks: action.cashBanks,
        cashFlowTypes: action.cashFlowTypes,
        paymentCategories: action.paymentCategories,
        paymentTerms: action.paymentTerms,
        departments: action.departments,
        projects: action.projects,
        couriers: action.couriers,
        prevTrans: action.prevTrans,
        prevTransConfirm: action.prevTransConfirm,
        purchaseOrders: action.purchaseOrders
      };
    }
    case "GET_PURCHASE_INVOICE_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_PURCHASE_INVOICE_REQUEST": {
      return {
        ...state,
        id_purchase_invoice: null,
        id_location: null,
        purchaseInvoice: null,
        itemDiscount: [],
        loadingTransactions: true,
        loadingPurchaseReceipts: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        current_page_pr: 1,
        page_size_pr: null,
        total_pr: null,
        tax_bases: []
      };
    }
    case "GET_FILTER_ITEMS_BY_SUPPLIER_PI_REQUEST": {
      return {
        ...state,
        loadingItems: true,
        search: action.payload.search,
        id_location: action.payload.id_location,
        id_supplier: action.payload.id_supplier
      };
    }
    case "GET_FILTER_ITEMS_BY_SUPPLIER_PI_SUCCESS": {
      return {
        ...state,
        loadingItems: false,
        items: action.items
      };
    }
    case "GET_FILTER_ITEMS_BY_SUPPLIER_PI_ERROR": {
      return {
        ...state,
        loadingItems: false,
        error: action.error.message
      };
    }
    case "GET_DISCOUNT_PI_REQUEST": {
      return {
        ...state,
        loadingTable: true
      };
    }
    case "GET_DISCOUNT_PI_SUCCESS": {
      return {
        ...state,
        loadingTable: false,
        itemDiscount: action.itemDiscount
      };
    }
    case "GET_DISCOUNT_PI_ERROR": {
      return {
        ...state,
        loadingTable: false,
        itemDiscount: action.itemDiscount,
        error: action.error.message
      };
    }
    case "GET_FILTER_PURCHASE_INVOICES_REQUEST": {
      return {
        ...state,
        loadingTransactions: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_FILTER_PURCHASE_INVOICES_SUCCESS": {
      return {
        ...state,
        loadingTransactions: false,
        transactions: action.transactions,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_FILTER_PURCHASE_INVOICES_ERROR": {
      return {
        ...state,
        loadingTransactions: false,
        error: action.error.message
      };
    }
    case "GET_FILTER_PURCHASE_RECEIPTS_PI_REQUEST": {
      return {
        ...state,
        loadingPurchaseReceipts: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page_pr: action.payload.page
      };
    }
    case "GET_FILTER_PURCHASE_RECEIPTS_SUCCESS": {
      return {
        ...state,
        loadingPurchaseReceipts: false,
        purchaseReceipts: action.purchaseReceipts,
        current_page_pr: action.current_page,
        page_size_pr: action.page_size,
        total_pr: action.total
      };
    }
    case "GET_FILTER_PURCHASE_RECEIPTS_ERROR": {
      return {
        ...state,
        loadingPurchaseReceipts: false,
        error: action.error.message
      };
    }
    case "STORE_PURCHASE_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_PURCHASE_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_purchase_invoice: action.id_purchase_invoice
      };
    }
    case "STORE_PURCHASE_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_PURCHASE_INVOICE_REQUEST": {
      return {
        ...state,
        loading: true,
        id_purchase_invoice: null,
        id_location: null,
        loadingTransactions: true,
        loadingPurchaseReceipts: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        current_page_pr: 1,
        page_size_pr: null,
        total_pr: null,
        tax_bases: []
      };
    }
    case "EDIT_PURCHASE_INVOICE_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        purchaseInvoice: action.purchaseInvoice
      };
    }
    case "EDIT_PURCHASE_INVOICE_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_PURCHASE_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_PURCHASE_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_purchase_invoice: action.id_purchase_invoice
      };
    }
    case "UPDATE_PURCHASE_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_PURCHASE_INVOICE_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_PURCHASE_INVOICE_SUCCESS": {
      return {
        ...state,
        loading: false,
        purchaseInvoices: action.purchaseInvoices
      };
    }
    case "CONFIRM_PURCHASE_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CONFIRM_PURCHASE_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CONFIRM_PURCHASE_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UNCONFIRM_PURCHASE_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "UNCONFIRM_PURCHASE_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UNCONFIRM_PURCHASE_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CLOSE_PURCHASE_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CLOSE_PURCHASE_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CLOSE_PURCHASE_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CANCEL_PURCHASE_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CANCEL_PURCHASE_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CANCEL_PURCHASE_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPLOAD_PURCHASE_INVOICE_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_PURCHASE_INVOICE_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_PURCHASE_INVOICE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_PURCHASE_INVOICE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_PURCHASE_INVOICE_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_PURCHASE_INVOICE_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_PURCHASE_INVOICE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "SET_TAX_BASES_PURCHASE_INVOICE": {
      return {
        ...state,
        tax_bases: action.payload.tax_bases
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null,
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
