
const initState = {
  sizes: [],
  size: null,
  units: [],
  search: null,
  //filter: null,
  loading: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function sizesReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_SIZES_REQUEST': {
      return {
        ...state,
        //sizes: [],
        loading: true,
        search: action.payload.search,
        //filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_SIZES_SUCCESS': {
      return {
        ...state,
        sizes: action.sizes,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        //filter: action.filter,
        loading: false,
      };
    }
    case 'GET_SIZES_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'CREATE_SIZE_REQUEST': {
      return {
        ...state,
        // size: null,
        errorField: null,
      };
    }
    case "GET_SIZE_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        units: action.units,
      }
    }
    case "GET_SIZE_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        errorField: action.error.errors,
        error: action.error.message
      }
    }
    case 'STORE_SIZE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_SIZE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_SIZE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_SIZE_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_SIZE_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        size: action.size,
      };
    }
    case 'EDIT_SIZE_ERROR': {
      return {
        ...state,
        //loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'UPDATE_SIZE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_SIZE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_SIZE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_SIZE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_SIZE_SUCCESS': {
      return {
        ...state,
        loading: false,
        sizes: action.sizes
      };
    }
    case "DEACTIVATE_SIZE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_SIZE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_SIZE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_SIZE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_SIZE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_SIZE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
