const initState = {
  serverAddress: "",
  result: [],
  validateStatus: "",
  loading: false,
  loadingModal: false,
  typing: false,
  buttonLabel: "page.submit",
  success: null,
  error: null
};

export default function reducer(state = initState, action) {
  switch (action.type) {
    case "SERVER_SETUP_REQUEST":
      return {
        ...state,
        serverAddress: "",
        result: [],
        validateStatus: "",
        loading: false,
        loadingModal: false,
        typing: false,
        buttonLabel: "page.submit",
        success: null,
        error: null
      };
    case "TYPING_SERVER":
      return {
        ...state,
        typing: true
      };
    case "SERVER_VALIDATE":
      return {
        ...state,
        validateStatus: "validating",
        loading: true,
        loadingModal: true,
        typing: false,
        buttonLabel: "page.validating",
        serverAddress: action.payload.serverAddress
      };
    case "SERVER_SUCCESS_VALIDATE":
      return {
        ...state,
        validateStatus: "success",
        loading: true,
        loadingModal: false,
        buttonLabel: "page.redirecting",
        result: action.result,
        success: "Successfully setup server"
      };
    case "SERVER_FAIL_VALIDATE":
      console.log("action", action)
      return {
        ...state,
        validateStatus: "error",
        loading: false,
        loadingModal: false,
        buttonLabel: "page.submit",
        result: [],
        error: action.message
      };
    case "SERVER_ERROR_VALIDATE":
      return {
        ...state,
        validateStatus: "error",
        loading: false,
        loadingModal: false,
        buttonLabel: "page.submit",
        result: [],
        error: "Please enter correct address"
      };
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    default:
      return state;
  }
}
