import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getRetainedEarningReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/retained-earnings?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [retained_earning_reports] = yield all([
        call(get, url)
        // call(get, "reports/account-histories/foreign-keys")
      ]);
  
      // Jika sukses get data retained_earning_reports
      if (
        retained_earning_reports.status === "success" &&
        retained_earning_reports.result !== null
      ) {
        
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_RETAINED_EARNING_REPORTS_SUCCESS",
          retained_earning_reports: retained_earning_reports.result,
          filter
        });
        
      } else if (
        retained_earning_reports.status === "success" &&
        retained_earning_reports.result === null
      ) {
        yield put({
          type: "GET_RETAINED_EARNING_REPORTS_SUCCESS",
          retained_earning_reports: [],
          filter
        });
      } else if (retained_earning_reports.status === "fail") {
        yield put({
          type: "GET_RETAINED_EARNING_REPORTS_ERROR",
          error: { message: retained_earning_reports.message }
        });
      } else {
        yield put({
          type: "GET_RETAINED_EARNING_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      yield put({
        type: "GET_RETAINED_EARNING_REPORTS_SUCCESS",
        retained_earning_reports: []
      });
    }
  } catch (error) {
    yield put({
      type: "GET_RETAINED_EARNING_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_RETAINED_EARNING_REPORTS_REQUEST",
      getRetainedEarningReports
    )
  ]);
}
