
const initState = {
  bank_reconciles: [],
  bank_reconcile: null,
  show_bank_reconcile: null,
  locations: [],
  cash_banks: [],
  currencies: [],
  messages: null,
  search: null,
  filter: null,
  loading: false,
  loadingEdit:false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_bank_reconcile: null,
};

export default function bankReconcilesReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_BANK_RECONCILES_REQUEST': {
      return {
        ...state,
        //bank_reconciles: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_BANK_RECONCILES_SUCCESS': {
      return {
        ...state,
        bank_reconciles: action.bank_reconciles,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_BANK_RECONCILES_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      }
    }
    case 'CREATE_BANK_RECONCILE_REQUEST': {
      return {
        ...state,
        show_bank_reconcile: null,
        errorField: null,
      };
    }

    case 'GET_BANK_RECONCILE_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        locations: action.locations,
        cash_banks: action.cash_banks,
        currencies: action.currencies,
        messages: action.messages
      };
    }
    case 'GET_BANK_RECONCILE_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message
      };
    }
    
    case 'STORE_BANK_RECONCILE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_BANK_RECONCILE_SUCCESS': {
      return {
        ...state,
        loading: false,
        id_bank_reconcile: action.id_bank_reconcile,
        success: action.success.message
      };
    }
    case 'STORE_BANK_RECONCILE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_BANK_RECONCILE_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit:true,
        errorField: null,
        show_bank_reconcile: null,
      };
    }
    case 'EDIT_BANK_RECONCILE_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit:false,
        show_bank_reconcile: action.bank_reconcile
      };
    }
    case 'EDIT_BANK_RECONCILE_ERROR': {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case 'UPDATE_BANK_RECONCILE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_BANK_RECONCILE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_BANK_RECONCILE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_BANK_RECONCILE_REQUEST': {
      return {
        ...state,
        loading: true,
        show_bank_reconcile: null, 
        errorField: null,
      };
    }
    case 'SHOW_BANK_RECONCILE_SUCCESS': {
      return {
        ...state,
        loading: false,
        show_bank_reconcile: action.show_bank_reconcile
      };
    }
    case 'SHOW_BANK_RECONCILE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'NULL_BANK_RECONCILE': {
      return {
        ...state,
        //loading: true,
        show_bank_reconcile: null, 
        //errorField: null,
      };
    }
    case "PROCESS_BANK_RECONCILE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_BANK_RECONCILE_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_BANK_RECONCILE_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_BANK_RECONCILE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_BANK_RECONCILE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_BANK_RECONCILE_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_BANK_RECONCILE_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_BANK_RECONCILE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
