import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getDeliveryOrders({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [deliveryOrders, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "sales/delivery-orders?search=" + search + "&page=" + page),
            call(get, "sales/delivery-orders/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "sales/delivery-orders?" + filter + "&page=" + page),
            call(get, "sales/delivery-orders/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "sales/delivery-orders?search=" + search),
          call(get, "sales/delivery-orders/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "sales/delivery-orders?" + filter),
          call(get, "sales/delivery-orders/foreign-keys")
        ]);

    // Jika sukses get data deliveryOrders
    if (deliveryOrders.status === "success") {
      // Jika di API menggunakan pagination
      if (deliveryOrders.result.current_page && search) {
        yield put({
          type: "GET_DELIVERY_ORDERS_SUCCESS",
          deliveryOrders: deliveryOrders.result.data,
          current_page: deliveryOrders.result.current_page,
          page_size: deliveryOrders.result.per_page,
          total: deliveryOrders.result.total,
          search: search
        });
      } else if (deliveryOrders.result.current_page && filter) {
        yield put({
          type: "GET_DELIVERY_ORDERS_SUCCESS",
          deliveryOrders: deliveryOrders.result.data,
          current_page: deliveryOrders.result.current_page,
          page_size: deliveryOrders.result.per_page,
          total: deliveryOrders.result.total,
          filter: filter
        });
      } else if (deliveryOrders.result.current_page) {
        yield put({
          type: "GET_DELIVERY_ORDERS_SUCCESS",
          deliveryOrders: deliveryOrders.result.data,
          current_page: deliveryOrders.result.current_page,
          page_size: deliveryOrders.result.per_page,
          total: deliveryOrders.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_DELIVERY_ORDERS_SUCCESS",
          deliveryOrders: deliveryOrders.result,
          page_size: null,
          total: null
        });
      }
    } else if (deliveryOrders.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_DELIVERY_ORDERS_ERROR",
        error: { message: deliveryOrders.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_DELIVERY_ORDERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_DELIVERY_ORDER_FOREIGN_KEYS_SUCCESS",
        customers: foreign.result.customers,
        departments: foreign.result.departments,
        employees: foreign.result.employees,
        locations: foreign.result.locations,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
        salesOrders: foreign.result.sales_orders
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_DELIVERY_ORDER_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_DELIVERY_ORDER_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_DELIVERY_ORDERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

