export function* getTransactions({ payload }) {
  const { page, search, filter } = payload;
  // console.log(filter);

  try {
    const transactions = filter
      ? yield call(
          get,
          "sales/sales-orders?transaction_status=1&" + filter + "&page=" + page
        )
      : search
      ? yield call(
          get,
          "sales/sales-orders?transaction_status=1&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(
          get,
          "sales/sales-orders?transaction_status=1" + "&page=" + page
        );

    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_SO_DELIVERY_ORDER_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_SO_DELIVERY_ORDER_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_SO_DELIVERY_ORDER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_SO_DELIVERY_ORDER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeDeliveryOrder({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "sales/delivery-orders", body);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_DELIVERY_ORDER_SUCCESS",
        info: resultStore.info,
        success: { message: resultStore.message },
        id_delivery_order: resultStore.result.id_delivery_order
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_DELIVERY_ORDER_ERROR",
        error: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_DELIVERY_ORDER_ERROR",
        error: { message: resultStore.message }
      });
    } else {
      yield put({
        type: "STORE_DELIVERY_ORDER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_DELIVERY_ORDER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editDeliveryOrder({ payload }) {
  // console.log("masuk");
  const { id_delivery_order } = payload;

  try {
    let transactions = yield call(get, "sales/sales-orders?transaction_status=1");
    const [deliveryOrder, foreign] = yield all([
      call(get, "sales/delivery-orders/" + id_delivery_order),
      call(get, "sales/delivery-orders/foreign-keys")
    ]);

    // console.log(deliveryOrder);

    // Jika sukses
    if (deliveryOrder.status === "success") {
      yield put({
        type: "EDIT_DELIVERY_ORDER_SUCCESS",
        deliveryOrder: deliveryOrder.result
      });
    } else if (deliveryOrder.status === "fail") {
      yield put({
        type: "EDIT_DELIVERY_ORDER_ERROR",
        error: { message: deliveryOrder.message }
      });
    } else {
      yield put({
        type: "EDIT_DELIVERY_ORDER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_SO_DELIVERY_ORDER_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_SO_DELIVERY_ORDER_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_SO_DELIVERY_ORDER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_DELIVERY_ORDER_FOREIGN_KEYS_SUCCESS",
        customers: foreign.result.customers,
        departments: foreign.result.departments,
        employees: foreign.result.employees,
        locations: foreign.result.locations,
        projects: foreign.result.projects,
        couriers: foreign.result.couriers,
        salesOrders: foreign.result.sales_orders
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_DELIVERY_ORDER_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_DELIVERY_ORDER_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_DELIVERY_ORDER_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateDeliveryOrder({ payload }) {
  const { id_delivery_order, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "sales/delivery-orders/" + id_delivery_order,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_DELIVERY_ORDER_SUCCESS",
          success: { message: resultUpdate.message },
          info: resultUpdate.info,
          id_delivery_order: id_delivery_order
        }),
        yield put({
          type: "EDIT_DELIVERY_ORDER_REQUEST",
          payload: {
            id_delivery_order: id_delivery_order
          }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_DELIVERY_ORDER_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_DELIVERY_ORDER_REQUEST",
          payload: {
            id_delivery_order: id_delivery_order
          }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_DELIVERY_ORDER_ERROR",
        error: { message: resultUpdate.message }
      });
    } else {
      yield put({
        type: "UPDATE_DELIVERY_ORDER_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_DELIVERY_ORDER_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

