// Upload Document
export function* uploadDeliveryOrderDocument({ payload }) {
  const { id_delivery_order, body } = payload;
  let data = {
    ...body,
    id_delivery_order
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "sales/delivery-orders/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_DELIVERY_ORDER_DOCUMENT_SUCCESS",
          message: uploadResult.message,
          success: uploadResult
        }),
        yield put({
          type: "EDIT_DELIVERY_ORDER_REQUEST",
          payload: { id_delivery_order }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_DELIVERY_ORDER_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        }),
        yield put({
          type: "EDIT_DELIVERY_ORDER_REQUEST",
          payload: { id_delivery_order }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_DELIVERY_ORDER_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        }),
        yield put({
          type: "EDIT_DELIVERY_ORDER_REQUEST",
          payload: { id_delivery_order }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_DELIVERY_ORDER_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_DELIVERY_ORDER_REQUEST",
          payload: { id_delivery_order }
        })
      ]);
    }

    // }
  } catch (error) {
    yield all([
      yield put({
        type: "UPLOAD_DELIVERY_ORDER_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_DELIVERY_ORDER_REQUEST",
        payload: { id_delivery_order }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

