import { all, takeEvery, put, call } from "redux-saga/effects";
import {
  get,
  put as putApi,
  post,
  postForm,
  axiosGet
} from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getRaptorSalesReturns({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [raptorSalesReturns, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "raptor/sales-returns?search=" + search + "&page=" + page
            ),
            call(get, "raptor/sales-invoices/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "raptor/sales-returns?" + filter + "&page=" + page),
            call(get, "raptor/sales-invoices/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "raptor/sales-returns?search=" + search),
          call(get, "raptor/sales-invoices/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "raptor/sales-returns?" + filter),
          call(get, "raptor/sales-invoices/foreign-keys")
        ]);

    // console.log(raptorSalesReturns);

    // Jika sukses get data raptorSalesReturns
    if (raptorSalesReturns.status === "success") {
      // Jika di API menggunakan pagination
      if (raptorSalesReturns.result.current_page && search) {
        yield put({
          type: "GET_RAPTOR_SALES_RETURNS_SUCCESS",
          raptorSalesReturns: raptorSalesReturns.result.data,
          current_page: raptorSalesReturns.result.current_page,
          page_size: raptorSalesReturns.result.per_page,
          total: raptorSalesReturns.result.total,
          search: search
        });
      } else if (raptorSalesReturns.result.current_page && filter) {
        yield put({
          type: "GET_RAPTOR_SALES_RETURNS_SUCCESS",
          raptorSalesReturns: raptorSalesReturns.result.data,
          current_page: raptorSalesReturns.result.current_page,
          page_size: raptorSalesReturns.result.per_page,
          total: raptorSalesReturns.result.total,
          filter: filter
        });
      } else if (raptorSalesReturns.result.current_page) {
        yield put({
          type: "GET_RAPTOR_SALES_RETURNS_SUCCESS",
          raptorSalesReturns: raptorSalesReturns.result.data,
          current_page: raptorSalesReturns.result.current_page,
          page_size: raptorSalesReturns.result.per_page,
          total: raptorSalesReturns.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_RAPTOR_SALES_RETURNS_SUCCESS",
          raptorSalesReturns: raptorSalesReturns.result,
          page_size: null,
          total: null
        });
      }
    } else if (raptorSalesReturns.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_RAPTOR_SALES_RETURNS_ERROR",
        error: { message: raptorSalesReturns.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_RAPTOR_SALES_RETURNS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      // console.log(foreign.result.delivery_orders);
      yield put({
        type: "GET_RAPTOR_SALES_RETURN_FOREIGN_KEYS_SUCCESS",
        base_currency: foreign.result.base_currency,
        cash_banks: foreign.result.cash_banks,
        customers: foreign.result.customers,
        info_message: foreign.result.info_message,
        item_categories: foreign.result.item_categories,
        locations: foreign.result.locations,
        units: foreign.result.units,
        warning_message: foreign.result.warning_message,
        payment_categories: foreign.result.payment_categories,
        payment_terms: foreign.result.payment_terms
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_RAPTOR_SALES_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_RAPTOR_SALES_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_RAPTOR_SALES_RETURNS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

export function* synchronizeRaptor() {
  try {
    const sync = yield call(axiosGet, "raptor/import-sales");

    if (sync.status === "success") {
      yield all([
        yield put({
          type: "SYNCHRONIZE_RAPTOR_SALES_RETURNS_SUCCESS",
          success: sync
        }),
        yield put({
          type: "GET_RAPTOR_SALES_RETURNS_REQUEST",
          payload: {
            page: 1,
            search: null,
            filter: undefined
          }
        })
      ]);
    } else if (sync.status === "fail") {
      yield put({
        type: "SYNCHRONIZE_RAPTOR_SALES_RETURNS_ERROR",
        error: { message: sync.message }
      });
    } else {
      yield put({
        type: "SYNCHRONIZE_RAPTOR_SALES_RETURNS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "SYNCHRONIZE_RAPTOR_SALES_RETURNS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Edit data
export function* editRaptorSalesReturn({ payload }) {
  const { id_sales_return } = payload;

  try {
    const [raptorSalesReturn, foreign] = yield all([
      call(get, "raptor/sales-returns/" + id_sales_return),
      call(get, "raptor/sales-invoices/foreign-keys")
    ]);

    // Jika sukses
    if (raptorSalesReturn.status === "success") {
      yield put({
        type: "EDIT_RAPTOR_SALES_RETURN_SUCCESS",
        raptorSalesReturn: raptorSalesReturn.result
      });
    } else if (raptorSalesReturn.status === "fail") {
      yield put({
        type: "EDIT_RAPTOR_SALES_RETURN_ERROR",
        error: { message: raptorSalesReturn.message }
      });
    } else {
      yield put({
        type: "EDIT_RAPTOR_SALES_RETURN_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_RAPTOR_SALES_RETURN_FOREIGN_KEYS_SUCCESS",
        base_currency: foreign.result.base_currency,
        cash_banks: foreign.result.cash_banks,
        customers: foreign.result.customers,
        info_message: foreign.result.info_message,
        item_categories: foreign.result.item_categories,
        locations: foreign.result.locations,
        units: foreign.result.units,
        warning_message: foreign.result.warning_message,
        payment_categories: foreign.result.payment_categories,
        payment_terms: foreign.result.payment_terms
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_RAPTOR_SALES_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_RAPTOR_SALES_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_RAPTOR_SALES_RETURN_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Update data
export function* updateRaptorSalesReturn({ payload }) {
  const { id_sales_return, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "raptor/sales-returns/" + id_sales_return,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        put({
          type: "UPDATE_RAPTOR_SALES_RETURN_SUCCESS",
          success: { message: resultUpdate.message },
          info: resultUpdate.info,
          id_sales_return: id_sales_return
        }),
        put({
          type: "EDIT_RAPTOR_SALES_RETURN_REQUEST",
          payload: {
            id_sales_return: id_sales_return
          }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_RAPTOR_SALES_RETURN_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_RAPTOR_SALES_RETURN_REQUEST",
          payload: {
            id_sales_return: id_sales_return
          }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_RAPTOR_SALES_RETURN_ERROR",
        error: { message: resultUpdate.message }
      });
    } else {
      yield put({
        type: "UPDATE_RAPTOR_SALES_RETURN_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_RAPTOR_SALES_RETURN_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// store new item
export function* storeItem({ payload }) {
  const { id_sales_return, body } = payload;

  try {
    const resultStore = yield call(post, "items", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield all([
        yield put({
          type: "STORE_ITEM_RAPTOR_SALES_RETURN_SUCCESS",
          id_sales_return: id_sales_return,
          success: resultStore
        }),
        yield put({
          type: "EDIT_RAPTOR_SALES_RETURN_REQUEST",
          payload: {
            id_sales_return: id_sales_return
          }
        })
      ]);
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_ITEM_RAPTOR_SALES_RETURN_ERROR",
        id_sales_return: id_sales_return,
        error: resultStore
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_ITEM_RAPTOR_SALES_RETURN_ERROR",
        id_sales_return: id_sales_return,
        error: resultStore,
        errors: resultStore
      });
    } else {
      yield put({
        type: "STORE_ITEM_RAPTOR_SALES_RETURN_ERROR",
        id_sales_return: id_sales_return,
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_ITEM_RAPTOR_SALES_RETURN_ERROR",
      id_sales_return: id_sales_return,
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

