// Upload Document
export function* uploadRaptorSalesReturnDocument({ payload }) {
  const { id_sales_return, body } = payload;
  let data = {
    ...body,
    id_sales_return
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "sales/sales-returns/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_RAPTOR_SALES_RETURN_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_RAPTOR_SALES_RETURN_REQUEST",
          payload: { id_sales_return }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_RAPTOR_SALES_RETURN_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_RAPTOR_SALES_RETURN_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_RAPTOR_SALES_RETURN_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        })
      ]);
    }

    // }
  } catch (error) {
    yield put({
      type: "UPLOAD_RAPTOR_SALES_RETURN_DOCUMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadRaptorSalesReturnDocument({ payload }) {
  const { id_raptor_sales_return_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "sales/sales-returns/documents/download/" +
        id_raptor_sales_return_document
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

