import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getAssetTypes({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
      const [asset_service_types, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "assets/service-types?search=" + search + "&page=" + page),
            call(get, "assets/service-types/foreign-keys"), 
          ])
        : // Jika filter
          yield all([
            call(get, "assets/service-types?" + filter + "&page=" + page),
            call(get, "assets/service-types/foreign-keys"), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "assets/service-types?search=" + search),  
          call(get, "assets/service-types/foreign-keys"), 
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "assets/service-types?" + filter),
          call(get, "assets/service-types/foreign-keys"), 
        ]);

    // console.log(asset_service_types);
    // console.log(accounts);

    // Jika sukses get data asset_service_types
    if (asset_service_types.status === "success") {
      // Jika di API menggunakan pagination
      if (asset_service_types.result.current_page && search) {
        yield put({
          type: 'GET_ASSET_SERVICE_TYPES_SUCCESS',
          asset_service_types: asset_service_types.result.data,
          current_page: asset_service_types.result.current_page,
          page_size: asset_service_types.result.per_page,
          total: asset_service_types.result.total,
          search: search
        });
      }
      else if(asset_service_types.result.current_page && filter)
      {
        yield put({
          type: 'GET_ASSET_SERVICE_TYPES_SUCCESS',
          asset_service_types: asset_service_types.result.data,
          current_page: asset_service_types.result.current_page,
          page_size: asset_service_types.result.per_page,
          total: asset_service_types.result.total,
          filter: filter
        });
      }
      else if (asset_service_types.result.current_page) {
        yield put({
          type: 'GET_ASSET_SERVICE_TYPES_SUCCESS',
          asset_service_types: asset_service_types.result.data,
          current_page: asset_service_types.result.current_page,
          page_size: asset_service_types.result.per_page,
          total: asset_service_types.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_ASSET_SERVICE_TYPES_SUCCESS',
          asset_service_types: asset_service_types.result,
          page_size: null,
          total: null
        });
      }
    }
    else if (asset_service_types.status === "fail") {
      yield put({
        type: 'GET_ASSET_SERVICE_TYPES_ERROR',
        error: asset_service_types
      })
    } 
    else {
      yield put({
        type: 'GET_ASSET_SERVICE_TYPES_ERROR',
        error: {message: "Check your internet connection"}
      })
    }

    if(foreign_keys.status === "success"){
      yield put({
        type: 'GET_ASSET_SERVICE_TYPE_FOREIGN_KEYS_SUCCESS',
        asset_categories: foreign_keys.result.asset_categories
      });
    }
    else{
      yield put({
        type: 'GET_ASSET_SERVICE_TYPE_FOREIGN_KEYS_ERROR',
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_ASSET_SERVICE_TYPES_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
  
}


// Create data
export function* createAssetType() {
  try {
    const [foreign_keys] = yield all([
      call(get, "assets/service-types/foreign-keys"),
    ]);
  
    if(foreign_keys.status === "success"){
      yield put({
        type: 'GET_ASSET_SERVICE_TYPE_FOREIGN_KEYS_SUCCESS',
        asset_categories: foreign_keys.result.asset_categories
      });
    }
    else{
      yield put({
        type: 'GET_ASSET_SERVICE_TYPE_FOREIGN_KEYS_ERROR',
        error : {message : "Check your internet connection"}
      });
    }
  
    yield put({
      type: 'CREATE_ASSET_SERVICE_TYPE_SUCCESS',
    }); 
  } catch (error) {
    yield put({
      type: 'GET_ASSET_SERVICE_TYPE_FOREIGN_KEYS_ERROR',
      error : {message : "Check your internet connection"}
    });
    bugsnagClient.notify(error);
  }
}

// Store data
export function* storeAssetType({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "assets/service-types", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_ASSET_SERVICE_TYPE_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_ASSET_SERVICE_TYPE_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_ASSET_SERVICE_TYPE_ERROR",
        error : {message : "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_ASSET_SERVICE_TYPE_ERROR",
      error : {message : "Check your internet connection"}
    });
    bugsnagClient.notify(error);
  }
}

// Edit data
export function* editAssetType({ payload }) {
  //console.log("masuk")
  const { id_asset_service_type } = payload;
  
  try {
    const [asset_service_type, foreign_keys] = yield all([
      call(get, "assets/service-types/" + id_asset_service_type),
      call(get, "assets/service-types/foreign-keys"), 
    ]);
  
    if(foreign_keys.status === "success"){
      yield put({
        type: 'GET_ASSET_SERVICE_TYPE_FOREIGN_KEYS_SUCCESS',
        asset_categories: foreign_keys.result.asset_categories
      });
    }
    else{
      yield put({
        type: 'GET_ASSET_SERVICE_TYPE_FOREIGN_KEYS_ERROR',
       // error : {message : "Check your internet connection"}
      });
    }
    
    // Jika sukses
    if (asset_service_type.status === "success") {
      yield put({
        type: 'EDIT_ASSET_SERVICE_TYPE_SUCCESS',
        asset_service_type: asset_service_type.result
      });
    }
    else{
      yield put({
        type: 'EDIT_ASSET_SERVICE_TYPE_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'EDIT_ASSET_SERVICE_TYPE_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

// Update data
export function* updateAssetType({ payload }) {
  const { id_asset_service_type, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "assets/service-types/" + id_asset_service_type,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_ASSET_SERVICE_TYPE_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_ASSET_SERVICE_TYPE_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_ASSET_SERVICE_TYPE_ERROR",
        error : {message : "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_ASSET_SERVICE_TYPE_ERROR",
      error : {message : "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showAssetType({ payload }) {
  const { id_asset_service_type } = payload;

  try {
    const resultShow = yield call(get, "assets/service-types/" + id_asset_service_type);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_ASSET_SERVICE_TYPE_SUCCESS',
        asset_service_type: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateAssetType({ payload }) {
  const { id_asset_service_type } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "assets/service-types/" + id_asset_service_type,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_ASSET_SERVICE_TYPE_SUCCESS',
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_SERVICE_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_ASSET_SERVICE_TYPE_ERROR',
          error : deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_SERVICE_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_ASSET_SERVICE_TYPE_ERROR',
          error : {message : "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_SERVICE_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_ASSET_SERVICE_TYPE_ERROR',
        error : {message : "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_ASSET_SERVICE_TYPES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateAssetType({ payload }) {
  const { id_asset_service_type } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "assets/service-types/" + id_asset_service_type,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_ASSET_SERVICE_TYPE_SUCCESS',
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_SERVICE_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: 'ACTIVATE_ASSET_SERVICE_TYPE_ERROR',
          error : activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_SERVICE_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_ASSET_SERVICE_TYPE_ERROR',
          error : {message : "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_SERVICE_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_ASSET_SERVICE_TYPE_ERROR',
        error : {message : "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_ASSET_SERVICE_TYPES_REQUEST',
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_ASSET_SERVICE_TYPES_REQUEST', getAssetTypes),
    yield takeEvery('CREATE_ASSET_SERVICE_TYPE_REQUEST', createAssetType),
    yield takeEvery('STORE_ASSET_SERVICE_TYPE_REQUEST', storeAssetType),
    yield takeEvery('EDIT_ASSET_SERVICE_TYPE_REQUEST', editAssetType),
    yield takeEvery('UPDATE_ASSET_SERVICE_TYPE_REQUEST', updateAssetType),
    yield takeEvery('SHOW_ASSET_SERVICE_TYPE_REQUEST', showAssetType),
    yield takeEvery('DEACTIVATE_ASSET_SERVICE_TYPE_REQUEST', deactivateAssetType),
    yield takeEvery('ACTIVATE_ASSET_SERVICE_TYPE_REQUEST', activateAssetType)
  ]);
}
