// Get Customer Data
export function* getCustomersForCustomerPriceList({ payload }) {
  const { search } = payload;
  let filter =
    "customers?is_active=1&limit=10" + (search ? "&search=" + search : "");

  try {
    const customers = yield call(get, filter);

    if (customers.status === "success") {
      yield put({
        type: "GET_CUSTOMERS_FOR_CUSTOMER_PRICE_LIST_SUCCESS",
        customers: customers.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_CUSTOMER_PRICE_LISTS_REQUEST", getCustomerPriceLists),
    yield takeEvery(
      "CREATE_CUSTOMER_PRICE_LIST_REQUEST",
      createCustomerPriceList
    ),
    yield takeEvery(
      "STORE_CUSTOMER_PRICE_LIST_REQUEST",
      storeCustomerPriceList
    ),
    yield takeEvery(
      "GET_FILTER_ITEMS_FOR_CUSTOMER_PRICE_LIST_REQUEST",
      getItemsForCustomerPriceList
    ),
    yield takeEvery("EDIT_CUSTOMER_PRICE_LIST_REQUEST", editCustomerPriceList),
    yield takeEvery(
      "UPDATE_CUSTOMER_PRICE_LIST_REQUEST",
      updateCustomerPriceList
    ),
    yield takeEvery(
      "DEACTIVATE_CUSTOMER_PRICE_LIST_REQUEST",
      deactivateCustomerPriceList
    ),
    yield takeEvery(
      "ACTIVATE_CUSTOMER_PRICE_LIST_REQUEST",
      activateCustomerPriceList
    ),
    yield takeEvery(
      "REJECT_CUSTOMER_PRICE_LIST_REQUEST",
      rejectCustomerPriceList
    ),
    yield takeEvery(
      "CONFIRM_CUSTOMER_PRICE_LIST_REQUEST",
      confirmCustomerPriceList
    ),
    yield takeEvery(
      "GET_CUSTOMERS_FOR_CUSTOMER_PRICE_LIST_REQUEST",
      getCustomersForCustomerPriceList
    )
  ]);
}
