import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getRoles({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [roles, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "users/roles?search=" + search + "&page=" + page),
            call(get, "users/roles/foreign-keys"), 
          ])
        : // Jika filter
          yield all([
            call(get, "users/roles?" + filter + "&page=" + page),
            call(get, "users/roles/foreign-keys"), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "users/roles?search=" + search),  
          call(get, "users/roles/foreign-keys"), 
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "users/roles?" + filter),
          call(get, "users/roles/foreign-keys"), 
        ]);

    // console.log(roles);
    // console.log(accounts);

    // Jika sukses get data roles
    if (roles.status === "success") {
      // Jika di API menggunakan pagination
      if (roles.result.current_page && search) {
        yield put({
          type: 'GET_ROLES_SUCCESS',
          roles: roles.result.data,
          current_page: roles.result.current_page,
          page_size: roles.result.per_page,
          total: roles.result.total,
          search: search
        });
      }
      else if(roles.result.current_page && filter)
      {
        yield put({
          type: 'GET_ROLES_SUCCESS',
          roles: roles.result.data,
          current_page: roles.result.current_page,
          page_size: roles.result.per_page,
          total: roles.result.total,
          filter: filter
        });
      }
      else if (roles.result.current_page) {
        yield put({
          type: 'GET_ROLES_SUCCESS',
          roles: roles.result.data,
          current_page: roles.result.current_page,
          page_size: roles.result.per_page,
          total: roles.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_ROLES_SUCCESS',
          roles: roles.result,
          page_size: null,
          total: null
        });
      }
    }
    else if (roles.status === "fail") {
      yield put({
        type: 'GET_ROLES_ERROR',
        error: roles
      })
    }
    else {
      yield put({
        type: 'GET_ROLES_ERROR',
        error: {message: "Check your internet connection"}
      })
    }

    if(foreign_keys.status === "success"){
      yield put({
        type: 'GET_ROLE_FOREIGN_KEYS_SUCCESS',
        raptor_module: parseInt(foreign_keys.result.raptor_module),
        permissions: foreign_keys.result.permissions,
        permission_list: foreign_keys.result.permission_list
      });
    }
    else{
      yield put({
        type: 'GET_ROLE_FOREIGN_KEYS_ERROR',
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_ROLES_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
  
}


// Create data
export function* createRole() {
  try {
    const [foreign_keys] = yield all([
      call(get, "users/roles/foreign-keys"),
    ]);
  
    if(foreign_keys.status === "success"){
      yield put({
        type: 'GET_ROLE_FOREIGN_KEYS_SUCCESS',
        raptor_module: parseInt(foreign_keys.result.raptor_module),
        permissions: foreign_keys.result.permissions,
        permission_list: foreign_keys.result.permission_list
      });
    }
    else{
      yield put({
        type: 'GET_ROLE_FOREIGN_KEYS_ERROR',
        error : {message : "Check your internet connection"}
      });
    }
  
    yield put({
      type: 'CREATE_ROLE_SUCCESS',
    });
  } catch (error) {
    yield put({
      type: 'GET_ROLE_FOREIGN_KEYS_ERROR',
      error : {message : "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  } 
}

// Store data
export function* storeRole({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "users/roles", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_ROLE_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_ROLE_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_ROLE_ERROR",
        error : {message : "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_ROLE_ERROR",
      error : {message : "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editRole({ payload }) {
  //console.log("masuk")
  const { id_role } = payload;
  
  try {
    const [role, foreign_keys] = yield all([
      call(get, "users/roles/" + id_role),
      call(get, "users/roles/foreign-keys"), 
    ]);
  
    if(foreign_keys.status === "success"){
      yield put({
        type: 'GET_ROLE_FOREIGN_KEYS_SUCCESS',
        raptor_module: parseInt(foreign_keys.result.raptor_module),
        permissions: foreign_keys.result.permissions,
        permission_list: foreign_keys.result.permission_list
      });
    }
    else{
      yield put({
        type: 'GET_ROLE_FOREIGN_KEYS_ERROR',
       // error : {message : "Check your internet connection"}
      });
    }
    
    // Jika sukses
    if (role.status === "success") {
      yield put({
        type: 'EDIT_ROLE_SUCCESS',
        role: role.result
      });
    }
    else{
      yield put({
        type: 'EDIT_ROLE_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'EDIT_ROLE_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateRole({ payload }) {
  const { id_role, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "users/roles/" + id_role,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_ROLE_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_ROLE_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_ROLE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_ROLE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showRole({ payload }) {
  const { id_role } = payload;

  try {
    const resultShow = yield call(get, "users/roles/" + id_role);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_ROLE_SUCCESS',
        role: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateRole({ payload }) {
  const { id_role } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "users/roles/" + id_role,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_ROLE_SUCCESS',
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ROLES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_ROLE_ERROR',
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ROLES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_ROLE_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ROLES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_ROLE_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_ROLES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateRole({ payload }) {
  const { id_role } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "users/roles/" + id_role,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_ROLE_SUCCESS',
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ROLES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: 'ACTIVATE_ROLE_ERROR',
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ROLES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_ROLE_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ROLES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_ROLE_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_ROLES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_ROLES_REQUEST', getRoles),
    yield takeEvery('CREATE_ROLE_REQUEST', createRole),
    yield takeEvery('STORE_ROLE_REQUEST', storeRole),
    yield takeEvery('EDIT_ROLE_REQUEST', editRole),
    yield takeEvery('UPDATE_ROLE_REQUEST', updateRole),
    yield takeEvery('SHOW_ROLE_REQUEST', showRole),
    yield takeEvery('DEACTIVATE_ROLE_REQUEST', deactivateRole),
    yield takeEvery('ACTIVATE_ROLE_REQUEST', activateRole)
  ]);
}
