const initState = {
  acc_pay_summary_reports: [],
  currencies: [],
  filter: null,
  filter_select: [],
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function accPaySummaryReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_ACC_PAY_SUMMARY_REPORTS_REQUEST": {
      return {
        ...state,
        //acc_pay_summary_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_ACC_PAY_SUMMARY_REPORTS_SUCCESS": {
      return {
        ...state,
        acc_pay_summary_reports: action.acc_pay_summary_reports,
        loading: false
      };
    }
    case "GET_ACC_PAY_SUMMARY_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "GET_ACC_PAY_SUMMARY_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        currencies: action.currencies
      };
    }
    case "GET_ACC_PAY_SUMMARY_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
