
const initState = {
  payment_terms: [],
  payment_term: null,
  search: null,
  //filter: null,
  loading: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function paymentTermsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_PAYMENT_TERMS_REQUEST': {
      return {
        ...state,
        //payment_terms: [],
        loading: true,
        search: action.payload.search,
        //filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_PAYMENT_TERMS_SUCCESS': {
      return {
        ...state,
        payment_terms: action.payment_terms,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        //filter: action.filter,
        loading: false,
      };
    }
    case 'GET_PAYMENT_TERMS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        // errorField: action.error.errors
      };
    }
    case 'CREATE_PAYMENT_TERM_REQUEST': {
      return {
        ...state,
        payment_term: null,
        errorField: null,
        loading: false,
      };
    }
    case 'CREATE_PAYMENT_TERM_SUCCESS': {
      return {
        ...state,
        loading: false,          
      };
    }
    
    case 'STORE_PAYMENT_TERM_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_PAYMENT_TERM_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_PAYMENT_TERM_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_PAYMENT_TERM_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_PAYMENT_TERM_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        payment_term: action.payment_term,
      };
    }
    case 'EDIT_PAYMENT_TERM_ERROR': {
      return {
        ...state,
        // loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'UPDATE_PAYMENT_TERM_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_PAYMENT_TERM_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_PAYMENT_TERM_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_PAYMENT_TERM_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_PAYMENT_TERM_SUCCESS': {
      return {
        ...state,
        loading: false,
        payment_terms: action.payment_terms
      };
    }
    case "DEACTIVATE_PAYMENT_TERM_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_PAYMENT_TERM_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_PAYMENT_TERM_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_PAYMENT_TERM_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_PAYMENT_TERM_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_PAYMENT_TERM_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
