// Upload Document
export function* uploadIssueReceiptDocument({ payload }) {
  const { id_issue_receipt, body } = payload;
  let data = {
    ...body,
    id_issue_receipt
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "inventories/issue-receipts/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_ISSUE_RECEIPT_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_ISSUE_RECEIPT_REQUEST",
          payload: { id_issue_receipt }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_ISSUE_RECEIPT_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        }),
        yield put({
          type: "EDIT_ISSUE_RECEIPT_REQUEST",
          payload: { id_issue_receipt }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_ISSUE_RECEIPT_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        }),
        yield put({
          type: "EDIT_ISSUE_RECEIPT_REQUEST",
          payload: { id_issue_receipt }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_ISSUE_RECEIPT_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_ISSUE_RECEIPT_REQUEST",
          payload: { id_issue_receipt }
        })
      ]);
    }

    // }
  } catch (error) {
    yield all([
      yield put({
        type: "UPLOAD_ISSUE_RECEIPT_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_ISSUE_RECEIPT_REQUEST",
        payload: { id_issue_receipt }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadIssueReceiptDocument({ payload }) {
  const { id_issue_receipt_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "inventories/issue-receipts/documents/download/" +
        id_issue_receipt_document
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// delete document
export function* deleteIssueReceiptDocument({ payload }) {
  const { id_issue_receipt_document, id_issue_receipt } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "inventories/issue-receipts/documents/" + id_issue_receipt_document
      //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_ISSUE_RECEIPT_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_ISSUE_RECEIPT_REQUEST",
          payload: { id_issue_receipt: id_issue_receipt }
        })
      ]);
    } else if (deleteResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_ISSUE_RECEIPT_DOCUMENT_ERROR",
          error: { message: deleteResult.message }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_ISSUE_RECEIPT_REQUEST",
          payload: { id_issue_receipt: id_issue_receipt }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_ISSUE_RECEIPT_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_ISSUE_RECEIPT_REQUEST",
          payload: { id_issue_receipt: id_issue_receipt }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DELETE_ISSUE_RECEIPT_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "EDIT_ISSUE_RECEIPT_REQUEST",
        payload: { id_issue_receipt: id_issue_receipt }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_ISSUE_RECEIPTS_REQUEST", getIssueReceipts),
    yield takeEvery("CREATE_ISSUE_RECEIPT_REQUEST", createIssueReceipt),
    yield takeEvery("GET_FILTER_ITEMS_IR_REQUEST", getItems),
    yield takeEvery("STORE_ISSUE_RECEIPT_REQUEST", storeIssueReceipt),
    yield takeEvery("EDIT_ISSUE_RECEIPT_REQUEST", editIssueReceipt),
    yield takeEvery("UPDATE_ISSUE_RECEIPT_REQUEST", updateIssueReceipt),
    yield takeEvery("SHOW_ISSUE_RECEIPT_REQUEST", showIssueReceipt),
    yield takeEvery("CONFIRM_ISSUE_RECEIPT_REQUEST", confirmIssueReceipt),
    yield takeEvery("CANCEL_ISSUE_RECEIPT_REQUEST", cancelIssueReceipt),
    yield takeEvery(
      "UPLOAD_ISSUE_RECEIPT_DOCUMENT_REQUEST",
      uploadIssueReceiptDocument
    ),
    yield takeEvery(
      "DOWNLOAD_ISSUE_RECEIPT_DOCUMENT_REQUEST",
      downloadIssueReceiptDocument
    ),
    yield takeEvery(
      "DELETE_ISSUE_RECEIPT_DOCUMENT_REQUEST",
      deleteIssueReceiptDocument
    )
  ]);
}
