// Upload Document
export function* uploadDiscountManagementDocument({ payload }) {
  const { id_discount_management, body } = payload;
  let data = {
    ...body,
    id_discount_management
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "promotions/discount-managements/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_DISCOUNT_MANAGEMENT_REQUEST",
          payload: { id_discount_management }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        }),
        yield put({
          type: "EDIT_DISCOUNT_MANAGEMENT_REQUEST",
          payload: { id_discount_management }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        }),
        yield put({
          type: "EDIT_DISCOUNT_MANAGEMENT_REQUEST",
          payload: { id_discount_management }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_DISCOUNT_MANAGEMENT_REQUEST",
          payload: { id_discount_management }
        })
      ]);
    }

    // }
  } catch (error) {
    yield all([
      yield put({
        type: "UPLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_DISCOUNT_MANAGEMENT_REQUEST",
        payload: { id_discount_management }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadDiscountManagementDocument({ payload }) {
  const { id_discount_management_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "promotions/discount-managements/documents/download/" +
        id_discount_management_document
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// delete document
export function* deleteDiscountManagementDocument({ payload }) {
  const { id_discount_management_document, id_discount_management } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "promotions/discount-managements/documents/" +
        id_discount_management_document
      //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_DISCOUNT_MANAGEMENT_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_DISCOUNT_MANAGEMENT_REQUEST",
          payload: { id_discount_management: id_discount_management }
        })
      ]);
    } else if (deleteResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_DISCOUNT_MANAGEMENT_DOCUMENT_ERROR",
          error: { message: deleteResult.message }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_DISCOUNT_MANAGEMENT_REQUEST",
          payload: { id_discount_management: id_discount_management }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_DISCOUNT_MANAGEMENT_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "EDIT_DISCOUNT_MANAGEMENT_REQUEST",
          payload: { id_discount_management: id_discount_management }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DELETE_DISCOUNT_MANAGEMENT_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "EDIT_DISCOUNT_MANAGEMENT_REQUEST",
        payload: { id_discount_management: id_discount_management }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_DISCOUNT_MANAGEMENTS_REQUEST", getDiscountManagements),
    yield takeEvery(
      "CREATE_DISCOUNT_MANAGEMENT_REQUEST",
      createDiscountManagement
    ),
    yield takeEvery("GET_ITEMS_LOCATION_DISMAN_REQUEST", getItems),
    yield takeEvery("GET_EMPLOYEES_LOCATION_DISMAN_REQUEST", getEmployees),
    yield takeEvery(
      "STORE_DISCOUNT_MANAGEMENT_REQUEST",
      storeDiscountManagement
    ),
    yield takeEvery("EDIT_DISCOUNT_MANAGEMENT_REQUEST", editDiscountManagement),
    yield takeEvery(
      "UPDATE_DISCOUNT_MANAGEMENT_REQUEST",
      updateDiscountManagement
    ),
    yield takeEvery("SHOW_DISCOUNT_MANAGEMENT_REQUEST", showDiscountManagement),
    yield takeEvery(
      "CONFIRM_DISCOUNT_MANAGEMENT_REQUEST",
      confirmDiscountManagement
    ),
    yield takeEvery(
      "CANCEL_DISCOUNT_MANAGEMENT_REQUEST",
      cancelDiscountManagement
    ),
    yield takeEvery(
      "UPLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_REQUEST",
      uploadDiscountManagementDocument
    ),
    yield takeEvery(
      "DOWNLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_REQUEST",
      downloadDiscountManagementDocument
    ),
    yield takeEvery(
      "DELETE_DISCOUNT_MANAGEMENT_DOCUMENT_REQUEST",
      deleteDiscountManagementDocument
    )
  ]);
}
