import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPaymentCategorys({ payload }) {
  const { page, search, filter } = payload;
  let url = "";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    url += filter;
  }
  if (page) {
    url ? (url += "&page=" + page) : (url += "page=" + page);
  }
  try {
    const [payment_categories, foreignKeys] = yield all([
      call(get, "payments/categories?" + url),
      call(get, "payments/categories/foreign-keys")
    ]);

    // Jika sukses get data payment_categories
    if (payment_categories.status === "success") {
      // Jika di API menggunakan pagination
      if (payment_categories.result.current_page && search) {
        yield put({
          type: "GET_PAYMENT_CATEGORIES_SUCCESS",
          payment_categories: payment_categories.result.data,
          current_page: payment_categories.result.current_page,
          page_size: payment_categories.result.per_page,
          total: payment_categories.result.total,
          search: search
        });
      } else if (payment_categories.result.current_page && filter) {
        yield put({
          type: "GET_PAYMENT_CATEGORIES_SUCCESS",
          payment_categories: payment_categories.result.data,
          current_page: payment_categories.result.current_page,
          page_size: payment_categories.result.per_page,
          total: payment_categories.result.total,
          filter: filter
        });
      } else if (payment_categories.result.current_page) {
        yield put({
          type: "GET_PAYMENT_CATEGORIES_SUCCESS",
          payment_categories: payment_categories.result.data,
          current_page: payment_categories.result.current_page,
          page_size: payment_categories.result.per_page,
          total: payment_categories.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_PAYMENT_CATEGORIES_SUCCESS",
          payment_categories: payment_categories.result,
          page_size: null,
          total: null
        });
      }
    } else if (payment_categories.status === "fail") {
      yield put({
        type: "GET_PAYMENT_CATEGORIES_ERROR",
        error: payment_categories
      });
    } else {
      yield put({
        type: "GET_PAYMENT_CATEGORIES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // Jika sukses get data account
    if (foreignKeys.status === "success") {
      yield put({
        type: "GET_PAYMENT_CATEGORY_FOREIGN_KEYS_SUCCESS",
        accounts: foreignKeys.result.accounts,
        cash_banks: foreignKeys.result.cash_banks,
        locations: foreignKeys.result.locations
      });
    }
  } catch (error) {
    yield put({
      type: "GET_PAYMENT_CATEGORY_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Create data
export function* createPaymentCategory() {
  try {
    const [foreignKeys] = yield all([
      call(get, "payments/categories/foreign-keys")
    ]);

    // Jika sukses get data account
    if (foreignKeys.status === "success") {
      yield put({
        type: "GET_PAYMENT_CATEGORY_FOREIGN_KEYS_SUCCESS",
        accounts: foreignKeys.result.accounts,
        cash_banks: foreignKeys.result.cash_banks,
        locations: foreignKeys.result.locations
      });
    } else {
      yield put({
        type: "GET_PAYMENT_CATEGORY_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    yield put({
      type: "CREATE_PAYMENT_CATEGORY_SUCCESS"
    });
  } catch (error) {
    yield put({
      type: "GET_FILTER_ACCOUNTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Store data
export function* storePaymentCategory({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "payments/categories", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_PAYMENT_CATEGORY_SUCCESS",
        success: resultStore
      });
    } else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_PAYMENT_CATEGORY_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_PAYMENT_CATEGORY_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_PAYMENT_CATEGORY_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Edit data
export function* editPaymentCategory({ payload }) {
  const { id_payment_category } = payload;

  try {
    const [payment_category, foreignKeys] = yield all([
      call(get, "payments/categories/" + id_payment_category),
      call(get, "payments/categories/foreign-keys")
    ]);

    // Jika sukses
    if (payment_category.status === "success") {
      yield put({
        type: "EDIT_PAYMENT_CATEGORY_SUCCESS",
        payment_category: payment_category.result
      });
    } else {
      yield put({
        type: "EDIT_PAYMENT_CATEGORY_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // Jika sukses get data account
    if (foreignKeys.status === "success") {
      yield put({
        type: "GET_PAYMENT_CATEGORY_FOREIGN_KEYS_SUCCESS",
        accounts: foreignKeys.result.accounts,
        cash_banks: foreignKeys.result.cash_banks,
        locations: foreignKeys.result.locations
      });
    }
  } catch (error) {
    yield put({
      type: "GET_PAYMENT_CATEGORY_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Update data
export function* updatePaymentCategory({ payload }) {
  const { id_payment_category, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "payments/categories/" + id_payment_category,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: "UPDATE_PAYMENT_CATEGORY_SUCCESS",
        success: resultUpdate
      });
    } else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_PAYMENT_CATEGORY_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_PAYMENT_CATEGORY_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_PAYMENT_CATEGORY_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Show data
export function* showPaymentCategory({ payload }) {
  const { id_payment_category } = payload;

  try {
    const resultShow = yield call(
      get,
      "payments/categories/" + id_payment_category
    );

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: "SHOW_PAYMENT_CATEGORY_SUCCESS",
        payment_category: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivatePaymentCategory({ payload }) {
  const { id_payment_category } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "payments/categories/" + id_payment_category,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: "DEACTIVATE_PAYMENT_CATEGORY_SUCCESS",
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PAYMENT_CATEGORIES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: "DEACTIVATE_PAYMENT_CATEGORY_ERROR",
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PAYMENT_CATEGORIES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "DEACTIVATE_PAYMENT_CATEGORY_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PAYMENT_CATEGORIES_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DEACTIVATE_PAYMENT_CATEGORY_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_PAYMENT_CATEGORIES_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activatePaymentCategory({ payload }) {
  const { id_payment_category } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "payments/categories/" + id_payment_category,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: "ACTIVATE_PAYMENT_CATEGORY_SUCCESS",
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PAYMENT_CATEGORIES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: "ACTIVATE_PAYMENT_CATEGORY_ERROR",
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PAYMENT_CATEGORIES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "ACTIVATE_PAYMENT_CATEGORY_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_PAYMENT_CATEGORIES_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "ACTIVATE_PAYMENT_CATEGORY_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_PAYMENT_CATEGORIES_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_PAYMENT_CATEGORIES_REQUEST", getPaymentCategorys),
    yield takeEvery("CREATE_PAYMENT_CATEGORY_REQUEST", createPaymentCategory),
    yield takeEvery("STORE_PAYMENT_CATEGORY_REQUEST", storePaymentCategory),
    yield takeEvery("EDIT_PAYMENT_CATEGORY_REQUEST", editPaymentCategory),
    yield takeEvery("UPDATE_PAYMENT_CATEGORY_REQUEST", updatePaymentCategory),
    yield takeEvery("SHOW_PAYMENT_CATEGORY_REQUEST", showPaymentCategory),
    yield takeEvery(
      "DEACTIVATE_PAYMENT_CATEGORY_REQUEST",
      deactivatePaymentCategory
    ),
    yield takeEvery(
      "ACTIVATE_PAYMENT_CATEGORY_REQUEST",
      activatePaymentCategory
    )
  ]);
}
