import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPurchaseOrderDetailReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/purchase-orders/details?";
  if (search) {
    url += search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [foreign_keys, purchase_order_detail_reports] = yield all([
        call(get, "reports/purchase-orders/foreign-keys"),
        call(get, url)
      ]);
  
      // Jika sukses get data purchase_order_detail_reports
      if (purchase_order_detail_reports.status === "success") {
        yield put({
          type: "GET_PURCHASE_ORDER_DETAIL_REPORTS_SUCCESS",
          purchase_order_detail_reports: purchase_order_detail_reports.result,
          filter: filter,
          search: search
        });
      } else if (purchase_order_detail_reports.status === "fail") {
        yield put({
          type: "GET_PURCHASE_ORDER_DETAIL_REPORTS_ERROR",
          error: purchase_order_detail_reports
        });
      } else {
        yield put({
          type: "GET_PURCHASE_ORDER_DETAIL_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data purchase_order_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_PURCHASE_ORDER_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS",
          accounts: foreign_keys.result.accounts,
          base_currency: foreign_keys.result.base_currency,
          cash_banks: foreign_keys.result.cash_banks,
          cash_flow_types: foreign_keys.result.cash_flow_types,
          currencies: foreign_keys.result.currencies,
          departments: foreign_keys.result.departments,
          locations: foreign_keys.result.locations,
          messages: foreign_keys.result.messages,
          payment_categories: foreign_keys.result.payment_categories,
          payment_terms: foreign_keys.result.payment_terms,
          po_detail_search_filter: foreign_keys.result.po_detail_search_filter,
          projects: foreign_keys.result.projects,
          suppliers: foreign_keys.result.suppliers,
          taxes: foreign_keys.result.taxes
        });
      } else {
        yield put({
          type: "GET_PURCHASE_ORDER_DETAIL_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/purchase-orders/foreign-keys")
      ]);
  
      yield put({
        type: "GET_PURCHASE_ORDER_DETAIL_REPORTS_SUCCESS",
        purchase_order_detail_reports: []
      });
  
      // Jika sukses get data purchase_order_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_PURCHASE_ORDER_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS",
          accounts: foreign_keys.result.accounts,
          base_currency: foreign_keys.result.base_currency,
          cash_banks: foreign_keys.result.cash_banks,
          cash_flow_types: foreign_keys.result.cash_flow_types,
          currencies: foreign_keys.result.currencies,
          departments: foreign_keys.result.departments,
          locations: foreign_keys.result.locations,
          messages: foreign_keys.result.messages,
          payment_categories: foreign_keys.result.payment_categories,
          payment_terms: foreign_keys.result.payment_terms,
          po_detail_search_filter: foreign_keys.result.po_detail_search_filter,
          projects: foreign_keys.result.projects,
          suppliers: foreign_keys.result.suppliers,
          taxes: foreign_keys.result.taxes
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_PURCHASE_ORDER_DETAIL_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      } else {
        yield put({
          type: "GET_PURCHASE_ORDER_DETAIL_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_PURCHASE_ORDER_DETAIL_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_PURCHASE_ORDER_DETAIL_REPORTS_REQUEST",
      getPurchaseOrderDetailReports
    )
  ]);
}
