const initState = {
  base_currency: {},
  cash_banks: [],
  current_page: 1,
  customers: [],
  doneList: [], //list dari transaksi yang berhasil dikonfirmasi pada proses multiple konfirmasi yang hasilnya 'warning'
  error: null,
  errorField: null,
  filter: null,
  formerList: [], //list dari transaksi yang sebelumnya direq ke api, pada proses multiple konfirmasi
  id_customer: null,
  id_location: null,
  id_sales_invoice: null,
  info: [],
  info_message: [],
  inventory_items: [],
  item_categories: [],
  loading: false,
  loadingCancel: false,
  loadingConfirm: false,
  loadingForeign: false,
  loadingSubmit: false,
  loadingImport: false,
  loadingUpdateRaptorSalesInvoiceDetails: false,
  locations: [],
  page_size: null,
  payment_categories: [],
  payment_terms: [],
  printData: {},
  raptorSalesInvoice: null,
  raptorSalesInvoices: [],
  search: null,
  success: null,
  tax_bases: [],
  total: null,
  units: [],
  warning: null,
  warning_message: null
};

export default function raptorSalesInvoicesReducer(state = initState, action) {
  switch (action.type) {
    case "GET_RAPTOR_SALES_INVOICES_REQUEST": {
      return {
        ...state,
        //raptorSalesInvoices: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_RAPTOR_SALES_INVOICES_SUCCESS": {
      return {
        ...state,
        raptorSalesInvoices: action.raptorSalesInvoices,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_RAPTOR_SALES_INVOICES_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "SYNCHRONIZE_RAPTOR_SALES_INVOICES_REQUEST": {
      return {
        ...state,
        //raptorSalesInvoices: [],
        loading: true,
        loadingImport: true
      };
    }
    case "SYNCHRONIZE_RAPTOR_SALES_INVOICES_SUCCESS": {
      return {
        ...state,
        loading: true,
        loadingImport: false,
        success: action.success.message
      };
    }
    case "SYNCHRONIZE_RAPTOR_SALES_INVOICES_ERROR": {
      return {
        ...state,
        loading: false,
        loadingImport: false,
        error: action.error.message
      };
    }
    case "GET_RAPTOR_SALES_INVOICE_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_RAPTOR_SALES_INVOICE_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        base_currency: action.base_currency,
        cash_banks: action.cash_banks,
        customers: action.customers,
        info_message: action.info_message,
        inventory_items: action.inventory_items,
        item_categories: action.item_categories,
        locations: action.locations,
        units: action.units,
        payment_categories: action.payment_categories,
        payment_terms: action.payment_terms,
        warning_message: action.warning_message
      };
    }
    case "GET_RAPTOR_SALES_INVOICE_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "EDIT_RAPTOR_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loading: true,
        id_sales_invoice: null,
        id_location: null,
        // loadingTransactions: true,
        loadingForeign: true,
        errorField: null,
        // current_page: 1,
        // page_size: null,
        // total: null,
        tax_bases: []
      };
    }
    case "EDIT_RAPTOR_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loading: false,
        // loadingTransactions: false, //bug
        loadingSubmit: false,
        raptorSalesInvoice: action.raptorSalesInvoice,
        info: action.info
      };
    }
    case "EDIT_RAPTOR_SALES_INVOICE_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_RAPTOR_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_RAPTOR_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_invoice: action.id_sales_invoice
      };
    }
    case "UPDATE_RAPTOR_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "STORE_ITEM_RAPTOR_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        id_sales_invoice: action.id_sales_invoice,
        errorField: null
      };
    }
    case "STORE_ITEM_RAPTOR_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_invoice: action.id_sales_invoice
      };
    }
    case "STORE_ITEM_RAPTOR_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        id_sales_invoice: action.id_sales_invoice,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "UPDATE_ITEM_RAPTOR_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        id_sales_invoice: action.id_sales_invoice,
        errorField: null
      };
    }
    case "UPDATE_ITEM_RAPTOR_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_invoice: action.id_sales_invoice
      };
    }
    case "UPDATE_ITEM_RAPTOR_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        id_sales_invoice: action.id_sales_invoice,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "UPDATE_ITEM_GROUPING_RAPTOR_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        id_sales_invoice: action.id_sales_invoice,
        errorField: null
      };
    }
    case "UPDATE_ITEM_GROUPING_RAPTOR_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_invoice: action.id_sales_invoice
      };
    }
    case "UPDATE_ITEM_GROUPING_RAPTOR_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        id_sales_invoice: action.id_sales_invoice,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "CONFIRM_RAPTOR_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingConfirm: true,
        info: []
      };
    }
    case "CONFIRM_RAPTOR_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingConfirm: false,
        success: action.success.message
      };
    }
    case "CONFIRM_RAPTOR_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingConfirm: false,
        error: action.error.message,
        info: action.info
      };
    }
    case "CONFIRM_RAPTOR_SALES_INVOICES_REQUEST": {
      return {
        ...state,
        doneList: [],
        formerList: [],
        info: [],
        loadingConfirm: true
      };
    }
    case "CONFIRM_RAPTOR_SALES_INVOICES_SUCCESS": {
      return {
        ...state,
        loadingConfirm: false,
        success: action.success.message
      };
    }
    case "CONFIRM_RAPTOR_SALES_INVOICES_WARNING": {
      return {
        ...state,
        doneList: action.doneList,
        formerList: action.formerList,
        loadingConfirm: false,
        warning: action.warning.message
      };
    }
    case "CONFIRM_RAPTOR_SALES_INVOICES_ERROR": {
      return {
        ...state,
        loadingConfirm: false,
        error: action.error.message,
        info: action.info
      };
    }
    case "CANCEL_RAPTOR_SALES_INVOICE_REQUEST": {
      return {
        ...state,
        loadingCancel: true
      };
    }
    case "CANCEL_RAPTOR_SALES_INVOICE_SUCCESS": {
      return {
        ...state,
        loadingCancel: false,
        success: action.success.message
      };
    }
    case "CANCEL_RAPTOR_SALES_INVOICE_ERROR": {
      return {
        ...state,
        loadingCancel: false,
        error: action.error.message
      };
    }
    case "UPLOAD_RAPTOR_SALES_INVOICE_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_RAPTOR_SALES_INVOICE_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_RAPTOR_SALES_INVOICE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_RAPTOR_SALES_INVOICE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_RAPTOR_SALES_INVOICE_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_RAPTOR_SALES_INVOICE_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_RAPTOR_SALES_INVOICE_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "SET_PRINT_DATA_RAPTOR_SALES_INVOICE": {
      return {
        ...state,
        printData: action.payload.body
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_WARNING": {
      return {
        ...state,
        warning: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    case "SET_TAX_BASES_RAPTOR_SALES_INVOICE": {
      return {
        ...state,
        tax_bases: action.payload.tax_bases
      };
    }
    default:
      return state;
  }
}
