import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import actions from "./actions";
import { bugsnagClient } from "../../..";

// Get all data
export function* getSite({ payload }) {
  const { page, search } = payload;

  try {
    // Cek apakah ada pagination di request atau tidak
    const siteResult = search
      ? page
        ? // Jika search ada
          yield call(get, "companies/sites?search=" + search + "&page=" + page)
        : //Jika
          yield call(get, "companies/sites?search=" + search)
      : page
      ? yield call(get, "companies/sites?&page=" + page)
      : yield call(get, "companies/sites");

    // Jika sukses get data
    if (siteResult.status === "success") {
      // Jika di API menggunakan pagination
      if (siteResult.result.current_page) {
        yield put({
          type: actions.GET_SITES_SUCCESS,
          sites: siteResult.result.data,
          page_size: siteResult.result.per_page,
          total: siteResult.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: actions.GET_SITES_SUCCESS,
          sites: siteResult.result,
          page_size: null,
          total: null
        });
      }
    } else if (siteResult.status === "fail") {
      yield put({
        type: "GET_SITES_ERROR",
        error: siteResult
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_SITES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_SITES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* storeSite({ payload }) {
  const { body } = payload;

  try {
    // POST data ke API
    const createResult = yield call(post, "companies/sites", body);
    //console.log(createResult);
    // Jika berhasil
    if (createResult.status === "success") {
      // Set message ke sessionStorage
      yield all([
        put({
          type: actions.STORE_SITE_SUCCESS,
          success: createResult
        })
      ]);
    } else if (createResult.status === "fail" || createResult.errors) {
      yield put({
        type: actions.STORE_SITE_ERROR,
        error: createResult
      });
    } else {
      // Jika error set session message
      yield put({
        type: actions.STORE_SITE_ERROR,
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: actions.STORE_SITE_ERROR,
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit form
export function* editSite({ payload }) {
  const { id_site } = payload;

  try {
    const site = yield call(get, "companies/sites/" + id_site);

    if (site.status === "success") {
      yield put({
        type: actions.EDIT_SITE_SUCCESS,
        site: site.result
      });
    } else if (site.status === "fail") {
      yield put({
        type: actions.EDIT_SITE_ERROR,
        error: site.result
      });
    } else {
      yield put({
        type: actions.EDIT_SITE_ERROR,
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: actions.EDIT_SITE_ERROR,
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateSite({ payload }) {
  const { id_site, body } = payload;

  // Uppercasing site_code
  // body.site_code = body.site_code.toUpperCase();

  try {
    // POST ke API
    const updateResult = yield call(putApi, "companies/sites/" + id_site, body);

    // Jika sukses
    if (updateResult.status === "success") {
      // Set message ke session
      yield put({
        type: actions.UPDATE_SITE_SUCCESS,
        success: updateResult
      });
    } else if (updateResult.status === "fail" || updateResult.errors) {
      yield put({
        type: actions.UPDATE_SITE_ERROR,
        error: updateResult
      });
    } else {
      // Jika gagal set session message
      yield put({
        type: actions.UPDATE_SITE_ERROR,
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: actions.UPDATE_SITE_ERROR,
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateSite({ payload }) {
  const { id_site } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "companies/sites/" + id_site,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      // Set session message
      yield all([
        put({
          type: actions.DEACTIVATE_SITE_SUCCESS,
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        put({
          type: actions.GET_SITES_REQUEST,
          payload: { page: false }
        })
      ]);
    } else if (deactivateResult.status === "fail" || deactivateResult.errors) {
      yield all([
        put({
          type: actions.DEACTIVATE_SITE_ERROR,
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: actions.GET_SITES_REQUEST,
          payload: { page: false }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        put({
          type: actions.DEACTIVATE_SITE_ERROR,
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: actions.GET_SITES_REQUEST,
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      put({
        type: actions.DEACTIVATE_SITE_ERROR,
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: actions.GET_SITES_REQUEST,
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error)
  }
}

// Activate data (mengubah is_active => 1)
export function* activateSite({ payload }) {
  const { id_site } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "companies/sites/" + id_site,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        put({
          type: actions.ACTIVATE_SITE_SUCCESS,
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        put({
          type: actions.GET_SITES_REQUEST,
          payload: { page: false }
        })
      ]);
    } else if (activateResult.status === "fail" || activateResult.errors) {
      yield all([
        put({
          type: actions.ACTIVATE_SITE_ERROR,
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: actions.GET_SITES_REQUEST,
          payload: { page: false }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        put({
          type: actions.ACTIVATE_SITE_ERROR,
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: actions.GET_SITES_REQUEST,
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      put({
        type: actions.ACTIVATE_SITE_ERROR,
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: actions.GET_SITES_REQUEST,
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error)
  }
}
export default function* rootSaga() {
  yield all([
    yield takeEvery(actions.GET_SITES_REQUEST, getSite),
    yield takeEvery(actions.STORE_SITE_REQUEST, storeSite),
    yield takeEvery(actions.EDIT_SITE_REQUEST, editSite),
    yield takeEvery(actions.UPDATE_SITE_REQUEST, updateSite),
    yield takeEvery(actions.DEACTIVATE_SITE_REQUEST, deactivateSite),
    yield takeEvery(actions.ACTIVATE_SITE_REQUEST, activateSite)
  ]);
}
