const initState = {
  base_currency: {},
  cash_banks: [],
  current_page: 1,
  current_page_si: 1,
  customers: [],
  filter: null,
  error: null,
  errorField: null,
  id_customer: null,
  id_location: null,
  id_sales_return: null,
  info_message: [],
  item_categories: [],
  loading: false,
  loadingCancel: false,
  loadingConfirm: false,
  loadingForeign: false,
  loadingImport: false,
  loadingSubmit: false,
  loadingUpdateRaptorSalesReturnDetails: false,
  locations: [],
  units: [],
  page_size: null,
  page_size_si: null,
  payment_categories: [],
  payment_terms: [],
  printData: {},
  raptorSalesReturn: null,
  raptorSalesReturns: [],
  salesInvoices: [],
  success: null,
  search: null,
  tax_bases: [],
  total: null,
  total_si: null,
  warning_message: null
};

export default function raptorSalesReturnsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_RAPTOR_SALES_RETURNS_REQUEST": {
      return {
        ...state,
        //raptorSalesReturns: [],
        itemDiscount: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_RAPTOR_SALES_RETURNS_SUCCESS": {
      return {
        ...state,
        raptorSalesReturns: action.raptorSalesReturns,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_RAPTOR_SALES_RETURNS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "SYNCHRONIZE_RAPTOR_SALES_RETURNS_REQUEST": {
      return {
        ...state,
        //raptorSalesInvoices: [],
        loading: true,
        loadingImport: true
      };
    }
    case "SYNCHRONIZE_RAPTOR_SALES_RETURNS_SUCCESS": {
      return {
        ...state,
        loading: true,
        loadingImport: false,
        success: action.success.message
      };
    }
    case "SYNCHRONIZE_RAPTOR_SALES_RETURNS_ERROR": {
      return {
        ...state,
        loading: false,
        loadingImport: false,
        error: action.error.message
      };
    }
    case "GET_RAPTOR_SALES_RETURN_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_RAPTOR_SALES_RETURN_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        base_currency: action.base_currency,
        cash_banks: action.cash_banks,
        customers: action.customers,
        info_message: action.info_message,
        item_categories: action.item_categories,
        loadingForeign: false,
        locations: action.locations,
        units: action.units,
        payment_categories: action.payment_categories,
        payment_terms: action.payment_terms,
        warning_message: action.warning_message
      };
    }
    case "GET_RAPTOR_SALES_RETURN_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "EDIT_RAPTOR_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loading: true,
        id_sales_return: null,
        id_location: null,
        // loadingTransactions: true,
        loadingForeign: true,
        errorField: null,
        // current_page: 1,
        // page_size: null,
        // total: null,
        tax_bases: []
      };
    }
    case "EDIT_RAPTOR_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loading: false,
        // loadingTransactions: false, //bug
        loadingSubmit: false,
        raptorSalesReturn: action.raptorSalesReturn
      };
    }
    case "EDIT_RAPTOR_SALES_RETURN_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_RAPTOR_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_RAPTOR_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_return: action.id_sales_return
      };
    }
    case "UPDATE_RAPTOR_SALES_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "STORE_ITEM_RAPTOR_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        id_sales_return: action.id_sales_return,
        errorField: null
      };
    }
    case "STORE_ITEM_RAPTOR_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_return: action.id_sales_return
      };
    }
    case "STORE_ITEM_RAPTOR_SALES_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        id_sales_return: action.id_sales_return,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "UPDATE_ITEM_RAPTOR_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        id_sales_return: action.id_sales_return,
        errorField: null
      };
    }
    case "UPDATE_ITEM_RAPTOR_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_return: action.id_sales_return
      };
    }
    case "UPDATE_ITEM_RAPTOR_SALES_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        id_sales_return: action.id_sales_return,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "CONFIRM_RAPTOR_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loadingConfirm: true
      };
    }
    case "CONFIRM_RAPTOR_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loadingConfirm: false,
        success: action.success.message,
      };
    }
    case "CONFIRM_RAPTOR_SALES_RETURN_ERROR": {
      return {
        ...state,
        loadingConfirm: false,
        error: action.error.message
      };
    }
    case "CANCEL_RAPTOR_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loadingCancel: true
      };
    }
    case "CANCEL_RAPTOR_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loadingCancel: false,
        success: action.success.message,
      };
    }
    case "CANCEL_RAPTOR_SALES_RETURN_ERROR": {
      return {
        ...state,
        loadingCancel: false,
        error: action.error.message
      };
    }
    case "UPLOAD_RAPTOR_SALES_RETURN_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_RAPTOR_SALES_RETURN_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_RAPTOR_SALES_RETURN_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_RAPTOR_SALES_RETURN_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_RAPTOR_SALES_RETURN_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_RAPTOR_SALES_RETURN_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_RAPTOR_SALES_RETURN_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "SET_PRINT_DATA_RAPTOR_SALES_RETURN": {
      return {
        ...state,
        printData: action.payload.body
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null,
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    case "SET_TAX_BASES_RAPTOR_SALES_RETURN": {
      return {
        ...state,
        tax_bases: action.payload.tax_bases
      };
    }
    case "GET_FILTER_SALES_INVOICES_RAPTOR_ST_REQUEST": {
      return {
        ...state,
        loadingSalesInvoices: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page_si: action.payload.page
      };
    }
    case "GET_FILTER_SALES_INVOICES_RAPTOR_ST_SUCCESS": {
      return {
        ...state,
        loadingSalesInvoices: false,
        salesInvoices: action.salesInvoices,
        current_page_si: action.current_page,
        page_size_si: action.page_size,
        total_si: action.total
      };
    }
    case "GET_FILTER_SALES_INVOICES_RAPTOR_ST_ERROR": {
      return {
        ...state,
        loadingSalesInvoices: false,
        error: action.error.message
      };
    }
    default:
      return state;
  }
}
