import { all, takeEvery, put, fork, call } from "redux-saga/effects";
import { push } from "react-router-redux";
import { getToken, clearToken, clearUserData } from "../../helpers/utility";
import { login, get, signout } from "../../helpers/helper";
import { bugsnagClient } from "../..";
import { secret_code, version } from "../../../package.json";

export function* checkLoginRequest() {
  yield takeEvery("CHECK_LOGIN_REQUEST", function* ({ payload }) {
    const { param } = payload;
    try {
      const checkResult = yield call(get, `user/check-login?username=${param.username}`);
      // Login Success
      if (checkResult && checkResult.status === "warning") {
        yield put({
          type: "CHECK_LOGIN_WARNING",
          warning: checkResult.message
        });
      } else {
        yield put({
          type: "LOGIN_REQUEST",
          payload: { param }
        });
      }
    } catch (error) {
      bugsnagClient.notify(error)
    }
  });
}

export function* loginRequest() {
  yield takeEvery("LOGIN_REQUEST", function* ({ payload }) {
    const { param } = payload;
    try {
      const loginResult = yield call(login, param);
      // Login Success
      if (loginResult && loginResult.token_type === "Bearer") {
        const bearer = loginResult.token_type + " " + loginResult.access_token;
        localStorage.setItem("bearer", bearer);

        const user = yield call(get, "user/show");
        if (user.status === "success") {

          // console.log(secret_code);
          // console.log(user ? user : "OK")
          // Jika tidak punya secret code
          if (!user) {
            yield put({
              type: "LOGIN_ERROR",
              error: { message: "Login failed, you must request a license again because your license cannot run on Application version " + version }
            });
          }
          else if (!user.result) {
            yield put({
              type: "LOGIN_ERROR",
              error: { message: "Login failed, you must request a license again because your license cannot run on Application version " + version }
            });
          }
          else if (!user.result.secret_code) {
            yield put({
              type: "LOGIN_ERROR",
              error: { message: "Login failed, you must request a license again because your license cannot run on Application version " + version }
            });
          }
          else if (user.result.secret_code !== secret_code) {
            yield put({
              type: "LOGIN_ERROR",
              error: { message: "Login failed, you must request a license again because your license cannot run on Application version " + version }
            });
          }
          else {
            const userData = JSON.stringify(user.result);
            localStorage.setItem("user", userData);
            localStorage.setItem("language", user.result.language);
            yield put({
              type: "LOGIN_SUCCESS",
              token: bearer,
              user: user.result
            });
          }


          // const userData = JSON.stringify(user.result);
          // localStorage.setItem("user", userData);
          // yield put({
          //   type: "LOGIN_SUCCESS",
          //   token: bearer,
          //   user: user.result
          // });
        }
      } else if (loginResult && loginResult.error && loginResult.message) {
        // Invalid credential
        // sessionStorage.setItem("login_error", loginResult.message);
        yield put({
          type: "LOGIN_ERROR",
          error: loginResult
        });
      } else if (loginResult && loginResult.status === "fail" && loginResult.message) {
        // sessionStorage.setItem("login_error", loginResult.message);
        yield put({
          type: "LOGIN_ERROR",
          error: loginResult
        });
      } else {
        // Internet connection error
        // sessionStorage.setItem("login_error", "Check your internet connection");
        yield put({
          type: "LOGIN_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } catch (error) {
      // console.log(error);
      // sessionStorage.setItem("login_error", error);
      yield put({
        type: "LOGIN_ERROR",
        error: { message: "Check your internet connection" }
      });
      bugsnagClient.notify(error)
    }
  });
}

export function* loginSuccess() {
  yield takeEvery("LOGIN_SUCCESS", function* (payload) {
    yield localStorage.setItem("bearer2", payload.token);
  });
}

export function* loginError() {
  yield takeEvery("LOGIN_ERROR", function* () { });
}

export function* logout() {
  yield takeEvery("LOGOUT", function* ({ payload }) {
    const { quit } = payload;
    const [logout, clear, clear2, red] = yield all([
      call(signout),
      clearToken(),
      clearUserData(),
      put(push("./"))
    ]);

    if (logout && logout.status === "success") {
      localStorage.removeItem("bearer2");

      if (quit === true) {
        window.ipcRenderer.send("APP_CAN_QUIT", "from app");
      }
    } else if (logout && logout.status && logout.status === "fail") {
      localStorage.removeItem("bearer2");

      yield put({
        type: "LOGOUT_ERROR",
        error: { message: logout.message },
      });

      if (quit === true) {
        window.ipcRenderer.send("APP_CAN_QUIT", "from app");
      }
    } else {
      yield put({
        type: "LOGOUT_ERROR",
        error: { message: "Check your internet connection" },
      });

      if (quit === true) {
        window.ipcRenderer.send("APP_CAN_QUIT", "from app");
      }
    }
  });
}
export function* checkAuthorization() {
  yield takeEvery("CHECK_AUTHORIZATION", function* () {
    const token = getToken().get("idToken");
    if (token) {
      yield put({
        type: "LOGIN_SUCCESS",
        token,
        profile: "Profile"
      });
    }
  });
}
export function* switchLanguage() {
  yield takeEvery("SWITCH_LANGUAGE", function* ({ payload }) {
    const { lang } = payload;
    try {
      yield call(get, `user/change-language?language=${lang}`);
    } catch (error) {
      bugsnagClient.notify(error)
    }
  });
}
export default function* rootSaga() {
  yield all([
    fork(checkAuthorization),
    fork(checkLoginRequest),
    fork(loginRequest),
    fork(loginSuccess),
    fork(loginError),
    fork(logout),
    fork(switchLanguage)
  ]);
}
