import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getReturnReasons({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [return_reasons] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "configuration-managements/return-reasons?search=" + search + "&page=" + page), 
          ])
        : // Jika filter
          yield all([
            call(get, "configuration-managements/return-reasons?" + filter + "&page=" + page), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "configuration-managements/return-reasons?search=" + search),  
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "configuration-managements/return-reasons?" + filter),
        ]);

    // console.log(return_reasons);
    // console.log(accounts);

    // Jika sukses get data return_reasons
    if (return_reasons.status === "success") {
      // Jika di API menggunakan pagination
      if (return_reasons.result.current_page && search) {
        yield put({
          type: 'GET_RETURN_REASONS_SUCCESS',
          return_reasons: return_reasons.result.data,
          current_page: return_reasons.result.current_page,
          page_size: return_reasons.result.per_page,
          total: return_reasons.result.total,
          search: search
        });
      }
      // else if(return_reasons.result.current_page && filter)
      // {
      //   yield put({
      //     type: 'GET_RETURN_REASONS_SUCCESS',
      //     return_reasons: return_reasons.result.data,
      //     current_page: return_reasons.result.current_page,
      //     page_size: return_reasons.result.per_page,
      //     total: return_reasons.result.total,
      //     filter: filter
      //   });
      // }
      else if (return_reasons.result.current_page) {
        yield put({
          type: 'GET_RETURN_REASONS_SUCCESS',
          return_reasons: return_reasons.result.data,
          current_page: return_reasons.result.current_page,
          page_size: return_reasons.result.per_page,
          total: return_reasons.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_RETURN_REASONS_SUCCESS',
          return_reasons: return_reasons.result,
          page_size: null,
          total: null
        });
      }
    }
    else if (return_reasons.status === "fail") {
      yield put({
        type: 'GET_RETURN_REASONS_ERROR',
        error: return_reasons
      });
    }
    else {
      yield put({
        type: 'GET_RETURN_REASONS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createReturnReason() {
  
}

// Store data
export function* storeReturnReason({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "configuration-managements/return-reasons", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_RETURN_REASON_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_RETURN_REASON_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_RETURN_REASON_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_RETURN_REASON_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editReturnReason({ payload }) {
  //console.log("masuk")
  const { id_return_reason } = payload;

  try {
    const [return_reason] = yield all([
      call(get, "configuration-managements/return-reasons/" + id_return_reason),
      
    ]);
  
    // Jika sukses
    if (return_reason.status === "success") {
      yield put({
        type: 'EDIT_RETURN_REASON_SUCCESS',
        return_reason: return_reason.result
      });
    }
    else{
      yield put({
        type: 'EDIT_RETURN_REASON_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'EDIT_RETURN_REASON_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateReturnReason({ payload }) {
  const { id_return_reason, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "configuration-managements/return-reasons/" + id_return_reason,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_RETURN_REASON_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_RETURN_REASON_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_RETURN_REASON_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_RETURN_REASON_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showReturnReason({ payload }) {
  const { id_return_reason } = payload;

  try {
    const resultShow = yield call(get, "configuration-managements/return-reasons/" + id_return_reason);
    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_RETURN_REASON_SUCCESS',
        return_reason: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateReturnReason({ payload }) {
  const { id_return_reason } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "configuration-managements/return-reasons/" + id_return_reason,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_RETURN_REASON_SUCCESS',
          success: deactivateResult
        }),
        yield put({
          type: 'GET_RETURN_REASONS_REQUEST',
          payload: { page: false }
        })
      ])
    } 
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_RETURN_REASON_ERROR',
          error: deactivateResult
        }),
        yield put({
          type: 'GET_RETURN_REASONS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_RETURN_REASON_ERROR',
          error: {message: "Check your internet connection"}
        }),
        yield put({
          type: 'GET_RETURN_REASONS_REQUEST',
          payload: { page: false }
        })
      ])    
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_RETURN_REASON_ERROR',
        error: {message: "Check your internet connection"}
      }),
      yield put({
        type: 'GET_RETURN_REASONS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateReturnReason({ payload }) {
  const { id_return_reason } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "configuration-managements/return-reasons/" + id_return_reason,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_RETURN_REASON_SUCCESS',
          success: activateResult
        }),
        yield put({
          type: 'GET_RETURN_REASONS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: 'ACTIVATE_RETURN_REASON_ERROR',
          error: activateResult
        }),
        yield put({
          type: 'GET_RETURN_REASONS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_RETURN_REASON_ERROR',
          error: {message: "Check your internet connection"}
        }),
        yield put({
          type: 'GET_RETURN_REASONS_REQUEST',
          payload: { page: false }
        })
      ])    
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_RETURN_REASON_ERROR',
        error: {message: "Check your internet connection"}
      }),
      yield put({
        type: 'GET_RETURN_REASONS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_RETURN_REASONS_REQUEST', getReturnReasons),
    yield takeEvery('CREATE_RETURN_REASON_REQUEST', createReturnReason),
    yield takeEvery('STORE_RETURN_REASON_REQUEST', storeReturnReason),
    yield takeEvery('EDIT_RETURN_REASON_REQUEST', editReturnReason),
    yield takeEvery('UPDATE_RETURN_REASON_REQUEST', updateReturnReason),
    yield takeEvery('SHOW_RETURN_REASON_REQUEST', showReturnReason),
    yield takeEvery('DEACTIVATE_RETURN_REASON_REQUEST', deactivateReturnReason),
    yield takeEvery('ACTIVATE_RETURN_REASON_REQUEST', activateReturnReason)
  ]);
}
