import { all, takeEvery, put, call, fork } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getAssetFiscals({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
      const [asset_fiscals] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "assets/fiscals?search=" + search + "&page=" + page), 
          ])
        : // Jika filter
          yield all([
            call(get, "assets/fiscals?" + filter + "&page=" + page), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "assets/fiscals?search=" + search),  
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "assets/fiscals?" + filter),
        ]);

    // console.log(asset_fiscals);
    // console.log(accounts);

    // Jika sukses get data asset_fiscals
    if (asset_fiscals.status === "success") {
      // Jika di API menggunakan pagination
      if (asset_fiscals.result.current_page && search) {
        yield put({
          type: 'GET_ASSET_FISCALS_SUCCESS',
          asset_fiscals: asset_fiscals.result.data,
          current_page: asset_fiscals.result.current_page,
          page_size: asset_fiscals.result.per_page,
          total: asset_fiscals.result.total,
          search: search
        });
      }
      // else if(asset_fiscals.result.current_page && filter)
      // {
      //   yield put({
      //     type: 'GET_ASSET_FISCALS_SUCCESS',
      //     asset_fiscals: asset_fiscals.result.data,
      //     current_page: asset_fiscals.result.current_page,
      //     page_size: asset_fiscals.result.per_page,
      //     total: asset_fiscals.result.total,
      //     filter: filter
      //   });
      // }
      else if (asset_fiscals.result.current_page) {
        yield put({
          type: 'GET_ASSET_FISCALS_SUCCESS',
          asset_fiscals: asset_fiscals.result.data,
          current_page: asset_fiscals.result.current_page,
          page_size: asset_fiscals.result.per_page,
          total: asset_fiscals.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_ASSET_FISCALS_SUCCESS',
          asset_fiscals: asset_fiscals.result,
          page_size: null,
          total: null
        });
      }
    }
    else if (asset_fiscals.status === "fail") {
      yield put({
        type: 'GET_ASSET_FISCALS_ERROR',
        error : asset_fiscals
      })
    }
    else {
      yield put({
        type: 'GET_ASSET_FISCALS_ERROR',
        error : {message : "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'GET_ASSET_FISCALS_ERROR',
      error : {message : "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createAssetFiscal() {
  
}

// Store data
export function* storeAssetFiscal({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "assets/fiscals", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_ASSET_FISCAL_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_ASSET_FISCAL_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_ASSET_FISCAL_ERROR",
        error : {message : "Check your internet connection"}
      });
    } 
  } catch (error) {
    bugsnagClient.notify(error)
    yield put({
      type: "STORE_ASSET_FISCAL_ERROR",
      error : {message : "Check your internet connection"}
    });
  }
}

// Edit data
export function* editAssetFiscal({ payload }) {
  //console.log("masuk")
  const { id_asset_fiscal } = payload;

  try {
    const [asset_fiscal] = yield all([
      call(get, "assets/fiscals/" + id_asset_fiscal),
      
    ]);
  
    // Jika sukses
    if (asset_fiscal.status === "success") {
      yield put({
        type: 'EDIT_ASSET_FISCAL_SUCCESS',
        asset_fiscal: asset_fiscal.result
      });
    }
    else{
      yield put({
        type: "EDIT_ASSET_FISCAL_ERROR",
        error : {message : "Check your internet connection"}
      });
    } 
  } catch (error) {
    yield put({
      type: "EDIT_ASSET_FISCAL_ERROR",
      error : {message : "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateAssetFiscal({ payload }) {
  const { id_asset_fiscal, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "assets/fiscals/" + id_asset_fiscal,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_ASSET_FISCAL_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_ASSET_FISCAL_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_ASSET_FISCAL_ERROR",
        error : {message : "Check your internet connection"}
      });
    } 
  } catch (error) {
    yield put({
      type: "UPDATE_ASSET_FISCAL_ERROR",
      error : {message : "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showAssetFiscal({ payload }) {
  const { id_asset_fiscal } = payload;

  try {
    const resultShow = yield call(get, "assets/fiscals/" + id_asset_fiscal);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_ASSET_FISCAL_SUCCESS',
        asset_fiscal: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateAssetFiscal({ payload }) {
  const { id_asset_fiscal } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "assets/fiscals/" + id_asset_fiscal,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_ASSET_FISCAL_SUCCESS',
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_FISCALS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_ASSET_FISCAL_ERROR',
          error : deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_FISCALS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_ASSET_FISCAL_ERROR',
          error : {message : "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_FISCALS_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_ASSET_FISCAL_ERROR',
        error : {message : "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_ASSET_FISCALS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error)
  }
}

// Activate data (mengubah is_active => 1)
export function* activateAssetFiscal({ payload }) {
  const { id_asset_fiscal } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "assets/fiscals/" + id_asset_fiscal,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_ASSET_FISCAL_SUCCESS',
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_FISCALS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: 'ACTIVATE_ASSET_FISCAL_ERROR',
          error : activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_FISCALS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_ASSET_FISCAL_ERROR',
          error : {message : "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_ASSET_FISCALS_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_ASSET_FISCAL_ERROR',
        error : {message : "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_ASSET_FISCALS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_ASSET_FISCALS_REQUEST', getAssetFiscals),
    yield takeEvery('CREATE_ASSET_FISCAL_REQUEST', createAssetFiscal),
    yield takeEvery('STORE_ASSET_FISCAL_REQUEST', storeAssetFiscal),
    yield takeEvery('EDIT_ASSET_FISCAL_REQUEST', editAssetFiscal),
    yield takeEvery('UPDATE_ASSET_FISCAL_REQUEST', updateAssetFiscal),
    yield takeEvery('SHOW_ASSET_FISCAL_REQUEST', showAssetFiscal),
    yield takeEvery('DEACTIVATE_ASSET_FISCAL_REQUEST', deactivateAssetFiscal),
    yield takeEvery('ACTIVATE_ASSET_FISCAL_REQUEST', activateAssetFiscal)
  ]);
}
