
const initState = {
  items: [],
  item: null,
  item_categories: [],
  locations: [],
  array_id_location: [],
  suppliers: [],
  taxes: [],
  default_id_item_tax: null,
  accounts: [],
  units: [],
  inventory_items: [],
  racks: [],
  colors: [],
  sizes: [],
  raptor_module: 0,
  search: null,
  filter: null,
  loading: true,
  loadingEdit: true,
  loadingImport: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_item:null,
  info_message: []
};

export default function itemsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_ITEMS_REQUEST': {
      return {
        ...state,
        //items: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_ITEMS_SUCCESS': {
      return {
        ...state,
        items: action.items,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_ITEMS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case "GET_ITEM_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        item_categories: action.item_categories,
        locations: action.locations,
        suppliers: action.suppliers,
        taxes: action.taxes,
        accounts: action.accounts,
        array_id_location: action.array_id_location,
        default_id_item_tax: action.default_id_item_tax,
        units:action.units,
        inventory_items: action.inventory_items,
        racks: action.racks,
        colors: action.colors,
        sizes: action.sizes,
        raptor_module: action.raptor_module,
        info_message: action.info_message
      }
    }
    case "GET_ITEM_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        errorField: action.error.errors,
        error: action.error.message
      }
    }
    case 'CREATE_ITEM_REQUEST': {
      return {
        ...state,
        // item: null,
        errorField: null,
      };
    }

    case 'IMPORT_RAPTOR_ITEMS_REQUEST': {
      return {
        ...state,
        loadingImport: true,
      };
    }
    case 'IMPORT_RAPTOR_ITEMS_SUCCESS': {
      return {
        ...state,
        loadingImport: false,
        success: action.success.message
      };
    }
    case 'IMPORT_RAPTOR_ITEMS_ERROR': {
      return {
        ...state,
        loadingImport: false,
        error: action.error.message,
      };
    }
    
    case 'STORE_ITEM_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_ITEM_SUCCESS': {
      return {
        ...state,
        loading: false,
        id_item: action.id_item,
        success: action.success.message
      };
    }
    case 'STORE_ITEM_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'STORE_ITEM_LOCATION_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_ITEM_LOCATION_SUCCESS': {
      return {
        ...state,
        loading: false,
        id_item: action.id_item,
        success: action.success.message
      };
    }
    case 'STORE_ITEM_LOCATION_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_ITEM_REQUEST': {
      return {
        ...state,
        item: null,
        loading: true,
        loadingEdit: true,
        errorField: null,
        item_inventory: null,
      };
    }
    case 'EDIT_ITEM_SUCCESS': {
      return {
        ...state,
        item: action.item,
        loading: false,
        loadingEdit: false,
      };
    }
    case 'EDIT_ITEM_ERROR': {
      return {
        ...state,
        //loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'EDIT_ITEM_LOCATION_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_ITEM_LOCATION_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
      };
    }
    case 'EDIT_ITEM_LOCATION_ERROR': {
      return {
        ...state,
        //loading: false,
        error: action.error.message,
        //errorField: action.error.errors
      };
    }
    case 'UPDATE_ITEM_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_ITEM_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_ITEM_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'UPDATE_ITEM_LOCATION_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_ITEM_LOCATION_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_ITEM_LOCATION_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'UPDATE_ITEM_SALES_PURCHASE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_ITEM_GROUP_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_ITEM_ACCOUNT_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_ITEM_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_ITEM_SUCCESS': {
      return {
        ...state,
        loading: false,
        items: action.items
      };
    }
    case "DEACTIVATE_ITEM_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_ITEM_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_ITEM_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_ITEM_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_ITEM_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_ITEM_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
