const initState = {
  current_page: 1,
  customerPriceList: null,
  customerPriceLists: [],
  errorField: null,
  filter: null,
  id_customer_pricelist: null,
  id_location: null,
  items: [],
  customers: [],
  loading: false,
  loadingCustomers: false,
  loadingItems: false,
  loadingSubmit: false,
  page_size: null,
  pricelist_info: null,
  search: null,
  success: null,
  total: null,
};

export default function customerPriceListsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_CUSTOMER_PRICE_LISTS_REQUEST": {
      return {
        ...state,
        current_page: action.payload.page,
        errorField: null,
        filter: action.payload.filter,
        loadingGetCP: true,
        search: action.payload.search
      };
    }
    case "GET_CUSTOMER_PRICE_LISTS_SUCCESS": {
      return {
        ...state,
        customerPriceLists: action.customerPriceLists,
        current_page: action.current_page,
        filter: action.filter,
        loadingGetCP: false,
        page_size: action.page_size,
        search: action.search,
        total: action.total
      };
    }
    case "GET_CUSTOMER_PRICE_LISTS_ERROR": {
      return {
        ...state,
        error: action.error.message,
        filter: action.filter,
        loadingGetCP: false,
        search: action.search
      };
    }
    case "CREATE_CUSTOMER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        errorField: null
      };
    }
    case "GET_CUSTOMER_PRICE_LIST_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        customer_types: action.customer_types,
        loading: false,
        locations: action.locations,
        pricelist_info: action.pricelist_info
      };
    }
    case "GET_CUSTOMER_PRICE_LIST_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message,
        errorField: action.error.errors,
        loading: false
      };
    }
    case "GET_CUSTOMERS_FOR_CUSTOMER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        id_location: action.payload.id_location,
        loadingCustomers: true
      };
    }
    case "GET_CUSTOMERS_FOR_CUSTOMER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        customers: action.customers,
        loadingCustomers: false
      };
    }
    case "GET_FILTER_ITEMS_FOR_CUSTOMER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        id_location: action.payload.id_location,
        loadingItems: true,
        search: action.payload.search
      };
    }
    case "GET_FILTER_ITEMS_FOR_CUSTOMER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        items: action.items,
        loadingItems: false
      };
    }
    case "GET_FILTER_ITEMS_FOR_CUSTOMER_PRICE_LIST_ERROR": {
      return {
        ...state,
        error: action.error.message,
        loadingItems: false
      };
    }
    case "STORE_CUSTOMER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        errorField: null,
        loadingSubmit: true
      };
    }
    case "STORE_CUSTOMER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        id_customer_pricelist: action.id_customer_pricelist,
        loadingSubmit: false,
        success: action.success.message,
      };
    }
    case "STORE_CUSTOMER_PRICE_LIST_ERROR": {
      return {
        ...state,
        error: action.error.message,
        errorField: action.error.errors,
        loadingSubmit: false
      };
    }
    case "EDIT_CUSTOMER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        customerPriceList: null,
        errorField: null,
        loading: true,
      };
    }
    case "EDIT_CUSTOMER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        customerPriceList: action.customerPriceList,
        loading: false,
      };
    }
    case "EDIT_CUSTOMER_PRICE_LIST_ERROR": {
      return {
        ...state,
        error: action.error.message,
        loading: false,
        loadingSubmit: false
      };
    }
    case "UPDATE_CUSTOMER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        errorField: null,
        loadingSubmit: true
      };
    }
    case "UPDATE_CUSTOMER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        id_customer_pricelist: action.id_customer_pricelist,
        loadingSubmit: false,
        success: action.success.message,
      };
    }
    case "UPDATE_CUSTOMER_PRICE_LIST_ERROR": {
      return {
        ...state,
        error: action.error.message,
        errorField: action.error.errors,
        loadingSubmit: false
      };
    }
    case "GET_FILTER_CUSTOMER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        customerPriceList: action.customerPriceList
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null,
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    case "DEACTIVATE_CUSTOMER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_CUSTOMER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_CUSTOMER_PRICE_LIST_ERROR": {
      return {
        ...state,
        error: action.error.message,
        loading: false
      };
    }
    case "ACTIVATE_CUSTOMER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_CUSTOMER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_CUSTOMER_PRICE_LIST_ERROR": {
      return {
        ...state,
        error: action.error.message,
        loading: false
      };
    }
    case "REJECT_CUSTOMER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "REJECT_CUSTOMER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "REJECT_CUSTOMER_PRICE_LIST_ERROR": {
      return {
        ...state,
        error: action.error.message,
        loading: false
      };
    }
    case "CONFIRM_CUSTOMER_PRICE_LIST_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CONFIRM_CUSTOMER_PRICE_LIST_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "CONFIRM_CUSTOMER_PRICE_LIST_ERROR": {
      return {
        ...state,
        error: action.error.message,
        loading: false
      };
    }
    default:
      return state;
  }
}
