import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getColors({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [colors] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "items/colors?search=" + search + "&page=" + page), 
          ])
        : // Jika filter
          yield all([
            call(get, "items/colors?" + filter + "&page=" + page), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "items/colors?search=" + search),  
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "items/colors?" + filter),
        ]);

    // console.log(colors);
    // console.log(accounts);

    // Jika sukses get data colors
    if (colors.status === "success") {
      // Jika di API menggunakan pagination
      if (colors.result.current_page && search) {
        yield put({
          type: 'GET_COLORS_SUCCESS',
          colors: colors.result.data,
          current_page: colors.result.current_page,
          page_color: colors.result.per_page,
          total: colors.result.total,
          search: search
        });
      }
      // else if(colors.result.current_page && filter)
      // {
      //   yield put({
      //     type: 'GET_COLORS_SUCCESS',
      //     colors: colors.result.data,
      //     current_page: colors.result.current_page,
      //     page_color: colors.result.per_page,
      //     total: colors.result.total,
      //     filter: filter
      //   });
      // }
      else if (colors.result.current_page) {
        yield put({
          type: 'GET_COLORS_SUCCESS',
          colors: colors.result.data,
          current_page: colors.result.current_page,
          page_color: colors.result.per_page,
          total: colors.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_COLORS_SUCCESS',
          colors: colors.result,
          page_color: null,
          total: null
        });
      }
    }
    else if(colors.status === "fail"){
      yield put({
        type: 'GET_COLORS_ERROR',
        error: colors
      })
    }
    else {
      yield put({
        type: 'GET_COLORS_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'GET_COLORS_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createColor() {

}

// Store data
export function* storeColor({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "items/colors", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_COLOR_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_COLOR_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_COLOR_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_COLOR_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editColor({ payload }) {
  //console.log("masuk")
  const { id_color } = payload;

  try {
    const [color] = yield all([
      call(get, "items/colors/" + id_color),
    ]);
  
    // Jika sukses
    if (color.status === "success") {
      yield put({
        type: 'EDIT_COLOR_SUCCESS',
        color: color.result
      });
    }
    else if(color.status === "fail"){
      yield put({
        type: 'EDIT_COLOR_ERROR',
        error: color
      })
    }
    else{
      yield put({
        type: 'EDIT_COLOR_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'EDIT_COLOR_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateColor({ payload }) {
  const { id_color, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "items/colors/" + id_color,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_COLOR_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_COLOR_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_COLOR_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_COLOR_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showColor({ payload }) {
  const { id_color } = payload;

  try {
    const resultShow = yield call(get, "items/colors/" + id_color);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_COLOR_SUCCESS',
        color: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateColor({ payload }) {
  const { id_color } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "items/colors/" + id_color,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_COLOR_SUCCESS',
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_COLORS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_COLOR_ERROR',
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_COLORS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_COLOR_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_COLORS_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_COLOR_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_COLORS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateColor({ payload }) {
  const { id_color } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "items/colors/" + id_color,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_COLOR_SUCCESS',
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_COLORS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: 'ACTIVATE_COLOR_ERROR',
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_COLORS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_COLOR_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_COLORS_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_COLOR_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_COLORS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_COLORS_REQUEST', getColors),
    yield takeEvery('CREATE_COLOR_REQUEST', createColor),
    yield takeEvery('STORE_COLOR_REQUEST', storeColor),
    yield takeEvery('EDIT_COLOR_REQUEST', editColor),
    yield takeEvery('UPDATE_COLOR_REQUEST', updateColor),
    yield takeEvery('SHOW_COLOR_REQUEST', showColor),
    yield takeEvery('DEACTIVATE_COLOR_REQUEST', deactivateColor),
    yield takeEvery('ACTIVATE_COLOR_REQUEST', activateColor)
  ]);
}
