
const initState = {
  journals: [],
  journal: null,
  locations: [],
  accounts: [],
  cashbanks: [],
  currencies: [],
  customers: [],
  projects: [],
  departments: [],
  messages: [],
  search: null,
  suppliers: [],
  filter: null,
  loading: false,
  loadingImport: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_journal: null,
  import_journals: [],
  import_current_page: 1,
  import_page_size: null,
  import_total: null,
  import_search: null,
  import_filter: null,
};

export default function journalsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_JOURNALS_REQUEST': {
      return {
        ...state,
        //journals: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_JOURNALS_SUCCESS': {
      return {
        ...state,
        journals: action.journals,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_JOURNALS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      }
    }
    case 'GET_IMPORT_JOURNALS_REQUEST': {
      return {
        ...state,
        //journals: [],
        loadingImport: true,
        import_search: action.payload.search,
        import_filter: action.payload.filter,
        import_current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_IMPORT_JOURNALS_SUCCESS': {
      return {
        ...state,
        import_journals: action.journals,
        import_current_page: action.current_page,
        import_page_size: action.page_size,
        import_total: action.total,
        import_search: action.search,
        import_filter: action.filter,
        loadingImport: false,
      };
    }
    case 'GET_IMPORT_JOURNALS_ERROR': {
      return {
        ...state,
        loadingImport: false,
        error: action.error.message
      }
    }
    case 'CREATE_JOURNAL_REQUEST': {
      return {
        ...state,
        journal: null,
        errorField: null,
      };
    }

    case 'GET_JOURNAL_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        accounts: action.accounts,
        cashbanks: action.cashbanks,
        currencies: action.currencies,
        customers: action.customers,
        departments: action.departments,
        messages: action.messages,
        locations: action.locations,
        projects: action.projects,
        suppliers: action.suppliers
      };
    }
    case 'GET_JOURNAL_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message
      };
    }

    case 'STORE_JOURNAL_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_JOURNAL_SUCCESS': {
      return {
        ...state,
        loading: false,
        id_journal: action.id_journal,
        success: action.success.message
      };
    }
    case 'STORE_JOURNAL_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_JOURNAL_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_JOURNAL_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        journal: action.journal
      };
    }
    case 'EDIT_JOURNAL_ERROR': {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case 'UPDATE_JOURNAL_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_JOURNAL_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_JOURNAL_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_JOURNAL_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_JOURNAL_SUCCESS': {
      return {
        ...state,
        loading: false,
        journals: action.journals
      };
    }
    case "PROCESS_JOURNAL_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "PROCESS_JOURNAL_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "PROCESS_JOURNAL_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CANCEL_JOURNAL_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CANCEL_JOURNAL_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "CANCEL_JOURNAL_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "UPLOAD_JOURNAL_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_JOURNAL_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_JOURNAL_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_JOURNAL_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_JOURNAL_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_JOURNAL_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_JOURNAL_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
