const initState = {
  account_payables: [],
  account_receivables: [],
  cashflow: [],
  loading: false,
  retained_earning: [],
  top_items_by_amount: [],
  top_items_by_qty: [],
  total_sales: 0
};

export default function dashboardReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_DASHBOARD_REPORTS_REQUEST":
      {
        return {
          ...state,
          loading: true
        };
      }
      break;
    case "GET_DASHBOARD_REPORTS_SUCCESS":
      return {
        ...state,
        loading: false,
        account_payables: action.account_payables,
        account_receivables: action.account_receivables,
        cash_flow: action.cash_flow,
        retained_earning: action.retained_earning,
        top_items_by_amount: action.top_items_by_amount,
        top_items_by_qty: action.top_items_by_qty,
        total_sales: action.total_sales
      };
    default:
      return state;
      break;
  }
}
