import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getTopCustomerReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/item-summaries/top-customers?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if(filter){
      const [top_customer_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/item-summaries/sales/foreign-keys")
      ]);
    
      // Jika sukses get data top_customer_reports
      if (top_customer_reports.status === "success") {
          // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_TOP_CUSTOMER_REPORTS_SUCCESS",
          top_customer_reports: top_customer_reports.result,
          filter
        });
      } else if (top_customer_reports.status === "fail") {
        yield put({
          type: "GET_TOP_CUSTOMER_REPORTS_ERROR",
          error: { message: top_customer_reports.message }
        });
      } else {
        yield put({
          type: "GET_TOP_CUSTOMER_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data top_customer_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TOP_CUSTOMER_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          customers: foreign_keys.result.customers,
          customer_types: foreign_keys.result.customer_types,
          item_categories: foreign_keys.result.item_categories,
          employees: foreign_keys.result.employees,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TOP_CUSTOMER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TOP_CUSTOMER_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/item-summaries/sales/foreign-keys")
      ]);
    
      yield put({
        type: "GET_TOP_CUSTOMER_REPORTS_SUCCESS",
        top_customer_reports: []
      });
  
      // Jika sukses get data top_customer_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TOP_CUSTOMER_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          customers: foreign_keys.result.customers,
          customer_types: foreign_keys.result.customer_types,
          item_categories: foreign_keys.result.item_categories,
          employees: foreign_keys.result.employees,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TOP_CUSTOMER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TOP_CUSTOMER_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_TOP_CUSTOMER_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_TOP_CUSTOMER_REPORTS_REQUEST", getTopCustomerReports)
  ]);
}
