const initState = {
  job_titles: [],
  job_title: null,
  search: null,
  //filter: null,
  loading: false,
  loadingSubmit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function jobTitlesReducer(state = initState, action) {
  switch (action.type) {
    case "GET_JOB_TITLES_REQUEST": {
      return {
        ...state,
        //job_titles: [],
        loading: true,
        search: action.payload.search,
        //filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_JOB_TITLES_SUCCESS": {
      return {
        ...state,
        job_titles: action.job_titles,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        //filter: action.filter,
        loading: false
      };
    }
    case "GET_JOB_TITLES_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "STORE_JOB_TITLE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_JOB_TITLE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "STORE_JOB_TITLE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_JOB_TITLE_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "EDIT_JOB_TITLE_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        job_title: action.job_title
      };
    }
    case "EDIT_JOB_TITLE_ERROR": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_JOB_TITLE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_JOB_TITLE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_JOB_TITLE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_JOB_TITLE_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_JOB_TITLE_SUCCESS": {
      return {
        ...state,
        loading: false,
        job_titles: action.job_titles
      };
    }
    case "DEACTIVATE_JOB_TITLE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_JOB_TITLE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_JOB_TITLE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_JOB_TITLE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_JOB_TITLE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_JOB_TITLE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
