const initState = {
  account_types: [],
  error: null,
  errorField: null,
  filter: null,
  filter_select: [],
  general_ledgers: [],
  items: [],
  loading: false,
  loadingForeign: false,
  loadingItem: false,
  loadingSubmit: false,
  search: null,
  success: null,
  total: null,
  reports: []
};

export default function stockValuationReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    case "GET_STOCK_VALUATION_REPORTS_REQUEST":
      return {
        ...state,
        current_page: action.payload.page,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        loading: true,
        reports: [],
        search: action.payload.search
      };
    case "GET_STOCK_VALUATION_REPORTS_SUCCESS":
      return {
        ...state,
        current_page: action.current_page,
        filter: action.filter,
        loading: false,
        page_size: action.page_size,
        reports: action.reports,
        search: action.search,
        total: action.total
      };
    case "GET_STOCK_VALUATION_REPORTS_ERROR":
      return {
        ...state,
        error: action.error.message,
        filter_select: [],
        loading: false
      };
    case "GET_STOCK_VALUATION_REPORT_FOREIGN_KEYS_REQUEST":
      return {
        ...state,
        loadingForeign: true
      };
    case "GET_STOCK_VALUATION_REPORT_FOREIGN_KEYS_SUCCESS":
      return {
        ...state,
        loading: false,
        loadingForeign: false,
        locations: action.locations
      };
    case "GET_STOCK_VALUATION_REPORT_FOREIGN_KEYS_ERROR":
      return {
        ...state,
        error: action.error.message,
        loadingForeign: false,
        loading: false,
      };
    case "GET_ITEMS_FOR_STOCK_VALUATION_REPORT_REQUEST":
      return {
        ...state,
        loadingItem: true
      };
    case "GET_FILTER_ITEMS_FOR_STOCK_VALUATION_REPORT_SUCCESS":
      return {
        ...state,
        items: action.items,
        loadingItem: false
      };
    case "GET_FILTER_ITEMS_FOR_STOCK_VALUATION_REPORT_ERROR": {
      return {
        ...state,
        loadingItem: false,
        error: action.error.message
      };
    }
    default:
      return state;
  }
}
