const initState = {
  customers: [],
  id_delivery_order: null,
  id_location: null,
  deliveryOrders: [],
  deliveryOrder: null,
  printData: {},
  departments: [],
  couriers: [],
  salesOrders: [],
  transactions: [],
  // selectedTransaction: null,
  locations: [],
  info: [],
  search: null,
  filter: null,
  loading: false,
  loadingConfirm: false,
  loadingReject: false,
  loadingCancel: false,
  loadingForeign: false,
  loadingSubmit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function deliveryOrdersReducer(state = initState, action) {
  switch (action.type) {
    case "GET_DELIVERY_ORDERS_REQUEST": {
      return {
        ...state,
        //deliveryOrders: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_DELIVERY_ORDERS_SUCCESS": {
      return {
        ...state,
        deliveryOrders: action.deliveryOrders,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_DELIVERY_ORDERS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_DELIVERY_ORDER_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_DELIVERY_ORDER_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        customers: action.customers,
        departments: action.departments,
        employees: action.employees,
        locations: action.locations,
        projects: action.projects,
        couriers: action.couriers,
        salesOrders: action.sales_orders
      };
    }
    case "GET_DELIVERY_ORDER_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_DELIVERY_ORDER_REQUEST": {
      return {
        ...state,
        id_delivery_order: null,
        deliveryOrder: null,
        loadingTransactions: true,
        loadingItemRequests: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        item_requests_current_page: 1,
        item_requests_page_size: null,
        item_requests_total: null
      };
    }

    case "GET_FILTER_SO_DELIVERY_ORDER_REQUEST": {
      return {
        ...state,
        loadingTransactions: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_FILTER_SO_DELIVERY_ORDER_SUCCESS": {
      return {
        ...state,
        loadingTransactions: false,
        transactions: action.transactions,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_FILTER_SO_DELIVERY_ORDER_ERROR": {
      return {
        ...state,
        loadingTransactions: false,
        error: action.error.message
      };
    }
    case "STORE_DELIVERY_ORDER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_DELIVERY_ORDER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_delivery_order: action.id_delivery_order,
        info: action.info
      };
    }
    case "STORE_DELIVERY_ORDER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_DELIVERY_ORDER_REQUEST": {
      return {
        ...state,
        loading: true,
        id_delivery_order: null,
        loadingTransactions: true,
        loadingItemRequests: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        item_requests_current_page: 1,
        item_requests_page_size: null,
        item_requests_total: null
      };
    }
    case "EDIT_DELIVERY_ORDER_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        deliveryOrder: action.deliveryOrder
      };
    }
    case "EDIT_DELIVERY_ORDER_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_DELIVERY_ORDER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_DELIVERY_ORDER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_delivery_order: action.id_delivery_order,
        info: action.info
      };
    }
    case "UPDATE_DELIVERY_ORDER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_DELIVERY_ORDER_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_DELIVERY_ORDER_SUCCESS": {
      return {
        ...state,
        loading: false,
        deliveryOrders: action.deliveryOrders
      };
    }
    case "CONFIRM_DELIVERY_ORDER_REQUEST": {
      return {
        ...state,
        loadingConfirm: true
      };
    }
    case "CONFIRM_DELIVERY_ORDER_SUCCESS": {
      return {
        ...state,
        loadingConfirm: false,
        success: action.success.message,
      };
    }
    case "CONFIRM_DELIVERY_ORDER_ERROR": {
      return {
        ...state,
        loadingConfirm: false,
        error: action.error.message
      };
    }
    case "REJECT_DELIVERY_ORDER_REQUEST": {
      return {
        ...state,
        loadingReject: true
      };
    }
    case "REJECT_DELIVERY_ORDER_SUCCESS": {
      return {
        ...state,
        loadingReject: false,
        success: action.success.message,
      };
    }
    case "REJECT_DELIVERY_ORDER_ERROR": {
      return {
        ...state,
        loadingReject: false,
        error: action.error.message
      };
    }
    case "CANCEL_DELIVERY_ORDER_REQUEST": {
      return {
        ...state,
        loadingCancel: true
      };
    }
    case "CANCEL_DELIVERY_ORDER_SUCCESS": {
      return {
        ...state,
        loadingCancel: false,
        success: action.success.message,
      };
    }
    case "CANCEL_DELIVERY_ORDER_ERROR": {
      return {
        ...state,
        loadingCancel: false,
        error: action.error.message
      };
    }
    case "UPLOAD_DELIVERY_ORDER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_DELIVERY_ORDER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.message
      };
    }
    case "UPLOAD_DELIVERY_ORDER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_DELIVERY_ORDER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_DELIVERY_ORDER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_DELIVERY_ORDER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.message
      };
    }
    case "DELETE_DELIVERY_ORDER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "SET_PRINT_DATA_DELIVERY_ORDER": {
      return {
        ...state,
        printData: action.payload.body
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null,
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
