const initState = {
  id_discount_management: null,
  id_location: [],
  id_customer: null,
  discountManagements: [],
  discountManagement: null,
  locations: [],
  customers: [],
  employees: [],
  customerTypes: [],
  itemCategories: [],
  paymentCategories: [],
  units: [],
  items: [],
  search: null,
  filter: null,
  loading: false,
  loadingItem: false,
  loadingForeign: false,
  loadingSubmit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function discountManagementsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_DISCOUNT_MANAGEMENTS_REQUEST": {
      return {
        ...state,
        //discountManagements: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_DISCOUNT_MANAGEMENTS_SUCCESS": {
      return {
        ...state,
        discountManagements: action.discountManagements,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_DISCOUNT_MANAGEMENTS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_DISCOUNT_MANAGEMENT_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_DISCOUNT_MANAGEMENT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        locations: action.locations,
        customers: action.customers,
        customerTypes: action.customerTypes,
        itemCategories: action.itemCategories,
        paymentCategories: action.paymentCategories,
        units: action.units
      };
    }
    case "GET_DISCOUNT_MANAGEMENT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_DISCOUNT_MANAGEMENT_REQUEST": {
      return {
        ...state,
        id_discount_management: null,
        id_location: null,
        discountManagement: null,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null
      };
    }
    case "GET_ITEMS_LOCATION_DISMAN_REQUEST": {
      return {
        ...state,
        loadingItem: true,
        items: []
      };
    }
    case "GET_ITEMS_LOCATION_DISMAN_SUCCESS": {
      return {
        ...state,
        loadingItem: false,
        items: action.items
      };
    }
    case "GET_ITEMS_LOCATION_DISMAN_ERROR": {
      return {
        ...state,
        loadingItem: false,
        error: action.error.message
      };
    }
    case "GET_EMPLOYEES_LOCATION_DISMAN_REQUEST": {
      return {
        ...state,
        loadingItem: true,
        employees: []
      };
    }
    case "GET_EMPLOYEES_LOCATION_DISMAN_SUCCESS": {
      return {
        ...state,
        loadingItem: false,
        employees: action.employees
      };
    }
    case "GET_EMPLOYEES_LOCATION_DISMAN_ERROR": {
      return {
        ...state,
        loadingItem: false,
        error: action.error.message
      };
    }
    case "STORE_DISCOUNT_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_DISCOUNT_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_discount_management: action.id_discount_management
      };
    }
    case "STORE_DISCOUNT_MANAGEMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_DISCOUNT_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loading: true,
        id_discount_management: null,
        id_location: null,
        loadingTransactions: true,
        loadingSaleReceipts: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null
      };
    }
    case "EDIT_DISCOUNT_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        discountManagement: action.discountManagement
      };
    }
    case "EDIT_DISCOUNT_MANAGEMENT_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_DISCOUNT_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_DISCOUNT_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_discount_management: action.id_discount_management
      };
    }
    case "UPDATE_DISCOUNT_MANAGEMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_DISCOUNT_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_DISCOUNT_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        discountManagements: action.discountManagements
      };
    }
    case "CONFIRM_DISCOUNT_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CONFIRM_DISCOUNT_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CONFIRM_DISCOUNT_MANAGEMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CANCEL_DISCOUNT_MANAGEMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CANCEL_DISCOUNT_MANAGEMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CANCEL_DISCOUNT_MANAGEMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_DISCOUNT_MANAGEMENT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_DISCOUNT_MANAGEMENT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_DISCOUNT_MANAGEMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null,
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
