import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getTransactionJournalReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/transaction-journals?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [transaction_journal_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/transaction-journals/foreign-keys")
      ]);
  
      // Jika sukses get data transaction_journal_reports
      if (
        transaction_journal_reports.status === "success" &&
        transaction_journal_reports.result !== null
      ) {
  
        yield put({
          type: "GET_TRANSACTION_JOURNAL_REPORTS_SUCCESS",
          transaction_journal_reports: transaction_journal_reports.result,
          filter
        });
  
      } else if (
        transaction_journal_reports.status === "success" &&
        transaction_journal_reports.result === null
      ) {
        yield put({
          type: "GET_TRANSACTION_JOURNAL_REPORTS_SUCCESS",
          transaction_journal_reports: [],
          filter
        });
      } else if (transaction_journal_reports.status === "fail") {
        yield put({
          type: "GET_TRANSACTION_JOURNAL_REPORTS_ERROR",
          error: { message: transaction_journal_reports.message }
        });
      } else {
        yield put({
          type: "GET_TRANSACTION_JOURNAL_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data account_history_department_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TRANSACTION_JOURNAL_REPORT_FOREIGN_KEYS_SUCCESS",
          accounts: foreign_keys.result.accounts,
          account_types: foreign_keys.result.account_types,
          locations: foreign_keys.result.locations,
          projects: foreign_keys.result.projects
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TRANSACTION_JOURNAL_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TRANSACTION_JOURNAL_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/account-histories/foreign-keys")
      ]);
  
      yield put({
        type: "GET_TRANSACTION_JOURNAL_REPORTS_SUCCESS",
        transaction_journal_reports: [],
      });
  
      // Jika sukses get data account_history_department_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TRANSACTION_JOURNAL_REPORT_FOREIGN_KEYS_SUCCESS",
          accounts: foreign_keys.result.accounts,
          account_types: foreign_keys.result.account_types,
          locations: foreign_keys.result.locations,
          projects: foreign_keys.result.projects
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TRANSACTION_JOURNAL_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TRANSACTION_JOURNAL_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_TRANSACTION_JOURNAL_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_TRANSACTION_JOURNAL_REPORTS_REQUEST",
      getTransactionJournalReports
    )
  ]);
}
