const initState = {
  currencies: [],
  accounts: [],
  loading: true,
  loadingEdit:true,
  loadingFilterAccounts: true,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function gLCurrenciesReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_GL_CURRENCIES_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField:null,
        loadingFilterAccounts: false,
      };
    }
    case 'GET_GL_CURRENCIES_SUCCESS': {
      return {
        ...state,
        currencies: action.currencies,
        accounts: action.accounts,
        loading: false,
        loadingFilterAccounts: false,
      };
    }
    case 'GET_GL_CURRENCIES_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case 'UPDATE_GL_CURRENCY_REQUEST': {
      return {
        ...state,
        loadingSubmit: true,
        errorField:null,
      };
    }
    case 'UPDATE_GL_CURRENCY_SUCCESS': {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }

    case 'UPDATE_GL_CURRENCY_ERROR': {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }

    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };

    default:
      return state;
  }
}
