const initState = {
  currencies: [],
  currency: null,
  check_is_used: null,
  accounts: [],
  search: null,
  filter: null,
  loading: true,
  loadingEdit:true,
  loadingFilterAccounts: true,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function currenciesReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_CURRENCIES_REQUEST': {
      return {
        ...state,
        //currencies: [],  
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField:null,
      };
    }
    case 'GET_CURRENCIES_SUCCESS': {
      return {
        ...state,
        currencies: action.currencies,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case "GET_CURRENCIES_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case 'CREATE_CURRENCY_REQUEST': {
      return {
        ...state,
        loading: true,
        currency: null,
        errorField:null,
        check_is_used: null,
        loadingFilterAccounts: true,
      };
    }
    case 'CREATE_CURRENCY_SUCCESS': {
      return {
        ...state,
        loading: false,
      };
    }
    case 'GET_CHECK_IS_USED_SUCCESS': {
      return {
        ...state,
        check_is_used: action.check_is_used
      };
    }
    case 'GET_FILTER_ACCOUNTS_SUCCESS': {
      return {
        ...state,
        loadingFilterAccounts: false,
        accounts: action.accounts
      };
    }
    case 'GET_FILTER_ACCOUNTS_ERROR': {
      return {
        ...state,
        error: action.error.message,
      };
    }
    case 'STORE_CURRENCY_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField:null,
      };
    }
    case 'STORE_CURRENCY_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }

    case 'STORE_CURRENCY_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }

    case 'EDIT_CURRENCY_REQUEST': {
      return {
        ...state,
        check_is_used: null,
        accounts: [],
        loading: true,
        loadingEdit:true,
        loadingFilterAccounts: true,
        errorField: null
      };
    }
    case 'EDIT_CURRENCY_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        currency: action.currency
      };
    }
    case 'EDIT_CURRENCY_ERROR': {
      return {
        ...state,
        //loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'UPDATE_CURRENCY_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField:null,
      };
    }
    case 'UPDATE_CURRENCY_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }

    case 'UPDATE_CURRENCY_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }

    case 'SHOW_CURRENCY_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case 'SHOW_CURRENCY_SUCCESS': {
      return {
        ...state,
        loading: false,
        currencies: action.currencies
      };
    }
    case "DEACTIVATE_CURRENCY_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_CURRENCY_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_CURRENCY_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_CURRENCY_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_CURRENCY_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_CURRENCY_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
