const initState = {
  cash_bank_summary_reports: [],
  cash_banks: [],
  locations: [],
  filter: null,
  filter_select: [],
  loading: false,
  // current_page: 1,
  // page_size: null,
  // total: null,
  success: null,
  error: null,
  errorField: null
};

export default function cashBankSummaryReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_CASH_BANK_SUMMARY_REPORTS_REQUEST": {
      return {
        ...state,
        //cash_bank_summary_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_CASH_BANK_SUMMARY_REPORTS_SUCCESS": {
      return {
        ...state,
        cash_bank_summary_reports: action.cash_bank_summary_reports,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_CASH_BANK_SUMMARY_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_CASH_BANK_SUMMARY_REPORT_REQUEST": {
      return {
        ...state,
        loading: false,
        cash_bank_summary_report: null,
        errorField: null
      };
    }

    case "GET_CASH_BANK_SUMMARY_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        cash_banks: action.cash_banks,
        locations: action.locations
      };
    }
    case "GET_CASH_BANK_SUMMARY_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
