
const initState = {
  cash_flow_types: [],
  cash_flow_type: null,
  cash_flow_groups: [],
  search: null,
  filter: null,
  loading: false,
  loadingEdit: false, 
  loadingFilterCashFLowGroups: false,
  current_page: 1,
  page_size: null,
  total: null,
  errorField: null,
  error: null,
  success: null,
};

export default function cashFlowTypesReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_CASH_FLOW_TYPES_REQUEST': {
      return {
        ...state,
        //cash_flow_types: [],
        loading: true,
        loadingFilterLocationTypes: true,
        loadingFilterSites: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_CASH_FLOW_TYPES_SUCCESS': {
      return {
        ...state,
        cash_flow_types: action.cash_flow_types,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_CASH_FLOW_TYPES_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message     
      };
    }
    case 'CREATE_CASH_FLOW_TYPE_REQUEST': {
      return {
        ...state,
        loadingFilterCashFLowGroups: true,
        cash_flow_type: null,
        errorField: null,
      };
    }

    case 'GET_CASH_FLOW_GROUPS_SUCCESS': {
      return {
        ...state,
        loadingFilterCashFLowGroups: false,
        cash_flow_groups: action.cash_flow_groups
      };
    }

    case 'GET_CASH_FLOW_GROUPS_ERROR': {
      return {
        ...state,
        loadingFilterCashFLowGroups: false,
        error: action.error.message     
      };
    }
    
    case 'STORE_CASH_FLOW_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_CASH_FLOW_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_CASH_FLOW_TYPE_ERROR': {
      return {
        ...state,
        loading: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case 'EDIT_CASH_FLOW_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        loadingFilterCashFLowGroups: true,
        errorField: null,
      };
    }
    case 'EDIT_CASH_FLOW_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        loadingFilterCashFLowGroups: false,
        cash_flow_type: action.cash_flow_type
      };
    }
    case 'EDIT_CASH_FLOW_ERROR': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        loadingFilterCashFLowGroups: false,
        error: action.error.message
      };
    }
    case 'UPDATE_CASH_FLOW_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_CASH_FLOW_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_CASH_FLOW_TYPE_ERROR': {
      return {
        ...state,
        loading: false,
        errorField: action.error.errors,
        error: action.error.message,
      };
    }
    case 'SHOW_CASH_FLOW_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_CASH_FLOW_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        cash_flow_types: action.cash_flow_types
      };
    }
    case "DEACTIVATE_CASH_FLOW_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_CASH_FLOW_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_CASH_FLOW_TYPE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_CASH_FLOW_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_CASH_FLOW_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
