const initState = {
  petty_cash_summary_reports: [],
  petty_cash_types: [],
  locations: [],
  filter: null,
  filter_select: [],
  loading: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function pettyCashSummaryReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_PETTY_CASH_SUMMARY_REPORTS_REQUEST": {
      return {
        ...state,
        //petty_cash_summary_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_PETTY_CASH_SUMMARY_REPORTS_SUCCESS": {
      return {
        ...state,
        petty_cash_summary_reports: action.petty_cash_summary_reports,
        // current_page: action.current_page,
        // page_size: action.page_size,
        // total: action.total,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PETTY_CASH_SUMMARY_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_PETTY_CASH_SUMMARY_REPORT_REQUEST": {
      return {
        ...state,
        petty_cash_summary_report: null,
        errorField: null
      };
    }

    case "GET_PETTY_CASH_SUMMARY_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        petty_cash_types: action.petty_cash_types,
        locations: action.locations
      };
    }
    case "GET_PETTY_CASH_SUMMARY_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
