const initState = {
  account_history_sub_ledger_department_reports: [],
  account_types: [],
  locations: [],
  departments: [],
  projects: [],
  customers: [],
  suppliers: [],
  filter: null,
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function accountHistorySubLedgerDepartmentReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_ACCOUNT_HISTORY_SUB_LEDGER_DEPARTMENT_REPORTS_REQUEST": {
      return {
        ...state,
        //account_history_sub_ledger_department_reports: [],
        loading: true,
        filter: action.payload.filter,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_ACCOUNT_HISTORY_SUB_LEDGER_DEPARTMENT_REPORTS_SUCCESS": {
      return {
        ...state,
        account_history_sub_ledger_department_reports: action.account_history_sub_ledger_department_reports,
        filter: action.filter,
        search: action.search,
        loading: false
      };
    }
    case "GET_ACCOUNT_HISTORY_SUB_LEDGER_DEPARTMENT_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CREATE_ACCOUNT_HISTORY_SUB_LEDGER_DEPARTMENT_REPORT_REQUEST": {
      return {
        ...state,
        errorField: null
      };
    }

    case "GET_ACCOUNT_HISTORY_SUB_LEDGER_DEPARTMENT_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        account_types: action.account_types,
        locations: action.locations,
        departments: action.departments,
        projects: action.projects,
        customers: action.customers,
        suppliers: action.suppliers
      };
    }
    case "GET_ACCOUNT_HISTORY_SUB_LEDGER_DEPARTMENT_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
