const initState = {
  accounts: [],
  cash_banks: [],
  current_page: 1,
  error: null,
  errorField: null,
  filter: null,
  locations: [],
  loading: false,
  loadingEdit: false,
  payment_categories: [],
  payment_category: null,
  page_size: null,
  search: null,
  total: null
};

export default function paymentCategoriesReducer(state = initState, action) {
  switch (action.type) {
    case "GET_PAYMENT_CATEGORIES_REQUEST": {
      return {
        ...state,
        //payment_categories: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_PAYMENT_CATEGORIES_SUCCESS": {
      return {
        ...state,
        payment_categories: action.payment_categories,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PAYMENT_CATEGORIES_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CREATE_PAYMENT_CATEGORY_REQUEST": {
      return {
        ...state,
        payment_category: null,
        errorField: null,
        loading: true
      };
    }
    case "CREATE_PAYMENT_CATEGORY_SUCCESS": {
      return {
        ...state,
        loading: false
      };
    }
    case "GET_PAYMENT_CATEGORY_FOREIGN_KEYS_SUCCESS":
      return {
        ...state,
        accounts: action.accounts,
        cash_banks: action.cash_banks,
        locations: action.locations
      };
    case "GET_FILTER_ACCOUNTS_SUCCESS": {
      return {
        ...state,
        accounts: action.accounts,
        loading: false
      };
    }
    case "GET_FILTER_ACCOUNTS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "STORE_PAYMENT_CATEGORY_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "STORE_PAYMENT_CATEGORY_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }

    case "STORE_PAYMENT_CATEGORY_ERROR": {
      return {
        ...state,
        loading: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }

    case "EDIT_PAYMENT_CATEGORY_REQUEST": {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null
      };
    }
    case "EDIT_PAYMENT_CATEGORY_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        payment_category: action.payment_category
      };
    }
    case "EDIT_PAYMENT_CATEGORY_ERROR": {
      return {
        ...state,
        // loading: false,
        // errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "UPDATE_PAYMENT_CATEGORY_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "UPDATE_PAYMENT_CATEGORY_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }

    case "UPDATE_PAYMENT_CATEGORY_ERROR": {
      return {
        ...state,
        loading: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }

    case "SHOW_PAYMENT_CATEGORY_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_PAYMENT_CATEGORY_SUCCESS": {
      return {
        ...state,
        loading: false,
        payment_categories: action.payment_categories
      };
    }
    case "DEACTIVATE_PAYMENT_CATEGORY_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_PAYMENT_CATEGORY_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_PAYMENT_CATEGORY_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_PAYMENT_CATEGORY_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_PAYMENT_CATEGORY_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_PAYMENT_CATEGORY_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
