const initState = {
  vat_in_reports: {
    purchase_invoices: [],
    purchase_returns : []
  },
  locations: [],
  filter: null,
  filter_select: [],
  filter_response: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function vatInReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_VAT_IN_REPORTS_REQUEST": {
      return {
        ...state,
        //vat_in_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_VAT_IN_REPORTS_SUCCESS": {
      return {
        ...state,
        vat_in_reports: action.vat_in_reports,
        filter_response: action.filter_response,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_VAT_IN_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        vat_in_reports: {
          purchase_invoices: [],
          purchase_returns : []
        },
        error: action.error.message
      };
    }
    case "CREATE_VAT_IN_REPORT_REQUEST": {
      return {
        ...state,
        //vat_in_report: null,
        errorField: null
      };
    }

    case "GET_VAT_IN_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        locations: action.locations
      };
    }
    case "GET_VAT_IN_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
