import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getGLAdjustmentTypes() {
  try {
    const [adjustment_types, accounts] = 
      yield all([
        call(get, "configuration-managements/adjustment-types?is_active=1"),
        call(get, "accounts?is_active=child"),
      ]);

    // console.log(adjustment_types);
    // console.log(accounts);

    // Jika sukses get data adjustment_types
    if (adjustment_types.status === "success" && accounts.status === "success"){   
      yield put({
        type: 'GET_GL_ADJUSTMENT_TYPES_SUCCESS',
        adjustment_types: adjustment_types.result,
        accounts: accounts.result
      })  
    }
    else {
      yield put({
        type: "GET_GL_ADJUSTMENT_TYPES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_GL_ADJUSTMENT_TYPES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateGLAdjustmentType({ payload }) {
  const { id_adjustment_type, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "configuration-managements/adjustment-types/" + id_adjustment_type,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      
      yield all([
        yield put({
          type: 'UPDATE_GL_ADJUSTMENT_TYPE_SUCCESS',
          success: resultUpdate
        }),
        yield put({
          type: 'GET_GL_ADJUSTMENT_TYPES_REQUEST'
        })
      ])
      
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_GL_ADJUSTMENT_TYPE_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_GL_ADJUSTMENT_TYPES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_GL_ADJUSTMENT_TYPES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_GL_ADJUSTMENT_TYPES_REQUEST', getGLAdjustmentTypes),
    yield takeEvery('UPDATE_GL_ADJUSTMENT_TYPE_REQUEST', updateGLAdjustmentType),
  ]);
}
