export function* confirmPosSalesInvoices({ payload }) {
  const { body } = payload;

  try {
    // POST data ke API
    const confirmResult = yield call(
      axiosPut,
      "pos/sales-invoices/process-transaction",
      body
    );

    // Jika sukses
    if (confirmResult.status === "success") {
      // Set session message
      yield all([
        put({
          type: "CONFIRM_POS_SALES_INVOICES_SUCCESS",
          success: { message: confirmResult.message }
        }),
        put({
          type: "GET_POS_SALES_INVOICES_REQUEST",
          payload: {
            page: 1,
            search: null,
            filter: undefined
          }
        })
      ]);
    }
    // Jika warning
    else if (confirmResult.status === "warning") {
      const { transactions } = body;
      yield all([
        put({
          type: "CONFIRM_POS_SALES_INVOICES_WARNING",
          warning: { message: confirmResult.message },
          formerList: transactions,
          doneList: confirmResult.info
        }),
        put({
          type: "GET_POS_SALES_INVOICES_REQUEST",
          payload: {
            page: 1,
            search: null,
            filter: undefined
          }
        })
      ]);
    }
    // Jika gagal
    else if (confirmResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_POS_SALES_INVOICES_ERROR",
          error: { message: confirmResult.message },
          info: confirmResult.info ? confirmResult.info : []
        })
        // Ulangi GET data untuk refresh
        // put({
        //   type: "EDIT_POS_SALES_INVOICE_REQUEST",
        //   payload: { id_sales_invoice: id_sales_invoice }
        // })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_POS_SALES_INVOICES_ERROR",
          error: { message: "Check your internet connection" }
        })
      ]);
    }
  } catch (error) {
    yield all([
      put({
        type: "CONFIRM_POS_SALES_INVOICES_ERROR",
        error: { message: "Check your internet connection" }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

