export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_POS_SALES_INVOICES_REQUEST", getPosSalesInvoices),
    yield takeEvery("SYNCHRONIZE_POS_SALES_INVOICES_REQUEST", synchronizePos),
    yield takeEvery("EDIT_POS_SALES_INVOICE_REQUEST", editPosSalesInvoice),
    yield takeEvery("UPDATE_POS_SALES_INVOICE_REQUEST", updatePosSalesInvoice),
    yield takeEvery("STORE_ITEM_POS_SALES_INVOICE_REQUEST", storeItem),
    yield takeEvery("UPDATE_ITEM_POS_SALES_INVOICE_REQUEST", updateItem),
    yield takeEvery(
      "UPDATE_ITEM_GROUPING_POS_SALES_INVOICE_REQUEST",
      updateItemGrouping
    ),
    yield takeEvery(
      "CONFIRM_POS_SALES_INVOICE_REQUEST",
      confirmPosSalesInvoice
    ),
    yield takeEvery(
      "CONFIRM_POS_SALES_INVOICES_REQUEST",
      confirmPosSalesInvoices
    ),
    yield takeEvery("CANCEL_POS_SALES_INVOICE_REQUEST", cancelPosSalesInvoice),
    yield takeEvery(
      "UPLOAD_POS_SALES_INVOICE_DOCUMENT_REQUEST",
      uploadPosSalesInvoiceDocument
    ),
    yield takeEvery(
      "DOWNLOAD_POS_SALES_INVOICE_DOCUMENT_REQUEST",
      downloadPosSalesInvoiceDocument
    ),
    yield takeEvery(
      "DELETE_POS_SALES_INVOICE_DOCUMENT_REQUEST",
      deletePosSalesInvoiceDocument
    )
  ]);
}
