import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCustomerTypes({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [customerTypes] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "customers/types?search=" + search + "&page=" + page)
          ])
        : // Jika filter
          yield all([call(get, "customers/types?" + filter + "&page=" + page)])
      : search
      ? // Jika search
        yield all([call(get, "customers/types?search=" + search)])
      : // Jika tidak ada
        yield all([call(get, "customers/types?" + filter)]);

    // console.log(customerTypes);

    // Jika sukses get data customerTypes
    if (customerTypes.status === "success") {
      // Jika di API menggunakan pagination
      if (customerTypes.result.current_page && search) {
        yield put({
          type: "GET_CUSTOMER_TYPES_SUCCESS",
          customerTypes: customerTypes.result.data,
          current_page: customerTypes.result.current_page,
          page_size: customerTypes.result.per_page,
          total: customerTypes.result.total,
          search: search
        });
      }
      // else if(customerTypes.result.current_page && filter)
      // {
      //   yield put({
      //     type: 'GET_CUSTOMER_TYPES_SUCCESS',
      //     customerTypes: customerTypes.result.data,
      //     current_page: customerTypes.result.current_page,
      //     page_size: customerTypes.result.per_page,
      //     total: customerTypes.result.total,
      //     filter: filter
      //   });
      // }
      else if (customerTypes.result.current_page) {
        yield put({
          type: "GET_CUSTOMER_TYPES_SUCCESS",
          customerTypes: customerTypes.result.data,
          current_page: customerTypes.result.current_page,
          page_size: customerTypes.result.per_page,
          total: customerTypes.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_CUSTOMER_TYPES_SUCCESS",
          customerTypes: customerTypes.result,
          page_size: null,
          total: null
        });
      }
    } else if (customerTypes.status === "fail") {
      yield put({
        type: "GET_CUSTOMER_TYPES_ERROR",
        error: customerTypes
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_CUSTOMER_TYPES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_CUSTOMER_TYPES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeCustomerType({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "customers/types", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_CUSTOMER_TYPE_SUCCESS",
        success: resultStore
      });
    } else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_CUSTOMER_TYPE_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_CUSTOMER_TYPE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_CUSTOMER_TYPE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editCustomerType({ payload }) {
  // console.log("masuk");
  const { id_customer_type } = payload;

  try {
    const customerType = yield call(get, "customers/types/" + id_customer_type);
    // console.log(customerType);

    // Jika sukses
    if (customerType.status === "success") {
      yield put({
        type: "EDIT_CUSTOMER_TYPE_SUCCESS",
        customerType: customerType.result
      });
    } else if (customerType.status === "fail") {
      yield put({
        type: "EDIT_CUSTOMER_TYPE_ERROR",
        error: customerType
      });
    } else {
      yield put({
        type: "EDIT_CUSTOMER_TYPE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_CUSTOMER_TYPE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateCustomerType({ payload }) {
  const { id_customer_type, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "customers/types/" + id_customer_type,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: "UPDATE_CUSTOMER_TYPE_SUCCESS",
        success: resultUpdate
      });
    } else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_CUSTOMER_TYPE_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_CUSTOMER_TYPE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showCustomerType({ payload }) {
  const { id_customer_type } = payload;

  try {
    const resultShow = yield call(get, "customers/types/" + id_customer_type);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: "SHOW_CUSTOMER_TYPE_SUCCESS",
        unit: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateCustomerType({ payload }) {
  const { id_customer_type } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "customers/types/" + id_customer_type,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DEACTIVATE_CUSTOMER_TYPE_SUCCESS",
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_CUSTOMER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: "DEACTIVATE_CUSTOMER_TYPE_ERROR",
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_CUSTOMER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DEACTIVATE_CUSTOMER_TYPE_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_CUSTOMER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DEACTIVATE_CUSTOMER_TYPE_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_CUSTOMER_TYPES_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateCustomerType({ payload }) {
  const { id_customer_type } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "customers/types/" + id_customer_type,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: "ACTIVATE_CUSTOMER_TYPE_SUCCESS",
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_CUSTOMER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: "ACTIVATE_CUSTOMER_TYPE_ERROR",
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_CUSTOMER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "ACTIVATE_CUSTOMER_TYPE_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_CUSTOMER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "ACTIVATE_CUSTOMER_TYPE_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_CUSTOMER_TYPES_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_CUSTOMER_TYPES_REQUEST", getCustomerTypes),
    yield takeEvery("STORE_CUSTOMER_TYPE_REQUEST", storeCustomerType),
    yield takeEvery("EDIT_CUSTOMER_TYPE_REQUEST", editCustomerType),
    yield takeEvery("UPDATE_CUSTOMER_TYPE_REQUEST", updateCustomerType),
    yield takeEvery("SHOW_CUSTOMER_TYPE_REQUEST", showCustomerType),
    yield takeEvery("DEACTIVATE_CUSTOMER_TYPE_REQUEST", deactivateCustomerType),
    yield takeEvery("ACTIVATE_CUSTOMER_TYPE_REQUEST", activateCustomerType)
  ]);
}
