import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getYearlySalesReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/sales-summaries/yearly-sales?";
  // if (search) {
  //   url += "search=" + search;
  // }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if(filter){
      const [yearly_sales_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/sales-summaries/foreign-keys")
      ]);
    
      // Jika sukses get data yearly_sales_reports
      if (yearly_sales_reports.status === "success") {
        yield put({
          type: "GET_YEARLY_SALES_REPORTS_SUCCESS",
          yearly_sales_reports: yearly_sales_reports.result,
          filter: filter,
          search: search
        });  
      } else if (yearly_sales_reports.status === "fail") {
        yield put({
          type: "GET_YEARLY_SALES_REPORTS_ERROR",
          error: { message: yearly_sales_reports.message }
        });
      } else {
        yield put({
          type: "GET_YEARLY_SALES_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data yearly_sales_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_YEARLY_SALES_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          customers: foreign_keys.result.customers
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_YEARLY_SALES_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_YEARLY_SALES_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/sales-summaries/foreign-keys")
      ]);
    
      yield put({
        type: "GET_YEARLY_SALES_REPORTS_SUCCESS",
        yearly_sales_reports: []
      });  
  
      // Jika sukses get data yearly_sales_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_YEARLY_SALES_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          customers: foreign_keys.result.customers
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_YEARLY_SALES_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_YEARLY_SALES_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_YEARLY_SALES_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_YEARLY_SALES_REPORTS_REQUEST",
      getYearlySalesReports
    )
  ]);
}
