const initState = {
  company: [],
  accounts: [],
  loading: false,
  loadingSubmit: false,
  success: null,
  error: null,
  errorField: null
};

export default function companyReducer(state = initState, action) {
  switch (action.type) {
    case "GET_COMPANY_REQUEST": {
      return {
        ...state,
        loading: true,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "GET_COMPANY_SUCCESS": {
      return {
        ...state,
        company: action.company,
        accounts: action.accounts,
        loadingSubmit: false,
        loading: false
      };
    }
    case "GET_COMPANY_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        loading:true,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "UPDATE_COMPANY_REQUEST": {
      return {
        ...state,
        loading:true,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_COMPANY_SUCCESS": {
      return {
        ...state,
        loading:false,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_COMPANY_ERROR": {
      return {
        ...state,
        loading:false,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
