import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getSalesReturns({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [salesReturns, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "sales/sales-returns?search=" + search + "&page=" + page),
            call(get, "sales/sales-returns/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "sales/sales-returns?" + filter + "&page=" + page),
            call(get, "sales/sales-returns/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "sales/sales-returns?search=" + search),
          call(get, "sales/sales-returns/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "sales/sales-returns?" + filter),
          call(get, "sales/sales-returns/foreign-keys")
        ]);

    // console.log(salesReturns);

    // Jika sukses get data salesReturns
    if (salesReturns.status === "success") {
      // Jika di API menggunakan pagination
      if (salesReturns.result.current_page && search) {
        yield put({
          type: "GET_SALES_RETURNS_SUCCESS",
          salesReturns: salesReturns.result.data,
          current_page: salesReturns.result.current_page,
          page_size: salesReturns.result.per_page,
          total: salesReturns.result.total,
          search: search
        });
      } else if (salesReturns.result.current_page && filter) {
        yield put({
          type: "GET_SALES_RETURNS_SUCCESS",
          salesReturns: salesReturns.result.data,
          current_page: salesReturns.result.current_page,
          page_size: salesReturns.result.per_page,
          total: salesReturns.result.total,
          filter: filter
        });
      } else if (salesReturns.result.current_page) {
        yield put({
          type: "GET_SALES_RETURNS_SUCCESS",
          salesReturns: salesReturns.result.data,
          current_page: salesReturns.result.current_page,
          page_size: salesReturns.result.per_page,
          total: salesReturns.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_SALES_RETURNS_SUCCESS",
          salesReturns: salesReturns.result,
          page_size: null,
          total: null
        });
      }
    } else if (salesReturns.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_SALES_RETURNS_ERROR",
        error: { message: salesReturns.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_SALES_RETURNS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_SALES_RETURN_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        customers: foreign.result.customers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        cashBanks: foreign.result.cash_banks,
        returnReasons: foreign.result.return_reasons,
        accounts: foreign.result.accounts,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        prevTrans: foreign.result.sales_receipts,
        salesman: foreign.result.salesman
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_SALES_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_SALES_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createSalesReturn() {
  try {
    const [deliveryOrders, foreign] = yield all([
      call(get, "sales/delivery-orders?get_old_trans=1&returned=1&status=2"),
      call(get, "sales/sales-returns/foreign-keys")
    ]);
  
    if (deliveryOrders.status === "success") {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_SUCCESS",
        deliveryOrders: deliveryOrders.result.data,
        current_page: deliveryOrders.result.current_page,
        page_size: deliveryOrders.result.per_page,
        total: deliveryOrders.result.total
      });
    } else if (deliveryOrders.status === "fail") {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_ERROR",
        error: { message: deliveryOrders.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_SALES_RETURN_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        customers: foreign.result.customers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        cashBanks: foreign.result.cash_banks,
        returnReasons: foreign.result.return_reasons,
        accounts: foreign.result.accounts,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        prevTrans: foreign.result.sales_receipts,
        salesman: foreign.result.salesman
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_SALES_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_SALES_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_SALES_RETURN_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getDeliveryOrders({ payload }) {
  const { page, search, filter } = payload;
  // console.log(filter);

  try {
    const deliveryOrders = filter
      ? yield call(
          get,
          "sales/delivery-orders?get_old_trans=1&returned=1&status=2&" +
            filter +
            "&page=" +
            page
        )
      : search
      ? yield call(
          get,
          "sales/delivery-orders?get_old_trans=1&returned=1&status=2&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(
          get,
          "sales/delivery-orders?get_old_trans=1&returned=1&status=2" + "&page=" + page
        );

    if (deliveryOrders.status === "success") {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_SUCCESS",
        deliveryOrders: deliveryOrders.result.data,
        current_page: deliveryOrders.result.current_page,
        page_size: deliveryOrders.result.per_page,
        total: deliveryOrders.result.total
      });
    } else if (deliveryOrders.status === "fail") {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_ERROR",
        error: { message: deliveryOrders.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_DELIVERY_ORDERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getSalesInvoices({ payload }) {
  let { page, search, filter } = payload;
  // console.log(filter);

  if (!filter.includes("payment_status=")) {
    filter += "&payment_status=0,1";
  }

  try {
    const salesInvoices = filter
      ? yield call(
          get,
          "sales/sales-invoices?get_old_trans=1&returned=1&status=2&transaction_type=1&" +
            filter +
            "&page=" +
            page
        )
      : search
      ? yield call(
          get,
          "sales/sales-invoices?get_old_trans=1&returned=1&status=2&payment_status=0,1&transaction_type=1&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(
          get,
          "sales/sales-invoices?get_old_trans=1&returned=1&status=2&payment_status=0,1&transaction_type=1&page=" + page
        );

    if (salesInvoices.status === "success") {
      yield put({
        type: "GET_FILTER_SALES_INVOICES_PT_SUCCESS",
        salesInvoices: salesInvoices.result.data,
        current_page: salesInvoices.result.current_page,
        page_size: salesInvoices.result.per_page,
        total: salesInvoices.result.total
      });
    } else if (salesInvoices.status === "fail") {
      yield put({
        type: "GET_FILTER_SALES_INVOICES_PT_ERROR",
        error: { message: salesInvoices.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_SALES_INVOICES_PT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_SALES_INVOICES_PT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeSalesReturn({ payload }) {
  const { body } = payload;
  
  try {
    const resultStore = yield call(post, "sales/sales-returns", body);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_SALES_RETURN_SUCCESS",
        success: { message: resultStore.message },
        id_sales_return: resultStore.result.id_sales_return
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_SALES_RETURN_ERROR",
        error: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_SALES_RETURN_ERROR",
        error: { message: resultStore.message }
      });
    } else {
      yield put({
        type: "STORE_SALES_RETURN_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_SALES_RETURN_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editSalesReturn({ payload }) {
  // console.log("masuk");
  const { id_sales_return } = payload;
  let deliveryOrders = [];

  try {
    const [salesReturn, foreign] = yield all([
      call(get, "sales/sales-returns/" + id_sales_return),
      call(get, "sales/sales-returns/foreign-keys")
    ]);
  
    // console.log(salesReturn);
  
    // Jika sukses
    if (salesReturn.status === "success") {
      // Get RQ by id_location
      const success = yield all([
        put({
          type: "EDIT_SALES_RETURN_SUCCESS",
          salesReturn: salesReturn.result
        }),
        call(
          get,
          "sales/delivery-orders?get_old_trans=1&id_location=" +
            salesReturn.result.id_location
        )
      ]);
      deliveryOrders = success[1];
    } else if (salesReturn.status === "fail") {
      yield put({
        type: "EDIT_SALES_RETURN_ERROR",
        error: { message: salesReturn.message }
      });
    } else {
      yield put({
        type: "EDIT_SALES_RETURN_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (deliveryOrders.status === "success") {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_SUCCESS",
        deliveryOrders: deliveryOrders.result.data,
        current_page: deliveryOrders.result.current_page,
        page_size: deliveryOrders.result.per_page,
        total: deliveryOrders.result.total
      });
    } else if (deliveryOrders.status === "fail") {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_ERROR",
        error: { message: deliveryOrders.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_DELIVERY_ORDERS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_SALES_RETURN_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        customers: foreign.result.customers,
        currencies: foreign.result.currencies,
        taxes: foreign.result.taxes,
        cashBanks: foreign.result.cash_banks,
        returnReasons: foreign.result.return_reasons,
        accounts: foreign.result.accounts,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        paymentTerms: foreign.result.payment_terms,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        prevTrans: foreign.result.sales_receipts,
        salesman: foreign.result.salesman
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_SALES_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_SALES_RETURN_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_DELIVERY_ORDERS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateSalesReturn({ payload }) {
  const { id_sales_return, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "sales/sales-returns/" + id_sales_return,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        put({
          type: "UPDATE_SALES_RETURN_SUCCESS",
          success: { message: resultUpdate.message },
          id_sales_return: id_sales_return
        }),
        put({
          type: "EDIT_SALES_RETURN_REQUEST",
          payload: { id_sales_return: id_sales_return }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_SALES_RETURN_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_SALES_RETURN_REQUEST",
          payload: {
            id_sales_return: id_sales_return
          }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_SALES_RETURN_ERROR",
        error: { message: resultUpdate.message }
      });
    } else {
      yield put({
        type: "UPDATE_SALES_RETURN_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_SALES_RETURN_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

