export function* confirmSalesReturn({ payload }) {
  const { id_sales_return } = payload;

  try {
    // POST data ke API
    const confirmResult = yield call(
      putApi,
      "sales/sales-returns/process-transaction/" + id_sales_return
    );

    // Jika sukses
    if (confirmResult.status === "success") {
      // Set session message
      yield all([
        put({
          type: "CONFIRM_SALES_RETURN_SUCCESS",
          success: { message: confirmResult.message },
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_SALES_RETURN_REQUEST",
          payload: {
            id_sales_return: id_sales_return
          }
        })
      ]);
    } else if (confirmResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_SALES_RETURN_ERROR",
          error: { message: confirmResult.message }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_SALES_RETURN_REQUEST",
          payload: { id_sales_return: id_sales_return }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_SALES_RETURN_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_SALES_RETURN_REQUEST",
          payload: { id_sales_return: id_sales_return }
        })
      ]);
    }
  } catch (error) {
    yield all([
      put({
        type: "CONFIRM_SALES_RETURN_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      put({
        type: "EDIT_SALES_RETURN_REQUEST",
        payload: { id_sales_return: id_sales_return }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

