export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_SALES_RETURNS_REQUEST", getSalesReturns),
    yield takeEvery("CREATE_SALES_RETURN_REQUEST", createSalesReturn),
    yield takeEvery("GET_FILTER_DELIVERY_ORDERS_ST_REQUEST", getDeliveryOrders),
    yield takeEvery("GET_FILTER_SALES_INVOICES_PT_REQUEST", getSalesInvoices),
    yield takeEvery("STORE_SALES_RETURN_REQUEST", storeSalesReturn),
    yield takeEvery("EDIT_SALES_RETURN_REQUEST", editSalesReturn),
    yield takeEvery("UPDATE_SALES_RETURN_REQUEST", updateSalesReturn),
    yield takeEvery("SHOW_SALES_RETURN_REQUEST", showSalesReturn),
    yield takeEvery("CONFIRM_SALES_RETURN_REQUEST", confirmSalesReturn),
    yield takeEvery("CANCEL_SALES_RETURN_REQUEST", cancelSalesReturn),
    yield takeEvery(
      "UPLOAD_SALES_RETURN_DOCUMENT_REQUEST",
      uploadDeliveryOrderDocument
    ),
    yield takeEvery(
      "DOWNLOAD_SALES_RETURN_DOCUMENT_REQUEST",
      downloadDeliveryOrderDocument
    ),
    yield takeEvery(
      "DELETE_SALES_RETURN_DOCUMENT_REQUEST",
      deleteDeliveryOrderDocument
    )
  ]);
}
