const initState = {
  id_sales_return: null,
  id_location: null,
  salesReturns: [],
  salesReturn: null,
  printData: {},
  items: [],
  salesman: [],
  deliveryOrders: [],
  salesInvoices: [],
  customers: [],
  taxes: [],
  accounts: [],
  cashBanks: [],
  returnReasons: [],
  cashFlowTypes: [],
  currencies: [],
  currencyRates: [],
  paymentCategories: [],
  paymentTerms: [],
  projects: [],
  departments: [],
  // selectedTransaction: null,
  locations: [],
  search: null,
  filter: null,
  loading: false,
  loadingForeign: false,
  loadingSubmit: false,
  loadingDeliveryOrders: false,
  loadingSalesInvoices: false,
  current_page: 1,
  page_size: null,
  total: null,
  current_page_pr: 1,
  page_size_pr: null,
  total_pr: null,
  current_page_si: 1,
  page_size_si: null,
  total_si: null,
  success: null,
  error: null,
  errorField: null
};

export default function salesReturnsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_SALES_RETURNS_REQUEST": {
      return {
        ...state,
        //salesReturns: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_SALES_RETURNS_SUCCESS": {
      return {
        ...state,
        salesReturns: action.salesReturns,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_SALES_RETURNS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_SALES_RETURN_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_SALES_RETURN_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        locations: action.locations,
        customers: action.customers,
        currencies: action.currencies,
        taxes: action.taxes,
        accounts: action.accounts,
        cashBanks: action.cashBanks,
        returnReasons: action.returnReasons,
        cashFlowTypes: action.cashFlowTypes,
        paymentCategories: action.paymentCategories,
        paymentTerms: action.paymentTerms,
        departments: action.departments,
        projects: action.projects,
        salesman: action.salesman
      };
    }
    case "GET_SALES_RETURN_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_SALES_RETURN_REQUEST": {
      return {
        ...state,
        id_sales_return: null,
        id_location: null,
        salesReturn: null,
        loadingTransactions: true,
        loadingDeliveryOrders: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        current_page_pr: 1,
        page_size_pr: null,
        total_pr: null
      };
    }
    case "GET_FILTER_ITEMS_BY_LOCATION_REQUEST": {
      return {
        ...state,
        loadingItems: true,
        search: action.payload.search,
        id_location: action.payload.id_location
      };
    }
    case "GET_FILTER_ITEMS_BY_LOCATION_SUCCESS": {
      return {
        ...state,
        loadingItems: false,
        items: action.items
      };
    }
    case "GET_FILTER_DELIVERY_ORDERS_ST_REQUEST": {
      return {
        ...state,
        loadingDeliveryOrders: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page_pr: action.payload.page
      };
    }
    case "GET_FILTER_DELIVERY_ORDERS_SUCCESS": {
      return {
        ...state,
        loadingDeliveryOrders: false,
        deliveryOrders: action.deliveryOrders,
        current_page_pr: action.current_page,
        page_size_pr: action.page_size,
        total_pr: action.total
      };
    }
    case "GET_FILTER_DELIVERY_ORDERS_ERROR": {
      return {
        ...state,
        loadingDeliveryOrders: false,
        error: action.error.message
      };
    }

    case "GET_FILTER_SALES_INVOICES_PT_REQUEST": {
      return {
        ...state,
        loadingSalesInvoices: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page_si: action.payload.page
      };
    }
    case "GET_FILTER_SALES_INVOICES_PT_SUCCESS": {
      return {
        ...state,
        loadingSalesInvoices: false,
        salesInvoices: action.salesInvoices,
        current_page_si: action.current_page,
        page_size_si: action.page_size,
        total_si: action.total
      };
    }
    case "GET_FILTER_SALES_INVOICES_PT_ERROR": {
      return {
        ...state,
        loadingSalesInvoices: false,
        error: action.error.message
      };
    }

    case "STORE_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_return: action.id_sales_return
      };
    }
    case "STORE_SALES_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loading: true,
        id_sales_return: null,
        id_location: null,
        loadingTransactions: true,
        loadingDeliveryOrders: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null,
        current_page_pr: 1,
        page_size_pr: null,
        total_pr: null
      };
    }
    case "EDIT_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        salesReturn: action.salesReturn
      };
    }
    case "EDIT_SALES_RETURN_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_sales_return: action.id_sales_return
      };
    }
    case "UPDATE_SALES_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loading: false,
        salesReturns: action.salesReturns
      };
    }
    case "CONFIRM_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CONFIRM_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
      };
    }
    case "CONFIRM_SALES_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UNCONFIRM_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "UNCONFIRM_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
      };
    }
    case "UNCONFIRM_SALES_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CLOSE_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CLOSE_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
      };
    }
    case "CLOSE_SALES_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CANCEL_SALES_RETURN_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CANCEL_SALES_RETURN_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
      };
    }
    case "CANCEL_SALES_RETURN_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPLOAD_SALES_RETURN_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_SALES_RETURN_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_SALES_RETURN_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_SALES_RETURN_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_SALES_RETURN_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_SALES_RETURN_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_SALES_RETURN_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "SET_PRINT_DATA_SALES_RETURN": {
      return {
        ...state,
        printData: action.payload.body
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null,
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
