const actions = {
  CHECK_AUTHORIZATION: "CHECK_AUTHORIZATION",
  checkAuthorization: () => ({ type: actions.CHECK_AUTHORIZATION }),
  checkLogin: param => ({
    type: "CHECK_LOGIN_REQUEST",
    payload: { param }
  }),
  login: param => ({
    type: "LOGIN_REQUEST",
    payload: { param }
  }),
  logout: quit => ({
    type: "LOGOUT",
    payload: { quit: quit }
  }),
  clearWarning: () => ({
    type: "CLEAR_WARNING"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  switchLanguage: lang => ({
    type: "SWITCH_LANGUAGE",
    payload: { lang: lang }
  })
};
export default actions;
