import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, postForm } from "../../helpers/helper";
import { bugsnagClient } from "../..";

// Import Excel
export function* importExcel({ payload }) {
  const { key, body } = payload;
  let method = postForm;
  let url = "";
  switch (key) {
    case "location":
      url = "companies/locations/import";
      break;
    case "currency":
      url = "configuration-managements/currencies/import";
      break;
    case "cashFlowType":
      url = "cash-flow-types/import";
      break;
    case "coaAccType":
      url = "accounts/import";
      break;
    case "glAccount":
      url = "gl-configs/import";
      // method = put;
      break;
    case "tax":
      url = "configuration-managements/taxes/import";
      break;
    case "cashBank":
      url = "accounts/cash-banks/import";
      break;
    case "paymentTerm":
      url = "payments/terms/import";
      break;
    case "paymentCategory":
      url = "payments/categories/import";
      break;
    case "supplierType":
      url = "suppliers/types/import";
      break;
    case "supplier":
      url = "suppliers/import";
      break;
    case "customerType":
      url = "customers/types/import";
      break;
    case "customer":
      url = "customers/import";
      break;
    case "jobTitle":
      url = "hrd/job-titles/import";
      break;
    case "employee":
      url = "hrd/employees/import";
      break;
    case "department":
      url = "companies/departments/import";
      break;
    case "project":
      url = "projects/import";
      break;
    case "unit":
      url = "items/units/import";
      break;
    case "itemCategory":
      url = "items/categories/import";
      break;
    case "item":
      url = "items/import";
      break;
    case "itemOB":
      url = "inventories/items/import";
      break;
    case "supplierOB":
      url = "suppliers/opening-balances/import";
      break;
    case "customerOB":
      url = "customers/opening-balances/import";
      break;

    default:
      break;
  }
  try {
    const uploadResult = yield call(method, url, body);

    // jika sukses
    if (uploadResult.status === "success") {
      yield put({
        type: "IMPORT_EXCEL_SUCCESS",
        success: uploadResult
      });
    }
    // jika gagal
    else if (
      uploadResult.errors ||
      uploadResult.status === "fail" ||
      uploadResult.message
    ) {
      yield put({
        type: "IMPORT_EXCEL_ERROR",
        errors: uploadResult.errors
          ? uploadResult.errors
          : uploadResult.result
          ? uploadResult.result
          : null,
        error: uploadResult
      });
    }
    // jika warning
    else if (uploadResult.result && uploadResult.status === "warning") {
      yield put({
        type: "IMPORT_EXCEL_WARNING",
        errors: uploadResult.result,
        warning: uploadResult
      });
    }
  } catch (error) {
    yield put({
      type: "IMPORT_EXCEL_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// List Data
export function* getListData({ payload }) {
  const { key, page, search, filter } = payload;
  let url = "";
  let foreign = "";
  switch (key) {
    case "location":
      url = "companies/locations?";
      break;
    case "currency":
      url = "configuration-managements/currencies?";
      break;
    case "cashFlowType":
      url = "cash-flow-types?";
      break;
    case "coaAccType":
      url = "accounts?children=1&";
      break;
    case "glAccount":
      url = "gl-configs?";
      foreign = "accounts?is_active=child";
      break;
    case "tax":
      url = "configuration-managements/taxes?";
      break;
    case "cashBank":
      url = "accounts/cash-banks?";
      break;
    case "paymentTerm":
      url = "payments/terms?";
      break;
    case "paymentCategory":
      url = "payments/categories?";
      break;
    case "supplierType":
      url = "suppliers/types?";
      break;
    case "supplier":
      url = "suppliers?";
      break;
    case "customerType":
      url = "customers/types?";
      break;
    case "customer":
      url = "customers?";
      break;
    case "jobTitle":
      url = "hrd/job-titles?";
      break;
    case "employee":
      url = "hrd/employees?";
      break;
    case "department":
      url = "companies/departments?is_active=treelist&";
      break;
    case "project":
      url = "projects?";
      break;
    case "unit":
      url = "items/units?";
      break;
    case "itemCategory":
      url = "items/categories?is_active=treelist&";
      break;
    case "item":
      url = "items?pagination=25&";
      break;
    case "itemOB":
      url = "reports/stock-valuations/summary-by-item?";
      break;
    case "supplierOB":
      url = "suppliers?";
      break;
    case "customerOB":
      url = "customers?";
      break;

    default:
      break;
  }

  try {
    const foreignData = foreign ? yield call(get, foreign) : null;
    // Cek apakah ada pagination dan search di request atau tidak
    const [listDatas] = page
      ? search
        ? // Jika search dan pagination
          yield all([call(get, url + "page=" + page + "&search=" + search)])
        : // Jika pagination
          yield all([
            call(get, url + "page=" + page + (filter ? "&" + filter : ""))
          ])
      : search
      ? // Jika search
        yield all([call(get, url + "search=" + search)])
      : // Jika tidak ada pagination
        yield all([call(get, url + filter)]);

    // jika api juga membutuhkan foreign
    if (foreignData) {
      if (foreignData.status === "success") {
        yield put({
          type: "GET_FOREIGN_LIST_DATA_SUCCESS",
          foreignData: foreignData.result
        });
      }
    }

    // Jika sukses get list data
    if (listDatas.status === "success") {
      // Jika di API menggunakan pagination
      if (listDatas.result.current_page && search) {
        yield put({
          type: "GET_LIST_DATA_SUCCESS",
          listData: listDatas.result.data,
          current_page: listDatas.result.current_page,
          page_size: listDatas.result.per_page,
          total: listDatas.result.total,
          search: search
        });
      } else if (listDatas.result.current_page && filter) {
        yield put({
          type: "GET_LIST_DATA_SUCCESS",
          listData: listDatas.result.data,
          current_page: listDatas.result.current_page,
          page_size: listDatas.result.per_page,
          total: listDatas.result.total,
          filter: filter
        });
      } else if (listDatas.result.current_page) {
        yield put({
          type: "GET_LIST_DATA_SUCCESS",
          listData: listDatas.result.data,
          current_page: listDatas.result.current_page,
          page_size: listDatas.result.per_page,
          total: listDatas.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        if (listDatas.result.tree) {
          yield put({
            type: "GET_LIST_DATA_SUCCESS",
            listData: listDatas.result.tree,
            page_size: null,
            total: null
          });
        } else {
          yield put({
            type: "GET_LIST_DATA_SUCCESS",
            listData: listDatas.result,
            page_size: null,
            total: null
          });
        }
      }
    } else if (listDatas.status === "fail") {
      yield put({
        type: "GET_LIST_DATA_ERROR",
        error: listDatas
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_LIST_DATA_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_LIST_DATA_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("IMPORT_EXCEL_REQUEST", importExcel),
    yield takeEvery("GET_LIST_DATA_REQUEST", getListData)
  ]);
}
