import { all, takeEvery, put, call, fork } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getLocation({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [locations, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "companies/locations?page=" + page + "&search=" + search),
            call(get, "companies/locations/foreign-keys")
          ])
        : // Jika pagination
          yield all([
            call(get, "companies/locations?page=" + page + "&" + filter),
            call(get, "companies/locations/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "companies/locations?search=" + search),
          call(get, "companies/locations/foreign-keys")
        ])
      : // Jika tidak ada pagination
        yield all([
          call(get, "companies/locations?" + filter),
          call(get, "companies/locations/foreign-keys")
        ]);

    // Jika sukses get data locations
    if (locations.status === "success") {
      // Jika di API menggunakan pagination
      if (locations.result.current_page && search) {
        yield put({
          type: "GET_LOCATIONS_SUCCESS",
          locations: locations.result.data,
          current_page: locations.result.current_page,
          page_size: locations.result.per_page,
          total: locations.result.total,
          search: search
        });
      } else if (locations.result.current_page && filter) {
        yield put({
          type: "GET_LOCATIONS_SUCCESS",
          locations: locations.result.data,
          current_page: locations.result.current_page,
          page_size: locations.result.per_page,
          total: locations.result.total,
          filter: filter
        });
      } else if (locations.result.current_page) {
        yield put({
          type: "GET_LOCATIONS_SUCCESS",
          locations: locations.result.data,
          current_page: locations.result.current_page,
          page_size: locations.result.per_page,
          total: locations.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_LOCATIONS_SUCCESS",
          locations: locations.result,
          page_size: null,
          total: null
        });
      }
    } else if (locations.status === "fail") {
      yield put({
        type: "GET_LOCATIONS_ERROR",
        error: locations
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_LOCATIONS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // foreign keys
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_LOCATION_FOREIGN_KEYS_SUCCESS",
        location_types: foreign_keys.result.location_types,
        sites: foreign_keys.result.sites,
        accounts: foreign_keys.result.accounts
      });
    } else {
      yield put({
        type: "GET_LOCATION_FOREIGN_KEYS_ERROR"
        //error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_LOCATIONS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Create data
export function* createLocation() {
  try {
    const [foreign_keys] = yield all([
      call(get, "companies/locations/foreign-keys")
    ]);

    // foreign keys
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_LOCATION_FOREIGN_KEYS_SUCCESS",
        location_types: foreign_keys.result.location_types,
        sites: foreign_keys.result.sites,
        accounts: foreign_keys.result.accounts
      });
    } else {
      yield put({
        type: "GET_LOCATION_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Store data
export function* storeLocation({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "companies/locations", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_LOCATION_SUCCESS",
        success: resultStore
      });
    } else if (resultStore.errors || resultStore.status === "fail") {
      yield put({
        type: "STORE_LOCATION_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_LOCATION_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_LOCATION_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Edit data
export function* editLocation({ payload }) {
  const { id_location } = payload;

  try {
    const [location, foreign_keys] = yield all([
      call(get, "companies/locations/" + id_location),
      call(get, "companies/locations/foreign-keys")
    ]);

    // Jika sukses
    if (location.status === "success") {
      yield put({
        type: "EDIT_LOCATION_SUCCESS",
        location: location.result
      });
    } else if (location.errors || location.status === "fail") {
      yield put({
        type: "EDIT_LOCATION_ERROR",
        error: location
      });
    } else {
      yield put({
        type: "EDIT_LOCATION_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // foreign keys
    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_LOCATION_FOREIGN_KEYS_SUCCESS",
        location_types: foreign_keys.result.location_types,
        sites: foreign_keys.result.sites,
        accounts: foreign_keys.result.accounts
      });
    } else {
      yield put({
        type: "GET_LOCATION_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_LOCATION_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Update data
export function* updateLocation({ payload }) {
  const { id_location, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "companies/locations/" + id_location,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: "UPDATE_LOCATION_SUCCESS",
        success: resultUpdate
      });
    } else if (resultUpdate.errors || resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_LOCATION_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_LOCATION_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_LOCATION_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Show data
export function* showLocation({ payload }) {
  const { id_location } = payload;

  try {
    const resultShow = yield call(get, "companies/locations/" + id_location);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: "SHOW_LOCATION_SUCCESS",
        location: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateLocation({ payload }) {
  const { id_location } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "companies/locations/" + id_location,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      // Set session message
      yield put({
        type: "DEACTIVATE_LOCATION_SUCCESS",
        success: deactivateResult
      });
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_LOCATIONS_REQUEST",
        payload: { page: false }
      });
    } else if (deactivateResult.status === "fail" || deactivateResult.errors) {
      // Jika gagal set session message
      yield put({
        type: "DEACTIVATE_LOCATION_ERROR",
        error: deactivateResult
      });
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_LOCATIONS_REQUEST",
        payload: { page: false }
      });
    } else {
      // Jika gagal set session message
      yield put({
        type: "DEACTIVATE_LOCATION_ERROR",
        error: { message: "Check your internet connection" }
      });
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_LOCATIONS_REQUEST",
        payload: { page: false }
      });
    }
  } catch (error) {
    bugsnagClient.notify(error);
    yield put({
      type: "DEACTIVATE_LOCATION_ERROR",
      error: { message: "Check your internet connection" }
    });
  }
}

// Activate data (mengubah is_active => 1)
export function* activateLocation({ payload }) {
  const { id_location } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "companies/locations/" + id_location,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield put({
        type: "ACTIVATE_LOCATION_SUCCESS",
        success: activateResult
      });
      yield put({
        type: "GET_LOCATIONS_REQUEST",
        payload: { page: false }
      });
    } else if (activateResult.status === "fail" || activateResult.errors) {
      yield put({
        type: "ACTIVATE_LOCATION_ERROR",
        error: activateResult
      });
      yield put({
        type: "GET_LOCATIONS_REQUEST",
        payload: { page: false }
      });
    } else {
      yield put({
        type: "ACTIVATE_LOCATION_ERROR",
        error: { message: "Check your internet connection" }
      });
      yield put({
        type: "GET_LOCATIONS_REQUEST",
        payload: { page: false }
      });
    }
  } catch (error) {
    bugsnagClient.notify(error);
    yield put({
      type: "ACTIVATE_LOCATION_ERROR",
      error: { message: "Check your internet connection" }
    });
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_LOCATIONS_REQUEST", getLocation),
    yield takeEvery("CREATE_LOCATION_REQUEST", createLocation),
    yield takeEvery("STORE_LOCATION_REQUEST", storeLocation),
    yield takeEvery("EDIT_LOCATION_REQUEST", editLocation),
    yield takeEvery("UPDATE_LOCATION_REQUEST", updateLocation),
    yield takeEvery("SHOW_LOCATION_REQUEST", showLocation),
    yield takeEvery("DEACTIVATE_LOCATION_REQUEST", deactivateLocation),
    yield takeEvery("ACTIVATE_LOCATION_REQUEST", activateLocation)
  ]);
}
