import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCompany() {
  try {
    const [company, foreign_keys] = 
      yield all([
        call(get, "companies"),
        call(get, "companies/foreign-keys"),
      ])
        
    // console.log(company);

    // Jika sukses get data company
    if (company.status === "success" && foreign_keys.status === "success") {
      yield put({
        type: "GET_COMPANY_SUCCESS",
        company: company.result,
        accounts: foreign_keys.result.accounts
      });
    } 
    else if (company.status === "fail") {
      yield put({
        type: "GET_COMPANY_ERROR",
        error: company
      });
    }
    else {
      yield put({
        type: "GET_COMPANY_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_COMPANY_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateCompany({payload}) {
  const { body } = payload;

  try {
    const resultUpdate = yield call(
      body.logo_small_url ? postForm : post,
      "companies",
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_COMPANY_SUCCESS",
          success: resultUpdate
        }),
        yield put({
          type : "GET_COMPANY_REQUEST"
        })
      ]);
    } else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_COMPANY_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_COMPANY_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_COMPANY_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}




export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_COMPANY_REQUEST", getCompany),
    yield takeEvery("UPDATE_COMPANY_REQUEST", updateCompany),
  ]);
}
