
const initState = {
  interbank_transfers: [],
  interbank_transfer: null,
  withdraw_locations: [],
  deposit_locations: [],
  cash_banks: [],
  accounts: [],
  cash_flow_types: [],  
  currencies: [],
  id_base_currency: null,
  search: null,
  filter: null,
  loading: false,
  loadingImport: false,
  loadingEdit:false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_interbank_transfer: null,
  import_interbank_transfers: [],
  import_current_page: 1,
  import_page_size: null,
  import_total: null,
  import_search: null,
  import_filter: null,
};

export default function interbankTransfersReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_INTERBANK_TRANSFERS_REQUEST': {
      return {
        ...state,
        //interbank_transfers: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_INTERBANK_TRANSFERS_SUCCESS': {
      return {
        ...state,
        interbank_transfers: action.interbank_transfers,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_INTERBANK_TRANSFERS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      }
    }
    case 'GET_IMPORT_INTERBANK_TRANSFERS_REQUEST': {
      return {
        ...state,
        //interbank_transfers: [],
        loadingImport: true,
        import_search: action.payload.search,
        import_filter: action.payload.filter,
        import_current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_IMPORT_INTERBANK_TRANSFERS_SUCCESS': {
      return {
        ...state,
        import_interbank_transfers: action.interbank_transfers,
        import_current_page: action.current_page,
        import_page_size: action.page_size,
        import_total: action.total,
        import_search: action.search,
        import_filter: action.filter,
        loadingImport: false,
      };
    }
    case 'GET_IMPORT_INTERBANK_TRANSFERS_ERROR': {
      return {
        ...state,
        loadingImport: false,
        error: action.error.message
      }
    }
    case 'CREATE_INTERBANK_TRANSFER_REQUEST': {
      return {
        ...state,
        interbank_transfer: null,
        errorField: null,
      };
    }

    case 'GET_INTERBANK_TRANSFER_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        withdraw_locations: action.withdraw_locations,
        deposit_locations: action.deposit_locations,
        cash_banks: action.cash_banks,
        accounts: action.accounts,
        cash_flow_types: action.cash_flow_types,  
        currencies: action.currencies,
        id_base_currency: action.id_base_currency,
      };
    }
    case 'GET_INTERBANK_TRANSFER_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message
      };
    }
    
    case 'STORE_INTERBANK_TRANSFER_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_INTERBANK_TRANSFER_SUCCESS': {
      return {
        ...state,
        loading: false,
        id_interbank_transfer: action.id_interbank_transfer,
        success: action.success.message
      };
    }
    case 'STORE_INTERBANK_TRANSFER_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_INTERBANK_TRANSFER_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit:true,
        errorField: null,
      };
    }
    case 'EDIT_INTERBANK_TRANSFER_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit:false,
        interbank_transfer: action.interbank_transfer
      };
    }
    case 'EDIT_INTERBANK_TRANSFER_ERROR': {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case 'UPDATE_INTERBANK_TRANSFER_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_INTERBANK_TRANSFER_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_INTERBANK_TRANSFER_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_INTERBANK_TRANSFER_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_INTERBANK_TRANSFER_SUCCESS': {
      return {
        ...state,
        loading: false,
        interbank_transfers: action.interbank_transfers
      };
    }
    case "PROCESS_INTERBANK_TRANSFER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "PROCESS_INTERBANK_TRANSFER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "PROCESS_INTERBANK_TRANSFER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CANCEL_INTERBANK_TRANSFER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CANCEL_INTERBANK_TRANSFER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "CANCEL_INTERBANK_TRANSFER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "UPLOAD_INTERBANK_TRANSFER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_INTERBANK_TRANSFER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_INTERBANK_TRANSFER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_INTERBANK_TRANSFER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_INTERBANK_TRANSFER_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_INTERBANK_TRANSFER_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_INTERBANK_TRANSFER_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
