import { all, takeEvery, put, call, fork } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCashFlowTypes({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [cash_flow_types, cash_flow_groups] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "cash-flow-types?search=" + search + "&page=" + page),
            call(get, "cash-flow-types/groups"),
          ])
        : // Jika filter
          yield all([
            call(get, "cash-flow-types?" + filter + "&page=" + page),
            call(get, "cash-flow-types/groups"),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "cash-flow-types?search=" + search),
          call(get, "cash-flow-types/groups"),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "cash-flow-types?" + filter),
          call(get, "cash-flow-types/groups"),
        ]);

    // console.log(cash_flow_types);
    // console.log(cash_flow_groups);

    // Jika sukses get data cash_flow_types
    if (cash_flow_types.status === "success") {
      // Jika di API menggunakan pagination
      if (cash_flow_types.result.current_page && search) {
        yield put({
          type: 'GET_CASH_FLOW_TYPES_SUCCESS',
          cash_flow_types: cash_flow_types.result.data,
          current_page: cash_flow_types.result.current_page,
          page_size: cash_flow_types.result.per_page,
          total: cash_flow_types.result.total,
          search: search
        });
      }
      else if(cash_flow_types.result.current_page && filter)
      {
        yield put({
          type: 'GET_CASH_FLOW_TYPES_SUCCESS',
          cash_flow_types: cash_flow_types.result.data,
          current_page: cash_flow_types.result.current_page,
          page_size: cash_flow_types.result.per_page,
          total: cash_flow_types.result.total,
          filter: filter
        });
      }
      else if (cash_flow_types.result.current_page) {
        yield put({
          type: 'GET_CASH_FLOW_TYPES_SUCCESS',
          cash_flow_types: cash_flow_types.result.data,
          current_page: cash_flow_types.result.current_page,
          page_size: cash_flow_types.result.per_page,
          total: cash_flow_types.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_CASH_FLOW_TYPES_SUCCESS',
          cash_flow_types: cash_flow_types.result,
          page_size: null,
          total: null
        });
      }
    } else if (cash_flow_types.status === "fail") {
      yield put({
        type: 'GET_CASH_FLOW_TYPES_ERROR',
        error: cash_flow_types
      })
    } else {
      yield put({
        type: 'GET_CASH_FLOW_TYPES_ERROR',
        error: { message : "Check your internet connection" }
      })
    }

    // Jika sukses get data cash_flow_type types
    if (cash_flow_groups.status === "success") {
      yield put({
        type: 'GET_CASH_FLOW_GROUPS_SUCCESS',
        cash_flow_groups: cash_flow_groups.result
      });
    }
    else{
      yield put({
        type: 'GET_CASH_FLOW_GROUPS_ERROR',
      });
    } 
  } catch (error) {
    yield put({
      type: 'GET_CASH_FLOW_TYPES_ERROR',
      error: { message : "Check your internet connection" }
    })
    bugsnagClient.notify(error);
  }
}

// Create data
export function* createCashFlowType() {
  try {
    const [cash_flow_groups] = yield all([
      call(get, "cash-flow-types/groups"),
    ]);
  
    // Jika sukses get data cash_flow_group
    if (cash_flow_groups.status === "success") {
      yield put({
        type: 'GET_CASH_FLOW_GROUPS_SUCCESS',
        cash_flow_groups: cash_flow_groups.result
      });
    }
    else{
      yield put({
        type: 'GET_CASH_FLOW_GROUPS_ERROR',
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_CASH_FLOW_GROUPS_ERROR',
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeCashFlowType({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "cash-flow-types", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_CASH_FLOW_TYPE_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_CASH_FLOW_TYPE_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_CASH_FLOW_TYPE_ERROR",
        error: { message : "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_CASH_FLOW_TYPE_ERROR",
      error: { message : "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editCashFlowType({ payload }) {
  const { id_cash_flow_type } = payload;

  try {
    const [cash_flow_type, cash_flow_groups] = yield all([
      call(get, "cash-flow-types/" + id_cash_flow_type),
      call(get, "cash-flow-types/groups"),
    ]);
  
    // Jika sukses
    if (cash_flow_type.status === "success") {
      yield put({
        type: 'EDIT_CASH_FLOW_TYPE_SUCCESS',
        cash_flow_type: cash_flow_type.result
      });
    }
    else if (cash_flow_type.status === "fail") {
      yield put({
        type: 'EDIT_CASH_FLOW_TYPE_ERROR',
        error: cash_flow_type
      });
    }
    else{
      yield put({
        type: 'EDIT_CASH_FLOW_TYPE_ERROR',
        error: { message : "Check your internet connection" }
      });
    }
  
    // Jika sukses get data cash_flow_type types
    if (cash_flow_groups.status === "success") {
      yield put({
        type: 'GET_CASH_FLOW_GROUPS_SUCCESS',
        cash_flow_groups: cash_flow_groups.result
      });
    }
    else{
      yield put({
        type: 'GET_CASH_FLOW_GROUPS_ERROR',
      });
    }
  } catch (error) {
    yield put({
      type: 'EDIT_CASH_FLOW_TYPE_ERROR',
      error: { message : "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateCashFlowType({ payload }) {
  const { id_cash_flow_type, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "cash-flow-types/" + id_cash_flow_type,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_CASH_FLOW_TYPE_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_CASH_FLOW_TYPE_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: 'UPDATE_CASH_FLOW_TYPE_ERROR',
        error: { message : "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: 'UPDATE_CASH_FLOW_TYPE_ERROR',
      error: { message : "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showCashFlowType({ payload }) {
  const { id_cash_flow_type } = payload;

  try {
    const resultShow = yield call(get, "cash-flow-types/" + id_cash_flow_type);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_CASH_FLOW_TYPE_SUCCESS',
        cash_flow_type: resultShow.result
      });
    } 
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateCashFlowType({ payload }) {
  const { id_cash_flow_type } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "cash-flow-types/" + id_cash_flow_type,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_CASH_FLOW_TYPE_SUCCESS',
          success: deactivateResult
        }),
        yield put({
          type: 'GET_CASH_FLOW_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    } else if (deactivateResult.status === "fail" || deactivateResult.errors) {
      yield all([
        yield put({
          type: 'DEACTIVATE_CASH_FLOW_TYPE_ERROR',
          error: deactivateResult
        }),
        yield put({
          type: 'GET_CASH_FLOW_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    } else {
      yield all([
        yield put({
          type: 'DEACTIVATE_CASH_FLOW_TYPE_ERROR',
          error: { message : "Check your internet connection" }
        }),
        yield put({
          type: 'GET_CASH_FLOW_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_CASH_FLOW_TYPE_ERROR',
        error: { message : "Check your internet connection" }
      }),
      yield put({
        type: 'GET_CASH_FLOW_TYPES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error)
  }
}

// Activate data (mengubah is_active => 1)
export function* activateCashFlowType({ payload }) {
  const { id_cash_flow_type } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "cash-flow-types/" + id_cash_flow_type,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_CASH_FLOW_TYPE_SUCCESS',
          success: activateResult
        }),
        yield put({
          type: 'GET_CASH_FLOW_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    } else if (activateResult.status === "fail" || activateResult.errors) {
      yield all([
        yield put({
          type: 'ACTIVATE_CASH_FLOW_TYPE_ERROR',
          error: activateResult
        }),
        yield put({
          type: 'GET_CASH_FLOW_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    } else {
      yield all([
        yield put({
          type: 'ACTIVATE_CASH_FLOW_TYPE_ERROR',
          error: { message : "Check your internet connection" }
        }),
        yield put({
          type: 'GET_CASH_FLOW_TYPES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_CASH_FLOW_TYPE_ERROR',
        error: { message : "Check your internet connection" }
      }),
      yield put({
        type: 'GET_CASH_FLOW_TYPES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_CASH_FLOW_TYPES_REQUEST', getCashFlowTypes),
    yield takeEvery('CREATE_CASH_FLOW_TYPE_REQUEST', createCashFlowType),
    yield takeEvery('STORE_CASH_FLOW_TYPE_REQUEST', storeCashFlowType),
    yield takeEvery('EDIT_CASH_FLOW_TYPE_REQUEST', editCashFlowType),
    yield takeEvery('UPDATE_CASH_FLOW_TYPE_REQUEST', updateCashFlowType),
    yield takeEvery('SHOW_CASH_FLOW_TYPE_REQUEST', showCashFlowType),
    yield takeEvery('DEACTIVATE_CASH_FLOW_TYPE_REQUEST', deactivateCashFlowType),
    yield takeEvery('ACTIVATE_CASH_FLOW_TYPE_REQUEST', activateCashFlowType)
  ]);
}
