import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCashFlowReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/cash-flows?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [cash_flow_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/cash-flows/foreign-keys")
      ]);
    
      // console.log(cash_flow_reports);
      // console.log(foreign_keys);
    
      // Jika sukses get data cash_flow_reports
      if (cash_flow_reports.status === "success") {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_CASH_FLOW_REPORTS_SUCCESS",
          cash_flow_reports: cash_flow_reports.result.cash_flows,
          opening_balance: cash_flow_reports.result.opening_balance,
          filter
        });
      } else if (cash_flow_reports.status === "fail") {
        yield put({
          type: "GET_CASH_FLOW_REPORTS_ERROR",
          error: { message: cash_flow_reports.message }
        });
      } else {
        yield put({
          type: "GET_CASH_FLOW_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data cash_flow_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_CASH_FLOW_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          cash_banks: foreign_keys.result.cash_banks,
          cash_flow_groups: foreign_keys.result.cash_flow_groups
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_CASH_FLOW_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_CASH_FLOW_REPORT_FOREIGN_KEYS_ERROR",
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/cash-flows/foreign-keys")
      ]);
    
      yield put({
        type: "GET_CASH_FLOW_REPORTS_SUCCESS",
        cash_flow_reports: [],
        opening_balance: null
      });
  
      // Jika sukses get data cash_flow_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_CASH_FLOW_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          cash_banks: foreign_keys.result.cash_banks,
          cash_flow_groups: foreign_keys.result.cash_flow_groups
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_CASH_FLOW_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_CASH_FLOW_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_CASH_FLOW_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_CASH_FLOW_REPORTS_REQUEST",
      getCashFlowReports
    )
  ]);
}
