
const initState = {
  asset_fiscals: [],
  asset_fiscal: null,
  search: null,
  //filter: null,
  loading: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function assetFiscalsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_ASSET_FISCALS_REQUEST': {
      return {
        ...state,
        //asset_fiscals: [],
        loading: true,
        search: action.payload.search,
        //filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_ASSET_FISCALS_SUCCESS': {
      return {
        ...state,
        asset_fiscals: action.asset_fiscals,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        //filter: action.filter,
        loading: false,
      };
    }
    case 'GET_ASSET_FISCALS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
      };
    }
    case 'CREATE_ASSET_FISCAL_REQUEST': {
      return {
        ...state,
        //asset_fiscal: null,
        errorField: null,
      };
    }
    
    case 'STORE_ASSET_FISCAL_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_ASSET_FISCAL_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_ASSET_FISCAL_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_ASSET_FISCAL_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null,
      };
    }
    case 'EDIT_ASSET_FISCAL_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        asset_fiscal: action.asset_fiscal,
      };
    }
    case 'EDIT_ASSET_FISCAL_ERROR': {
      return {
        ...state,
        // loading: false,
        // loadingEdit: false,
        error: action.error.message,
      };
    }
    case 'UPDATE_ASSET_FISCAL_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_ASSET_FISCAL_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_ASSET_FISCAL_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_ASSET_FISCAL_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_ASSET_FISCAL_SUCCESS': {
      return {
        ...state,
        loading: false,
        asset_fiscals: action.asset_fiscals
      };
    }
    case "DEACTIVATE_ASSET_FISCAL_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_ASSET_FISCAL_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_ASSET_FISCAL_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_ASSET_FISCAL_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_ASSET_FISCAL_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_ASSET_FISCAL_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
