import { all, takeEvery, put, call, race } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCashBanks({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [cash_banks, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "accounts/cash-banks?search=" + search + "&page=" + page),
            call(get, "accounts/cash-banks/foreign-keys"),
          ])
        : // Jika filter
          yield all([
            call(get, "accounts/cash-banks?" + filter + "&page=" + page),
            call(get, "accounts/cash-banks/foreign-keys"),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "accounts/cash-banks?search=" + search),
          call(get, "accounts/cash-banks/foreign-keys"),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "accounts/cash-banks?" + filter),
          call(get, "accounts/cash-banks/foreign-keys"),
        ]);

    // console.log(cash_banks);
    // console.log(accounts);

    // Jika sukses get data cash_banks
    if (cash_banks.status === "success") {
      // Jika di API menggunakan pagination
      if (cash_banks.result.current_page && search) {
        yield put({
          type: 'GET_CASH_BANKS_SUCCESS',
          cash_banks: cash_banks.result.data,
          current_page: cash_banks.result.current_page,
          page_size: cash_banks.result.per_page,
          total: cash_banks.result.total,
          search: search
        });
      }
      else if(cash_banks.result.current_page && filter)
      {
        yield put({
          type: 'GET_CASH_BANKS_SUCCESS',
          cash_banks: cash_banks.result.data,
          current_page: cash_banks.result.current_page,
          page_size: cash_banks.result.per_page,
          total: cash_banks.result.total,
          filter: filter
        });
      }
      else if (cash_banks.result.current_page) {
        yield put({
          type: 'GET_CASH_BANKS_SUCCESS',
          cash_banks: cash_banks.result.data,
          current_page: cash_banks.result.current_page,
          page_size: cash_banks.result.per_page,
          total: cash_banks.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_CASH_BANKS_SUCCESS',
          cash_banks: cash_banks.result,
          page_size: null,
          total: null
        });
      }
    } else if (cash_banks.status === "fail") {
      yield put({
        type: 'GET_CASH_BANKS_ERROR',
        error: cash_banks
      })
    } else {
      yield put({
        type: 'GET_CASH_BANKS_ERROR',
        error: {message: 'Check your internet connection'}
      })
    }

    // Jika sukses get data foreign key
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_CASH_BANK_FOREIGN_KEYS_SUCCESS',
        accounts: foreign_keys.result.accounts,
        locations: foreign_keys.result.locations,
        currencies: foreign_keys.result.currencies
      });
    }
    else{
      yield put({
        type: 'GET_CASH_BANK_FOREIGN_KEYS_ERROR',
        error: {message: 'Check your internet connection'}
      })
    } 
  } catch (error) {
    yield put({
      type: 'GET_CASH_BANKS_ERROR',
      error: {message: 'Check your internet connection'}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createCashBank() {
  try {
    const [foreign_keys] = yield all([
      call(get, "accounts/cash-banks/foreign-keys")
    ]);
  
    // Jika sukses get data foreign key
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_CASH_BANK_FOREIGN_KEYS_SUCCESS',
        accounts: foreign_keys.result.accounts,
        locations: foreign_keys.result.locations,
        currencies: foreign_keys.result.currencies
      });
    }
    else{
      yield put({
        type: 'GET_CASH_BANK_FOREIGN_KEYS_ERROR',
        error: {message: 'Check your internet connection'}
      })
    }
  
    yield put({
      type: 'CREATE_CASH_BANK_SUCCESS'
    })
  } catch (error) {
    yield put({
      type: 'GET_CASH_BANK_FOREIGN_KEYS_ERROR',
      error: {message: 'Check your internet connection'}
    })
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeCashBank({ payload }) {
  const { body } = payload;
  // console.log(body);
  try {
    const resultStore = yield call(post, "accounts/cash-banks", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_CASH_BANK_SUCCESS',
        success: resultStore
      });
    }
    // else if(resultStore.f)
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_CASH_BANK_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_CASH_BANK_ERROR",
        error: {message: 'Check your internet connection'}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_CASH_BANK_ERROR",
      error: {message: 'Check your internet connection'}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editCashBank({ payload }) {
  const { id_cash_bank } = payload;

  try {
    const [cash_bank, foreign_keys] = yield all([
      call(get, "accounts/cash-banks/" + id_cash_bank),
      call(get, "accounts/cash-banks/foreign-keys")
  
    ]);
  
    // Jika sukses get data foreign key
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_CASH_BANK_FOREIGN_KEYS_SUCCESS',
        accounts: foreign_keys.result.accounts,
        locations: foreign_keys.result.locations,
        currencies: foreign_keys.result.currencies
      });
    }
    else{
      yield put({
        type: 'GET_CASH_BANK_FOREIGN_KEYS_ERROR',
        error: {message: 'Check your internet connection'}
      })
    }
  
    // Jika sukses
    if (cash_bank.status === "success") {
      yield put({
        type: 'EDIT_CASH_BANK_SUCCESS',
        cash_bank: cash_bank.result
      });
    }
    else{
      yield put({
        type: 'EDIT_CASH_BANK_ERROR',
        error: {message: 'Check your internet connection'}
      });
    }
  } catch (error) {
    yield put({
        type: 'EDIT_CASH_BANK_ERROR',
        error: {message: 'Check your internet connection'}
      });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateCashBank({ payload }) {
  const { id_cash_bank, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "accounts/cash-banks/" + id_cash_bank,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_CASH_BANK_SUCCESS',
        success: resultUpdate
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_CASH_BANK_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_CASH_BANK_ERROR",
        error: {message: 'Check your internet connection'}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_CASH_BANK_ERROR",
      error: {message: 'Check your internet connection'}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showCashBank({ payload }) {
  const { id_cash_bank } = payload;
  try {
    const resultShow = yield call(get, "accounts/cash-banks/" + id_cash_bank);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_CASH_BANK_SUCCESS',
        cash_bank: resultShow.result
      });
    } 
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateCashBank({ payload }) {
  const { id_cash_bank } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "accounts/cash-banks/" + id_cash_bank,
      body
    );
    //console.log(deactivateResult);
    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_CASH_BANK_SUCCESS',
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CASH_BANKS_REQUEST',
          payload: { page: false }
        })
      ])
      
    }
    else if (deactivateResult.status === "fail" || deactivateResult.errors) {
      yield all([
        yield put({
          type: 'DEACTIVATE_CASH_BANK_ERROR',
          error: deactivateResult
        }),
        yield put({
          type: 'GET_CASH_BANKS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      // console.log("masuk")
      yield all([
        yield put({
          type: 'DEACTIVATE_CASH_BANK_ERROR',
          error: {message: 'Check your internet connection'}
        }),
        yield put({
          type: 'GET_CASH_BANKS_REQUEST',
          payload: { page: false }
        })
      ]) 
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_CASH_BANK_ERROR',
        error: {message: 'Check your internet connection'}
      }),
      yield put({
        type: 'GET_CASH_BANKS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error)
  }
}

// Activate data (mengubah is_active => 1)
export function* activateCashBank({ payload }) {
  const { id_cash_bank } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "accounts/cash-banks/" + id_cash_bank,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_CASH_BANK_SUCCESS',
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CASH_BANKS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail" || activateResult.errors) {
      yield all([
        yield put({
          type: 'ACTIVATE_CASH_BANK_ERROR',
          error: activateResult
        }),
        yield put({
          type: 'GET_CASH_BANKS_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_CASH_BANK_ERROR',
          error: {message: 'Check your internet connection'}
        }),
        yield put({
          type: 'GET_CASH_BANKS_REQUEST',
          payload: { page: false }
        })
      ]) 
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_CASH_BANK_ERROR',
        error: {message: 'Check your internet connection'}
      }),
      yield put({
        type: 'GET_CASH_BANKS_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_CASH_BANKS_REQUEST', getCashBanks),
    yield takeEvery('CREATE_CASH_BANK_REQUEST', createCashBank),
    yield takeEvery('STORE_CASH_BANK_REQUEST', storeCashBank),
    yield takeEvery('EDIT_CASH_BANK_REQUEST', editCashBank),
    yield takeEvery('UPDATE_CASH_BANK_REQUEST', updateCashBank),
    yield takeEvery('SHOW_CASH_BANK_REQUEST', showCashBank),
    yield takeEvery('DEACTIVATE_CASH_BANK_REQUEST', deactivateCashBank),
    yield takeEvery('ACTIVATE_CASH_BANK_REQUEST', activateCashBank)
  ]);
}
