import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper"
import { bugsnagClient } from "../../..";

// Get all data
export function* getFixedAssetDetailReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/fixed-asset-details?";
  if (search) {
    url += search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [fixed_asset_detail_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/fixed-asset-details/foreign-keys")
      ]);
  
      // Jika sukses get data fixed_asset_detail_reports
      if (fixed_asset_detail_reports.status === "success") {
        yield put({
          type: "GET_FIXED_ASSET_DETAIL_REPORTS_SUCCESS",
          fixed_asset_detail_reports: fixed_asset_detail_reports.result,
          filter: filter,
          search: search
        });
      }
      else if(fixed_asset_detail_reports.status === "fail"){
        yield put({
          type: "GET_FIXED_ASSET_DETAIL_REPORTS_ERROR",
          error: fixed_asset_detail_reports
        });
      }
      else {
        yield put({
          type: "GET_FIXED_ASSET_DETAIL_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data item_grouping_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_FIXED_ASSET_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          fixed_assets: foreign_keys.result.fixed_assets,
          asset_types: foreign_keys.result.asset_types,
          asset_categories: foreign_keys.result.asset_categories,
          departments: foreign_keys.result.departments,
        });
      } else {
        yield put({
          type: "GET_FIXED_ASSET_DETAIL_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/fixed-asset-details/foreign-keys")
      ]);
  
      yield put({
        type: "GET_FIXED_ASSET_DETAIL_REPORTS_SUCCESS",
        fixed_asset_detail_reports: null
      });
  
      // Jika sukses get data item_grouping_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_FIXED_ASSET_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          fixed_assets: foreign_keys.result.fixed_assets,
          asset_types: foreign_keys.result.asset_types,
          asset_categories: foreign_keys.result.asset_categories,
          departments: foreign_keys.result.departments,
        });
      } else {
        yield put({
          type: "GET_FIXED_ASSET_DETAIL_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_FIXED_ASSET_DETAIL_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}


export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_FIXED_ASSET_DETAIL_REPORTS_REQUEST",
      getFixedAssetDetailReports
    ),
  ]);
}
