const initState = {
  suppliers: [],
  supplier: null,
  supplier_contact: null,
  supplier_address: null,
  supplier_types: [],
  locations: [],
  currencies: [],
  accounts: [],
  taxes: [],
  cash_banks: [],
  payment_categories: [],
  payment_terms: [],
  employees: [],
  countries: [],
  provinces: [],
  cities: [],
  districts: [],
  default_id_country: null,
  default_id_payable_account: null,
  search: null,
  filter: null,
  loading: false,
  loadingEdit: false,
  loadingSubmit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_supplier: null
};

export default function suppliersReducer(state = initState, action) {
  switch (action.type) {
    case "GET_SUPPLIERS_REQUEST": {
      return {
        ...state,
        //suppliers: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_SUPPLIERS_SUCCESS": {
      return {
        ...state,
        suppliers: action.suppliers,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_SUPPLIERS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_SUPPLIER_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        supplier_types: action.supplier_types,
        locations: action.locations,
        currencies: action.currencies,
        accounts: action.accounts,
        taxes: action.taxes,
        cash_banks: action.cash_banks,
        payment_categories: action.payment_categories,
        payment_terms: action.payment_terms,
        employees: action.employees,
        countries: action.countries,
        provinces: action.provinces,
        cities: action.cities,
        districts: action.districts,
        default_id_country: action.default_id_country,
        default_id_payable_account: action.default_id_payable_account
      };
    }
    case "GET_SUPPLIER_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        //loading: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "CREATE_SUPPLIER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CREATE_SUPPLIER_SUCCESS": {
      return {
        ...state,
        loading: false
      };
    }
    case "STORE_SUPPLIER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null,
        id_supplier: null
      };
    }
    case "STORE_SUPPLIER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_supplier: action.id_supplier
      };
    }
    case "STORE_SUPPLIER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "STORE_SUPPLIER_CONTACT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null,
        id_supplier: null
      };
    }
    case "STORE_SUPPLIER_ADDRESS_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null,
        id_supplier: null
      };
    }
    case "STORE_SUPPLIER_OPENING_BALANCE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null,
        id_supplier: null
      };
    }
    case "EDIT_SUPPLIER_REQUEST": {
      return {
        ...state,
        supplier: null,
        loading: true,
        loadingEdit: true,
        errorField: null
      };
    }
    case "EDIT_SUPPLIER_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        loadingEdit: false,
        supplier: action.supplier
      };
    }
    case "EDIT_SUPPLIER_ERROR": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_SUPPLIER_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_SUPPLIER_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_SUPPLIER_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "UPDATE_SUPPLIER_CONTACT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_SUPPLIER_CONTACT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_SUPPLIER_CONTACT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "UPDATE_SUPPLIER_ADDRESS_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_SUPPLIER_ADDRESS_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_SUPPLIER_ADDRESS_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "UPDATE_SUPPLIER_OPENING_BALANCE_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_SUPPLIER_OPENING_BALANCE_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "UPDATE_SUPPLIER_OPENING_BALANCE_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_SUPPLIER_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_SUPPLIER_SUCCESS": {
      return {
        ...state,
        loading: false,
        suppliers: action.suppliers
      };
    }
    case "DEACTIVATE_SUPPLIER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_SUPPLIER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_SUPPLIER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DEACTIVATE_SUPPLIER_CONTACT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_SUPPLIER_CONTACT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_SUPPLIER_CONTACT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DEACTIVATE_SUPPLIER_ADDRESS_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_SUPPLIER_ADDRESS_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_SUPPLIER_ADDRESS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_SUPPLIER_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_SUPPLIER_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_SUPPLIER_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
