import { all, takeEvery, put, call, actionChannel } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getAccPayTransHistoryReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/account-payables/histories?";
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if (filter) {
      const [acc_pay_trans_history_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/account-payables/histories/foreign-keys")
      ]);
  
      // Jika sukses get data acc_pay_trans_history_reports
      if (acc_pay_trans_history_reports.status === "success") {
        
        yield put({
          type: "GET_ACC_PAY_TRANS_HISTORY_REPORTS_SUCCESS",
          acc_pay_trans_history_reports:acc_pay_trans_history_reports.result,
          filter
        });
        
      } else if (acc_pay_trans_history_reports.status === "fail") {
        yield put({
          type: "GET_ACC_PAY_TRANS_HISTORY_REPORTS_ERROR",
          error: { message: acc_pay_trans_history_reports.message }
        });
      } else {
        yield put({
          type: "GET_ACC_PAY_TRANS_HISTORY_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data acc_pay_trans_history_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ACC_PAY_TRANS_HISTORY_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          supplier_types: foreign_keys.result.supplier_types,
          currencies: foreign_keys.result.currencies,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ACC_PAY_TRANS_HISTORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ACC_PAY_TRANS_HISTORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/account-payables/histories/foreign-keys")
      ]);
  
      yield put({
        type: "GET_ACC_PAY_TRANS_HISTORY_REPORTS_SUCCESS",
        acc_pay_trans_history_reports: [],
        current_page: 1,
        page_size: null,
        total: null
      });
  
      // Jika sukses get data acc_pay_trans_history_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_ACC_PAY_TRANS_HISTORY_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          supplier_types: foreign_keys.result.supplier_types,
          currencies: foreign_keys.result.currencies,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_ACC_PAY_TRANS_HISTORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_ACC_PAY_TRANS_HISTORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_ACC_PAY_TRANS_HISTORY_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_ACC_PAY_TRANS_HISTORY_REPORTS_REQUEST",
      getAccPayTransHistoryReports
    )
  ]);
}
