const initState = {
  purchase_order_detail_reports: [],
  total_amount: null,
  total_discount: null,
  total_qty: null,
  total_tax: null,
  total_item: null,
  locations: [],
  suppliers: [],
  currencies: [],
  taxes: [],
  cash_banks: [],
  accounts: [],
  cash_flow_types: [],
  payment_categories: [],
  payment_terms: [],
  departments: [],
  projects: [],
  base_currency: null,
  messages: [],
  search: null,
  filter: null,
  filter_select: [],
  loading: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function purchaseOrderDetailReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_PURCHASE_ORDER_DETAIL_REPORTS_REQUEST": {
      return {
        ...state,
        //purchase_order_detail_reports: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        //current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_PURCHASE_ORDER_DETAIL_REPORTS_SUCCESS": {
      return {
        ...state,
        purchase_order_detail_reports: action.purchase_order_detail_reports,
        // current_page: action.current_page,
        // page_size: action.page_size,
        // total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PURCHASE_ORDER_DETAIL_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_PURCHASE_ORDER_DETAIL_REPORT_REQUEST": {
      return {
        ...state,
        purchase_order_report: null,
        errorField: null
      };
    }

    case "GET_PURCHASE_ORDER_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        accounts: action.accounts,
        base_currency: action.base_currency,
        cash_banks: action.cash_banks,
        cash_flow_types: action.cash_flow_types,
        currencies: action.currencies,
        departments: action.departments,
        loading: false,
        locations: action.locations,
        messages: action.messages,
        payment_categories: action.payment_categories,
        payment_terms: action.payment_terms,
        po_detail_search_filter: action.po_detail_search_filter,
        projects: action.projects,
        suppliers: action.suppliers,
        taxes: action.taxes,
      };
    }
    case "GET_PURCHASE_ORDER_DETAIL_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
