const initState = {
  cash_bank_reports: [],
  cash_banks: [],
  opening_balance: null,
  locations: [],
  cash_bank: null,
  filter: null,
  filter_select: [],
  filter_response: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function cashBankReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_CASH_BANK_REPORTS_REQUEST": {
      return {
        ...state,
        //cash_bank_reports: [],
        opening_balance: null,
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_CASH_BANK_REPORTS_SUCCESS": {
      return {
        ...state,
        cash_bank_reports: action.cash_bank_reports,
        cash_bank : action.cash_bank,
        filter_response: action.filter_response,
        filter: action.filter,
        opening_balance: action.opening_balance,
        loading: false
      };
    }
    case "GET_CASH_BANK_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_CASH_BANK_REPORT_REQUEST": {
      return {
        ...state,
        cash_bank_report: null,
        errorField: null
      };
    }

    case "GET_CASH_BANK_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        cash_banks: action.cash_banks,
        locations: action.locations
      };
    }
    case "GET_CASH_BANK_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
