import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getSupplierTypes({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [supplierTypes] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "suppliers/types?search=" + search + "&page=" + page)
          ])
        : // Jika filter
          yield all([call(get, "suppliers/types?" + filter + "&page=" + page)])
      : search
      ? // Jika search
        yield all([call(get, "suppliers/types?search=" + search)])
      : // Jika tidak ada
        yield all([call(get, "suppliers/types?" + filter)]);

    // console.log(supplierTypes);

    // Jika sukses get data supplierTypes
    if (supplierTypes.status === "success") {
      // Jika di API menggunakan pagination
      if (supplierTypes.result.current_page && search) {
        yield put({
          type: "GET_SUPPLIER_TYPES_SUCCESS",
          supplierTypes: supplierTypes.result.data,
          current_page: supplierTypes.result.current_page,
          page_size: supplierTypes.result.per_page,
          total: supplierTypes.result.total,
          search: search
        });
      }
      // else if(supplierTypes.result.current_page && filter)
      // {
      //   yield put({
      //     type: 'GET_SUPPLIER_TYPES_SUCCESS',
      //     supplierTypes: supplierTypes.result.data,
      //     current_page: supplierTypes.result.current_page,
      //     page_size: supplierTypes.result.per_page,
      //     total: supplierTypes.result.total,
      //     filter: filter
      //   });
      // }
      else if (supplierTypes.result.current_page) {
        yield put({
          type: "GET_SUPPLIER_TYPES_SUCCESS",
          supplierTypes: supplierTypes.result.data,
          current_page: supplierTypes.result.current_page,
          page_size: supplierTypes.result.per_page,
          total: supplierTypes.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_SUPPLIER_TYPES_SUCCESS",
          supplierTypes: supplierTypes.result,
          page_size: null,
          total: null
        });
      }
    } else if (supplierTypes.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_SUPPLIER_TYPES_ERROR",
        error: supplierTypes
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_SUPPLIER_TYPES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_SUPPLIER_TYPES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }  
}

// Store data
export function* storeSupplierType({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "suppliers/types", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_SUPPLIER_TYPE_SUCCESS",
        success: resultStore
      });
    } else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_SUPPLIER_TYPE_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_SUPPLIER_TYPE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_SUPPLIER_TYPE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editSupplierType({ payload }) {
  // console.log("masuk");
  const { id_supplier_type } = payload;

  try {
    const supplierType = yield call(get, "suppliers/types/" + id_supplier_type);
    // console.log(supplierType);

    // Jika sukses
    if (supplierType.status === "success") {
      yield put({
        type: "EDIT_SUPPLIER_TYPE_SUCCESS",
        supplierType: supplierType.result
      });
    } else if (supplierType.status === "fail") {
      yield put({
        type: "EDIT_SUPPLIER_TYPE_ERROR",
        error: supplierType
      });
    } else {
      yield put({
        type: "EDIT_SUPPLIER_TYPE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_SUPPLIER_TYPE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateSupplierType({ payload }) {
  const { id_supplier_type, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "suppliers/types/" + id_supplier_type,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: "UPDATE_SUPPLIER_TYPE_SUCCESS",
        success: resultUpdate
      });
    } else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_SUPPLIER_TYPE_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_SUPPLIER_TYPE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_SUPPLIER_TYPE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showSupplierType({ payload }) {
  const { id_supplier_type } = payload;
  
  try {
    const resultShow = yield call(get, "suppliers/types/" + id_supplier_type);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: "SHOW_SUPPLIER_TYPE_SUCCESS",
        unit: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateSupplierType({ payload }) {
  const { id_supplier_type } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "suppliers/types/" + id_supplier_type,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DEACTIVATE_SUPPLIER_TYPE_SUCCESS",
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_SUPPLIER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (deactivateResult.status === "fail" || deactivateResult.errors) {
      yield all([
        yield put({
          type: "DEACTIVATE_SUPPLIER_TYPE_ERROR",
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_SUPPLIER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DEACTIVATE_SUPPLIER_TYPE_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_SUPPLIER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DEACTIVATE_SUPPLIER_TYPE_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_SUPPLIER_TYPES_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateSupplierType({ payload }) {
  const { id_supplier_type } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "suppliers/types/" + id_supplier_type,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: "ACTIVATE_SUPPLIER_TYPE_SUCCESS",
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_SUPPLIER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (activateResult.status === "fail" || activateResult.errors) {
      yield all([
        yield put({
          type: "ACTIVATE_SUPPLIER_TYPE_ERROR",
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_SUPPLIER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "ACTIVATE_SUPPLIER_TYPE_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_SUPPLIER_TYPES_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "ACTIVATE_SUPPLIER_TYPE_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_SUPPLIER_TYPES_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_SUPPLIER_TYPES_REQUEST", getSupplierTypes),
    yield takeEvery("STORE_SUPPLIER_TYPE_REQUEST", storeSupplierType),
    yield takeEvery("EDIT_SUPPLIER_TYPE_REQUEST", editSupplierType),
    yield takeEvery("UPDATE_SUPPLIER_TYPE_REQUEST", updateSupplierType),
    yield takeEvery("SHOW_SUPPLIER_TYPE_REQUEST", showSupplierType),
    yield takeEvery("DEACTIVATE_SUPPLIER_TYPE_REQUEST", deactivateSupplierType),
    yield takeEvery("ACTIVATE_SUPPLIER_TYPE_REQUEST", activateSupplierType)
  ]);
}
