import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCashManagementReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/cash-managements?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if(filter){
      const [cash_management_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/cash-managements/foreign-keys")
      ]);
    
      // Jika sukses get data cash_management_reports
      if (cash_management_reports.status === "success" && cash_management_reports.result !== null) {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_CASH_MANAGEMENT_REPORTS_SUCCESS",
          cash_management_reports: cash_management_reports.result,
          filter
        });
      }
      else if (cash_management_reports.status === "success" && cash_management_reports.result === null) {
        yield put({
          type: "GET_CASH_MANAGEMENT_REPORTS_SUCCESS",
          cash_management_reports: [],
          filter
        });
      }
      else if (cash_management_reports.status === "fail") {
        yield put({
          type: "GET_CASH_MANAGEMENT_REPORTS_ERROR",
          error: { message: cash_management_reports.message }
        });
      }
      else {
        yield put({
          type: "GET_CASH_MANAGEMENT_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data cash_management_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_CASH_MANAGEMENT_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          cash_banks: foreign_keys.result.cash_banks,
          cash_flow_groups: foreign_keys.result.cash_flow_groups,
          currencies: foreign_keys.result.currencies,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_CASH_MANAGEMENT_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_CASH_MANAGEMENT_REPORT_FOREIGN_KEYS_ERROR",
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/cash-managements/foreign-keys")
      ]);
    
      yield put({
        type: "GET_CASH_MANAGEMENT_REPORTS_SUCCESS",
        cash_management_reports: [],
      });
    
      // Jika sukses get data cash_management_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_CASH_MANAGEMENT_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          cash_banks: foreign_keys.result.cash_banks,
          cash_flow_groups: foreign_keys.result.cash_flow_groups,
          currencies: foreign_keys.result.currencies,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_CASH_MANAGEMENT_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_CASH_MANAGEMENT_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_CASH_MANAGEMENT_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_CASH_MANAGEMENT_REPORTS_REQUEST",
      getCashManagementReports
    )
  ]);
}
