import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getEmployees({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [employees, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "hrd/employees?search=" + search + "&page=" + page),
            call(get, "hrd/employees/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "hrd/employees?" + filter + "&page=" + page),
            call(get, "hrd/employees/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "hrd/employees?search=" + search),
          call(get, "hrd/employees/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "hrd/employees?" + filter),
          call(get, "hrd/employees/foreign-keys")
        ]);

    // console.log(employees);
    // console.log(foreign_keys);

    // Jika sukses get data employees
    if (employees.status === "success") {
      // Jika di API menggunakan pagination
      if (employees.result.current_page && search) {
        yield put({
          type: "GET_EMPLOYEES_SUCCESS",
          employees: employees.result.data,
          current_page: employees.result.current_page,
          page_size: employees.result.per_page,
          total: employees.result.total,
          search: search
        });
      } else if (employees.result.current_page && filter) {
        yield put({
          type: "GET_EMPLOYEES_SUCCESS",
          employees: employees.result.data,
          current_page: employees.result.current_page,
          page_size: employees.result.per_page,
          total: employees.result.total,
          filter: filter
        });
      } else if (employees.result.current_page) {
        yield put({
          type: "GET_EMPLOYEES_SUCCESS",
          employees: employees.result.data,
          current_page: employees.result.current_page,
          page_size: employees.result.per_page,
          total: employees.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_EMPLOYEES_SUCCESS",
          employees: employees.result,
          page_size: null,
          total: null
        });
      }
    } else if (employees.status === "fail") {
      yield put({
        type: "GET_EMPLOYEES_ERROR",
        error: employees
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_EMPLOYEES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_EMPLOYEE_FOREIGN_KEYS_SUCCESS",
        roles: foreign_keys.result.roles,
        job_titles: foreign_keys.result.job_titles,
        locations: foreign_keys.result.locations,
        departments: foreign_keys.result.departments,
        countries: foreign_keys.result.countries,
        provinces: foreign_keys.result.provinces,
        cities: foreign_keys.result.cities,
        districts: foreign_keys.result.districts,
        default_id_country: foreign_keys.result.default_id_country,
        array_id_location: foreign_keys.result.array_id_location,
        users: foreign_keys.result.users
      });
    } else {
      yield put({
        type: "GET_EMPLOYEE_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_EMPLOYEES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Create data
export function* createEmployee() {
  try {
    const [foreign_keys] = yield all([call(get, "hrd/employees/foreign-keys")]);

    // console.log(foreign_keys);

    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_EMPLOYEE_FOREIGN_KEYS_SUCCESS",
        roles: foreign_keys.result.roles,
        job_titles: foreign_keys.result.job_titles,
        locations: foreign_keys.result.locations,
        departments: foreign_keys.result.departments,
        countries: foreign_keys.result.countries,
        provinces: foreign_keys.result.provinces,
        cities: foreign_keys.result.cities,
        districts: foreign_keys.result.districts,
        default_id_country: foreign_keys.result.default_id_country,
        array_id_location: foreign_keys.result.array_id_location,
        users: foreign_keys.result.users
      });
    } else {
      yield put({
        type: "GET_EMPLOYEE_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    yield put({
      type: "CREATE_EMPLOYEE_SUCCESS"
    });
  } catch (error) {
    yield put({
      type: "GET_EMPLOYEE_FOREIGN_KEYS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Store data
export function* storeEmployee({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "hrd/employees", body);
    // console.log(body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_EMPLOYEE_SUCCESS",
        id_employee: resultStore.result.id_employee,
        success: resultStore
      });
    } else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_EMPLOYEE_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_EMPLOYEE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_EMPLOYEE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Edit data Employee
export function* editEmployee({ payload }) {
  // console.log("masuk");
  const { id_employee } = payload;

  try {
    const [employee, foreign_keys] = yield all([
      call(get, "hrd/employees/" + id_employee),
      call(get, "hrd/employees/foreign-keys")
    ]);

    // console.log(employee);
    // console.log(foreign_keys);

    if (foreign_keys.status === "success") {
      yield put({
        type: "GET_EMPLOYEE_FOREIGN_KEYS_SUCCESS",
        roles: foreign_keys.result.roles,
        job_titles: foreign_keys.result.job_titles,
        locations: foreign_keys.result.locations,
        departments: foreign_keys.result.departments,
        countries: foreign_keys.result.countries,
        provinces: foreign_keys.result.provinces,
        cities: foreign_keys.result.cities,
        districts: foreign_keys.result.districts,
        default_id_country: foreign_keys.result.default_id_country,
        array_id_location: foreign_keys.result.array_id_location,
        users: foreign_keys.result.users
      });
    } else {
      yield put({
        type: "GET_EMPLOYEE_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    // Jika sukses
    if (employee.status === "success") {
      yield put({
        type: "EDIT_EMPLOYEE_SUCCESS",
        employee: employee.result
      });
    } else if (employee.status === "fail") {
      yield put({
        type: "EDIT_EMPLOYEE_ERROR",
        error: employee
      });
    } else {
      yield put({
        type: "EDIT_EMPLOYEE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_EMPLOYEE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Update data
export function* updateEmployee({ payload }) {
  const { id_employee, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "hrd/employees/" + id_employee,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        put({
          type: "UPDATE_EMPLOYEE_SUCCESS",
          success: resultUpdate
        }),
        put({
          type: "EDIT_EMPLOYEE_REQUEST",
          payload: { id_employee: id_employee }
        })
      ]);
    } else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_EMPLOYEE_ERROR",
        error: resultUpdate
      });
    } else {
      yield put({
        type: "UPDATE_EMPLOYEE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_EMPLOYEE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Show data
export function* showEmployee({ payload }) {
  const { id_employee } = payload;

  try {
    const resultShow = yield call(get, "hrd/employees/" + id_employee);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: "SHOW_EMPLOYEE_SUCCESS",
        unit: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateEmployee({ payload }) {
  const { id_employee } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "hrd/employees/" + id_employee,
      body
    );
    // console.log(deactivateResult);
    // Jika sukses
    if (deactivateResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DEACTIVATE_EMPLOYEE_SUCCESS",
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_EMPLOYEES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: "DEACTIVATE_EMPLOYEE_ERROR",
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_EMPLOYEES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DEACTIVATE_EMPLOYEE_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_EMPLOYEES_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DEACTIVATE_EMPLOYEE_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_EMPLOYEES_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

// Activate data (mengubah is_active => 1)
export function* activateEmployee({ payload }) {
  const { id_employee } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "hrd/employees/" + id_employee,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: "ACTIVATE_EMPLOYEE_SUCCESS",
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_EMPLOYEES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: "ACTIVATE_EMPLOYEE_ERROR",
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_EMPLOYEES_REQUEST",
          payload: { page: false }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "ACTIVATE_EMPLOYEE_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: "GET_EMPLOYEES_REQUEST",
          payload: { page: false }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "ACTIVATE_EMPLOYEE_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: "GET_EMPLOYEES_REQUEST",
        payload: { page: false }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_EMPLOYEES_REQUEST", getEmployees),
    yield takeEvery("CREATE_EMPLOYEE_REQUEST", createEmployee),
    yield takeEvery("STORE_EMPLOYEE_REQUEST", storeEmployee),
    yield takeEvery("EDIT_EMPLOYEE_REQUEST", editEmployee),
    yield takeEvery("UPDATE_EMPLOYEE_REQUEST", updateEmployee),
    yield takeEvery("SHOW_EMPLOYEE_REQUEST", showEmployee),
    yield takeEvery("DEACTIVATE_EMPLOYEE_REQUEST", deactivateEmployee),
    yield takeEvery("ACTIVATE_EMPLOYEE_REQUEST", activateEmployee)
  ]);
}
