import { all, takeEvery, put, call } from "redux-saga/effects";
import {
  get,
  put as putApi,
  post,
  postForm,
  axiosGet,
  axiosPut
} from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getRaptorSalesInvoices({ payload }) {
  const { page, search, filter } = payload;
  const url = "raptor/sales-invoices?all_transaction=1";

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [raptorSalesInvoices, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, url + "&search=" + search + "&page=" + page),
            call(get, "raptor/sales-invoices/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, url + "&" + filter + "&page=" + page),
            call(get, "raptor/sales-invoices/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, url + "&search=" + search),
          call(get, "raptor/sales-invoices/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, url + "&" + filter),
          call(get, "raptor/sales-invoices/foreign-keys")
        ]);

    // console.log(raptorSalesInvoices);

    // Jika sukses get data raptorSalesInvoices
    if (raptorSalesInvoices.status === "success") {
      // Jika di API menggunakan pagination
      if (raptorSalesInvoices.result.current_page && search) {
        yield put({
          type: "GET_RAPTOR_SALES_INVOICES_SUCCESS",
          raptorSalesInvoices: raptorSalesInvoices.result.data,
          current_page: raptorSalesInvoices.result.current_page,
          page_size: raptorSalesInvoices.result.per_page,
          total: raptorSalesInvoices.result.total,
          search: search
        });
      } else if (raptorSalesInvoices.result.current_page && filter) {
        yield put({
          type: "GET_RAPTOR_SALES_INVOICES_SUCCESS",
          raptorSalesInvoices: raptorSalesInvoices.result.data,
          current_page: raptorSalesInvoices.result.current_page,
          page_size: raptorSalesInvoices.result.per_page,
          total: raptorSalesInvoices.result.total,
          filter: filter
        });
      } else if (raptorSalesInvoices.result.current_page) {
        yield put({
          type: "GET_RAPTOR_SALES_INVOICES_SUCCESS",
          raptorSalesInvoices: raptorSalesInvoices.result.data,
          current_page: raptorSalesInvoices.result.current_page,
          page_size: raptorSalesInvoices.result.per_page,
          total: raptorSalesInvoices.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_RAPTOR_SALES_INVOICES_SUCCESS",
          raptorSalesInvoices: raptorSalesInvoices.result,
          page_size: null,
          total: null
        });
      }
    } else if (raptorSalesInvoices.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_RAPTOR_SALES_INVOICES_ERROR",
        error: { message: raptorSalesInvoices.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_RAPTOR_SALES_INVOICES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      // console.log(foreign.result.delivery_orders);
      yield put({
        type: "GET_RAPTOR_SALES_INVOICE_FOREIGN_KEYS_SUCCESS",
        base_currency: foreign.result.base_currency,
        cash_banks: foreign.result.cash_banks,
        customers: foreign.result.customers,
        info_message: foreign.result.info_message,
        inventory_items: foreign.result.inventory_items,
        item_categories: foreign.result.item_categories,
        locations: foreign.result.locations,
        payment_categories: foreign.result.payment_categories,
        payment_terms: foreign.result.payment_terms,
        units: foreign.result.units,
        warning_message: foreign.result.warning_message
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_RAPTOR_SALES_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_RAPTOR_SALES_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_RAPTOR_SALES_INVOICES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

export function* synchronizeRaptor() {
  try {
    const sync = yield call(axiosGet, "raptor/import-sales");

    if (sync.status === "success") {
      yield all([
        yield put({
          type: "SYNCHRONIZE_RAPTOR_SALES_INVOICES_SUCCESS",
          success: sync
        }),
        yield put({
          type: "GET_RAPTOR_SALES_INVOICES_REQUEST",
          payload: {
            page: 1,
            search: null,
            filter: undefined
          }
        })
      ]);
    } else if (sync.status === "fail") {
      yield put({
        type: "SYNCHRONIZE_RAPTOR_SALES_INVOICES_ERROR",
        error: { message: sync.message }
      });
    } else {
      yield put({
        type: "SYNCHRONIZE_RAPTOR_SALES_INVOICES_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "SYNCHRONIZE_RAPTOR_SALES_INVOICES_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Edit data
export function* editRaptorSalesInvoice({ payload }) {
  const { id_sales_invoice } = payload;

  try {
    const [raptorSalesInvoice, foreign] = yield all([
      call(get, "raptor/sales-invoices/" + id_sales_invoice),
      call(get, "raptor/sales-invoices/foreign-keys")
    ]);

    // Jika sukses
    if (raptorSalesInvoice.status === "success") {
      yield put({
        type: "EDIT_RAPTOR_SALES_INVOICE_SUCCESS",
        raptorSalesInvoice: raptorSalesInvoice.result,
        info: raptorSalesInvoice.info ? raptorSalesInvoice.info : []
      });
    } else if (raptorSalesInvoice.status === "fail") {
      yield put({
        type: "EDIT_RAPTOR_SALES_INVOICE_ERROR",
        error: { message: raptorSalesInvoice.message }
      });
    } else {
      yield put({
        type: "EDIT_RAPTOR_SALES_INVOICE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_RAPTOR_SALES_INVOICE_FOREIGN_KEYS_SUCCESS",
        base_currency: foreign.result.base_currency,
        cash_banks: foreign.result.cash_banks,
        customers: foreign.result.customers,
        locations: foreign.result.locations,
        info_message: foreign.result.info_message,
        inventory_items: foreign.result.inventory_items,
        item_categories: foreign.result.item_categories,
        payment_categories: foreign.result.payment_categories,
        payment_terms: foreign.result.payment_terms,
        warning_message: foreign.result.warning_message,
        units: foreign.result.units
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_RAPTOR_SALES_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_RAPTOR_SALES_INVOICE_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_RAPTOR_SALES_INVOICE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// Update data
export function* updateRaptorSalesInvoice({ payload }) {
  const { id_sales_invoice, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "raptor/sales-invoices/" + id_sales_invoice,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        put({
          type: "UPDATE_RAPTOR_SALES_INVOICE_SUCCESS",
          success: { message: resultUpdate.message },
          info: resultUpdate.info,
          id_sales_invoice: id_sales_invoice
        }),
        put({
          type: "EDIT_RAPTOR_SALES_INVOICE_REQUEST",
          payload: {
            id_sales_invoice: id_sales_invoice
          }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_RAPTOR_SALES_INVOICE_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_RAPTOR_SALES_INVOICE_REQUEST",
          payload: {
            id_sales_invoice: id_sales_invoice
          }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_RAPTOR_SALES_INVOICE_ERROR",
        error: { message: resultUpdate.message }
      });
    } else {
      yield put({
        type: "UPDATE_RAPTOR_SALES_INVOICE_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_RAPTOR_SALES_INVOICE_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

// store new item
export function* storeItem({ payload }) {
  const { id_sales_invoice, body } = payload;

  try {
    const resultStore = yield call(post, "items", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield all([
        yield put({
          type: "STORE_ITEM_RAPTOR_SALES_INVOICE_SUCCESS",
          id_sales_invoice: id_sales_invoice,
          success: resultStore
        }),
        yield put({
          type: "EDIT_RAPTOR_SALES_INVOICE_REQUEST",
          payload: {
            id_sales_invoice: id_sales_invoice
          }
        })
      ]);
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_ITEM_RAPTOR_SALES_INVOICE_ERROR",
        id_sales_invoice: id_sales_invoice,
        error: resultStore
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_ITEM_RAPTOR_SALES_INVOICE_ERROR",
        id_sales_invoice: id_sales_invoice,
        error: resultStore,
        errors: resultStore
      });
    } else {
      yield put({
        type: "STORE_ITEM_RAPTOR_SALES_INVOICE_ERROR",
        id_sales_invoice: id_sales_invoice,
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_ITEM_RAPTOR_SALES_INVOICE_ERROR",
      id_sales_invoice: id_sales_invoice,
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

