// Upload Document
export function* uploadRaptorSalesInvoiceDocument({ payload }) {
  const { id_sales_invoice, body } = payload;
  let data = {
    ...body,
    id_sales_invoice
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "sales/sales-invoices/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_RAPTOR_SALES_INVOICE_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_RAPTOR_SALES_INVOICE_REQUEST",
          payload: { id_sales_invoice }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_RAPTOR_SALES_INVOICE_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_RAPTOR_SALES_INVOICE_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_RAPTOR_SALES_INVOICE_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        })
      ]);
    }

    // }
  } catch (error) {
    yield put({
      type: "UPLOAD_RAPTOR_SALES_INVOICE_DOCUMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

