const initState = {
  id_item_request: null,
  itemRequests: [],
  itemRequest: null,
  printData: {},
  items: [],
  transactions: [],
  suppliers: [],
  // selectedTransaction: null,
  locations: [],
  departments: [],
  projects: [],
  search: null,
  filter: null,
  loading: false,
  loadingSubmit: false,
  loadingItems: false,
  loadingForeign: false,
  loadingTransactions: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function itemRequestsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_ITEM_REQUESTS_REQUEST": {
      return {
        ...state,
        //itemRequests: [],
        loading: true,
        loadingForeign: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_ITEM_REQUESTS_SUCCESS": {
      return {
        ...state,
        itemRequests: action.itemRequests,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
        loadingForeign: false
      };
    }
    case "GET_ITEM_REQUESTS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_ITEM_REQUEST_REQUEST": {
      return {
        ...state,
        id_item_request: null,
        items: [],
        loadingTransactions: true,
        search: null,
        errorField: null,
        current_page: null,
        page_size: null,
        total: null
      };
    }
    case "GET_FILTER_ITEMS_RQ_REQUEST": {
      return {
        ...state,
        loadingItems: true,
        search: action.search
      };
    }
    case "GET_FILTER_ITEMS_RQ_SUCCESS": {
      return {
        ...state,
        loadingItems: false,
        items: action.items
      };
    }
    case "GET_ITEM_REQUEST_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_ITEM_REQUEST_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        locations: action.locations,
        suppliers: action.suppliers,
        departments: action.departments,
        projects: action.projects
      };
    }
    case "GET_ITEM_REQUEST_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "GET_TRANSACTIONS_REQUEST": {
      return {
        ...state,
        loadingTransactions: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_TRANSACTIONS_SUCCESS": {
      return {
        ...state,
        loadingTransactions: false,
        transactions: action.transactions,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_TRANSACTIONS_ERROR": {
      return {
        ...state,
        loadingTransactions: false,
        error: action.error.message
      };
    }
    case "STORE_ITEM_REQUEST_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_ITEM_REQUEST_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_item_request: action.id_item_request
      };
    }
    case "STORE_ITEM_REQUEST_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_ITEM_REQUEST_REQUEST": {
      return {
        ...state,
        id_item_request: null,
        loading: true,
        loadingTransactions: true,
        search: null,
        errorField: null,
        current_page: null,
        page_size: null,
        total: null
      };
    }
    case "EDIT_ITEM_REQUEST_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        itemRequest: action.itemRequest
      };
    }
    case "EDIT_ITEM_REQUEST_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_ITEM_REQUEST_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_ITEM_REQUEST_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_item_request: action.id_item_request
      };
    }
    case "UPDATE_ITEM_REQUEST_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_ITEM_REQUEST_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_ITEM_REQUEST_SUCCESS": {
      return {
        ...state,
        loading: false,
        itemRequests: action.itemRequests
      };
    }
    case "CONFIRM_ITEM_REQUEST_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CONFIRM_ITEM_REQUEST_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CONFIRM_ITEM_REQUEST_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CLOSE_ITEM_REQUEST_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CLOSE_ITEM_REQUEST_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CLOSE_ITEM_REQUEST_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPLOAD_ITEM_REQUEST_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_ITEM_REQUEST_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_ITEM_REQUEST_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_ITEM_REQUEST_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_ITEM_REQUEST_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_ITEM_REQUEST_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_ITEM_REQUEST_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "SET_PRINT_DATA_ITEM_REQUEST": {
      return {
        ...state,
        printData: action.payload.body
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
