import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getReceivablePayments({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [receivablePayments, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "financials/receivable-payments?search=" + search + "&page=" + page
            ),
            call(get, "financials/receivable-payments/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(
              get,
              "financials/receivable-payments?" + filter + "&page=" + page
            ),
            call(get, "financials/receivable-payments/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "financials/receivable-payments?search=" + search),
          call(get, "financials/receivable-payments/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "financials/receivable-payments?" + filter),
          call(get, "financials/receivable-payments/foreign-keys")
        ]);

    // console.log(receivablePayments);

    // Jika sukses get data receivablePayments
    if (receivablePayments.status === "success") {
      // Jika di API menggunakan pagination
      if (receivablePayments.result.current_page && search) {
        yield put({
          type: "GET_RECEIVABLE_PAYMENTS_SUCCESS",
          receivablePayments: receivablePayments.result.data,
          current_page: receivablePayments.result.current_page,
          page_size: receivablePayments.result.per_page,
          total: receivablePayments.result.total,
          search: search
        });
      } else if (receivablePayments.result.current_page && filter) {
        yield put({
          type: "GET_RECEIVABLE_PAYMENTS_SUCCESS",
          receivablePayments: receivablePayments.result.data,
          current_page: receivablePayments.result.current_page,
          page_size: receivablePayments.result.per_page,
          total: receivablePayments.result.total,
          filter: filter
        });
      } else if (receivablePayments.result.current_page) {
        yield put({
          type: "GET_RECEIVABLE_PAYMENTS_SUCCESS",
          receivablePayments: receivablePayments.result.data,
          current_page: receivablePayments.result.current_page,
          page_size: receivablePayments.result.per_page,
          total: receivablePayments.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_RECEIVABLE_PAYMENTS_SUCCESS",
          receivablePayments: receivablePayments.result,
          page_size: null,
          total: null
        });
      }
    } else if (receivablePayments.status === "fail") {
      // Set message ke session storage
      yield put({
        type: "GET_RECEIVABLE_PAYMENTS_ERROR",
        error: { message: receivablePayments.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_RECEIVABLE_PAYMENTS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_RECEIVABLE_PAYMENT_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        customers: foreign.result.customers,
        currencies: foreign.result.currencies,
        cashBanks: foreign.result.cash_banks,
        accounts: foreign.result.accounts,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        messages: foreign.result.messages,
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_RECEIVABLE_PAYMENT_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_RECEIVABLE_PAYMENT_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_RECEIVABLE_PAYMENTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createReceivablePayment() {
  try {
    const [transactions, foreign] = yield all([
      call(
        get,
        "sales/sales-invoices?get_old_trans=1&all_transaction=1&status=2&payment_status=0,1"
      ),
      call(get, "financials/receivable-payments/foreign-keys")
    ]);
  
    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_SALE_INVOICES_RP_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_SALE_INVOICES_RP_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_SALE_INVOICES_RP_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_RECEIVABLE_PAYMENT_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        customers: foreign.result.customers,
        currencies: foreign.result.currencies,
        cashBanks: foreign.result.cash_banks,
        accounts: foreign.result.accounts,
        departments: foreign.result.departments,
        projects: foreign.result.projects,
        cashFlowTypes: foreign.result.cash_flow_types,
        paymentCategories: foreign.result.payment_categories,
        messages: foreign.result.messages,
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_RECEIVABLE_PAYMENT_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_RECEIVABLE_PAYMENT_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_SALE_INVOICES_RP_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getTransactions({ payload }) {
  const { page, search, filter } = payload;
  // console.log(filter);

  try {
    const transactions = filter
      ? yield call(
          get,
          "sales/sales-invoices?get_old_trans=1&all_transaction=1&status=2&" +
            filter +
            "&page=" +
            page
        )
      : search
      ? yield call(
          get,
          "sales/sales-invoices?get_old_trans=1&all_transaction=1&status=2&payment_status=0,1&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(
          get,
          "sales/sales-invoices?get_old_trans=1&all_transaction=1&status=2&payment_status=0,1" +
            "&page=" +
            page
        );

    if (transactions.status === "success") {
      yield put({
        type: "GET_FILTER_SALE_INVOICES_RP_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_FILTER_SALE_INVOICES_RP_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_SALE_INVOICES_RP_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_SALE_INVOICES_RP_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getCreditMemos({ payload }) {
  const { id_location, id_customer } = payload;
  // console.log(filter);
  const filter = `financials/credit-memos?is_active=1&status=1&id_location=${id_location}&id_customer=${id_customer}`;

  try {
    const creditMemos = yield call(get, filter);

    if (creditMemos.status === "success") {
      yield put({
        type: "GET_FILTER_CREDIT_MEMOS_RP_SUCCESS",
        creditMemos: creditMemos.result
      });
    } else if (creditMemos.status === "fail") {
      yield put({
        type: "GET_FILTER_CREDIT_MEMOS_RP_ERROR",
        error: { message: creditMemos.message }
      });
    } else {
      yield put({
        type: "GET_FILTER_CREDIT_MEMOS_RP_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_CREDIT_MEMOS_RP_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeReceivablePayment({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "financials/receivable-payments", body);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_RECEIVABLE_PAYMENT_SUCCESS",
        success: { message: resultStore.message },
        id_receivable_payment: resultStore.result.id_receivable_payment
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_RECEIVABLE_PAYMENT_ERROR",
        error: resultStore
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_RECEIVABLE_PAYMENT_ERROR",
        error: { message: resultStore.message }
      });
    } else {
      yield put({
        type: "STORE_RECEIVABLE_PAYMENT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_RECEIVABLE_PAYMENT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

