// Update data
export function* updateReceivablePayment({ payload }) {
  const { id_receivable_payment, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "financials/receivable-payments/" + id_receivable_payment,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        put({
          type: "UPDATE_RECEIVABLE_PAYMENT_SUCCESS",
          success: { message: resultUpdate.message },
          id_receivable_payment: id_receivable_payment
        }),
        put({
          type: "EDIT_RECEIVABLE_PAYMENT_REQUEST",
          payload: {
            id_receivable_payment: id_receivable_payment
          }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_RECEIVABLE_PAYMENT_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_RECEIVABLE_PAYMENT_REQUEST",
          payload: {
            id_receivable_payment: id_receivable_payment
          }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield all([
        yield put({
          type: "UPDATE_RECEIVABLE_PAYMENT_ERROR",
          error: { message: resultUpdate.message }
        }),
        yield put({
          type: "EDIT_RECEIVABLE_PAYMENT_REQUEST",
          payload: {
            id_receivable_payment: id_receivable_payment
          }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPDATE_RECEIVABLE_PAYMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_RECEIVABLE_PAYMENT_REQUEST",
          payload: {
            id_receivable_payment: id_receivable_payment
          }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "UPDATE_RECEIVABLE_PAYMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_RECEIVABLE_PAYMENT_REQUEST",
        payload: {
          id_receivable_payment: id_receivable_payment
        }
      })
    ]);
    bugsnagClient.notify(error)
  }
}

