// Upload Document
export function* uploadReceivablePaymentDocument({ payload }) {
  const { id_receivable_payment, body } = payload;
  let data = {
    ...body,
    id_receivable_payment
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "financials/receivable-payments/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_RECEIVABLE_PAYMENT_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_RECEIVABLE_PAYMENT_REQUEST",
          payload: { id_receivable_payment }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_RECEIVABLE_PAYMENT_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        }),
        yield put({
          type: "EDIT_RECEIVABLE_PAYMENT_REQUEST",
          payload: { id_receivable_payment }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_RECEIVABLE_PAYMENT_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        }),
        yield put({
          type: "EDIT_RECEIVABLE_PAYMENT_REQUEST",
          payload: { id_receivable_payment }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_RECEIVABLE_PAYMENT_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_RECEIVABLE_PAYMENT_REQUEST",
          payload: { id_receivable_payment }
        })
      ]);
    }

    // }
  } catch (error) {
    yield all([
      yield put({
        type: "UPLOAD_RECEIVABLE_PAYMENT_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_RECEIVABLE_PAYMENT_REQUEST",
        payload: { id_receivable_payment }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

