export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_RECEIVABLE_PAYMENTS_REQUEST", getReceivablePayments),
    yield takeEvery(
      "CREATE_RECEIVABLE_PAYMENT_REQUEST",
      createReceivablePayment
    ),
    yield takeEvery("GET_FILTER_SALE_INVOICES_RP_REQUEST", getTransactions),
    yield takeEvery("GET_FILTER_CREDIT_MEMOS_RP_REQUEST", getCreditMemos),
    yield takeEvery("STORE_RECEIVABLE_PAYMENT_REQUEST", storeReceivablePayment),
    yield takeEvery("EDIT_RECEIVABLE_PAYMENT_REQUEST", editReceivablePayment),
    yield takeEvery(
      "UPDATE_RECEIVABLE_PAYMENT_REQUEST",
      updateReceivablePayment
    ),
    yield takeEvery("SHOW_RECEIVABLE_PAYMENT_REQUEST", showReceivablePayment),
    yield takeEvery(
      "CONFIRM_RECEIVABLE_PAYMENT_REQUEST",
      confirmReceivablePayment
    ),
    yield takeEvery(
      "CANCEL_RECEIVABLE_PAYMENT_REQUEST",
      cancelReceivablePayment
    ),
    yield takeEvery(
      "UPLOAD_RECEIVABLE_PAYMENT_DOCUMENT_REQUEST",
      uploadReceivablePaymentDocument
    ),
    yield takeEvery(
      "DOWNLOAD_RECEIVABLE_PAYMENT_DOCUMENT_REQUEST",
      downloadReceivablePaymentDocument
    ),
    yield takeEvery(
      "DELETE_RECEIVABLE_PAYMENT_DOCUMENT_REQUEST",
      deleteReceivablePaymentDocument
    )
  ]);
}
