const initState = {
  cost_to_sales_ratio_by_item_reports: [],
  error: null,
  errorField: null,
  filter: null,
  filter_select: null,
  item_cost_sales_sort_by_list: [],
  loading: false,
  locations: [],
  success: null,
}

export default function getCostToSalesRatioByItemReportsReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_COST_TO_SALES_RATIO_BY_ITEM_REPORTS_REQUEST':
      return {
        ...state,
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select
      }
    case 'GET_COST_TO_SALES_RATIO_BY_ITEM_REPORTS_SUCCESS':
      return {
        ...state,
        cost_to_sales_ratio_by_item_reports: action.cost_to_sales_ratio_by_item_reports,
        loading: false,
      }
    case "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORTS_ERROR":
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    case "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations
      };
    }
    case "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}