import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getDeliveryOrderDetailReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/delivery-orders/details?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (filter) {
      const [delivery_order_detail_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/delivery-orders/foreign-keys")
      ]);
      //console.log(delivery_order_detail_reports.result)
      // Jika sukses get data delivery_order_detail_reports
      if (delivery_order_detail_reports.status === "success") {
        yield put({
          type: "GET_DELIVERY_ORDER_DETAIL_REPORTS_SUCCESS",
          delivery_order_detail_reports: delivery_order_detail_reports.result,
          filter: filter,
          search: search
        });
      } else {
        yield put({
          type: "GET_DELIVERY_ORDER_DETAIL_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data delivery_order_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_DELIVERY_ORDER_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          customers: foreign_keys.result.customers,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          base_currency: foreign_keys.result.base_currency,
          messages: foreign_keys.result.messages
        });
      } else {
        yield put({
          type: "GET_DELIVERY_ORDER_DETAIL_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/delivery-orders/foreign-keys")
      ]);
  
      yield put({
        type: "GET_DELIVERY_ORDER_DETAIL_REPORTS_SUCCESS",
        delivery_order_detail_reports: []
      });
  
      // Jika sukses get data delivery_order_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_DELIVERY_ORDER_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          customers: foreign_keys.result.customers,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          base_currency: foreign_keys.result.base_currency,
          messages: foreign_keys.result.messages
        });
      } else {
        yield put({
          type: "GET_DELIVERY_ORDER_DETAIL_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_DELIVERY_ORDER_DETAIL_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_DELIVERY_ORDER_DETAIL_REPORTS_REQUEST",
      getDeliveryOrderDetailReports
    )
  ]);
}
