import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCashBankReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/cash-banks?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if(filter){
      const [cash_bank_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/cash-banks/foreign-keys")
      ]);
    
      // Jika sukses get data cash_bank_reports
      if (cash_bank_reports.status === "success" && cash_bank_reports.result !== null) {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_CASH_BANK_REPORTS_SUCCESS",
          cash_bank_reports: cash_bank_reports.result.cash_banks,
          cash_bank : cash_bank_reports.result.cash_bank,
          filter_response : cash_bank_reports.result.filter,
          opening_balance : cash_bank_reports.result.opening_balance,
          filter
        });
      }
      else if (cash_bank_reports.status === "success" && cash_bank_reports.result === null) {
        yield put({
          type: "GET_CASH_BANK_REPORTS_SUCCESS",
          cash_bank_reports: [],
          cash_bank : null,
          filter_response : null,
          opening_balance : null,
          filter
        });
      }
      else if (cash_bank_reports.status === "fail") {
        yield put({
          type: "GET_CASH_BANK_REPORTS_ERROR",
          error: { message: cash_bank_reports.message }
        });
      }
      else {
        yield put({
          type: "GET_CASH_BANK_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data cash_bank_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_CASH_BANK_REPORT_FOREIGN_KEYS_SUCCESS",
          cash_banks: foreign_keys.result.cash_banks,
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_CASH_BANK_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_CASH_BANK_REPORT_FOREIGN_KEYS_ERROR",
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/cash-banks/foreign-keys")
      ]);
    
      yield put({
        type: "GET_CASH_BANK_REPORTS_SUCCESS",
        cash_bank_reports: [],
        cash_bank : null,
        filter_response : null,
        opening_balance : null,
      });
    
      // Jika sukses get data cash_bank_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_CASH_BANK_REPORT_FOREIGN_KEYS_SUCCESS",
          cash_banks: foreign_keys.result.cash_banks,
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_CASH_BANK_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_CASH_BANK_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_CASH_BANK_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_CASH_BANK_REPORTS_REQUEST",
      getCashBankReports
    )
  ]);
}
