const initState = {
  petty_cashes: [],
  petty_cash: null,
  locations: [],
  petty_cash_types: [],
  employees: [],
  accounts: [],
  base_currency: null,
  search: null,
  filter: null,
  loading: false,
  loadingEdit: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
  id_petty_cash: null
};

export default function pettyCashesReducer(state = initState, action) {
  switch (action.type) {
    case "GET_PETTY_CASHES_REQUEST": {
      return {
        ...state,
        //petty_cashes: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_PETTY_CASHES_SUCCESS": {
      return {
        ...state,
        petty_cashes: action.petty_cashes,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PETTY_CASHES_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CREATE_PETTY_CASH_REQUEST": {
      return {
        ...state,
        petty_cash: null,
        errorField: null
      };
    }

    case "GET_PETTY_CASH_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        locations: action.locations,
        petty_cash_types: action.petty_cash_types,
        employees: action.employees,
        accounts: action.accounts,
        base_currency: action.base_currency
      };
    }
    case "GET_PETTY_CASH_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }

    case "STORE_PETTY_CASH_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "STORE_PETTY_CASH_SUCCESS": {
      return {
        ...state,
        loading: false,
        id_petty_cash: action.id_petty_cash,
        success: action.success.message
      };
    }
    case "STORE_PETTY_CASH_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "EDIT_PETTY_CASH_REQUEST": {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        errorField: null
      };
    }
    case "EDIT_PETTY_CASH_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        petty_cash: action.petty_cash
      };
    }
    case "EDIT_PETTY_CASH_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case "UPDATE_PETTY_CASH_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "UPDATE_PETTY_CASH_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPDATE_PETTY_CASH_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_PETTY_CASH_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_PETTY_CASH_SUCCESS": {
      return {
        ...state,
        loading: false,
        petty_cashes: action.petty_cashes
      };
    }
    case "PROCESS_PETTY_CASH_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "PROCESS_PETTY_CASH_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "PROCESS_PETTY_CASH_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CANCEL_PETTY_CASH_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "CANCEL_PETTY_CASH_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "CANCEL_PETTY_CASH_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "UPLOAD_PETTY_CASH_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_PETTY_CASH_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_PETTY_CASH_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_PETTY_CASH_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_PETTY_CASH_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_PETTY_CASH_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_PETTY_CASH_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
