import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getUncompletePurchaseOrderReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/purchase-orders/uncompletes?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if (search || filter) {
      const [uncomplete_purchase_order_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/purchase-orders/foreign-keys")
      ]);
  
      // Jika sukses get data uncomplete_purchase_order_reports
      if (uncomplete_purchase_order_reports.status === "success") {
        yield put({
          type: "GET_UNCOMPLETE_PURCHASE_ORDER_REPORTS_SUCCESS",
          uncomplete_purchase_order_reports: uncomplete_purchase_order_reports.result,
          filter: filter,
          search: search
        });  
      } else if (uncomplete_purchase_order_reports.status === "fail") {
        yield put({
          type: "GET_UNCOMPLETE_PURCHASE_ORDER_REPORTS_ERROR",
          error: { message: uncomplete_purchase_order_reports.message }
        });
      } else {
        yield put({
          type: "GET_UNCOMPLETE_PURCHASE_ORDER_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data uncomplete_purchase_order_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_UNCOMPLETE_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          currencies: foreign_keys.result.currencies
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_UNCOMPLETE_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_UNCOMPLETE_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/purchase-orders/foreign-keys")
      ]);
  
      yield put({
        type: "GET_UNCOMPLETE_PURCHASE_ORDER_REPORTS_SUCCESS",
        uncomplete_purchase_order_reports: []
      });  
  
      // Jika sukses get data uncomplete_purchase_order_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_UNCOMPLETE_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          currencies: foreign_keys.result.currencies
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_UNCOMPLETE_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_UNCOMPLETE_PURCHASE_ORDER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_UNCOMPLETE_PURCHASE_ORDER_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_UNCOMPLETE_PURCHASE_ORDER_REPORTS_REQUEST", getUncompletePurchaseOrderReports)
  ]);
}
