
const initState = {
  petty_cash_types: [],
  petty_cash_type: null,
  accounts: [],
  search: null,
  filter: null,
  loading: false,
  loadingEdit: false,
  loadingFilterAccounts: false,
  current_page: 1,
  page_size: null,
  total: null,
  errorField: null,
  error: null,
  success: null,
};

export default function pettyCashTypesReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_PETTY_CASH_TYPES_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingFilterLocationTypes: true,
        loadingFilterSites: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_PETTY_CASH_TYPES_SUCCESS': {
      return {
        ...state,
        loading: false,
        petty_cash_types: action.petty_cash_types,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter
      };
    }
    case 'GET_PETTY_CASH_TYPES_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case 'CREATE_PETTY_CASH_TYPE_REQUEST': {
      return {
        ...state,
        loadingFilterAccounts: true,
        petty_cash_type: null,
        errorField: null,
      };
    }

    case 'GET_ACCOUNTS_SUCCESS': {
      return {
        ...state,
        loadingFilterAccounts: false,
        accounts: action.accounts
      };
    }

    case 'GET_ACCOUNTS_ERROR': {
      return {
        ...state,
        loadingFilterAccounts: false,
        error: action.error.message
      };
    }
    
    case 'STORE_PETTY_CASH_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_PETTY_CASH_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_PETTY_CASH_TYPE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_PETTY_CASH_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit: true,
        loadingFilterAccounts: true,
        errorField: null,
      };
    }
    case 'EDIT_PETTY_CASH_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        loadingFilterAccounts: false,
        petty_cash_type: action.petty_cash_type,
        //success: action.success.message
      };
    }
    case 'EDIT_PETTY_CASH_TYPE_ERROR': {
      return {
        ...state,
        loading: false,
        loadingEdit: false,
        error: action.error.message,
      };
    }
    case 'UPDATE_PETTY_CASH_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_PETTY_CASH_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_PETTY_CASH_TYPE_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_PETTY_CASH_TYPE_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_PETTY_CASH_TYPE_SUCCESS': {
      return {
        ...state,
        loading: false,
        petty_cash_types: action.petty_cash_types
      };
    }
    case "DEACTIVATE_PETTY_CASH_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_PETTY_CASH_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_PETTY_CASH_TYPE_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_PETTY_CASH_TYPE_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_PETTY_CASH_TYPE_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
