const initState = {
  petty_cash_reports: [],
  petty_cash_types: [],
  opening_balance: null,
  locations: [],
  petty_cash: null,
  filter: null,
  filter_select: [],
  filter_response: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function cashBankReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_PETTY_CASH_REPORTS_REQUEST": {
      return {
        ...state,
        //petty_cash_reports: [],
        opening_balance: null,
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_PETTY_CASH_REPORTS_SUCCESS": {
      return {
        ...state,
        petty_cash_reports: action.petty_cash_reports,
        petty_cash : action.petty_cash,
        filter_response: action.filter_response,
        filter: action.filter,
        opening_balance: action.opening_balance,
        loading: false
      };
    }
    case "GET_PETTY_CASH_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_PETTY_CASH_REPORT_REQUEST": {
      return {
        ...state,
        petty_cash_report: null,
        errorField: null
      };
    }

    case "GET_PETTY_CASH_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        petty_cash_types: action.petty_cash_types,
        locations: action.locations
      };
    }
    case "GET_PETTY_CASH_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
