import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getTopItemCategoryReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/item-summaries/top-category-solds?";
  // if (search) {
  //   url += "search=" + search;
  // }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if (filter) {
      const [top_item_category_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/item-summaries/sales/foreign-keys")
      ]);
  
      // Jika sukses get data top_item_category_reports
      if (top_item_category_reports.status === "success") { 
        yield put({
          type: "GET_TOP_ITEM_CATEGORY_REPORTS_SUCCESS",
          top_item_category_reports: top_item_category_reports.result,
          filter: filter,
          search: search
        }); 
      } else if (top_item_category_reports.status === "fail") {
        yield put({
          type: "GET_TOP_ITEM_CATEGORY_REPORTS_ERROR",
          error: { message: top_item_category_reports.message }
        });
      } else {
        yield put({
          type: "GET_TOP_ITEM_CATEGORY_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
  
      // Jika sukses get data top_item_category_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TOP_ITEM_CATEGORY_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          customers: foreign_keys.result.customers,
          customer_types: foreign_keys.result.customer_types,
          item_categories: foreign_keys.result.item_categories
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TOP_ITEM_CATEGORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TOP_ITEM_CATEGORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/item-summaries/sales/foreign-keys")
      ]);
  
      yield put({
        type: "GET_TOP_ITEM_CATEGORY_REPORTS_SUCCESS",
        top_item_category_reports: []
      });
      // Jika sukses get data top_item_category_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TOP_ITEM_CATEGORY_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          customers: foreign_keys.result.customers,
          customer_types: foreign_keys.result.customer_types,
          item_categories: foreign_keys.result.item_categories
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TOP_ITEM_CATEGORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TOP_ITEM_CATEGORY_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_TOP_ITEM_CATEGORY_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery(
      "GET_TOP_ITEM_CATEGORY_REPORTS_REQUEST",
      getTopItemCategoryReports
    )
  ]);
}
