import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getBankReconciles({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [bank_reconciles, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "financials/bank-reconciles?search=" + search + "&page=" + page),
            call(get, "financials/bank-reconciles/foreign-keys"),
          ])
        : // Jika filter
          yield all([
            call(get, "financials/bank-reconciles?" + filter + "&page=" + page),
            call(get, "financials/bank-reconciles/foreign-keys"),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "financials/bank-reconciles?search=" + search),
          call(get, "financials/bank-reconciles/foreign-keys"),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "financials/bank-reconciles?" + filter),
          call(get, "financials/bank-reconciles/foreign-keys"),
        ]);

    // console.log(bank_reconciles);
    // console.log(foreign_keys);

    // Jika sukses get data bank_reconciles
    if (bank_reconciles.status === "success") {
      // Jika di API menggunakan pagination
      if (bank_reconciles.result.current_page && search) {
        yield put({
          type: 'GET_BANK_RECONCILES_SUCCESS',
          bank_reconciles: bank_reconciles.result.data,
          current_page: bank_reconciles.result.current_page,
          page_size: bank_reconciles.result.per_page,
          total: bank_reconciles.result.total,
          search: search
        });
      }
      else if(bank_reconciles.result.current_page && filter)
      {
        yield put({
          type: 'GET_BANK_RECONCILES_SUCCESS',
          bank_reconciles: bank_reconciles.result.data,
          current_page: bank_reconciles.result.current_page,
          page_size: bank_reconciles.result.per_page,
          total: bank_reconciles.result.total,
          filter: filter
        });
      }
      else if (bank_reconciles.result.current_page) {
        yield put({
          type: 'GET_BANK_RECONCILES_SUCCESS',
          bank_reconciles: bank_reconciles.result.data,
          current_page: bank_reconciles.result.current_page,
          page_size: bank_reconciles.result.per_page,
          total: bank_reconciles.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_BANK_RECONCILES_SUCCESS',
          bank_reconciles: bank_reconciles.result,
          page_size: null,
          total: null
        });
      }
    } else if (bank_reconciles.status === "fail") {
      yield put({
        type: 'GET_BANK_RECONCILES_ERROR', 
        error: bank_reconciles
      })
    } else {
      yield put({
        type: 'GET_BANK_RECONCILES_ERROR', 
        error: {message: "Check your internet connection"}
      })
    }

    // Jika sukses get data bank_reconcile types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_BANK_RECONCILE_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        cash_banks: foreign_keys.result.cash_banks,
        currencies: foreign_keys.result.currencies,
        messages: foreign_keys.result.messages,
      });
    }
    else{
      yield put({
        type: 'GET_BANK_RECONCILE_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_BANK_RECONCILES_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createBankReconcile() {
  try {
    const [foreign_keys] = yield all([
      call(get, "financials/bank-reconciles/foreign-keys"),
    ]);
  
    // Jika sukses get data account
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_BANK_RECONCILE_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        cash_banks: foreign_keys.result.cash_banks,
        currencies: foreign_keys.result.currencies,
        messages: foreign_keys.result.messages,
      });
    }
    else{
      yield put({
        type: 'GET_BANK_RECONCILE_FOREIGN_KEYS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_BANK_RECONCILE_FOREIGN_KEYS_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeBankReconcile({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "financials/bank-reconciles", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_BANK_RECONCILE_SUCCESS',
        id_bank_reconcile: resultStore.result.id_bank_reconcile,
        success: resultStore
      });
    }
    else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_BANK_RECONCILE_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_BANK_RECONCILE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editBankReconcile({ payload }) {
  const { id_bank_reconcile } = payload;

  try {
    const [bank_reconcile, foreign_keys] = yield all([
      call(get, "financials/bank-reconciles/" + id_bank_reconcile),
      call(get, "financials/bank-reconciles/foreign-keys"),
    ]);
  
    // Jika sukses
    if (bank_reconcile.status === "success") {
      yield put({
        type: 'EDIT_BANK_RECONCILE_SUCCESS',
        bank_reconcile: bank_reconcile.result
      });
    }
    else{
      yield put({
        type: "EDIT_BANK_RECONCILE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
    
    // Jika sukses get data bank_reconcile types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_BANK_RECONCILE_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        cash_banks: foreign_keys.result.cash_banks,
        currencies: foreign_keys.result.currencies,
        messages: foreign_keys.result.messages,
      });
    }
    else{
      yield put({
        type: 'GET_BANK_RECONCILE_FOREIGN_KEYS_ERROR',
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_BANK_RECONCILE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateBankReconcile({ payload }) {
  const { id_bank_reconcile, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "financials/bank-reconciles/" + id_bank_reconcile,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: 'UPDATE_BANK_RECONCILE_SUCCESS',
          success: resultUpdate
        }),
        yield put({
          type: 'EDIT_BANK_RECONCILE_REQUEST',
          payload: { id_bank_reconcile }
        })
      ])
      
    }
    else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_BANK_RECONCILE_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_BANK_RECONCILE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_BANK_RECONCILE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showBankReconcile({ payload }) {
  const { id_cash_bank, start_date, reconcile_date } = payload;

  try {

    const resultShow = yield call(
      get, "financials/bank-reconciles/new/" + id_cash_bank + "/" + start_date + "/" + reconcile_date
    );
  
    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_BANK_RECONCILE_SUCCESS',
        show_bank_reconcile: resultShow.result
      });
    }
    else if (resultShow.status === "fail") {
      yield put({
        type: "SHOW_BANK_RECONCILE_ERROR",
        error: resultShow
      });
    }
    else {
      yield put({
        type: "SHOW_BANK_RECONCILE_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  }
  catch (error) {
    yield put({
      type: "SHOW_BANK_RECONCILE_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error);
  }
}

// Upload Document
export function* uploadBankReconcileDocument({ payload }) {
  const { id_bank_reconcile, body } = payload;
  let data = {
    ...body,
    id_bank_reconcile 
  }

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "financials/bank-reconciles/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: 'UPLOAD_BANK_RECONCILE_DOCUMENT_SUCCESS',
          success: uploadResult
        }),
        yield put({
          type: 'EDIT_BANK_RECONCILE_REQUEST',
          payload: { id_bank_reconcile }
        })
      ])
    }
    else if(uploadResult.errors){
      yield put({
        type: 'UPLOAD_BANK_RECONCILE_DOCUMENT_ERROR',
        error: {message: uploadResult.errors.document[0]}
      })
    }
    else if(uploadResult.status === "fail"){
      yield put({
        type: 'UPLOAD_BANK_RECONCILE_DOCUMENT_ERROR',
        error: uploadResult
      })
    }
    else{
      yield put({
        type: 'UPLOAD_BANK_RECONCILE_DOCUMENT_ERROR',
        error: {message: "Check your internet connection"}
      })
    }

    // }
  } catch (error) {
    yield put({
      type: 'UPLOAD_BANK_RECONCILE_DOCUMENT_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

// Download Document
export function* downloadBankReconcileDocument({ payload }) {
  const { id_bank_reconcile_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "financials/bank-reconciles/documents/download/" + id_bank_reconcile_document,
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error);
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deleteBankReconcileDocument({ payload }) {
  const { id_bank_reconcile_document } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "financials/bank-reconciles/documents/" + id_bank_reconcile_document,
    //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_BANK_RECONCILE_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'EDIT_BANK_RECONCILE_REQUEST',
          payload: { id_bank_reconcile: deleteResult.result.id_bank_reconcile }
        })
      ]);
    }
    else if (deleteResult.status === "fail") {
      yield all([
        yield put({
          type: "DELETE_BANK_RECONCILE_DOCUMENT_ERROR",
          error: deleteResult
        }),
      ]);
    }
    else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_BANK_RECONCILE_DOCUMENT_ERROR",
          error: {message: "Check your internet connection"}
        }),
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "DELETE_BANK_RECONCILE_DOCUMENT_ERROR",
        error: {message: "Check your internet connection"}
      }),
    ]);
    bugsnagClient.notify(error);
  }
}


export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_BANK_RECONCILES_REQUEST', getBankReconciles),
    yield takeEvery('CREATE_BANK_RECONCILE_REQUEST', createBankReconcile),
    yield takeEvery('STORE_BANK_RECONCILE_REQUEST', storeBankReconcile),
    yield takeEvery('EDIT_BANK_RECONCILE_REQUEST', editBankReconcile),
    yield takeEvery('UPDATE_BANK_RECONCILE_REQUEST', updateBankReconcile),
    yield takeEvery('SHOW_BANK_RECONCILE_REQUEST', showBankReconcile),
    yield takeEvery('UPLOAD_BANK_RECONCILE_DOCUMENT_REQUEST', uploadBankReconcileDocument),
    yield takeEvery('DOWNLOAD_BANK_RECONCILE_DOCUMENT_REQUEST', downloadBankReconcileDocument),
    yield takeEvery('DELETE_BANK_RECONCILE_DOCUMENT_REQUEST', deleteBankReconcileDocument),

  ]);
}
