import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getItemRequests({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [itemRequests, foreign] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(
              get,
              "inventories/item-requests?search=" + search + "&page=" + page
            ),
            call(get, "inventories/item-requests/foreign-keys")
          ])
        : // Jika filter
          yield all([
            call(get, "inventories/item-requests?" + filter + "&page=" + page),
            call(get, "inventories/item-requests/foreign-keys")
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "inventories/item-requests?search=" + search),
          call(get, "inventories/item-requests/foreign-keys")
        ])
      : // Jika tidak ada
        yield all([
          call(get, "inventories/item-requests?" + filter),
          call(get, "inventories/item-requests/foreign-keys")
        ]);

    // console.log(itemRequests);

    // Jika sukses get data itemRequests
    if (itemRequests.status === "success") {
      // Jika di API menggunakan pagination
      if (itemRequests.result.current_page && search) {
        yield put({
          type: "GET_ITEM_REQUESTS_SUCCESS",
          itemRequests: itemRequests.result.data,
          current_page: itemRequests.result.current_page,
          page_size: itemRequests.result.per_page,
          total: itemRequests.result.total,
          search: search
        });
      } else if (itemRequests.result.current_page && filter) {
        yield put({
          type: "GET_ITEM_REQUESTS_SUCCESS",
          itemRequests: itemRequests.result.data,
          current_page: itemRequests.result.current_page,
          page_size: itemRequests.result.per_page,
          total: itemRequests.result.total,
          filter: filter
        });
      } else if (itemRequests.result.current_page) {
        yield put({
          type: "GET_ITEM_REQUESTS_SUCCESS",
          itemRequests: itemRequests.result.data,
          current_page: itemRequests.result.current_page,
          page_size: itemRequests.result.per_page,
          total: itemRequests.result.total
        });
      } else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: "GET_ITEM_REQUESTS_SUCCESS",
          itemRequests: itemRequests.result,
          page_size: null,
          total: null,
          filter: filter
        });
      }
    } else if (itemRequests.status === "fail") {
      yield put({
        type: "GET_ITEM_REQUESTS_ERROR",
        error: { message: itemRequests.message }
      });
    } else {
      // Set message ke session storage
      yield put({
        type: "GET_ITEM_REQUESTS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }

    if (foreign.status === "success") {
      yield put({
        type: "GET_ITEM_REQUEST_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        departments: foreign.result.departments,
        projects: foreign.result.projects
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_ITEM_REQUEST_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_ITEM_REQUEST_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_ITEM_REQUESTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Get Items data
export function* getItems({ payload }) {
  const { id_location, search } = payload;
  let filter = "items?is_active=1&limit=1";
  filter = search
    ? `${filter}&search=${search}&id_location=${id_location}&item_type=1,2,4`
    : `${filter}&id_location=${id_location}&item_type=1,2,4`;

  try {
    const items = yield call(get, filter);

    if (items.status === "success") {
      yield put({
        type: "GET_FILTER_ITEMS_RQ_SUCCESS",
        items: items.result
      });
    } else if (items.status === "fail") {
      yield put({
        type: "GET_FILTER_ITEMS_RQ_ERROR",
        error: items.message
      });
    } else {
      yield put({
        type: "GET_FILTER_ITEMS_RQ_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_FILTER_ITEMS_RQ_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createItemRequest() {
  try {
    const [transactions, foreign] = yield all([
      call(get, "inventories/item-requests?get_old_trans=1"),
      call(get, "inventories/item-requests/foreign-keys")
    ]);
  
    if (transactions.status === "success") {
      yield put({
        type: "GET_TRANSACTIONS_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_TRANSACTIONS_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_TRANSACTIONS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_ITEM_REQUEST_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        departments: foreign.result.departments,
        projects: foreign.result.projects
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_ITEM_REQUEST_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_ITEM_REQUEST_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_TRANSACTIONS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export function* getTransactions({ payload }) {
  const { page, search, filter } = payload;
  // console.log(filter);

  try {
    const transactions = filter
      ? yield call(
          get,
          "inventories/item-requests?get_old_trans=1&" + filter + "&page=" + page
        )
      : search
      ? yield call(
          get,
          "inventories/item-requests?get_old_trans=1&search=" +
            search +
            "&page=" +
            page
        )
      : yield call(
          get,
          "inventories/item-requests?get_old_trans=1" + "&page=" + page
        );

    if (transactions.status === "success") {
      yield put({
        type: "GET_TRANSACTIONS_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_TRANSACTIONS_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_TRANSACTIONS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_TRANSACTIONS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeItemRequest({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "inventories/item-requests", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: "STORE_ITEM_REQUEST_SUCCESS",
        success: { message: resultStore.message },
        id_item_request: resultStore.result.id_item_request
      });
    } else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_ITEM_REQUEST_ERROR",
        error: { message: resultStore.message }
      });
    } else if (resultStore.errors) {
      yield put({
        type: "STORE_ITEM_REQUEST_ERROR",
        error: resultStore
      });
    } else {
      yield put({
        type: "STORE_ITEM_REQUEST_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_ITEM_REQUEST_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editItemRequest({ payload }) {
  // console.log("masuk");
  const { id_item_request } = payload;

  try {
    const [itemRequest, transactions, foreign] = yield all([
      call(get, "inventories/item-requests/" + id_item_request),
      call(get, "inventories/item-requests?get_old_trans=1"),
      call(get, "inventories/item-requests/foreign-keys")
    ]);
  
    // console.log(itemRequest);
  
    // Jika sukses
    if (itemRequest.status === "success") {
      yield put({
        type: "EDIT_ITEM_REQUEST_SUCCESS",
        itemRequest: itemRequest.result
      });
    } else if (itemRequest.status === "fail") {
      yield put({
        type: "EDIT_ITEM_REQUEST_ERROR",
        error: { message: itemRequest.message }
      });
    } else {
      yield put({
        type: "EDIT_ITEM_REQUEST_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (transactions.status === "success") {
      yield put({
        type: "GET_TRANSACTIONS_SUCCESS",
        transactions: transactions.result.data,
        current_page: transactions.result.current_page,
        page_size: transactions.result.per_page,
        total: transactions.result.total
      });
    } else if (transactions.status === "fail") {
      yield put({
        type: "GET_TRANSACTIONS_ERROR",
        error: { message: transactions.message }
      });
    } else {
      yield put({
        type: "GET_TRANSACTIONS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    if (foreign.status === "success") {
      yield put({
        type: "GET_ITEM_REQUEST_FOREIGN_KEYS_SUCCESS",
        locations: foreign.result.locations,
        suppliers: foreign.result.suppliers,
        departments: foreign.result.departments,
        projects: foreign.result.projects
      });
    } else if (foreign.status === "fail") {
      yield put({
        type: "GET_ITEM_REQUEST_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      yield put({
        type: "GET_ITEM_REQUEST_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_TRANSACTIONS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateItemRequest({ payload }) {
  const { id_item_request, body } = payload;
  // console.log(body);

  try {
    const resultUpdate = yield call(
      putApi,
      "inventories/item-requests/" + id_item_request,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: "UPDATE_ITEM_REQUEST_SUCCESS",
          success: { message: resultUpdate.message },
          id_item_request: id_item_request
        }),
        yield put({
          type: "EDIT_ITEM_REQUEST_REQUEST",
          payload: { id_item_request: id_item_request }
        })
      ]);
    } else if (resultUpdate.errors) {
      yield all([
        yield put({
          type: "UPDATE_ITEM_REQUEST_ERROR",
          error: resultUpdate
        }),
        yield put({
          type: "EDIT_ITEM_REQUEST_REQUEST",
          payload: { id_item_request: id_item_request }
        })
      ]);
    } else if (resultUpdate.status === "fail") {
      yield all([
        yield put({
          type: "UPDATE_ITEM_REQUEST_ERROR",
          error: { message: resultUpdate.message }
        }),
        yield put({
          type: "EDIT_ITEM_REQUEST_REQUEST",
          payload: { id_item_request: id_item_request }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPDATE_ITEM_REQUEST_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_ITEM_REQUEST_REQUEST",
          payload: { id_item_request: id_item_request }
        })
      ]);
    }
  } catch (error) {
    yield all([
      yield put({
        type: "UPDATE_ITEM_REQUEST_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_ITEM_REQUEST_REQUEST",
        payload: { id_item_request: id_item_request }
      })
    ]);
    bugsnagClient.notify(error)
  }
}

