export function* confirmItemRequest({ payload }) {
  const { id_item_request } = payload;

  try {
    // POST data ke API
    const confirmResult = yield call(
      putApi,
      "inventories/item-requests/request-transaction/" + id_item_request
    );

    // Jika sukses
    if (confirmResult.status === "success") {
      // Set session message
      yield all([
        put({
          type: "CONFIRM_ITEM_REQUEST_SUCCESS",
          success: { message: confirmResult.message }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_ITEM_REQUEST_REQUEST",
          payload: { id_item_request: id_item_request }
        })
      ]);
    } else if (confirmResult.status === "fail") {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_ITEM_REQUEST_ERROR",
          error: { message: confirmResult.message }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_ITEM_REQUEST_REQUEST",
          payload: { id_item_request: id_item_request }
        })
      ]);
    } else {
      // Jika gagal set session message
      yield all([
        put({
          type: "CONFIRM_ITEM_REQUEST_ERROR",
          error: { message: "Check your internet connection" }
        }),
        // Ulangi GET data untuk refresh
        put({
          type: "EDIT_ITEM_REQUEST_REQUEST",
          payload: { id_item_request: id_item_request }
        })
      ]);
    }
  } catch (error) {
    yield all([
      put({
        type: "CONFIRM_ITEM_REQUEST_ERROR",
        error: { message: "Check your internet connection" }
      }),
      // Ulangi GET data untuk refresh
      put({
        type: "EDIT_ITEM_REQUEST_REQUEST",
        payload: { id_item_request: id_item_request }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

