// Upload Document
export function* uploadItemRequestDocument({ payload }) {
  const { id_item_request, body } = payload;
  let data = {
    ...body,
    id_item_request
  };

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "inventories/item-requests/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: "UPLOAD_ITEM_REQUEST_DOCUMENT_SUCCESS",
          success: uploadResult
        }),
        yield put({
          type: "EDIT_ITEM_REQUEST_REQUEST",
          payload: { id_item_request }
        })
      ]);
    } else if (uploadResult.errors) {
      yield all([
        yield put({
          type: "UPLOAD_ITEM_REQUEST_DOCUMENT_ERROR",
          error: { message: uploadResult.errors.document[0] }
        }),
        yield put({
          type: "EDIT_ITEM_REQUEST_REQUEST",
          payload: { id_item_request }
        })
      ]);
    } else if (uploadResult.status === "fail") {
      yield all([
        yield put({
          type: "UPLOAD_ITEM_REQUEST_DOCUMENT_ERROR",
          error: { message: uploadResult.message }
        }),
        yield put({
          type: "EDIT_ITEM_REQUEST_REQUEST",
          payload: { id_item_request }
        })
      ]);
    } else {
      yield all([
        yield put({
          type: "UPLOAD_ITEM_REQUEST_DOCUMENT_ERROR",
          error: { message: "Check your internet connection" }
        }),
        yield put({
          type: "EDIT_ITEM_REQUEST_REQUEST",
          payload: { id_item_request }
        })
      ]);
    }

    // }
  } catch (error) {
    yield all([
      yield put({
        type: "UPLOAD_ITEM_REQUEST_DOCUMENT_ERROR",
        error: { message: "Check your internet connection" }
      }),
      yield put({
        type: "EDIT_ITEM_REQUEST_REQUEST",
        payload: { id_item_request }
      })
    ]);
    bugsnagClient.notify(error);
  }
}

