export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_ITEM_REQUESTS_REQUEST", getItemRequests),
    yield takeEvery("CREATE_ITEM_REQUEST_REQUEST", createItemRequest),
    yield takeEvery("GET_TRANSACTIONS_REQUEST", getTransactions),
    yield takeEvery("GET_FILTER_ITEMS_RQ_REQUEST", getItems),
    yield takeEvery("STORE_ITEM_REQUEST_REQUEST", storeItemRequest),
    yield takeEvery("EDIT_ITEM_REQUEST_REQUEST", editItemRequest),
    yield takeEvery("UPDATE_ITEM_REQUEST_REQUEST", updateItemRequest),
    yield takeEvery("SHOW_ITEM_REQUEST_REQUEST", showItemRequest),
    yield takeEvery("CONFIRM_ITEM_REQUEST_REQUEST", confirmItemRequest),
    yield takeEvery("CLOSE_ITEM_REQUEST_REQUEST", closeItemRequest),
    yield takeEvery(
      "UPLOAD_ITEM_REQUEST_DOCUMENT_REQUEST",
      uploadItemRequestDocument
    ),
    yield takeEvery(
      "DOWNLOAD_ITEM_REQUEST_DOCUMENT_REQUEST",
      downloadItemRequestDocument
    ),
    yield takeEvery(
      "DELETE_ITEM_REQUEST_DOCUMENT_REQUEST",
      deleteItemRequestDocument
    )
  ]);
}
