import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getCurrencies({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [currencies] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "configuration-managements/currencies?search=" + search + "&page=" + page),
          ])
        : // Jika filter
          yield all([
            call(get, "configuration-managements/currencies?" + filter + "&page=" + page), 
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "configuration-managements/currencies?search=" + search),  
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "configuration-managements/currencies?" + filter),
        ]);

    // console.log(currencies);
    // console.log(accounts);

    // Jika sukses get data currencies
    if (currencies.status === "success") {
      // Jika di API menggunakan pagination
      if (currencies.result.current_page && search) {
        yield put({
          type: 'GET_CURRENCIES_SUCCESS',
          currencies: currencies.result.data,
          current_page: currencies.result.current_page,
          page_size: currencies.result.per_page,
          total: currencies.result.total,
          search: search
        });
      }
      else if(currencies.result.current_page && filter)
      {
        yield put({
          type: 'GET_CURRENCIES_SUCCESS',
          currencies: currencies.result.data,
          current_page: currencies.result.current_page,
          page_size: currencies.result.per_page,
          total: currencies.result.total,
          filter: filter
        });
      }
      else if (currencies.result.current_page) {
        yield put({
          type: 'GET_CURRENCIES_SUCCESS',
          currencies: currencies.result.data,
          current_page: currencies.result.current_page,
          page_size: currencies.result.per_page,
          total: currencies.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_CURRENCIES_SUCCESS',
          currencies: currencies.result,
          page_size: null,
          total: null
        });
      }
    } else if (currencies.status === "fail") {
      yield put({
        type: 'GET_CURRENCIES_ERROR',
        error: currencies
      })
    } else {
      yield put({
        type: 'GET_CURRENCIES_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'GET_CURRENCIES_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Create data
export function* createCurrency() {
  try {
    const [check_is_used, accounts] = yield all([
      call(get, "configuration-managements/currencies/check-is-used"),
      call(get, "accounts?is_active=child"),
    ]);
  
    // Jika sukses get data check_is_used
    if (check_is_used.status === "success") {
      yield put({
        type: 'GET_CHECK_IS_USED_SUCCESS',
        check_is_used: check_is_used.result
      });
    }
  
    // Jika sukses get data account receivable
    if (accounts.status === "success") {
      yield put({
        type: 'GET_FILTER_ACCOUNTS_SUCCESS',
        accounts: accounts.result
      });
    }
    else{
      yield put({
        type: 'GET_FILTER_ACCOUNTS_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  
    yield put({
      type: 'CREATE_CURRENCY_SUCCESS',
    });
  } catch (error) {
    yield put({
      type: 'GET_FILTER_ACCOUNTS_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeCurrency({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "configuration-managements/currencies", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_CURRENCY_SUCCESS',
        success: resultStore
      });
    }
    else if (resultStore.status === "fail" || resultStore.errors) {
      yield put({
        type: "STORE_CURRENCY_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_CURRENCY_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_CURRENCY_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editCurrency({ payload }) {
  const { id_currency } = payload;

  try {
    const [check_is_used , accounts, currency] = yield all([
      call(get, "configuration-managements/currencies/check-is-used"),
      call(get, "accounts?is_active=child"),
      call(get, "configuration-managements/currencies/" + id_currency),
    ]);
  
    // Jika sukses get data check_is_used
    if (check_is_used.status === "success") {
      yield put({
        type: 'GET_CHECK_IS_USED_SUCCESS',
        check_is_used: check_is_used.result
      });
    }
  
    if (accounts.status === "success") {
      yield put({
        type: 'GET_FILTER_ACCOUNTS_SUCCESS',
        accounts: accounts.result
      });
    }
  
    // Jika sukses
    if (currency.status === "success") {
      yield put({
        type: 'EDIT_CURRENCY_SUCCESS',
        currency: currency.result
      });
    }
    else{
      yield put({
        type: 'EDIT_CURRENCY_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'EDIT_CURRENCY_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateCurrency({ payload }) {
  const { id_currency, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "configuration-managements/currencies/" + id_currency,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield put({
        type: 'UPDATE_CURRENCY_SUCCESS',
        success: {message: "Successfully update Currency"}
      });
    }
    else if (resultUpdate.status === "fail" || resultUpdate.errors) {
      yield put({
        type: "UPDATE_CURRENCY_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_CURRENCY_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_CURRENCY_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Show data
export function* showCurrency({ payload }) {
  const { id_currency } = payload;

  try {
    const resultShow = yield call(get, "configuration-managements/currencies/" + id_currency);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_CURRENCY_SUCCESS',
        currency: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deactivateCurrency({ payload }) {
  const { id_currency } = payload;
  const body = { is_active: 0 };

  try {
    // POST data ke API
    const deactivateResult = yield call(
      putApi,
      "configuration-managements/currencies/" + id_currency,
      body
    );

    // Jika sukses
    if (deactivateResult.status === "success") {
      yield all([
        yield put({
          type: 'DEACTIVATE_CURRENCY_SUCCESS',
          success: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCIES_REQUEST',
          payload: { page: false }
        })
      ])
    } 
    else if (deactivateResult.status === "fail") {
      yield all([
        yield put({
          type: 'DEACTIVATE_CURRENCY_ERROR',
          error: deactivateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCIES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'DEACTIVATE_CURRENCY_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCIES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'DEACTIVATE_CURRENCY_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_CURRENCIES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error)
  }
}

// Activate data (mengubah is_active => 1)
export function* activateCurrency({ payload }) {
  const { id_currency } = payload;
  const body = { is_active: 1 };

  try {
    // POST ke API
    const activateResult = yield call(
      putApi,
      "configuration-managements/currencies/" + id_currency,
      body
    );

    // Jika sukses
    if (activateResult.status === "success") {
      yield all([
        yield put({
          type: 'ACTIVATE_CURRENCY_SUCCESS',
          success: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCIES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else if (activateResult.status === "fail") {
      yield all([
        yield put({
          type: 'ACTIVATE_CURRENCY_ERROR',
          error: activateResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCIES_REQUEST',
          payload: { page: false }
        })
      ])
    }
    else {
      yield all([
        yield put({
          type: 'ACTIVATE_CURRENCY_ERROR',
          error: {message: "Check your internet connection"}
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'GET_CURRENCIES_REQUEST',
          payload: { page: false }
        })
      ])
    }
  } catch (error) {
    yield all([
      yield put({
        type: 'ACTIVATE_CURRENCY_ERROR',
        error: {message: "Check your internet connection"}
      }),
      // Ulangi GET data untuk refresh
      yield put({
        type: 'GET_CURRENCIES_REQUEST',
        payload: { page: false }
      })
    ])
    bugsnagClient.notify(error);
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_CURRENCIES_REQUEST', getCurrencies),
    yield takeEvery('CREATE_CURRENCY_REQUEST', createCurrency),
    yield takeEvery('STORE_CURRENCY_REQUEST', storeCurrency),
    yield takeEvery('EDIT_CURRENCY_REQUEST', editCurrency),
    yield takeEvery('UPDATE_CURRENCY_REQUEST', updateCurrency),
    yield takeEvery('SHOW_CURRENCY_REQUEST', showCurrency),
    yield takeEvery('DEACTIVATE_CURRENCY_REQUEST', deactivateCurrency),
    yield takeEvery('ACTIVATE_CURRENCY_REQUEST', activateCurrency)
  ]);
}
