import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getPurchaseReturnReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/purchase-returns?";
  if(search){
    url += "search=" + search  
  }
  if(filter){
    search
      ? url += "&" + filter
      : url += filter  
  }
  if(page){
    search || filter
      ? url += "&page=" + page
      : url += "page=" + page 
  }

  try {
    if(filter){
      const [purchase_return_reports, foreign_keys] = 
      yield all([
        call(get, url),
        call(get, "reports/purchase-returns/foreign-keys"),
      ])
  
      // Jika sukses get data purchase_return_reports
      if (purchase_return_reports.status === "success") {
        yield put({
          type: 'GET_PURCHASE_RETURN_REPORTS_SUCCESS',
          purchase_return_reports: purchase_return_reports.result.details,
          total: purchase_return_reports.result.summaries.total,
          total_amount: purchase_return_reports.result.summaries.total_amount,
          total_discount: purchase_return_reports.result.summaries.total_discount,
          total_qty: purchase_return_reports.result.summaries.total_qty,
          total_tax: purchase_return_reports.result.summaries.total_tax,
          total_item: purchase_return_reports.result.summaries.total_item,
          filter: filter,
          search: search
        });
      } else if (purchase_return_reports.status === "fail") {
        yield put({
          type: 'GET_PURCHASE_RETURN_REPORTS_ERROR', 
          error: purchase_return_reports
        })
      } else {
        yield put({
          type: 'GET_PURCHASE_RETURN_REPORTS_ERROR', 
          error: {message: "Check your internet connection"}
        })
      }
  
      // Jika sukses get data purchase_return_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: 'GET_PURCHASE_RETURN_REPORT_FOREIGN_KEYS_SUCCESS',
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          base_currency: foreign_keys.result.base_currency,
          messages: foreign_keys.result.messages,
        });
      }
      else{
        yield put({
          type: 'GET_PURCHASE_RETURN_REPORT_FOREIGN_KEYS_ERROR',
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [ foreign_keys] = 
      yield all([
        call(get, "reports/purchase-returns/foreign-keys"),
      ])
  
      yield put({
        type: 'GET_PURCHASE_RETURN_REPORTS_SUCCESS',
        purchase_return_reports: [],
        total: null,
        total_amount: null,
        total_discount: null,
        total_qty: null,
        total_tax: null,
        total_item: null,
      });
  
      // Jika sukses get data purchase_return_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: 'GET_PURCHASE_RETURN_REPORT_FOREIGN_KEYS_SUCCESS',
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          departments: foreign_keys.result.departments,
          projects: foreign_keys.result.projects,
          base_currency: foreign_keys.result.base_currency,
          messages: foreign_keys.result.messages,
        });
      }
      else if (foreign_keys.status === "fail") {
        yield put({
          type: 'GET_PURCHASE_RETURN_REPORT_FOREIGN_KEYS_ERROR',
          error:foreign_keys
        });
      } else{
        yield put({
          type: 'GET_PURCHASE_RETURN_REPORT_FOREIGN_KEYS_ERROR',
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: 'GET_PURCHASE_RETURN_REPORTS_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}



export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_PURCHASE_RETURN_REPORTS_REQUEST', getPurchaseReturnReports),
  ]);
}
