import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

export function* getCostToSalesRatioByItemReports({ payload }) {
  const { filter } = payload;

  try {
    if (filter) {
      const [cost_to_sales_ratio_by_item_reports] = yield all([
        call(get, "reports/item-summaries/item-cost-sales?" + filter),
        // call(get, "reports/item-summaries/item-cost-sales/foreign-keys")
      ])
  
      if (cost_to_sales_ratio_by_item_reports.status === "success") {
        yield put({
          type: "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORTS_SUCCESS",
          cost_to_sales_ratio_by_item_reports: cost_to_sales_ratio_by_item_reports.result,
        });
  
      } else if (cost_to_sales_ratio_by_item_reports.status === "fail") {
        yield put({
          type: "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORTS_ERROR",
          error: { message: cost_to_sales_ratio_by_item_reports.message }
        });
      } else {
        yield put({
          type: "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    } else {
      const [foreign_keys] = yield all([
        call(get, "reports/item-summaries/cost-sales/foreign-keys")
      ]);
  
      yield put({
        type: "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORTS_SUCCESS",
        cost_to_sales_ratio_by_item_reports: [],
      });
  
      // Jika sukses get data credit_memo_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORT_FOREIGN_KEYS_SUCCESS",
          item_cost_sales_sort_by_list: foreign_keys.result.item_cost_sales_sort_by_list,
          locations: foreign_keys.result.locations
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_COST_TO_SALES_RATIO_BY_ITEM_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_COST_TO_SALES_RATIO_BY_ITEM_REPORTS_REQUEST", getCostToSalesRatioByItemReports)
  ])
}