const initState = {
  trial_balance_department_reports: [],
  locations: [],
  departments: [],
  gl_types: [],
  filter: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function trialBalanceDepartmentReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_TRIAL_BALANCE_DEPARTMENT_REPORTS_REQUEST": {
      return {
        ...state,
        //trial_balance_department_reports: [],
        loading: true,
        filter: action.payload.filter,
        errorField: null
      };
    }
    case "GET_TRIAL_BALANCE_DEPARTMENT_REPORTS_SUCCESS": {
      return {
        ...state,
        trial_balance_department_reports:
          action.trial_balance_department_reports,
        // filter: action.filter,
        loading: false
      };
    }
    case "GET_TRIAL_BALANCE_DEPARTMENT_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CREATE_TRIAL_BALANCE_DEPARTMENT_REPORT_REQUEST": {
      return {
        ...state,
        trial_balance_department_report: null,
        errorField: null
      };
    }

    case "GET_TRIAL_BALANCE_DEPARTMENT_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        locations: action.locations,
        departments: action.departments,
        gl_types: action.gl_types
      };
    }
    case "GET_TRIAL_BALANCE_DEPARTMENT_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
