const initState = {
  id_payable_payment: null,
  id_location: null,
  id_supplier: null,
  payablePayments: [],
  payablePayment: null,
  printData: {},
  items: [],
  prevTransConfirm: {},
  transactions: [],
  debitMemos: [],
  prevTrans: [],
  locations: [],
  suppliers: [],
  accounts: [],
  departments: [],
  projects: [],
  currencies: [],
  cashBanks: [],
  cashFlowTypes: [],
  messages: [],
  search: null,
  filter: null,
  loading: false,
  loadingForeign: false,
  loadingSubmit: false,
  loadingTransactions: false,
  loadingDebitMemos: false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function payablePaymentsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_PAYABLE_PAYMENTS_REQUEST": {
      return {
        ...state,
        //payablePayments: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null
      };
    }
    case "GET_PAYABLE_PAYMENTS_SUCCESS": {
      return {
        ...state,
        payablePayments: action.payablePayments,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_PAYABLE_PAYMENTS_ERROR": {
      return {
        ...state,
        search: action.search,
        filter: action.filter,
        loading: false,
        error: action.error.message
      };
    }
    case "GET_PAYABLE_PAYMENT_FOREIGN_KEYS_REQUEST": {
      return {
        ...state,
        loadingForeign: true
      };
    }
    case "GET_PAYABLE_PAYMENT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loadingForeign: false,
        locations: action.locations,
        suppliers: action.suppliers,
        currencies: action.currencies,
        accounts: action.accounts,
        departments: action.departments,
        projects: action.projects,
        cashBanks: action.cashBanks,
        cashFlowTypes: action.cashFlowTypes,
        messages: action.messages
      };
    }
    case "GET_PAYABLE_PAYMENT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loadingForeign: false,
        error: action.error.message
      };
    }
    case "CREATE_PAYABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        id_payable_payment: null,
        id_location: null,
        payablePayment: null,
        itemDiscount: [],
        loadingTransactions: true,
        loadingPurchaseReceipts: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null
      };
    }
    case "GET_FILTER_PURCHASE_INVOICES_PP_REQUEST": {
      return {
        ...state,
        loadingTransactions: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page
      };
    }
    case "GET_FILTER_PURCHASE_INVOICES_PP_SUCCESS": {
      return {
        ...state,
        loadingTransactions: false,
        transactions: action.transactions,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total
      };
    }
    case "GET_FILTER_PURCHASE_INVOICES_PP_ERROR": {
      return {
        ...state,
        loadingTransactions: false,
        error: action.error.message
      };
    }
    case "GET_FILTER_DEBIT_MEMOS_PP_REQUEST": {
      return {
        ...state,
        loadingDebitMemos: true
      };
    }
    case "GET_FILTER_DEBIT_MEMOS_PP_SUCCESS": {
      return {
        ...state,
        loadingDebitMemos: false,
        debitMemos: action.debitMemos
      };
    }
    case "GET_FILTER_DEBIT_MEMOS_PP_ERROR": {
      return {
        ...state,
        loadingDebitMemos: false,
        error: action.error.message
      };
    }
    case "STORE_PAYABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "STORE_PAYABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_payable_payment: action.id_payable_payment
      };
    }
    case "STORE_PAYABLE_PAYMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        errorField: action.error.errors,
        error: action.error.message
      };
    }
    case "EDIT_PAYABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loading: true,
        id_payable_payment: null,
        id_location: null,
        loadingTransactions: true,
        loadingPurchaseReceipts: true,
        loadingForeign: true,
        errorField: null,
        current_page: 1,
        page_size: null,
        total: null
      };
    }
    case "EDIT_PAYABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        loadingSubmit: false,
        payablePayment: action.payablePayment
      };
    }
    case "EDIT_PAYABLE_PAYMENT_ERROR": {
      return {
        ...state,
        // loading: false,
        // loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPDATE_PAYABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true,
        errorField: null
      };
    }
    case "UPDATE_PAYABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message,
        id_payable_payment: action.id_payable_payment
      };
    }
    case "UPDATE_PAYABLE_PAYMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case "SHOW_PAYABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loading: true,
        errorField: null
      };
    }
    case "SHOW_PAYABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        payablePayments: action.payablePayments
      };
    }
    case "CONFIRM_PAYABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CONFIRM_PAYABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CONFIRM_PAYABLE_PAYMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "CANCEL_PAYABLE_PAYMENT_REQUEST": {
      return {
        ...state,
        loadingSubmit: true
      };
    }
    case "CANCEL_PAYABLE_PAYMENT_SUCCESS": {
      return {
        ...state,
        loadingSubmit: false,
        success: action.success.message
      };
    }
    case "CANCEL_PAYABLE_PAYMENT_ERROR": {
      return {
        ...state,
        loadingSubmit: false,
        error: action.error.message
      };
    }
    case "UPLOAD_PAYABLE_PAYMENT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "UPLOAD_PAYABLE_PAYMENT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "UPLOAD_PAYABLE_PAYMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DOWNLOAD_PAYABLE_PAYMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "DELETE_PAYABLE_PAYMENT_DOCUMENT_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DELETE_PAYABLE_PAYMENT_DOCUMENT_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DELETE_PAYABLE_PAYMENT_DOCUMENT_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "SET_PRINT_DATA_PAYABLE_PAYMENT": {
      return {
        ...state,
        printData: action.payload.body
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
