import { all, takeEvery, takeLatest, put, call } from "redux-saga/effects";
import { get, put as putApi, post } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

export function* getAccounts({ payload }) {
  // Cek apakah ada pagination di request atau tidak
  const { id_account_type } = payload;

  let accounts, accountList, foreign;
  try {
    if (id_account_type) {
      [accounts, accountList, foreign] = yield all([
        call(get, "accounts?children=1&id_account_type=" + id_account_type),
        call(get, "accounts?children=0&id_account_type=" + id_account_type),
        call(get, "accounts/foreign-keys")
      ]);
    } else {
      [accounts, accountList, foreign] = yield all([
        call(get, "accounts?children=1"),
        call(get, "accounts?children=0"),
        call(get, "accounts/foreign-keys")
      ]);
    }
  
    // Jika sukses get data account
    if (accounts.status === "success") {
      yield put({
        type: "GET_ACCOUNTS_SUCCESS",
        accounts: accounts.result
      });
    } else if (accounts.status === "fail") {
      // Set message ke reducer
      yield put({
        type: "GET_ACCOUNTS_ERROR",
        error: { message: accounts.message }
      });
    } else {
      // Set message ke reducer
      yield put({
        type: "GET_ACCOUNTS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    // Jika sukses get data account list
    if (accountList.status === "success") {
      yield put({
        type: "GET_ACCOUNT_LIST_SUCCESS",
        accountList: accountList.result
      });
    } else if (accountList.status === "fail") {
      // Set message ke reducer
      yield put({
        type: "GET_ACCOUNT_LIST_ERROR",
        error: { message: accountList.message }
      });
    } else {
      // Set message ke reducer
      yield put({
        type: "GET_ACCOUNT_LIST_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  
    // Jika sukses get data account types
    if (foreign.status === "success") {
      yield put({
        type: "GET_ACCOUNT_FOREIGN_KEYS_SUCCESS",
        accountTypes: foreign.result.account_types,
        currencies: foreign.result.currencies,
        locations: foreign.result.locations,
        cashFlowTypes: foreign.result.cash_flow_types
      });
    } else if (foreign.status === "fail") {
      // Set message ke reducer
      yield put({
        type: "GET_ACCOUNT_FOREIGN_KEYS_ERROR",
        error: { message: foreign.message }
      });
    } else {
      // Set message ke reducer
      yield put({
        type: "GET_ACCOUNT_FOREIGN_KEYS_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "GET_ACCOUNTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
}

export function* storeAccount({ payload }) {
  const { body } = payload;

  try {
    const account = yield call(post, "accounts", body);

    // Jika sukses get data account
    if (account.status === "success") {
      yield all([
        put({
          type: "STORE_ACCOUNT_SUCCESS",
          account: account.result,
          success: { message: account.message }
        }),
        // put({
        //   type: "GET_ACCOUNTS_REQUEST",
        //   payload: { id_account_type: null }
        // }),
        put({
          type: "SHOW_ACCOUNT_REQUEST",
          payload: { id_account: account.result.id_account }
        })
      ]);
    } else if (account.errors) {
      yield put({
        type: "STORE_ACCOUNT_ERROR",
        error: account
      });
    } else if (account.status === "fail") {
      // Set message ke reducer
      yield put({
        type: "STORE_ACCOUNT_ERROR",
        error: { message: account.message }
      });
    } else {
      // Set message ke reducer
      yield put({
        type: "STORE_ACCOUNT_ERROR",
        error: { message: "Check your internet connection" }
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_ACCOUNT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error);
  }
  
}

export function* showAccount({ payload }) {
  const { id_account } = payload;

  try {
    const account = yield call(get, "accounts/" + id_account);

    // Jika sukses get data account
    if (account.status === "success") {
      yield put({
        type: "SHOW_ACCOUNT_SUCCESS",
        account: account.result
      });
    } else if (account.errors) {
      // Set message ke reducer
      yield put({ type: "SHOW_ACCOUNT_ERROR", error: account });
    } else if (account.status === "fail") {
      // Set message ke reducer
      yield put({
        type: "SHOW_ACCOUNT_ERROR",
        error: { message: account.message }
      });
    } else {
      // Set message ke reducer
      yield put({
        type: "SHOW_ACCOUNT_ERROR",
        error: { message: "Check your internet connection" }
      });
    } 
  } catch (error) {
    yield put({
      type: "SHOW_ACCOUNT_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

