const initState = {
  delivery_order_detail_reports: [],
  total_qty_order: null,
  total_qty_delivered: null,
  total_item: null,
  locations: [],
  customers: [],
  departments: [],
  projects: [],
  base_currency: null,
  messages: [],
  search: null,
  filter: null,
  filter_select: [],
  loading: false,
  // current_page: 1,
  // page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null
};

export default function deliveryOrderDetailReportsReducer(
  state = initState,
  action
) {
  switch (action.type) {
    case "GET_DELIVERY_ORDER_DETAIL_REPORTS_REQUEST": {
      return {
        ...state,
        //delivery_order_detail_reports: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        errorField: null
      };
    }
    case "GET_DELIVERY_ORDER_DETAIL_REPORTS_SUCCESS": {
      return {
        ...state,
        delivery_order_detail_reports: action.delivery_order_detail_reports,
        // current_page: action.current_page,
        // page_size: action.page_size,
        search: action.search,
        filter: action.filter,
        loading: false
      };
    }
    case "GET_DELIVERY_ORDER_DETAIL_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "CREATE_DELIVERY_ORDER_DETAIL_REPORT_REQUEST": {
      return {
        ...state,
        delivery_order_detail_report: null,
        errorField: null
      };
    }

    case "GET_DELIVERY_ORDER_DETAIL_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
        customers: action.customers,
        departments: action.departments,
        projects: action.projects,
        base_currency: action.base_currency,
        messages: action.messages
      };
    }
    case "GET_DELIVERY_ORDER_DETAIL_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
