
const initState = {
  racks: [],
  rack: null,
  locations: [],
  search: null,
  filter: null,
  loading: false,
  loadingEdit:false,
  current_page: 1,
  page_size: null,
  total: null,
  success: null,
  error: null,
  errorField: null,
};

export default function racksReducer(state = initState, action) {
  switch (action.type) {
    case 'GET_RACKS_REQUEST': {
      return {
        ...state,
        //racks: [],
        loading: true,
        search: action.payload.search,
        filter: action.payload.filter,
        current_page: action.payload.page,
        errorField: null,
      };
    }
    case 'GET_RACKS_SUCCESS': {
      return {
        ...state,
        racks: action.racks,
        current_page: action.current_page,
        page_size: action.page_size,
        total: action.total,
        search: action.search,
        filter: action.filter,
        loading: false,
      };
    }
    case 'GET_RACKS_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message
      }
    }
    case 'CREATE_RACK_REQUEST': {
      return {
        ...state,
        rack: null,
        errorField: null,
      };
    }

    case 'GET_RACK_FOREIGN_KEYS_SUCCESS': {
      return {
        ...state,
        locations: action.locations
      };
    }
    case 'GET_RACK_FOREIGN_KEYS_ERROR': {
      return {
        ...state,
        error: action.error.message
      };
    }
    
    case 'STORE_RACK_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'STORE_RACK_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'STORE_RACK_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'EDIT_RACK_REQUEST': {
      return {
        ...state,
        loading: true,
        loadingEdit:true,
        errorField: null,
      };
    }
    case 'EDIT_RACK_SUCCESS': {
      return {
        ...state,
        loading: false,
        loadingEdit:false,
        rack: action.rack
      };
    }
    case 'EDIT_RACK_ERROR': {
      return {
        ...state,
        // loading: false,
        // loadingEdit:false,
        error: action.error.message
      };
    }
    case 'UPDATE_RACK_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'UPDATE_RACK_SUCCESS': {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case 'UPDATE_RACK_ERROR': {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errorField: action.error.errors
      };
    }
    case 'SHOW_RACK_REQUEST': {
      return {
        ...state,
        loading: true,
        errorField: null,
      };
    }
    case 'SHOW_RACK_SUCCESS': {
      return {
        ...state,
        loading: false,
        racks: action.racks
      };
    }
    case "DEACTIVATE_RACK_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "DEACTIVATE_RACK_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "DEACTIVATE_RACK_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "ACTIVATE_RACK_REQUEST": {
      return {
        ...state,
        loading: true
      };
    }
    case "ACTIVATE_RACK_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message
      };
    }
    case "ACTIVATE_RACK_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
