import { all, takeEvery, put, call } from "redux-saga/effects";
import { get, put as putApi, post, postForm } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getFixedAssetRevaluations({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [fixed_asset_revaluations, foreign_keys] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "fixed-assets/revaluations?search=" + search + "&page=" + page),
            call(get, "fixed-assets/revaluations/foreign-keys"),
          ])
        : // Jika filter
          yield all([
            call(get, "fixed-assets/revaluations?" + filter + "&page=" + page),
            call(get, "fixed-assets/revaluations/foreign-keys"),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "fixed-assets/revaluations?search=" + search),
          call(get, "fixed-assets/revaluations/foreign-keys"),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "fixed-assets/revaluations?" + filter),
          call(get, "fixed-assets/revaluations/foreign-keys"),
        ]);

    // console.log(fixed_asset_revaluations);
    // console.log(foreign_keys);

    // Jika sukses get data fixed_asset_revaluations
    if (fixed_asset_revaluations.status === "success") {
      // Jika di API menggunakan pagination
      if (fixed_asset_revaluations.result.current_page && search) {
        yield put({
          type: 'GET_FIXED_ASSET_REVALUATIONS_SUCCESS',
          fixed_asset_revaluations: fixed_asset_revaluations.result.data,
          current_page: fixed_asset_revaluations.result.current_page,
          page_size: fixed_asset_revaluations.result.per_page,
          total: fixed_asset_revaluations.result.total,
          search: search
        });
      }
      else if(fixed_asset_revaluations.result.current_page && filter)
      {
        yield put({
          type: 'GET_FIXED_ASSET_REVALUATIONS_SUCCESS',
          fixed_asset_revaluations: fixed_asset_revaluations.result.data,
          current_page: fixed_asset_revaluations.result.current_page,
          page_size: fixed_asset_revaluations.result.per_page,
          total: fixed_asset_revaluations.result.total,
          filter: filter
        });
      }
      else if (fixed_asset_revaluations.result.current_page) {
        yield put({
          type: 'GET_FIXED_ASSET_REVALUATIONS_SUCCESS',
          fixed_asset_revaluations: fixed_asset_revaluations.result.data,
          current_page: fixed_asset_revaluations.result.current_page,
          page_size: fixed_asset_revaluations.result.per_page,
          total: fixed_asset_revaluations.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_FIXED_ASSET_REVALUATIONS_SUCCESS',
          fixed_asset_revaluations: fixed_asset_revaluations.result,
          page_size: null,
          total: null
        });
      }
    } else if (fixed_asset_revaluations.status === "fail") {
      yield put({
        type: 'GET_FIXED_ASSET_REVALUATIONS_ERROR', 
        error: fixed_asset_revaluations
      })
    } else {
      yield put({
        type: 'GET_FIXED_ASSET_REVALUATIONS_ERROR', 
        error: {message: "Check your internet connection"}
      })
    }

    // Jika sukses get data fixed_asset_revaluation types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_FIXED_ASSET_REVALUATION_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        accounts: foreign_keys.result.accounts,
        sub_ledger_accounts: foreign_keys.result.sub_ledger_accounts,
        projects: foreign_keys.result.projects,
        departments: foreign_keys.result.departments,
        asset_types: foreign_keys.result.asset_types,  
        asset_categories: foreign_keys.result.asset_categories,
        asset_service_types: foreign_keys.result.asset_service_types,
        base_currency: foreign_keys.result.base_currency,
      });
    }
    else{
      yield put({
        type: 'GET_FIXED_ASSET_REVALUATION_FOREIGN_KEYS_ERROR',
        //error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_FIXED_ASSET_REVALUATIONS_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error);
  }
}

// Get all data
export function* getImportFixedAssets({ payload }) {
  const { page, search, filter } = payload;

  try {
    // Cek apakah ada pagination dan search di request atau tidak
    const [fixed_assets] = page
      ? search
        ? // Jika search dan pagination
          yield all([
            call(get, "fixed-assets?get_old_trans=1&search=" + search + "&page=" + page),
          ])
        : // Jika filter
          yield all([
            call(get, "fixed-assets?get_old_trans=1&" + filter + "&page=" + page),
          ])
      : search
      ? // Jika search
        yield all([
          call(get, "fixed-assets?get_old_trans=1&search=" + search),
        ])
      : // Jika tidak ada 
        yield all([
          call(get, "fixed-assets?get_old_trans=1&" + (filter ? "&" + filter : "")),
        ]);

    // console.log(fixed_assets);

    // Jika sukses get data fixed_assets
    if (fixed_assets.status === "success") {
      // Jika di API menggunakan pagination
      if (fixed_assets.result.current_page && search) {
        yield put({
          type: 'GET_IMPORT_FIXED_ASSETS_FAR_SUCCESS',
          fixed_assets: fixed_assets.result.data,
          current_page: fixed_assets.result.current_page,
          page_size: fixed_assets.result.per_page,
          total: fixed_assets.result.total,
          search: search
        });
      }
      else if(fixed_assets.result.current_page && filter)
      {
        yield put({
          type: 'GET_IMPORT_FIXED_ASSETS_FAR_SUCCESS',
          fixed_assets: fixed_assets.result.data,
          current_page: fixed_assets.result.current_page,
          page_size: fixed_assets.result.per_page,
          total: fixed_assets.result.total,
          filter: filter
        });
      }
      else if (fixed_assets.result.current_page) {
        yield put({
          type: 'GET_IMPORT_FIXED_ASSETS_FAR_SUCCESS',
          fixed_assets: fixed_assets.result.data,
          current_page: fixed_assets.result.current_page,
          page_size: fixed_assets.result.per_page,
          total: fixed_assets.result.total
        });
      }
      else {
        // Jika API tidak menggunakan pagination
        yield put({
          type: 'GET_IMPORT_FIXED_ASSETS_FAR_SUCCESS',
          fixed_assets: fixed_assets.result,
          page_size: null,
          total: null
        });
      }
    } else if (fixed_assets.status === "fail") {
      yield put({
        type: 'GET_IMPORT_FIXED_ASSETS_FAR_ERROR', 
        error: fixed_assets
      })
    } else {
      yield put({
        type: 'GET_IMPORT_FIXED_ASSETS_FAR_ERROR', 
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'GET_IMPORT_FIXED_ASSETS_FAR_ERROR', 
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}


// Create data
export function* createFixedAssetRevaluation() {
  try {
    const [foreign_keys] = yield all([
      call(get, "fixed-assets/revaluations/foreign-keys"),
    ]);
  
    // Jika sukses get data account
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_FIXED_ASSET_REVALUATION_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        accounts: foreign_keys.result.accounts,
        sub_ledger_accounts: foreign_keys.result.sub_ledger_accounts,
        projects: foreign_keys.result.projects,
        departments: foreign_keys.result.departments,
        asset_types: foreign_keys.result.asset_types,  
        asset_categories: foreign_keys.result.asset_categories,
        asset_service_types: foreign_keys.result.asset_service_types,
        base_currency: foreign_keys.result.base_currency,
      });
    }
    else{
      yield put({
        type: 'GET_FIXED_ASSET_REVALUATION_FOREIGN_KEYS_ERROR',
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: 'GET_FIXED_ASSET_REVALUATION_FOREIGN_KEYS_ERROR',
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Store data
export function* storeFixedAssetRevaluation({ payload }) {
  const { body } = payload;

  try {
    const resultStore = yield call(post, "fixed-assets/revaluations", body);
    // console.log(resultStore);
    if (resultStore.status === "success") {
      yield put({
        type: 'STORE_FIXED_ASSET_REVALUATION_SUCCESS',
        id_fixed_asset_revaluation: resultStore.result.id_fixed_asset_revaluation,
        success: resultStore
      });
    }
    else if (resultStore.status === "fail") {
      yield put({
        type: "STORE_FIXED_ASSET_REVALUATION_ERROR",
        error: resultStore
      });
    }
    else {
      yield put({
        type: "STORE_FIXED_ASSET_REVALUATION_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "STORE_FIXED_ASSET_REVALUATION_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Edit data
export function* editFixedAssetRevaluation({ payload }) {
  const { id_fixed_asset_revaluation } = payload;

  try {
    const [fixed_asset_revaluation, foreign_keys] = yield all([
      call(get, "fixed-assets/revaluations/" + id_fixed_asset_revaluation),
      call(get, "fixed-assets/revaluations/foreign-keys"),
    ]);
  
    // Jika sukses
    if (fixed_asset_revaluation.status === "success") {
      yield put({
        type: 'EDIT_FIXED_ASSET_REVALUATION_SUCCESS',
        fixed_asset_revaluation: fixed_asset_revaluation.result
      });
    }
    else{
      yield put({
        type: "EDIT_FIXED_ASSET_REVALUATION_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
    
    // Jika sukses get data fixed_asset_revaluation types
    if (foreign_keys.status === "success") {
      yield put({
        type: 'GET_FIXED_ASSET_REVALUATION_FOREIGN_KEYS_SUCCESS',
        locations: foreign_keys.result.locations,
        accounts: foreign_keys.result.accounts,
        sub_ledger_accounts: foreign_keys.result.sub_ledger_accounts,
        projects: foreign_keys.result.projects,
        departments: foreign_keys.result.departments,
        asset_types: foreign_keys.result.asset_types,  
        asset_categories: foreign_keys.result.asset_categories,
        asset_service_types: foreign_keys.result.asset_service_types,
        base_currency: foreign_keys.result.base_currency,
      });
    }
    else{
      yield put({
        type: 'GET_FIXED_ASSET_REVALUATION_FOREIGN_KEYS_ERROR',
        // error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "EDIT_FIXED_ASSET_REVALUATION_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  }
}

// Update data
export function* updateFixedAssetRevaluation({ payload }) {
  const { id_fixed_asset_revaluation, body } = payload;

  try {
    const resultUpdate = yield call(
      putApi,
      "fixed-assets/revaluations/" + id_fixed_asset_revaluation,
      body
    );
    // console.log(resultUpdate);
    if (resultUpdate.status === "success") {
      yield all([
        yield put({
          type: 'UPDATE_FIXED_ASSET_REVALUATION_SUCCESS',
          success: resultUpdate
        }),
        yield put({
          type: 'EDIT_FIXED_ASSET_REVALUATION_REQUEST',
          payload: { id_fixed_asset_revaluation }
        })
      ])
      
    }
    else if (resultUpdate.status === "fail") {
      yield put({
        type: "UPDATE_FIXED_ASSET_REVALUATION_ERROR",
        error: resultUpdate
      });
    }
    else {
      yield put({
        type: "UPDATE_FIXED_ASSET_REVALUATION_ERROR",
        error: {message: "Check your internet connection"}
      });
    }
  } catch (error) {
    yield put({
      type: "UPDATE_FIXED_ASSET_REVALUATION_ERROR",
      error: {message: "Check your internet connection"}
    });
    bugsnagClient.notify(error)
  } 
}

// Show data
export function* showFixedAssetRevaluation({ payload }) {
  const { id_fixed_asset_revaluation } = payload;

  try {
    const resultShow = yield call(get, "fixed-assets/revaluations/" + id_fixed_asset_revaluation);

    // Jika sukses
    if (resultShow.status === "success") {
      yield put({
        type: 'SHOW_FIXED_ASSET_REVALUATION_SUCCESS',
        fixed_asset_revaluation: resultShow.result
      });
    }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Decancel data (mengubah is_active => 0)
export function* processFixedAssetRevaluation({ payload }) {
  const { id_fixed_asset_revaluation } = payload;

  try {
    // POST data ke API
    const processResult = yield call(
      putApi,
      "fixed-assets/revaluations/process-transaction/" + id_fixed_asset_revaluation,
      // body
    );

    // Jika sukses
    if (processResult.status === "success") {
      yield all([
        yield put({
          type: 'PROCESS_FIXED_ASSET_REVALUATION_SUCCESS',
          success: processResult
        }),
        yield put({
          type: 'EDIT_FIXED_ASSET_REVALUATION_REQUEST',
          payload: { id_fixed_asset_revaluation }
        })
      ])
    }
    else if(processResult.status === "fail"){
      yield put({
        type: 'PROCESS_FIXED_ASSET_REVALUATION_ERROR',
        error: processResult
      })
    }
    else{
      yield put({
        type: 'PROCESS_FIXED_ASSET_REVALUATION_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'PROCESS_FIXED_ASSET_REVALUATION_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Activate data (mengubah is_active => 1)
export function* cancelFixedAssetRevaluation({ payload }) {
  const { id_fixed_asset_revaluation, body } = payload;

  try {
    // POST ke API
    const cancelResult = yield call(
      putApi,
      "fixed-assets/revaluations/cancel-transaction/" + id_fixed_asset_revaluation,
      body
    );

    // Jika sukses
    if (cancelResult.status === "success") {
      yield all([
        yield put({
          type: 'CANCEL_FIXED_ASSET_REVALUATION_SUCCESS',
          success: cancelResult
        }),
        yield put({
          type: 'EDIT_FIXED_ASSET_REVALUATION_REQUEST',
          payload: { id_fixed_asset_revaluation }
        })
      ])
    }
    else if(cancelResult.status === "fail") {
      yield put({
        type: 'CANCEL_FIXED_ASSET_REVALUATION_ERROR',
        error: cancelResult
      })
    }
    else {
      yield put({
        type: 'CANCEL_FIXED_ASSET_REVALUATION_ERROR',
        error: {message: "Check your internet connection"}
      })
    }
  } catch (error) {
    yield put({
      type: 'CANCEL_FIXED_ASSET_REVALUATION_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Upload Document
export function* uploadFixedAssetRevaluationDocument({ payload }) {
  const { id_fixed_asset_revaluation, body } = payload;
  let data = {
    ...body,
    id_fixed_asset_revaluation 
  }

  // console.log(data);

  try {
    // POST ke API
    //console.log("masukk")
    const uploadResult = yield call(
      postForm,
      "fixed-assets/revaluations/documents",
      data
    );

    // console.log(uploadResult)

    // Jika sukses
    if (uploadResult.status === "success") {
      yield all([
        yield put({
          type: 'UPLOAD_FIXED_ASSET_REVALUATION_DOCUMENT_SUCCESS',
          success: uploadResult
        }),
        yield put({
          type: 'EDIT_FIXED_ASSET_REVALUATION_REQUEST',
          payload: { id_fixed_asset_revaluation }
        })
      ])
    }
    else if(uploadResult.errors){
      yield put({
        type: 'UPLOAD_FIXED_ASSET_REVALUATION_DOCUMENT_ERROR',
        error: {message: uploadResult.errors.document[0]}
      })
    }
    else if(uploadResult.status === "fail"){
      yield put({
        type: 'UPLOAD_FIXED_ASSET_REVALUATION_DOCUMENT_ERROR',
        error: uploadResult
      })
    }
    else{
      yield put({
        type: 'UPLOAD_FIXED_ASSET_REVALUATION_DOCUMENT_ERROR',
        error: {message: "Check your internet connection"}
      })
    }

    // }
  } catch (error) {
    yield put({
      type: 'UPLOAD_FIXED_ASSET_REVALUATION_DOCUMENT_ERROR',
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}

// Download Document
export function* downloadFixedAssetRevaluationDocument({ payload }) {
  const { id_fixed_asset_revaluation_document } = payload;

  try {
    // POST ke API
    // console.log("masukk")
    yield call(
      get,
      "fixed-assets/revaluations/documents/download/" + id_fixed_asset_revaluation_document,
    );
    // }
  } catch (error) {
    bugsnagClient.notify(error)
  }
}

// Deactivate data (mengubah is_active => 0)
export function* deleteFixedAssetRevaluationDocument({ payload }) {
  const { id_fixed_asset_revaluation_document } = payload;
  // const body = { is_active: 0 };

  try {
    // POST data ke API
    const deleteResult = yield call(
      putApi,
      "fixed-assets/revaluations/documents/" + id_fixed_asset_revaluation_document,
    //  body
    );

    // Jika sukses
    if (deleteResult.status === "success") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_FIXED_ASSET_REVALUATION_DOCUMENT_SUCCESS",
          success: deleteResult
        }),
        // Ulangi GET data untuk refresh
        yield put({
          type: 'EDIT_FIXED_ASSET_REVALUATION_REQUEST',
          payload: { id_fixed_asset_revaluation: deleteResult.result.id_fixed_asset_revaluation }
        })
      ]);
    }
    else if (deleteResult.status === "fail") {
      // Set session message
      yield all([
        yield put({
          type: "DELETE_FIXED_ASSET_REVALUATION_DOCUMENT_ERROR",
          error: deleteResult
        }),
      ]);
    }
    else {
      // Jika gagal set session message
      yield all([
        yield put({
          type: "DELETE_FIXED_ASSET_REVALUATION_DOCUMENT_ERROR",
          error: {message: "Check your internet connection"}
        }),
      ]);
    }
  } catch (error) {
    yield put({
      type: "DELETE_FIXED_ASSET_REVALUATION_DOCUMENT_ERROR",
      error: {message: "Check your internet connection"}
    })
    bugsnagClient.notify(error)
  }
}


export default function* rootSaga() {
  yield all([
    yield takeEvery('GET_FIXED_ASSET_REVALUATIONS_REQUEST', getFixedAssetRevaluations),
    yield takeEvery('GET_IMPORT_FIXED_ASSETS_FAR_REQUEST', getImportFixedAssets),
    yield takeEvery('CREATE_FIXED_ASSET_REVALUATION_REQUEST', createFixedAssetRevaluation),
    yield takeEvery('STORE_FIXED_ASSET_REVALUATION_REQUEST', storeFixedAssetRevaluation),
    yield takeEvery('EDIT_FIXED_ASSET_REVALUATION_REQUEST', editFixedAssetRevaluation),
    yield takeEvery('UPDATE_FIXED_ASSET_REVALUATION_REQUEST', updateFixedAssetRevaluation),
    yield takeEvery('SHOW_FIXED_ASSET_REVALUATION_REQUEST', showFixedAssetRevaluation),
    yield takeEvery('PROCESS_FIXED_ASSET_REVALUATION_REQUEST', processFixedAssetRevaluation),
    yield takeEvery('CANCEL_FIXED_ASSET_REVALUATION_REQUEST', cancelFixedAssetRevaluation),
    yield takeEvery('UPLOAD_FIXED_ASSET_REVALUATION_DOCUMENT_REQUEST', uploadFixedAssetRevaluationDocument),
    yield takeEvery('DOWNLOAD_FIXED_ASSET_REVALUATION_DOCUMENT_REQUEST', downloadFixedAssetRevaluationDocument),
    yield takeEvery('DELETE_FIXED_ASSET_REVALUATION_DOCUMENT_REQUEST', deleteFixedAssetRevaluationDocument),

  ]);
}
