const initState = {
  job_costing_reports: [],
  locations: [],
  accounts: [],
  filter: null,
  filter_select: [],
  search: null,
  loading: false,
  success: null,
  error: null,
  errorField: null
};

export default function jobCostingReportsReducer(state = initState, action) {
  switch (action.type) {
    case "GET_JOB_COSTING_REPORTS_REQUEST": {
      return {
        ...state,
        //job_costing_reports: [],
        loading: true,
        filter: action.payload.filter,
        filter_select: action.payload.filter_select,
        search: action.payload.search,
        errorField: null
      };
    }
    case "GET_JOB_COSTING_REPORTS_SUCCESS": {
      return {
        ...state,
        job_costing_reports: action.job_costing_reports,
        filter: action.filter,
        search: action.search,
        loading: false
      };
    }
    case "GET_JOB_COSTING_REPORTS_ERROR": {
      return {
        ...state,
        loading: false,
        filter_select: [],
        error: action.error.message
      };
    }
    case "GET_JOB_COSTING_REPORT_FOREIGN_KEYS_SUCCESS": {
      return {
        ...state,
        loading: false,
        locations: action.locations,
      };
    }
    case "GET_JOB_COSTING_REPORT_FOREIGN_KEYS_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS":
      return {
        ...state,
        success: null
      };
    case "CLEAR_ERROR":
      return {
        ...state,
        error: null
      };
    case "CLEAR_ERROR_FIELD":
      return {
        ...state,
        errorField: null
      };
    default:
      return state;
  }
}
