import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getJobCostingReports({ payload }) {
  const { page, search, filter } = payload;

  let url = "reports/job-costings?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  if (page) {
    search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  }

  try {
    if(filter){
      const [job_costing_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/job-costings/foreign-keys")
      ]);
    
      // Jika sukses get data job_costing_reports
      if (job_costing_reports.status === "success") {
        yield put({
          type: "GET_JOB_COSTING_REPORTS_SUCCESS",
          job_costing_reports: job_costing_reports.result,
          filter,
          search
        });
      } else if (job_costing_reports.status === "fail") {
        yield put({
          type: "GET_JOB_COSTING_REPORTS_ERROR",
          error: { message: job_costing_reports.message }
        });
      } else {
        yield put({
          type: "GET_JOB_COSTING_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data job_costing_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_JOB_COSTING_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_JOB_COSTING_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_JOB_COSTING_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/job-costings/foreign-keys")
      ]);
  
      yield put({
        type: "GET_JOB_COSTING_REPORTS_SUCCESS",
        job_costing_reports: []
      });
    
      // Jika sukses get data job_costing_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_JOB_COSTING_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_JOB_COSTING_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_JOB_COSTING_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_JOB_COSTING_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_JOB_COSTING_REPORTS_REQUEST", getJobCostingReports)
  ]);
}
