import { all, takeEvery, put, call } from "redux-saga/effects";
import { get } from "../../../helpers/helper";
import { bugsnagClient } from "../../..";

// Get all data
export function* getTopSupplierReports({ payload }) {
  const { search, filter } = payload;

  let url = "reports/item-summaries/top-suppliers?";
  if (search) {
    url += "search=" + search;
  }
  if (filter) {
    search ? (url += "&" + filter) : (url += filter);
  }
  // if (page) {
  //   search || filter ? (url += "&page=" + page) : (url += "page=" + page);
  // }

  try {
    if(filter){
      const [top_supplier_reports, foreign_keys] = yield all([
        call(get, url),
        call(get, "reports/item-summaries/purchases/foreign-keys")
      ]);
    
      // Jika sukses get data top_supplier_reports
      if (top_supplier_reports.status === "success") {
        yield put({
          type: "GET_TOP_SUPPLIER_REPORTS_SUCCESS",
          top_supplier_reports: top_supplier_reports.result,
          filter
        }); 
      } 
      else if (top_supplier_reports.status === "fail") {
        yield put({
          type: "GET_TOP_SUPPLIER_REPORTS_ERROR",
          error: { message: top_supplier_reports.message }
        });
      }
      else {
        yield put({
          type: "GET_TOP_SUPPLIER_REPORTS_ERROR",
          error: { message: "Check your internet connection" }
        });
      }
    
      // Jika sukses get data top_supplier_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TOP_SUPPLIER_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          supplier_types: foreign_keys.result.supplier_types,
          item_categories: foreign_keys.result.item_categories,
          employees: foreign_keys.result.employees,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TOP_SUPPLIER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TOP_SUPPLIER_REPORT_FOREIGN_KEYS_ERROR"
          //error: {message: "Check your internet connection"}
        });
      }
    }
    else{
      const [foreign_keys] = yield all([
        call(get, "reports/item-summaries/purchases/foreign-keys")
      ]);
  
      yield put({
        type: "GET_TOP_SUPPLIER_REPORTS_SUCCESS",
        top_supplier_reports: []
      }); 
    
      // Jika sukses get data top_supplier_report types
      if (foreign_keys.status === "success") {
        yield put({
          type: "GET_TOP_SUPPLIER_REPORT_FOREIGN_KEYS_SUCCESS",
          locations: foreign_keys.result.locations,
          suppliers: foreign_keys.result.suppliers,
          supplier_types: foreign_keys.result.supplier_types,
          item_categories: foreign_keys.result.item_categories,
          employees: foreign_keys.result.employees,
        });
      } else if (foreign_keys.status === "fail") {
        yield put({
          type: "GET_TOP_SUPPLIER_REPORT_FOREIGN_KEYS_ERROR",
          error: { message: foreign_keys.message }
        });
      } else {
        yield put({
          type: "GET_TOP_SUPPLIER_REPORT_FOREIGN_KEYS_ERROR",
          error: {message: "Check your internet connection"}
        });
      }
    }
  } catch (error) {
    yield put({
      type: "GET_TOP_SUPPLIER_REPORTS_ERROR",
      error: { message: "Check your internet connection" }
    });
    bugsnagClient.notify(error)
  }
}

export default function* rootSaga() {
  yield all([
    yield takeEvery("GET_TOP_SUPPLIER_REPORTS_REQUEST", getTopSupplierReports)
  ]);
}
