const initState = {
  current_page: null,
  error: null, //menyimpan hasil kesalahan dari proses upload/importing data
  errors: null, //menyimpan list kesalahan dari data
  foreignData: [], //menyimpan list dari foreign data (tambahan data untuk mendukung data dari api utama)
  listData: [],
  loading: false, //parameter loading untuk upload/post
  loadingList: false, //parameter loading untuk get list
  message: null,
  page_size: null,
  success: null,
  successes: false,
  total: null,
  warning: null //menyimpan hasil peringatan dari proses upload/importing
};

export default function issueReceiptsReducer(state = initState, action) {
  switch (action.type) {
    case "IMPORT_EXCEL_REQUEST": {
      return {
        ...state,
        errors: null,
        loading: true,
        successes: false
      };
    }
    case "IMPORT_EXCEL_SUCCESS": {
      return {
        ...state,
        loading: false,
        success: action.success.message,
        successes: true
      };
    }
    case "IMPORT_EXCEL_ERROR": {
      return {
        ...state,
        loading: false,
        error: action.error.message,
        errors: action.errors
      };
    }
    case "IMPORT_EXCEL_WARNING": {
      return {
        ...state,
        loading: false,
        warning: action.warning.message,
        errors: action.errors
      };
    }

    case "GET_LIST_DATA_REQUEST": {
      return {
        ...state,
        foreignData: [],
        loadingList: true,
        listData: []
      };
    }
    case "GET_LIST_DATA_SUCCESS": {
      return {
        ...state,
        current_page: action.current_page,
        filter: action.filter,
        loadingList: false,
        listData: action.listData,
        page_size: action.page_size,
        search: action.search,
        total: action.total
      };
    }
    case "GET_FOREIGN_LIST_DATA_SUCCESS": {
      return {
        ...state,
        foreignData: action.foreignData
      };
    }
    case "GET_LIST_DATA_ERROR": {
      return {
        ...state,
        loadingList: false,
        error: action.error.message
      };
    }
    case "CLEAR_SUCCESS": {
      return {
        ...state,
        success: null
      };
    }
    case "CLEAR_WARNING": {
      return {
        ...state,
        warning: null
      };
    }
    case "CLEAR_ERROR": {
      return {
        ...state,
        error: null
      };
    }
    case "CLEAR_ERROR_FIELD": {
      return {
        ...state,
        errorField: null
      };
    }
    default:
      return state;
  }
}
